.styles-block {
  padding-top: 77px;
  padding-bottom: 93px;

  background-color: $beige-extralight;

  h2 {
    margin-bottom: 25px;
  }

  p {
    margin: 0;

    margin-bottom: 25px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

.styles-block__links {
  display: flex;
  flex-wrap: wrap;
  margin-top: 42px;

  a {
    margin-top: 13px;
    margin-right: 17px;

    &:last-child {
      margin-right: 0;
    }
  }
}

@include viewport--lg {
  .styles-block {
    padding-bottom: 70px;

    h2 {
      margin-bottom: 29px;

      font-size: 36px;
      line-height: 42px;
    }
  }

  .styles-block__links {
    margin-top: 18px;
  }
}

@include viewport--md {
  .styles-block {
    padding-top: 35px;
    padding-bottom: 53px;

    .container {
      padding: 0 25px;
    }

    h2 {
      margin-bottom: 19px;

      font-size: 28px;
      line-height: 32px;
    }

    p {
      margin-bottom: 13px;

      font-size: 12px;
      line-height: 22px;
    }
  }

  .styles-block__links {
    margin-top: 13px;

    a {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

@include viewport--sm {
  .styles-block {
    .container {
      padding: 0 17px;
    }
  }
}
