.more-here {
  padding: 70px 3px 59px;
  border-bottom: 2px solid $color-beigelight;
  display: flex;
  align-items: center;

  @include viewport--xl {
    padding: 64px 1px 49px;
  }

  @include viewport--md {
    padding: 53px 1px;
    align-items: flex-start;
  }

  &__text {
    @include reset-item;
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    a {
      color: $color-blue;
      transition: color 0.3s;

      &:hover {
        color: $color-darkmain;
      }
    }

    @include viewport--xl {
      font-size: 24px;
      line-height: 36px;
    }

    @include viewport--md {
      line-height: 34px;
    }
  }

  svg {
    fill: transparent;
    margin-right: 20px;
    min-width: 24px;
    min-height: 24px;

    @include viewport--xl {
      margin-right: 10px;
    }

    @include viewport--md {
      min-width: 16px;
      min-height: 16px;
    }
  }
}
