.counter {
  position: relative;
  border: 2px solid $color-extralightgray;
  padding: 18px 48px 18px 8px;

  input[type="number"] {
    width: 35px;
    padding: 0;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    appearance: textfield;
    border: none;
  }

  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    display: none;
  }

  button {
    position: absolute;
    background: none;
    border: none;
    width: 40px;
    height: 30px;
    padding: 0;
    cursor: pointer;
    overflow: hidden;
    margin: 0;

    &::after {
      content: "";
      position: absolute;
      width: 13px;
      height: 13px;
      border: 2px solid $color-lightgray;
      border-top: none;
      border-left: none;
      transition: border-color $default-transition-settings;
    }

    &:hover {
      &::after {
        border-color: $color-darkmain;
      }
    }
  }

  &__plus {
    top: 0;
    right: 8px;

    &::after {
      top: 15px;
      transform: translateX(-50%) rotate(225deg);
    }
  }

  &__minus {
    bottom: 0;
    right: 8px;

    &::after {
      bottom: 15px;
      transform: translateX(-50%) rotate(45deg);
    }
  }
}
