.search-result-empty {
  padding-top: 55px;
  padding-bottom: 155px;
}

.search-result-empty__text {
  margin: 0;

  font-size: 22px;
  line-height: 25px;
  font-weight: bold;
  margin-bottom: 20px;
  letter-spacing: -0.02em;

  span {
    color: $color-darkred;
  }
}

.search-result-empty__text-wrapper {
  max-width: 870px;

  .link {
    font-size: inherit;
  }

  .paragraph {
    font-weight: 600;
  }
}
