.our-promo {
  width: 100%;
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  min-height: 680px;
  display: flex;
  justify-content: flex-end;
  padding: 86px 92px;

  @include viewport--xl {
    padding: 86px 80px;
    min-height: 646px;
  }

  @include viewport--md {
    min-height: 644px;
    padding: 49px 18px;
  }

  .quality-icon {
    @include viewport--sm {
      position: absolute;
      right: 0;
      top: -22px;
    }

    @include viewport--mm {
      position: static;
      margin-left: auto;
    }
  }

  &__img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;

    picture {
      width: 100%;
      height: 100%;
      display: flex;
    }

    img {
      object-fit: cover;
      height: auto;
      width: auto;
      min-width: 100%;
      min-height: 100%;
      object-position: center;
    }
  }

  &__wrapper {
    width: 100%;
    max-width: 581px;
    position: relative;
    z-index: 2;

    @include viewport--xl {
      max-width: 524px;
    }
  }

  &__title {
    @include reset-item;
    font-weight: 500;
    font-size: 48px;
    line-height: 54px;
    letter-spacing: -0.02em;
    color: $color-default-white;
    margin-bottom: 33px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;

    @include viewport--xl {
      margin-bottom: 23px;
      font-size: 44px;
      line-height: 54px;
    }

    @include viewport--md {
      font-size: 32px;
      line-height: 38px;
      margin-bottom: 12px;
      position: relative;
    }

    @include viewport--sm {
      padding-right: 130px;
    }

    @include viewport--mm {
      flex-direction: column-reverse;
      padding-right: 0;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
    letter-spacing: -0.02em;
    color: $color-default-white;

    @include viewport--xl {
      font-size: 18px;
      line-height: 28px;
    }

    @include viewport--md {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
