.post-content {
  padding-top: 49px;
  padding-bottom: 112px;

  .container {
    position: relative;
    padding: 0 18.2%;

    &>img,
    &>picture img {
      margin: 29px auto;
    }
  }

  .tick-list {
    margin-top: 41px;
    margin-bottom: 48px;
  }

  &__announcement {
    margin: 0;

    display: block;
    margin-bottom: 40px;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    font-style: italic;
  }

  .title {
    margin-bottom: 15px;
  }

  .title--incut {
    margin-top: 64px;
    margin-bottom: 54px;

    display: block;
    letter-spacing: -0.02em;
  }

  &--news {
    padding-bottom: 80px;

    .title {
      margin-bottom: 35px;
    }

    .post-content__announcement {
      margin-bottom: 33px;
    }
  }
}

.post-content--text-page {
  width: 67.8%;
  padding-bottom: 188px;
  margin-right: auto;

  .btn {
    padding: 14px 10px 8px 10px;
    display: inline-block;
    margin-top: 8px;
    min-width: 266px;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }

  .tick-list {
    margin-top: 41px;
    margin-bottom: 48px;
  }

  .post-content__text-wrapper {
    margin-bottom: 56px;

    b {
      display: block;
      margin-bottom: 29px;

      font-weight: bold;
      font-size: 34px;
      line-height: 36px;
      letter-spacing: -0.02em;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.post-content__subtitle {
  margin: 0;
}

.post-content--theory {
  padding-bottom: 88px;

  .title {
    margin-bottom: 34px;
  }

  .post-content__subtitle {
    padding-right: 10%;
    line-height: 28px;
  }
}

.post-content__wrapper {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;

  &>p {
    margin: 0;

    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &>img,
  &>picture img {
    margin: 29px auto;
  }
}

.post-content__article-img {
  margin: 29px auto 29px 0;

  img {
    width: 100%;
    height: auto;
  }
}

.modal-submenu__tags-wrapper {
  .tags-list {
    margin-top: 10px;
    margin-bottom: 0;

    @include viewport--lg {
      margin-top: 16px;
    }
  }
}

.tags-list {
  margin-top: 67px;
}

.tags-list__item {
  margin-right: 10px;
  margin-bottom: 10px;
}

.post-content__tags a {
  @include reset-link;
}

@include viewport--xl {
  .post-content--text-page {
    width: 64.7%;

    .post-content__text-wrapper {
      margin-bottom: 59px;
    }

    .tick-list {
      margin-top: 17px;
      margin-bottom: 0;
    }
  }

  .post-content--theory {
    .post-content__subtitle {
      padding-right: 0;
    }
  }
}

@include viewport--lg {
  .post-content {
    padding-bottom: 69px;

    .container {
      padding: 0 11.5%;

      &>img,
      &>picture img {
        margin-top: 22px;
        margin-bottom: 42px;
      }
    }

    &__announcement {
      padding: 0;
      margin-bottom: 28px;
    }

    .title--incut {
      font-size: 100px;
      line-height: 90px;

      margin-top: 48px;
      margin-bottom: 45px;
    }
  }

  .post-content--theory {
    padding-bottom: 76px;

    .title {
      margin-bottom: 30px;

      font-size: 46px;
      line-height: 50px;
    }

    .post-content__subtitle {
      padding-right: 30px;

      font-size: 14px;
      line-height: 24px;
    }
  }

  .post-content--text-page {
    padding-bottom: 130px;
    width: 65.3%;

    .post-content__text-wrapper {
      margin-bottom: 30px;

      b {
        margin-bottom: 21px;

        font-weight: bold;
        font-size: 28px;
        line-height: 32px;
        letter-spacing: -0.02em;
      }
    }

    .tick-list {
      padding-right: 29%;
      margin-top: 25px;
    }
  }

  .post-content__wrapper {

    &>img,
    &>picture img {
      margin-top: 22px;
      margin-bottom: 42px;
    }
  }

  .post-content__article-img {
    margin-bottom: 42px;
    margin-top: 22px;
  }

  .tags-list {
    margin-top: 41px;
  }
}

@include viewport--md {
  .post-content {
    padding-top: 25px;
  }

  .post-content--text-page {
    width: 100%;
    padding-bottom: 41px;

    .title {
      margin-bottom: 19px;
    }

    .plan {
      h2 {
        margin-bottom: 19px;
        font-size: 22px;
        line-height: 26px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }
    }

    .post-content__text-wrapper {
      margin-bottom: 37px;

      b {
        margin-bottom: 16px;

        font-size: 22px;
        line-height: 26px;
      }
    }

    .post-content__announcement {
      font-size: 14px;
      line-height: 24px;
    }

    .tick-list {
      margin-bottom: 5px;
      padding-right: 0;
      margin-top: 21px;
    }
  }

  .post-content--theory {
    padding-top: 25px;

    .title {
      margin-bottom: 23px;

      font-size: 38px;
      line-height: 42px;
    }

    .post-content__subtitle {
      padding-right: 0;

      font-size: 12px;
      line-height: 22px;
    }
  }
}

@include viewport--sm {
  .post-content {
    padding-bottom: 41px;

    .container {
      padding: 0 4.2%;

      &>img,
      &>picture img {
        margin-bottom: 29px;
        margin-top: 18px;
      }
    }

    &__announcement {
      margin-bottom: 22px;

      font-size: 14px;
      line-height: 26px;
    }

    .title--incut {
      margin-top: 73px;
      margin-bottom: 38px;

      font-size: 78px;
      line-height: 72px;

      span {
        display: none;
      }
    }
  }

  .post-content__wrapper {
    &>p {
      margin-bottom: 12px;

      font-size: 12px;
      line-height: 22px;
    }

    &>img,
    &>picture img {
      margin-bottom: 29px;
      margin-top: 18px;
    }
  }

  .post-content__article-img {
    margin-bottom: 29px;
    margin-top: 18px;
  }

  .tags-list__item {
    margin-bottom: 9px;
  }
}

@media (max-width: 374px) {
  .post-content {
    .title--incut {
      font-size: 67px;
    }
  }
}

@media (min-width: 1440px) {
  .post-content {
    margin: 0 auto;

    .container {
      padding: 0;
      max-width: 914px;
    }
  }

  .post-content--text-page {
    margin: 0;
    margin-right: auto;
  }
}
