.link {
  @include reset-link;

  transition: all $default-transition-settings;
}

.text-wrapper a:not([class]) {
  @include reset-link;
  @include link;
  color: $color-blue;
  border-bottom: 2px solid transparent;
  transition: all $default-transition-settings;

  &:hover {
    color: $color-blue;
    border-bottom: 2px solid $color-lightblue;
  }
}

.link-regular {
  @include link;

  &:hover {
    color: $color-blue;
  }

  &--blue { // deprecated, use .text-wrapper a
    color: $color-blue;

    border-bottom: 2px solid transparent;

    &:hover {
      color: $color-blue;

      border-bottom: 2px solid $color-lightblue;
    }
  }

  &--dark {
    color: $color-darkmain;

    border-bottom: 2px solid $color-lightblue;

    &:hover {
      color: $color-blue;

      border-bottom: 2px solid $color-blue;
    }
  }
}

.link-header {
  font-weight: $font-weight--bold;
  font-size: 28px;
  line-height: 32px;
  color: $color-blue;
  letter-spacing: -0.02em;

  border-bottom: 2px solid transparent;

  &--blue {
    color: $color-blue;

    &:hover {
      color: $color-darkmain;

      border-bottom: 2px solid $color-lightblue;
    }
  }

  &--dark {
    color: $color-darkmain;

    &:hover {
      color: $color-blue;

      border-bottom: 2px solid $color-lightblue;
    }
  }
}

.link.hassublist {
  padding-right: 21px;
}

.link--important {
  font-weight: $font-weight--bold;
  font-size: 11px;
  line-height: 14px;
  color: $color-darkmain;

  svg {
    height: 24px;
    width: 24px;
    min-width: 24px;
    margin-right: 8px;
  }

  &:hover {
    color: $color-green;
  }

  @include viewport--lg {
    svg {
      margin-right: 4px;
    }
  }
}

.link--gray {
  color: $color-lightgray;

  &:hover {
    color: $color-darkgray;
  }
}

.link--tel {
  font-weight: $font-weight--medium;
  font-size: 18px;
  line-height: 23px;

  &:hover {
    color: $color-green;
  }
}

.link--simple {
  display: block;
  line-height: 18px;
  color: $color-mediumgray;
  font-size: 11px;
  font-weight: $font-weight--regular;

  &:hover {
    color: $color-darkgray;
  }
}
