.result-slider {
  position: relative;
  overflow: hidden;

  picture {
    width: 100%;
    height: 100%;
    display: flex;
  }

  img {
    min-width: 100%;
    max-width: unset;
    min-height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  .slider-button-fill-next,
  .slider-button-fill-prev {
    position: absolute;
    z-index: 3;
    width: 36px;
    height: 36px;
    top: 50%;
    transform: translateY(-50%);

    @include viewport--md {
      width: 22px;
      height: 22px;
    }
  }

  .slider-button-fill-next {
    right: 20px;
    transform: translateY(-50%) rotate(180deg);

    @include viewport--md {
      right: 12px;
    }
  }

  .slider-button-fill-prev {
    left: 20px;

    @include viewport--md {
      left: 12px;
    }
  }
}
