.platforms {
  padding: 76px 15px 7px 112px;
  display: flex;
  align-items: center;

  @include viewport--lg {
    padding: 54px 15px 7px 81px;
    align-items: flex-start;
  }

  @include viewport--sm {
    flex-direction: column;
    padding: 18px 15px 7px 0px;
  }

  &__text {
    padding: 0;
    max-width: 240px;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    margin: 0 56px 0 0;

    @include viewport--lg {
      max-width: 180px;
      font-size: 14px;
      line-height: 20px;
      margin: 0 25px 0 0;
    }

    @include viewport--sm {
      max-width: 100%;
      margin-bottom: 30px;
    }

  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 41px;

    @include viewport--lg {
      gap: 21px;

      svg {
        max-width: 100px;
      }
    }
  }

  a {
    transition: opacity $default-transition-settings;
  }

  a:hover {
    opacity: 0.7;
  }
}
