.info-list-slider {
  overflow: hidden;
  position: relative;

  @include viewport--lg {
    padding-bottom: 29px;
  }

  .swiper {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      display: block;
      right: -2px;
      top: 0;
      height: 100%;
      width: 122px;
      z-index: 2;
      background-image: linear-gradient(to left,rgba(255,255,255,.8) 0%,rgba(255,255,255,.1) 80%,rgba(255,255,255,0) 100%);
    }
  }

  &__flex {
    margin-bottom: 16px;
    display: flex;
    align-items: center;

    @include viewport--lg {
      margin-bottom: 18px;
    }

    @include viewport--sm {
      margin-bottom: 11px;
    }
  }

  .slider-buttons {
    margin-left: auto;
    height: 48px;
    position: relative;
    z-index: 1;
  }

  &__title {
    margin-right: 15px;
    margin-bottom: 0;
  }
}
