.design {
  display: block;
  position: relative;
  padding: 73px 0 80px;
  margin-bottom: 30px;

  @include viewport--lg {
    padding: 72px 20px 52px 78px;
  }

  @include viewport--md {
    padding: 72px 20px 52px 24px
  }

  @include viewport--sm {
    padding: 39px 20px 60px 22px;
  }

  &__background {
    position: absolute;
    border-radius: 16px;
    top: 0;
    left: 0;
    min-height: 620px;
    width: 100%;
    z-index: -1;

    @include viewport--lg {
      min-height: 590px;
    }

    @include viewport--lg {
      min-height: 100%;
    }

    background: $design-gradient;
    background-size: 400% 400%;
    animation: AnimationBg 16s ease infinite;
  }

  &__inner {
    z-index: 1;
    display: flex;
    justify-content: space-between;
    max-width: 1137px;
    margin: 0 auto;

    @media(max-width: 829px) {
      flex-direction: column-reverse;
      align-items: center
    }
  }

  &__img {
    width: 445px;
    height: 445px;
    flex: 0 0 auto;
    border-radius: 100%;
    background-color: $color-darkmain;
    margin-right: 15px;

    @include viewport--lg {
      width: 330px;
      height: 330px;
    }

    @include viewport--sm {
      margin-top: 14px;
      margin-left: 14px;
      width: 304px;
      height: 304px;
    }

    @include viewport--mm {
      width: 275px;
      height: 275px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 100%;
    }
  }

  &__descriptions {
    max-width: 557px;
    padding: 20px 47px 0 0;

    @include viewport--lg {
      padding: 0 22px 0 63px;
    }

    @include viewport--md {
      padding: 0 22px 0 20px;
    }

    @include viewport--sm {
      padding: 0;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-default-white;
    margin: 0 0 30px 0;

    @include viewport--lg {
      font-size: 36px;
      line-height: 46px;
      margin: 0 0 25px 0;
    }

    @include viewport--sm {
      font-size: 28px;
      line-height: 46px;
      margin: 0 0 15px 0;
    }
  }

  &__descriptions-text {
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
    color: $color-beigelight;
    margin: 0 0 30px 0;

    @include viewport--lg {
      font-size: 18px;
      line-height: 28px;
      margin: 0 0 25px 0;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 24px;
      margin: 0 0 18px 0;
    }
  }

  &__descriptions-subtext {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-beigelight;

    @include viewport--sm {
      font-size: 12px;
      line-height: 22px;
    }
  }
}


@keyframes AnimationBg {
  0% {
    background-position: 0% 50%
  }

  50% {
    background-position: 100% 50%
  }

  100% {
    background-position: 0% 50%
  }
}
