.header-links {
  background-color: $color-beigelight;

  .container {
    justify-content: space-between;

    @include viewport--xl {
      padding-right: 35px;
    }

    @include viewport--lg {
      padding-right: 35px;
    }

    @include viewport--md {
      padding-right: 25px;
    }

    @include viewport--sm {
      display: none;
    }
  }

  &__whom,
  &__sub {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    width: 50%;
    padding-top: 15px;
    padding-bottom: 12px;
    min-height: 50px;
    height: 1px;
    overflow: visible;
  }

  &__item {
    @include reset-item;

    .link {
      height: 100%;
      display: flex;
      align-items: center;
    }
  }

  &__whom {
    margin-left: -13px;
    flex-grow: 1;

    .header-links__item {
      padding: 0 13.5px;

      &:last-of-type {
        padding-right: 0;
      }
    }
  }

  &__sub {
    justify-content: flex-end;
    padding-right: 41px;
    position: relative;
    right: -16px;
    flex-grow: 0;

    svg {
      width: 24px;
      height: 24px;
      min-width: 24px;
    }

    @include viewport--xl {
      padding-right: 0;
    }

    .header-links__item {
      padding: 0 16px;

      @include viewport--lg {
        padding: 0 17px;
      }

      &:first-of-type {
        padding-left: 0;
      }
    }
  }
}
