.pay-success {
  padding-top: 49px;
  padding-bottom: 76px;

  .breadcrumbs {
    display: flex;
    justify-content: center;
  }

  .title {
    text-align: center;
    margin-bottom: 20px;

    span {
      color: $color-green;
    }
  }
}

.pay-success__wrapper {
  display: flex;
  margin-bottom: 74px;
  padding: 0 19% 0 17.2%;

  img,
  picture {
    flex-shrink: 0;
  }

  img {
    width: 241px;
    height: auto;
    object-fit: cover;
    margin-right: 16px;
  }
}

.pay-success__text-wrapper {
  padding-top: 26px;

  font-weight: 500;
  font-size: 16px;
  line-height: 28px;

  p {
    margin: 0;

    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@include viewport--lg {
  .pay-success {
    padding-bottom: 61px;

    .title {
      margin-bottom: 1px;
    }
  }

  .pay-success__wrapper {
    padding: 0 9%;
    margin-bottom: 44px;

    img {
      margin-right: -3px;
      width: 204px;
      height: 193px;
    }
  }

  .pay-success__text-wrapper {
    padding-top: 29px;

    font-size: 14px;
    line-height: 24px;
  }
}

@include viewport--md {
  .pay-success {
    padding-top: 25px;
    padding-bottom: 101px;

    .title {
      margin-bottom: 24px;
    }
  }

  .pay-success__wrapper {
    flex-direction: column;
    padding: 0;
    margin-bottom: 40px;

    img {
      margin: 0 auto;
      margin-bottom: -20px;

      width: 184px;
      height: 174px;
    }
  }

  .pay-success__text-wrapper {
    padding-top: 29px;
    text-align: center;

    font-size: 12px;
    line-height: 22px;
  }
}
