.socials-list {
  position: absolute;
  left: 5.8%;
  top: 23px;

  .ya-share2__title {
    display: none;
  }

  li {
    height: 54px;
    margin: 0;
  }
}

@media (min-width: 1440px) {
  .socials-list {
    left: -19.7%;
  }
}

@include viewport--lg {
  .socials-list {
    left: 2%;

    li {
      height: 50px;
    }
  }
}

@include viewport--sm {
  .socials-list .ya-share2__list.ya-share2__list_direction_vertical > .ya-share2__item:first-child {
    margin-top: 4px;
  }

  .socials-list {
    position: static;
    margin-top: 20px;

    ul {
      display: flex;
      align-items: center;
      margin-bottom: 18px;

      flex-wrap: wrap;
    }

    li {
      margin: 0;
    }
  }
}
