.reviews-slider {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    left: 0;
    top: -25px;
    width: 143px;
    height: calc(100% + 50px);
    background: linear-gradient(to right, rgba($beige-extralight, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    right: 0;
    top: -25px;
    width: 143px;
    height: calc(100% + 50px);
    background: linear-gradient(to left, rgba($beige-extralight, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  .slider-button-fill-next {
    position: absolute;
    z-index: 3;
    right: 32px;
    top: 50%;
    transform: rotate(180deg) translateY(50%);
  }

  .slider-button-fill-prev {
    position: absolute;
    z-index: 3;
    left: 32px;
    top: 50%;
    transform: translateY(-50%);
  }

  .swiper-wrapper {
    position: relative;
  }

  &--simple {
    .swiper-wrapper {
      position: relative;
      left: auto;
      margin-left: 0;
    }
  }

  .swiper-slide {
    width: 337px;


    img {
      transition: $default-transition-settings;
      border-radius: 8px 8px 0 0;
      height: 236px;
      width: 100%;
      object-fit: cover;
    }

    .account__avatar {
      img {
        height: 45px;
        border-radius: 50%;
      }
    }
  }
}

@include viewport--lg {
  .reviews-slider {
    .swiper-wrapper {
      margin-left: -813px;
    }

    &--main {
      .swiper-wrapper {
        margin-left: 0;
      }
    }
  }
}

@include viewport--md {
  .reviews-slider {
    .swiper-wrapper {
      margin-left: -472px;
    }

    .swiper-slide {
      width: 260px;

      .account__avatar {
        img {
          height: 47px;
        }
      }

      img {
        height: 166px;
      }
    }

    &--main {
      .swiper-wrapper {
        margin-left: 0;
      }
    }
  }
}

@include viewport--sm {
  .reviews-slider {
    &::before {
      width: 50px;
      height: 100%;
    }

    &::after {
      width: 50px;
      height: 100%;
    }
  }
}
