.wallpaper-categories {
  padding-top: 41px;
  padding-bottom: 100px;

  .title {
    margin: 0;
    margin-bottom: 30px;
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
  }

  .wallpaper-categories-list {
    margin-bottom: 75px;
  }

  &__tags {
    .title {
      display: block;
      margin-bottom: 15px;
    }
  }

  .tags-list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-top: -10px;
  }

  .tags-list__item {
    @include reset-item;
    margin-left: 10px;
    margin-top: 10px;
  }
}

@include viewport--xl {
  .wallpaper-categories {
    padding-bottom: 91px;
  }
}

@include viewport--lg {
  .wallpaper-categories {
    padding-top: 34px;
    padding-bottom: 84px;

    .title {
      margin-bottom: 25px;
    }
  }
}

@include viewport--md {
  .wallpaper-categories {
    padding-top: 0;
    padding-bottom: 54px;

    .title {
      margin-bottom: 17px;
      line-height: 28px;
    }

    p {
      font-size: 14px;
      line-height: 22px;
    }

    &__tags {
      .title {
        margin-bottom: 20px;
      }
    }

    .wallpaper-categories-list {
      margin-bottom: 47px;
    }
  }
}
