@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Mont";

  font-display: swap;
  src:
    local("mont-Light"),
    local("mont-Light"),
    url("../fonts/mont-light.woff2") format("woff2"),
    url("../fonts/mont-light.woff") format("woff");
}

@font-face {
  font-style: italic;
  font-weight: 300;
  font-family: "Mont";

  font-display: swap;
  src:
    local("mont-LightItalic"),
    local("mont-LightItalic"),
    url("../fonts/mont-lightitalic.woff2") format("woff2"),
    url("../fonts/mont-lightitalic.woff") format("woff");
}

