.color-proof {
  display: flex;
  padding: 17px 24px 22px 19px;
  align-items: center;

  transition: border $default-transition-settings, background-color $default-transition-settings;
  border: 2px solid $color-beigelight;
  border-radius: 4px;
  background-color: transparent;
  cursor: pointer;

  svg {
    flex-shrink: 0;
    margin-right: 22px;
    width: 59px;
    min-width: 59px;
    height: 56px;
  }

  b {
    margin-right: 27px;
    display: block;

    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.02em;
    text-align: left;
  }

  &:hover {
    background-color: $beige-extralight;
    border: 2px solid $beige-extralight;

    .color-proof__desc {
      border-left: 1px solid $color-beigelight;
    }
  }
}

.color-proof__desc {
  padding-left: 23px;
  padding-top: 3px;

  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.02em;
  text-align: left;

  border-left: 1px solid $beige-extralight;
  color: $color-mediumgray;
  transition: border-left $default-transition-settings;
}

.color-proof__wrapper {
  display: flex;
  align-items: center;
}

.color-proof-form {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  opacity: 0;
  padding: 115px 56px 56px 56px;

  height: 0;

  background-color: #ffffff;
  transition: $default-transition-settings;

  i {
    margin-bottom: 40px;
    display: block;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.02em;
  }

  .checkbox-select {
    margin-bottom: 39px;
  }

  &.is-active {
    opacity: 1;
    z-index: 1;
    height: auto;
    min-height: 100%;
  }
}

.color-proof-form__header-wrapper {
  margin-bottom: 14px;

  .title {
    display: inline;
    position: relative;
    padding-right: 69px;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;

      width: 51px;
      height: 48px;

      background-size: contain;
      background-repeat: no-repeat;
      background-image: url("../img/sprite/icon-colors.svg");
    }
  }
}

.color-proof-form__price-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 7px;

  b {
    display: block;
    margin-right: 33px;
    width: max-content;
    font-weight: 800;
    font-size: 36px;
    line-height: 44px;
    letter-spacing: -0.02em;
    white-space: nowrap;
  }

  i {
    width: 200px;
    margin-bottom: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: -0.02em;
  }

  a {
    margin-left: auto;
  }
}


@include viewport--sm {
  .color-proof {
    padding: 14px 24px 15px 19px;
    align-items: start;

    &:hover {
      .color-proof__desc {
        border-left: 0;
      }
    }

    b {
      margin-bottom: 3px;
      margin-right: 0;
      text-align: left;
      font-size: 15px;
      line-height: 20px;
    }

    span {
      padding: 0;
      font-size: 11px;
    }
  }

  .color-proof__desc {
    border-left: none;
  }

  .color-proof-form {
    padding: 70px 29px 29px;
    width: 100%;

    i {
      font-size: 12px;
      line-height: 20px;
    }

    .checkbox-select {
      margin-bottom: 27px;

      button {
        padding: 18px 45px 20px 25px;

        span {
          font-size: 12px;
          line-height: 20px;
        }
      }
    }
  }

  .color-proof-form__header-wrapper {
    margin-bottom: 10px;

    .title {
      padding-right: 42px;

      &::after {
        width: 34px;
        height: 32px;
      }
    }
  }

  .color-proof-form__price-wrapper {
    b {
      margin-right: 23px;
      font-size: 28px;
      line-height: 44px;
    }

    i {
      font-size: 11px;
      line-height: 16px;
    }

    .cost-calculate__about {
      display: none;
    }
  }

  .color-proof__wrapper {
    flex-direction: column;
    align-items: start;
  }
}
