.link-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .title {
    margin: 0;
    //margin-top: 6px;
    margin-right: 30px;
  }

  .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 10px 8px 10px;
    width: 191px;

    font-weight: bold;
    font-size: 12px;
    text-align: center;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}
