.footer {
  margin-top: auto;
  overflow: hidden;
  padding-top: 73px;
  color: $color-darkmain;
  background-color: $color-beigelight;

  @include viewport--lg {
    padding-top: 75px;
  }

  @include viewport--sm {
    padding-top: 30px;
  }
}

@include viewport--sm {
  .footer .container {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.footer__contacts-columns-wrapper {
  margin-bottom: 24px;
  display: flex;
  flex-wrap: wrap;

  @include viewport--xl {
    margin-bottom: 8px;
  }

  @include viewport--lg {
    margin-bottom: 27px;
  }

  @include viewport--sm {
    flex-direction: column;
    margin-bottom: 38px;
  }
}

.footer__contacts {
  width: 21.5%;
  min-width: 190px;
  margin-right: 54px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @include viewport--xl {
    margin-right: 25px;
  }

  @include viewport--lg {
    margin-right: 50px;
    width: 17.8%;
  }

  @include viewport--sm {
    margin-right: 0;
    width: 100%;
  }
}

.footer__office-address {
  margin-bottom: 31px;

  b {
    display: block;
    margin-bottom: 11px;

    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.02em;
  }

  @include viewport--md {
    margin-bottom: 22px;
  }
}

.footer__address {
  margin-bottom: 50px;
  display: flex;
  align-items: center;

  @include viewport--xl {
    margin-bottom: 62px;
  }

  @include viewport--lg {
    margin-bottom: 36px;
    flex-direction: column;
    align-items: flex-start;
  }

  @include viewport--sm {
    margin-bottom: 32px;
    flex-direction: row;
  }
}

.footer__address img {
  margin-right: 15px;

  @include viewport--xl {
    width: 72px;
    height: 79px;
  }

  @include viewport--lg {
    margin-right: 0;
    margin-bottom: 12px;
    width: 84px;
    height: 92px;
  }

  @include viewport--sm {
    margin-bottom: 0;
    margin-right: 13px;
    width: 72px;
    height: 77px;
  }
}

.footer__address p {
  font-family: $font-family--Mont;
  font-size: 12px;
  line-height: 20px;
  font-weight: $font-weight--medium;
  max-width: 195px;

  @include viewport--sm {
    padding-top: 9px;
  }
}

.footer__contacts-logos {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @include viewport--sm {
    margin-bottom: 34px;
  }

  .footer__office-address {
    p {
      &:last-child {
        color: $color-darkmain;
      }
    }
  }
}

.footer__logos-wrapper {
  padding-top: 11px;
  margin-bottom: 15px;
  display: flex;
  flex-wrap: wrap;

  .footer__whatsapp,
  .footer__telegram {
    @include reset-link;
    color: $color-default-black;
    font-size: 11px;
    line-height: 16px;
  }

  .footer__whatsapp svg {
    width: 78px;
    min-width: 78px;
    height: 18px;
  }

  .footer__telegram svg {
    width: 70px;
    min-width: 70px;
    height: 18px;
  }

  @include viewport--xl {
    margin-bottom: 10px;
  }

  @include viewport--lg {
    margin-bottom: 6px;
  }

  @include viewport--sm {
    margin-bottom: 11px;
  }
}

.footer__logos-wrapper svg:first-child {
  margin-right: 21px;
}

.footer__mobile-phone {
  margin-bottom: 4px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--xl {
    margin-bottom: 2px;
    font-size: 24px;
  }

  @include viewport--lg {
    font-size: 20px;
  }

  @include viewport--sm {
    margin-bottom: 4px;
    font-size: 28px;
  }
}

.footer__city-phone {
  margin-bottom: 4px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 24px;
  line-height: 26px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--xl {
    margin-bottom: 2px;
  }

  @include viewport--lg {
    margin-bottom: 0;
    font-size: 20px;
  }

  @include viewport--sm {
    font-size: 22px;
  }
}

.footer__contacts-logos p {
  margin: 0;
  font-family: $font-family--Mont;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}

.footer__contacts-logos hr {
  margin-bottom: 30px;
  margin-top: 24px;
  background-color: $color-lightgray;

  @include viewport--xl {
    margin-bottom: 27px;
    margin-top: 27px;
  }

  @include viewport--lg {
    margin-bottom: 17px;
    margin-top: 17px;
  }

  @include viewport--md {
    margin-bottom: 27px;
    margin-top: 26px;
    width: 100%;
  }
}

.footer__contacts-bottom-wrapper {
  .separator {
    display: none;
  }

  @include viewport--sm {
    .separator {
      display: block;
      margin-bottom: -1px;
    }
  }
}

.footer__city-phone-wrapper {
  margin-bottom: 54px;

  p {
    font-weight: 600;
    color: $color-mediumgray;
  }

  @include viewport--md {
    margin-bottom: 33px;
  }
}

.footer__all-contacts-link {
  @include reset-link;
  font-family: $font-family--Mont;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-darkgray;
  transition: color $default-transition-settings;

  &:hover {
    color: $color-blue;
  }
}

.footer__columns-wrapper {
  padding-top: 11px;
  display: flex;
  align-items: flex-start;
  flex-grow: 1;

  @include viewport--xl {
    padding-top: 9px;
  }

  @include viewport--sm {
    padding-top: 0;
    flex-direction: column;
  }
}

.footer__column {
  padding-top: 0;
  width: 30%;

  @include viewport--sm {
    width: auto;
  }

  &:not(:nth-child(3n)) {
    margin-right: 4%;

    @include viewport--lg {
      margin-right: 4.4%;
    }

    @include viewport--sm {
      margin-right: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &:last-of-type .footer__column-item {
    margin-bottom: 19px;

    @include viewport--xl {
      margin-bottom: 16px;
    }

    @include viewport--lg {
      margin-bottom: 57px;
    }

    @include viewport--sm {
      margin-bottom: 34px;
    }
  }
}

.footer__column-item {
  margin-bottom: 42px;

  @include viewport--lg {
    margin-bottom: 41px;
  }

  @include viewport--sm {
    margin-bottom: 20px;
  }
}

.footer__list {
  @include reset-list;

  @include viewport--sm {
    display: none;
    padding-left: 30px;
  }
}

.footer__item {
  @include reset-item;

  @include viewport--lg {
    margin-bottom: 12px;
    max-width: 160px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  @include viewport--sm {
    margin-bottom: 17px;
  }
}

.footer__link {
  @include reset-link;
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 14px;
  line-height: 36px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  &:hover {
    color: $color-darkgray;
  }

  @include viewport--lg {
    font-size: 13px;
    line-height: 18px;
  }
}

.footer__column-item span {
  display: block;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 14px;
  line-height: 22px;
  color: $color-darkmain;
  position: relative;
  margin-bottom: 14px;
  letter-spacing: 0.01em;

  @include viewport--lg {
    margin-bottom: 19px;
  }

  @include viewport--sm {
    display: inline-block;
    padding-right: 27px;
    margin-bottom: 0;
    font-size: 14px;
    cursor: pointer;
  }
}

.footer__switch-menu-button.switсh-menu {
  &::after {
    display: none;
    position: absolute;
    content: "";
    top: 3px;
    right: 0;
    width: 14px;
    height: 14px;
    background: url("../img/svg/arrow-down.svg") no-repeat 0 3px;
  }

  &--active {
    &::after {
      transform: rotate(180deg);
      transition: all $default-transition-settings;
    }

    & + ul {
      display: block;
    }
  }

  @include viewport--sm {
    &--active {
      margin-bottom: 14px;
    }

    &::after {
      display: block;
    }
  }
}

.footer__pay-button {
  @include reset-link;
  color: $color-default-white;
  font-size: 10px;
  line-height: 14px;
  letter-spacing: 0.05em;
  max-width: 170px;
  word-break: break-word;

  @include viewport--sm {
    width: 170px;
  }
}

.footer__social-warranty-wrapper {
  padding: 37px 0;
  display: flex;
  align-items: center;

  @include viewport--xl {
    padding: 28px 0;
  }

  @include viewport--lg {
    padding-top: 31px;
    padding-bottom: 23px;
  }

  @include viewport--sm {
    align-items: flex-start;
    flex-wrap: wrap;
    padding-top: 28px;
    padding-bottom: 30px;
  }
}

.footer__social-list {
  margin-right: 14.5%;
  display: flex;

  @include viewport--xl {
    margin-right: 13%;
    transform: translateY(3px);
  }

  @include viewport--lg {
    transform: translateY(0);
    margin-right: 6.5%;
  }

  @include viewport--sm {
    margin-bottom: 19px;
  }
}

.footer__social-item {
  margin-right: 11px;

  &:last-of-type {
    margin-right: 0;
  }

  @include viewport--lg {
    margin-right: 12px;
  }

  svg {
    stroke-width: 0;
    stroke: $color-mediumgray;
    fill: $color-mediumgray;
    width: 42px;
    min-width: 42px;
    height: 42px;
  }
}

.footer__social-item a {
  line-height: 0;
  font-size: 0;
}

.footer__social-item svg:hover {
  fill: $color-darkmain;
  stroke: $color-darkmain;
}

.footer__payment-systems {
  margin-right: auto;
  display: flex;
  transform: translateY(-2px);
  align-items: center;

  @include viewport--xl {
    transform: translateY(2px);
  }

  @include viewport--lg {
    transform: translateY(-3px);
  }

  @include viewport--sm {
    display: none;
  }
}

.footer__payment-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;

  @include viewport--lg {
    transform: translateY(5px);
  }
}

.footer__payment-item {
  svg {
    width: 42px;
    min-width: 42px;
    height: 42px;
  }

  .footer__payment-item--visa svg {
    width: 70px;
    min-width: 70px;
    height: 30px;
  }

  .footer__payment-item--mastercard svg {
    width: 90px;
    min-width: 90px;
    height: 32px;
  }

  @include reset-item;
  margin-right: 28px;

  @include viewport--xl {
    margin-right: 25px;
  }

  @include viewport--lg {
    margin-right: 23px;

    &:last-child {
      margin-right: 16px;
    }
  }

  @media (max-width: 970px) {
    margin-right: 15px;
  }
}

@include viewport--xl {
  .footer__payment-item--visa svg {
    width: 62px;
    height: 30px;
  }

  .footer__payment-item--mastercard svg {
    width: 80px;
    height: 32px;
  }
}

.footer__payment-systems p {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;
  max-width: 193px;

  @include viewport--lg {
    max-width: 183px;
  }

  @media (max-width: 970px) {
    display: none;
  }
}

.footer__rating-vidgets {
  display: flex;
}

.footer__rating-vidgets iframe {
  margin-right: 30px;
  border: 0;
  border-radius: 4.7px;

  @include viewport--xl {
    margin-right: 23px;
  }

  @include viewport--lg {
    margin-right: 11px;
  }

  @include viewport--sm {
    margin-right: 18px;
  }
}

.footer__zoon-vidget {
  @include viewport--sm {
    align-self: center;
  }
}

.footer__zoon-desktop {
  display: block;
  width: 180px;
  min-width: 180px;
  height: 34px;

  @include viewport--sm {
    display: none;
  }
}

.footer__zoon-mobile {
  display: none;
  width: 76px;
  min-width: 76px;
  height: 36px;

  @include viewport--sm {
    display: block;
  }
}

.footer__copyright {
  padding-top: 28px;
  padding-bottom: 20px;
  background-color: $color-darkmain;
  color: $color-mediumgray;

  @include viewport--lg {
    padding-top: 29px;
    padding-bottom: 21px;
  }

  @include viewport--sm {
    padding-top: 27px;
    padding-bottom: 29px;
  }
}

.footer__copyright .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include viewport--xl {
    padding-left: 32px;
  }

  @include viewport--lg {
    padding-left: 25px;
  }

  @include viewport--sm {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.footer__copyright p {
  margin: 0;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--sm {
    margin-bottom: 21px;
  }
}

.footer__copyright-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.footer__copyright-item {
  @include reset-item;
  line-height: 14px;

  @include viewport--sm {
    margin-bottom: 6px;
  }
}

.footer__copyright-item:not(:last-of-type) {
  margin-right: 38px;

  @include viewport--xl {
    margin-right: 26px;
  }

  @include viewport--lg {
    margin-right: 19px;
  }
}

.footer__copyright-list .link {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.02em;
  color: $color-darkgray;
  font-weight: $font-weight--regular;
}

.footer__bottom-separator {
  background-color: $color-lightgray;

  @include viewport--sm {
    transform: translateX(-60px);
    width: 200%;
  }
}
