.input-wrapper {
  svg {
    width: 16px;
    height: 16px;
    min-width: 16px;
  }

  &:not(:last-child) {
  }
  margin-bottom: 24px;

  @include viewport--sm {
    margin-bottom: 15px;
  }

  input {
    caret-color: $color-darkmain;
  }
}

.input-wrapper--regular {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;

  input {
    border: 2px solid $color-extralightgray;
    width: 100%;
    height: 65px;

    padding: 20px 25px 15px;
    cursor: pointer;
    position: relative;
    font-size: 14px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    line-height: 14px;

    & ~ label {
      font-size: 14px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      padding-left: 26px;
      padding-right: 18px;
      width: 100%;
      cursor: pointer;
      z-index: 1;
      color: $color-mediumgray;
      opacity: 1;
      font-weight: $font-weight--regular;
      transition: font-size $default-transition-settings, top $default-transition-settings;
      will-change: transform;
    }
  }

  .input--focused {
    font-size: 14px;
    border: 2px solid $color-mediumgray;
    outline: none;
    font-weight: $font-weight--regular;

    & ~ label {
      font-size: 12px;
      transition: font-size $default-transition-settings, top $default-transition-settings;
      top: 15px;
      position: absolute;
      width: 100%;
      opacity: 1;
    }
  }

  .input--valued {
    & ~ label {
      font-size: 12px;
      top: 15px;
      position: absolute;
      width: 100%;
      opacity: 1;
    }
  }

  &.valid {
    &::after {
      content: "";
      position: absolute;
      background: url("../img/sprite/icon-v-green.svg") no-repeat;
      background-size: contain;
      width: 20px;
      height: 15px;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }

    & .input--focused ~ label::after {
      top: 29px;
    }

    & .input--valued ~ label::after {
      top: 29px;
    }
  }

  &.invalid {
    input {
      border: 2px solid $color-red;
    }

    &::after {
      content: "";
      position: absolute;
      background: url("../img/sprite/icon-x-red.svg") no-repeat;
      background-size: contain;
      width: 14px;
      height: 14px;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }

    & .input--focused ~ label::after {
      top: 29px;
    }

    & .input--valued ~ label::after {
      top: 29px;
    }
  }

  &.invalid ~ .invalid-message {
    margin: 0;

    display: block;
    font-weight: $font-weight--regular;
    font-size: 12px;
    line-height: 18px;

    letter-spacing: -0.02em;
    color: $color-red;
  }
}

.input-wrapper--textarea {

  display: flex;
  flex-direction: column;
  position: relative;
  width: 640px;

  label {
    font-size: 14px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 26px;
    padding-right: 18px;
    width: 100%;
    cursor: pointer;
    z-index: 1;
    color: $color-mediumgray;
    opacity: 1;
    font-weight: $font-weight--regular;
  }

  textarea {
    height: 235px;
    padding: 52px 28px 10px 25px;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.02em;
    border: 2px solid $color-extralightgray;

    & ~ label {
      font-size: 12px;
      top: 30px;
      position: absolute;
      width: 90%;
      cursor: pointer;
      z-index: 1;
      color: $color-mediumgray;
      opacity: 1;

      svg {
        width: 22px;
        min-width: 22px;
        height: 6px;
        margin-left: 12px;
        fill: $color-lightgray;
        transition: fill $default-transition-settings;
      }
    }

    &:-ms-input-placeholder {
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &::placeholder {
      opacity: 1;
      padding-top: 3px;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &.input--focused {
      padding: 52px 28px 53px 25px;
      border-color: $color-lightgray;

      &::placeholder {
        opacity: 0;
      }

      & ~ label {
        top: 30px;

        svg {
          fill: $color-blue;
        }
      }
    }

    &.input--valued {
      padding: 52px 28px 53px 25px;

      &::placeholder {
        opacity: 0;
      }

      & ~ label {
        top: 30px;

        svg {
          fill: $color-green;
        }
      }
    }
  }
}

// чекбокс

.input-wrapper--checkbox {
  display: flex;
  align-items: center;
}

.checkbox-label {
  position: relative;

  padding-left: 42px;
  font-size: 13px;
  line-height: 28px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
  font-weight: $font-weight--regular;
  user-select: none;

  cursor: pointer;

  a {
    font-size: inherit;
    font-weight: inherit;
    line-height: inherit;

    color: $color-blue;

    transition: color $default-transition-settings;

    &:hover {
      color: $color-lightblue;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;

    display: inline-block;
    width: 28px;
    height: 28px;

    background-color: $color-beigelight;
    border: 1px solid transparent;
    border-radius: 2px;
    cursor: pointer;
    transition: border $default-transition-settings;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    z-index: 2;

    display: inline-block;
    width: 28px;
    height: 28px;
    background: url("../img/sprite/icon-check-white.svg") no-repeat center;
    background-size: 18px 14px;
  }
}

.input-wrapper--checkbox input {
  width: 0;
  height: 0;
  margin: 0;

  opacity: 0;
}

.input-wrapper--checkbox input + label::after {
  content: none;
}

.input-wrapper--checkbox input:checked + label::after {
  content: "";
}

.input-wrapper--checkbox input:checked + label::before {
  background-color: $color-green;
}

// радиокнопка

.input-wrapper--radio {
  display: flex;
  align-items: center;
}

.radio-label {
  position: relative;

  padding-left: 42px;
  font-size: 13px;
  line-height: 28px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;

    display: inline-block;
    width: 24px;
    height: 24px;
    border-radius: 50%;

    background-color: $color-beigelight;
    border: 1px solid transparent;
    cursor: pointer;
    transition: border $default-transition-settings;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%) translateX(100%);
    z-index: 2;

    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: $color-default-white;
    background-size: 18px 14px;
  }
}

.input-wrapper--radio input {
  width: 0;
  height: 0;
  margin: 0;

  opacity: 0;
}

.input-wrapper--radio input + label::after {
  content: none;
}

.input-wrapper--radio input:checked + label::after {
  content: "";
}

.input-wrapper--radio input:checked + label::before {
  background: $color-green;
}

.input-wrapper--city {
  label {
    display: flex;
    justify-content: space-between;
    align-items: center;

    svg {
      fill: $color-lightgray;
      position: relative;
      top: 0;
    }
  }

  .input--focused {
    & ~ label {
      svg {
        fill: $color-blue;
        top: 17px;
      }
    }
  }

  .input--valued {
    & ~ label {
      svg {
        top: 17px;
      }
    }
  }

  .city {
    position: absolute;
    background-color: $color-default-white;
    top: 65px;
    width: 100%;
    z-index: 10;

    ul {
      @include reset-list;

      height: 100%;
      max-height: 230px;
      overflow-y: auto;
      overflow-x: hidden;
      display: block;

      &.bordered {
        border: 2px solid $color-extralightgray;
        border-top: none;
      }

      &.hidden {
        display: none;
      }

      li {
        @include reset-item;
        height: 46px;
        width: 100%;
        cursor: pointer;
        padding-left: 25px;
        display: flex;
        align-items: center;

        &:hover {
          background-color: $color-extralightgray;
        }
      }
    }
  }
}

.input-city {
  position: relative;
  width: 100%;
  height: 300px;
}

.autocomplete-suggestions {
  position: absolute;
  background-color: $color-default-white;
  top: 65px;
  width: 100%;
  z-index: 10;
  max-height: 238px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  display: block;
  border: 2px solid $color-extralightgray;
  border-top: none;
}

.autocomplete-suggestions .autocomplete-suggestion {
  min-height: 46px;
  width: 100%;
  cursor: pointer;
  padding-left: 25px;
  display: flex;
  align-items: center;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.02em;

  &:hover {
    background-color: $color-extralightgray;

    .sup-suggestion {
      display: flex;
    }
  }

  .sup-suggestion {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    margin-left: 10px;
    position: relative;
    display: none;
    align-items: center;

    span {
      position: relative;
      margin-left: 30px;

      &::before {
        content: "→";
        position: absolute;
        padding-right: 15px;
        top: 50%;
        left: -20px;
        transform: translateY(-50%);
        color: $color-mediumgray;
        line-height: 20px;
        font-size: 12px;
      }

      @media (max-width: 375px) {
        margin-left: 20px;

        &::before {
          left: -14px;
        }
      }
    }

    @include viewport--sm {
      margin-left: 5px;
      padding-right: 15px;
    }

    @media (max-width: 375px) {
      margin-left: 0;
    }
  }

  strong {
    display: inline;
    color: $color-green;
    font-weight: $font-weight--medium;
  }

  @media (max-width: 375px) {
    &:hover {
      padding-left: 10px;
    }
  }
}

.input-wrapper--upload {
  width: 640px;

  .input-upload__zone {
    min-height: 80px;
    position: relative;
    margin-bottom: 10px;
    width: 100%;
    overflow: visible;
  }

  label {
    border: 2px dashed $color-extralightgray;
    padding: 24px 28px 24px 25px;
    height: 100%;
    width: 100%;
    transition: all $default-transition-settings;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    font-weight: $font-weight--regular;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.02em;

    span {
      width: 90%;
    }

    svg {
      width: 33px;
      min-width: 33px;
      height: 31px;
      fill: $color-lightgray;
      transition: fill $default-transition-settings;
    }

    &:hover {
      border: 2px dashed $color-blue;
      cursor: pointer;
      color: $color-blue;

      svg {
        fill: $color-blue;
      }
    }
  }

  .input-upload__zone.dragover {
    label {
      border: 2px dashed $color-blue;
      cursor: pointer;
      color: $color-blue;

      svg {
        fill: $color-blue;
      }
    }
  }

  .input-upload__zone > label > input {
    display: none;
  }

  .input-upload__wrapper {
    display: flex;
    justify-content: space-between;
    padding-right: 35px;
    position: relative;
    align-items: center;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      left: 0;
      bottom: 0;
      background-color: $color-beigelight;
    }

    &:last-of-type {
      &::after {
        content: none;
      }
    }

    .filename {
      display: flex;
      font-weight: $font-weight--medium;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: -0.02em;
      color: $color-green;
      padding: 10px 0 10px 25px;
      position: relative;
      min-height: 44px;
      align-items: center;
      width: 90%;
      word-break: break-all;

      &::before {
        content: "";
        position: absolute;
        width: 20px;
        height: 15px;
        left: 0;
        transform: translateY(-50%);
        top: 50%;
        background-image: url("../img/sprite/icon-v-green.svg");
        background-repeat: no-repeat;
      }
    }
  }

  .btn--close-upload {
    width: 20px;
    height: 20px;
    position: relative;


    &::after {
      content: "";
      position: absolute;
      width: 14px;
      height: 14px;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      top: 50%;
      background-image: url("../img/sprite/icon-x-red.svg");
      background-size: 14px;
      background-repeat: no-repeat;
    }
  }

  .input-upload__progress-bar {
    position: relative;
    width: 100%;
    height: 6px;
    background-color: $color-extralightgray;

    span {
      position: absolute;
      height: 6px;
      background-color: $color-green;
    }
  }
}

.bg {
  background-color: $color-beigelight;
  height: 150px;
  padding: 20px 20px;
}

.input-wrapper--withbutton {
  display: flex;
  align-items: center;
  flex-direction: row;

  input {
    height: 50px;
    padding: 15px 25px;
    border: none;
    font-weight: 500;

    &::placeholder {
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &::-ms-input-placeholder {
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &:focus {
      padding: 15px 25px;
      border: none;
    }
  }

  button {
    height: 50px;
  }

  input:focus ~ .btn {
    background-color: $color-green;
  }
}

.input-wrapper--withcircle {
  display: flex;
  align-items: center;
  position: relative;
  flex-direction: row;
  width: 394px;

  input {
    height: 50px;
    padding: 15px 25px;
    border: none;
    max-width: 394px;
    font-weight: 500;

    &::placeholder {
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &::-ms-input-placeholder {
      letter-spacing: -0.02em;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;
    }

    &:focus {
      padding: 15px 25px;
      border: none;
    }
  }

  input:hover ~ .btn {
    background-color: $color-green;
  }

  input:focus ~ .btn {
    background-color: $color-green;
  }
}

.search-input-wrapper {
  position: relative;
  width: 100%;

  svg {
    position: absolute;
    fill: $color-lightgray;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);

    width: 23px;
    height: 22px;
  }

  input {
    padding-left: 24px;
    padding-right: 67px;

    display: block;
    height: 60px;
    width: 100%;

    border: 0;

    color: $color-darkmain;

    &::placeholder {
      font-weight: 500;
      font-size: 16px;
      letter-spacing: -0.02em;

      color: $color-mediumgray;
    }
  }
}
