/*
Служебные стили для разделения компонентов по блокам для удобства.
А то все смешивается и не очень понятно что где.
 */

.components {
  .margin-js {
    background-color: #9acd32;
    color: #ffffff;
    margin-top: 20px;
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .input-wrapper--regular {
    max-width: 640px;
  }

  .input-city {
    max-width: 640px;
  }
}

.components__links,
.components__titles,
.components__texts,
.components__buttons,
.components__selects,
.components__inputs,
.components__breadcrumbs,
.components__tags,
.components__tabs,
.components__infoblocks,
.components__subcategoryes {
  padding: 50px 0;

  border-bottom: 2px solid #000000;

  @include viewport--md {
    padding: 50px 0;
  }

  & > ol > li {
    @include reset-item;
    margin-bottom: 20px;
  }
}

.components__title {
  font-size: 40px;
  line-height: 46px;
  margin: 0;
}

