.map {
  padding-top: 91px;
  background-color: $beige-extralight;

  .container {
    margin-bottom: 29px;
  }

  .title {
    margin-bottom: 20px;

    font-weight: 600;
    margin-top: 0;
    text-align: center;
  }
}

.map__address {
  margin: 0;
  margin-bottom: 18px;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  text-align: center;
}

.map__wrapper {
  position: relative;

  iframe {
    border: 0;
    width: 100%;
    height: 500px;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    pointer-events: none;

    width: 100%;
    height: 51px;
    background: linear-gradient(180deg, #2d3344 0%, rgba(196, 196, 196, 0) 100%);
    opacity: 0.1;
  }
}

@include viewport--lg {
  .map {
    padding-top: 87px;

    .title {
      margin-bottom: 22px;
    }
  }

  .map__address {
    margin-bottom: 20px;

    font-size: 15px;
    line-height: 28px;
  }
}

@include viewport--md {
  .map {
    padding-top: 61px;

    .title {
      margin: 0 auto;
      margin-bottom: 18px;
      width: 78%;
    }
  }

  .map__address {
    margin-bottom: 17px;

    font-size: 14px;
    line-height: 24px;
  }

  .metro-list {
    p {
      font-size: 12px;
      line-height: 28px;
    }
  }

  .map__wrapper {
    iframe {
      height: 400px;
    }
  }
}
