.achievements {
  position: relative;
  display: block;
  padding: 20px 0 36px;
  color: $color-default-white;
  margin-bottom: 17px;

  @include viewport--sm {
    margin-bottom: 10px;
  }

  &__title {
    font-weight: 400;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-beigelight;
    margin: 0 0 55px;

    @include viewport--lg {
      font-size: 36px;
      line-height: 46px;
      margin: 0 0 33px;
    }

    @include viewport--sm {
      font-size: 28px;
      line-height: 46px;
    }
  }

  &__img {
    z-index: -1;
    background-color: #767f86;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;

    img {
      width: 100vw;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }


  &__description {
    max-width: 1150px;
    margin: 0 auto;
    min-height: 802px;
    z-index: 1;
    padding: 157px 20px 0 0;

    @media(min-width: 1480px) {
      max-width: 1132px;
      margin: 0 auto;
      padding: 157px 20px 0 0;
    }

    @include viewport--lg {
      padding: 87px 20px 0 67px;
      min-height: 554px;
    }

    @include viewport--md {
      padding: 97px 0 0 57px;
    }

    @include viewport--sm {
      padding: 97px 0 0 15px;
    }
  }

  &__factoid {
    font-weight: 950;
    font-size: 220px;
    line-height: 220px;
    letter-spacing: 0;
    margin: 0;

    sup {
      top: - 0.7em;
      font-size: 62%;
    }

    @include viewport--lg {
      font-size: 140px;
      line-height: 140px;
    }

    @include viewport--md {
      font-size: 130px;
      line-height: 130px;
    }

    @include viewport--sm {
      font-size: 72px;
      line-height: 68px;
      margin-bottom: 10px;
    }

    @include viewport--mm {
      font-size: 52px;
      line-height: 68px;
      margin-bottom: 10px;
    }
  }

  &__factoid-text {
    position: relative;
    font-weight: 500;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: -0.02em;
    color: $color-beigelight;
    max-width: 1050px;
    margin: 0 0 117px;
    padding-right: 10px;

    @include viewport--lg {
      max-width: 905px;
      font-size: 20px;
      line-height: 30px;
      margin: 0 0 39px;
      padding-right: 25px;
    }

    @include viewport--md {
      font-size: 18px;
      line-height: 35px;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 25px;
      margin: 0 0 32px;
    }

    span {
      display: inline-block;
      position: relative;
      padding-right: 45px;

      @include viewport--md {
        padding-right: 70px;
      }

      img {
        display: inline-block;
        position: absolute;
        margin-left: 10px;

        @include viewport--lg {
          width: 27px;
          height: 27px;
        }
      }

      img~img {
        margin-left: 30px;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 940px;
    padding: 0;
    margin: 0 0 20px;

    @include viewport--lg {
      max-width: 870px;
    }
  }

  &__item {
    width: 244px;
    padding: 0;
    margin: 0 0 25px;

    @include viewport--md {
      width: 208px;
    }

    @include viewport--sm {
      width: 230px;
    }
  }

  &__item-number {
    font-weight: 700;
    font-size: 60px;
    line-height: 68px;
    letter-spacing: 0;
    margin: 0 0 5px 0;

    @include viewport--lg {
      font-size: 40px;
      line-height: 68px;
      margin-bottom: -4px;
    }
  }

  &__item-text {
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: $color-mediumgray;
    margin: 0;

    @include viewport--lg {
      font-size: 14px;
      line-height: 22px;
    }
  }

  &--studio {
    margin-bottom: 0;

    @include viewport--sm {
      margin-bottom: -18px;
    }

    .achievements {
      @include viewport--sm {
        padding: 20px 0 55px;
      }
    }

    .achievements__description {
      padding: 57px 20px 0 0;

      @media(min-width: 1480px) {
        max-width: 1150px;
        margin: 0 auto;
        padding: 80px 20px 0 0;
      }

      @include viewport--lg {
        padding: 30px 20px 0 35px;
      }

      @include viewport--sm {
        padding: 30px 20px 0 15px;
      }
    }

    .achievements__factoid {
      @include viewport--sm {
        letter-spacing: -0.85px;
        margin-bottom: 20px;
      }
    }

    .achievements__factoid-text {
      @include viewport--sm {
        padding-right: 0;
      }
    }
  }
}
