.product-card {
  img {
    transition: $default-transition-settings;
  }

  position: relative;

  a {
    @include reset-link;

    display: block;
    z-index: 3;
    position: absolute;
    width: 100%;
    height: 100%;

    left: 0;
    top: 0;
  }

  p {
    margin: 0;
  }

  &:hover {
    img {
      opacity: 0.7;
    }

    .product-card__title-in-extra {
      border-bottom: 2px solid $color-lightblue;
    }
  }
}

.product-card__title-in-extra {
  display: inline;
  transition: border-bottom $default-transition-settings;
  border-bottom: 2px solid $color-transparent;
}

.product-card--in-basket {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include viewport--sm {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.product-card__img-wrapper-in-basket {
  display: flex;
  width: 63%;

  @include viewport--sm {
    width: auto;
    margin-bottom: 19px;
  }
}

.product-card__title-wrapper-in-basket {
  padding-top: 11px;
  padding-right: 15px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  box-sizing: border-box;

  @include viewport--lg {
    width: auto;
  }

  @include viewport--sm {
    padding-top: 0;
  }

  .breadcrumbs {
    margin-bottom: 7px;

    @include viewport--sm {
      margin-bottom: 0;
    }

    .link {
      position: static;
      font-weight: $font-weight--medium;
      width: auto;
    }

    .breadcrumbs__item {
      @include viewport--sm {
        padding-top: 0;

        &::after {
          top: 42%;
          right: -10px;
        }
      }
    }
  }
}

.product-card__image-in-basket {
  margin-right: 4%;
  width: 115px;
  height: 115px;
  object-fit: cover;

  @include viewport--xl {
    margin-right: 5%;
  }

  @include viewport--sm {
    margin-right: 6%;
    width: 80px;
    height: 80px;
  }
}


.product-card__title-in-basket {
  margin: 0;
  margin-bottom: 6px;
  padding: 0;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--sm {
    font-size: 12px;
    line-height: 18px;
  }
}

.product-card__article-in-basket {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;
}

.product-card__price-button-wrapper {
  display: flex;
  width: 316px;
  justify-content: space-between;

  @include viewport--lg {
    width: 325px;
  }

  @include viewport--md {
    width: 245px;
  }

  @include viewport--sm {
    width: auto;
  }
}

.product-card__price-wrapper-in-basket {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 230px;

  @include viewport--lg {
    width: 225px;
  }

  @include viewport--md {
    width: 200px;
  }
}

.product-card--in-basket input {
  margin-right: 24.5%;
  align-self: center;
  text-align: center;
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.02em;
  color: $color-darkgray;
  max-width: 60px;
  min-height: 50px;
  border: 2px solid $color-beigelight;
  cursor: pointer;

  &:focus {
    border: 2px solid $color-mediumgray;
  }

  @include viewport--lg {
    margin-right: 26%;
  }

  @include viewport--md {
    margin-right: 26%;
  }

  @include viewport--md {
    margin-right: 20%;
  }
}

.product-card__price-in-basket {
  margin: 0;

  align-self: center;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
  white-space: nowrap;
}

.product-card .product-card__price-in-basket {
  margin-right: 8px;

  @include viewport--lg {
    margin-right: 11px;
  }
}

.product-card__delete-button-in-basket {
  align-self: center;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  background-image: url("../img/sprite/icon-close-basket.svg");
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: 0 0;
  cursor: pointer;
  width: 16px;
  height: 16px;

  &:hover {
    background-image: url("../img/sprite/icon-close-basket-hover.svg");
  }
}

.product-card__image-in-extra {
  width: 324px;
  height: 324px;
  object-fit: cover;

  @include viewport--lg {
    width: 223px;
    height: 223px;
  }

  @include viewport--md {
    width: 342px;
    height: 342px;
  }
}

.product-card__article-in-extra {
  padding: 0;
  margin: 0;
  margin-bottom: 13px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--xl {
    margin-bottom: 8px;
  }

  @include viewport--sm {
    margin-bottom: 13px;
  }
}

.product-card__discount-price-wrapper {
  margin-bottom: 9px;
  display: flex;
  flex-wrap: wrap;
}

.product-card__regular-price {
  margin: 0;

  margin-right: 25px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 14px;
  line-height: 32px;
  letter-spacing: -0.02em;
  white-space: nowrap;

  color: $color-mediumgray;

  &.green {
    color: $color-green;
  }

  span {
    font-size: 14px;
  }
}

.product-card__price-with-discount {
  margin: 0;

  position: relative;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 14px;
  line-height: 32px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;
  white-space: nowrap;

  &::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 54px;
    height: 2px;
    background-color: $color-darkred;
    transform: translate(-50%, -50%) rotate(-19deg);
  }
}

.product-card__buttons-wrapper-in-extra {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.product-card__add-good-button {
  margin-right: 16px;
  z-index: 3;
  min-height: 0;
  max-width: 170px;
}

.product-card__add-good-button span {
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 10px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.product-card__like-button {
  border: none;
  background-color: transparent;
  font-size: 0;
  line-height: 0;
  background-image: url("../img/sprite/icon-heart-basket.svg");
  background-repeat: no-repeat;
  background-size: 24px 20px;
  background-position: 0 0;
  width: 24px;
  height: 20px;
  outline: none;
  cursor: pointer;

  &:hover {
    background-image: url("../img/sprite/icon-heart-basket-hover.svg");
  }

  &--checked {
    background-image: url("../img/sprite/icon-heart-basket-checked.svg");
  }
}

.product-card__img-wrapper-in-extra {
  position: relative;
  margin-bottom: 20px;

  button:not(.like) {
    width: 36px;
    height: 36px;
    margin-right: 6px;

    &:last-child {
      margin-right: 0;
    }

    svg {
      margin-top: 2px;
      margin-left: 1px;
      width: 22px;
      height: 19px;
    }
  }

  .article-number {
    position: absolute;
    z-index: 2;
    left: 14px;
    bottom: 15px;

    background: rgba(255, 255, 255, 0.8);
    color: $color-darkgray;
  }
}

.product-card__buttons-wrapper {
  position: absolute;
  z-index: 3;
  bottom: 15px;
  right: 14px;

  display: flex;
}

.product-card__img-icons {
  position: absolute;
  display: flex;
  top: 14px;
  right: 14px;

  svg {
    margin-left: 8px;
    width: 36px;
    height: 36px;
    min-width: 36px;
  }

  @include viewport--xl {
    right: 9px;
  }

  @include viewport--sm {
    top: 32px;
    right: 31px;

    svg {
      width: 24px;
      height: 24px;
      min-width: 24px;
    }
  }
}
