.virtual-main {
  margin-top: 10px;
  position: relative;
  padding: 47px 115px 30px;
  min-height: 620px;
  border-radius: 8px;


  @include viewport--lg {
    padding: 47px 50px 30px;
  }

  @include viewport--md {
    min-height: 500px;
  }

  @media(max-width:425px) {
    padding: 47px 20px 30px;
  }

  @include viewport--sm {
    margin-top: 20px;
    padding: 35px 20px 30px;
  }

  &__img {
    border-radius: 8px;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;

    img {
      border-radius: 8px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }

  &__title {
    letter-spacing: -0.02em;
    color: $color-default-white;
    margin-top: 82px;
    margin-bottom: 46px;
    max-width: 630px;

    @include viewport--md {
      margin-top: 30px;
    }

    @include viewport--sd {
      font-weight: 700;
      font-size: 28px;
      line-height: 36px;
      max-width: 200px;
      margin-bottom: 20px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    color: $color-default-white;
    max-width: 660px;
    margin: 0 0 60px 0;

    @include viewport--sd {
      font-size: 14px;
      line-height: 24px;
      max-width: 250px;
      margin: 0 0 25px 0;
    }

  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
  }
}
