.project-card {
  position: relative;
  display: block;

  &__author {
    @include reset-item;
    font-weight: 500;
    font-size: 12px;
    line-height: 28px;
    letter-spacing: -0.02em;
    color: $color-default-white;
  }

  &__title {
    @include reset-item;
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: -0.02em;
    color: $color-default-white;

    @include viewport--md {
      font-size: 16px;
      line-height: 20px;
    }
  }

  picture {
    line-height: 0;
    font-size: 0;
    width: 100%;
    height: 100%;
  }

  &__img {
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    width: 100%;
    display: flex;
    height: 315px;
    -webkit-mask-image: -webkit-radial-gradient(white, black);

    @include viewport--xl {
      height: 289px;
    }

    @include viewport--md {
      height: 212px;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(360deg, rgba($color-darkmain, 0.48) 0%, rgba($color-darkmain, 0) 100%);
      z-index: 1;
      border-radius: 8px;
      top: 0;
      left: 0;
    }

    img {
      width: 100%;
      min-height: 315px;
      height: auto;
      object-fit: cover;
      object-position: center;
      transform: scale(1);
      transition: transform 0.3s;
      border-radius: 8px;
    }

    &--shadow {
      position: absolute;
      top: 7px;
      left: 7px;
      z-index: -1;
      border-radius: 8px;
      opacity: 0.4;

      &:after {
        background: linear-gradient(360deg, rgba($color-darkmain, 0.19) 0%, rgba($color-darkmain, 0) 100%);
      }
    }
  }

  &:hover {
    img {
      transform: scale(1.1);
    }
  }

  &__wrapper {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding: 17px 35px 17px 24px;

    @include viewport--xl {
      padding-bottom: 23px;
      padding-right: 55px;
    }

    @include viewport--md {
      padding-top: 10px;
      padding-left: 13px;
      padding-bottom: 15px;
    }
  }

  .card-tags {
    position: absolute;
    right: 12px;
    top: 9px;
    color: $color-darkmain;

    svg {
      width: 42px;
      height: 42px;
    }
  }
}
