.product-review {
  padding-top: 60px;
  padding-bottom: 88px;

  background-color: $beige-extralight;

  .container {
    display: flex;
    padding-right: 0;
  }

  .reviews-slider {
    overflow: hidden;
    padding-top: 40px;
    padding-bottom: 20px;
    margin-left: -22px;
    padding-left: 22px;
    width: calc(66.9% + 22px);


    .slider-button-fill-prev,
    .slider-button-fill-next {
      transition: opacity 0.3s;

      &.swiper-button-disabled {
        opacity: 0;
      }
    }

    &::before {
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      right: 0;
      top: 0;
      width: 143px;
      height: 100%;
      background: linear-gradient(to left, $beige-extralight 0%, rgba(245, 245, 245, 0) 100%);
    }

    .swiper-slide {
      width: 338px;
    }

    .swiper-slide-prev {
      a {
        box-shadow: unset;
      }
    }

    .swiper-wrapper {
      left: unset;
      margin-left: unset;
    }
  }
}

.product-review__text-wrapper {
  padding-top: 73px;
  padding-left: 116px;
  padding-right: 40px;
  width: 33.1%;

  .title {
    margin-bottom: 26px;
  }

  .btn {
    margin-top: 41px;
    width: fit-content;
    padding: 14px 40px 8px 47px;
    display: block;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@include viewport--lg {
  .product-review {
    padding-top: 65px;

    .reviews-slider {
      width: calc(69.3% + 13px);
      margin-left: -13px;
      padding-left: 13px;

      .slider-button-fill-next {
        right: 18px;
      }
    }
  }

  .product-review__text-wrapper {
    padding-top: 54px;
    padding-left: 0;
    padding-right: 16px;
    width: 30.7%;

    .title {
      margin-bottom: 32px;
    }
  }
}

@include viewport--md {
  .product-review {
    position: relative;
    padding-top: 46px;
    padding-bottom: 124px;

    .title {
      margin-bottom: 14px;
    }

    .reviews-slider {
      padding-top: 24px;
      width: calc(100% + 13px);

      .swiper-slide {
        width: 260px;

        a {
          &:hover {
            transform: translateY(-10px);
          }
        }
      }

      &::after {
        width: 77px;
      }
    }

    .container {
      flex-direction: column;
    }

    .btn {
      position: absolute;
      bottom: 56px;
      width: auto;

      padding: 9px 34px 4px 34px;

      font-size: 12px;
      line-height: 32px;
    }
  }

  .product-review__text-wrapper {
    padding-top: 0;
    width: 100%;
  }
}
