.collections-slider {
  padding-top: 35px;
  padding-bottom: 43px;

  .title {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .slider-buttons {
    position: absolute;
  }

  .collections-cards-slider {
    margin-bottom: 68px;
  }
}

@include viewport--lg {
  .collections-slider {
    padding-bottom: 45px;

    .collections-cards-slider {
      margin-bottom: 79px;
    }
  }
}

@include viewport--md {
  .collections-slider {
    padding-top: 0;
    padding-bottom: 0;

    .slider-button-fill-next {
      right: 0;
    }

    .collections-cards-slider {
      margin-bottom: 49px;
    }
  }
}
