.modal-submenu {
  $b: #{&};
  position: absolute;
  top: 56px;

  height: 0;
  will-change: transform;

  overflow: hidden;
  visibility: visible;

  background-color: transparent;
  opacity: 1;
  transform: translateZ(0);

  @include viewport--md {
    overflow: hidden;
    transition-duration: 0.3s;
  }

  @include viewport--sm {
    transition-duration: 0.3s;
    top: 56px;
  }

  &__swiper-container {
    border-radius: 4px;
  }

  &__wrapper {
    padding-right: 35px;
    padding-left: 36px;

    @include viewport--md {
      padding-right: 25px;
      padding-left: 25px;
      overflow: auto;
    }

    @include viewport--sm {
      padding-right: 55px;
      padding-left: 0;
    }
  }

  &__overlay {
    position: absolute;

    width: 100%;
    height: calc(var(--vh, 1vh) * 100);

    background-color: $color-beigelight;
    opacity: 0;
  }

  &__content {
    will-change: transform;
    transform: translateZ(0);
    background-color: $color-default-white;
    display: flex;
    max-width: 1368px;
    position: relative;
    flex-direction: row;
    flex-wrap: wrap;
    box-sizing: border-box;
    width: 100%;
    margin: 10px auto 0;
    padding: 0;
    min-height: 525px;

    box-shadow: 0 4px 32px rgba(0, 0, 0, 0.15);

    @include viewport--lg {
      // align-items: flex-start;
      // align-content: flex-start;
    }

    @include viewport--sm {
      margin-top: 0;
    }
  }

  &__column-wrapper {
    box-sizing: border-box;
    padding-top: 12px;
    padding-right: 27px;
    padding-bottom: 36px;
    padding-left: 25px;
    border-right: 1px solid $color-beigelight;
    background-color: $color-default-white;

    @include viewport--xl {
      padding-bottom: 30px;
    }

    @include viewport--lg {
      max-height: 550px;
      padding-right: 10px;
      padding-left: 25px;
    }

    @include viewport--md {
      min-height: auto;
      max-height: 100%;
    }

    @include viewport--sm {
      width: 100%;
      min-height: auto;
      padding-bottom: 27px;
      border-right: none;
      border-bottom: 1px solid $color-beigelight;
      padding-bottom: 13px;
    }
  }

  &__column-wrapper--1 {
    width: 24.6%;

    @include viewport--xl {
      padding-right: 16px;
      padding-left: 19px;
    }

    @include viewport--lg {
      width: 35%;
      padding-right: 26px;
      padding-left: 25px;
    }

    @include viewport--md {
      width: 50%;
    }

    @include viewport--sm {
      width: 100%;
      padding-top: 15px;
      padding-right: 23px;
      padding-left: 16px;
    }
  }

  &__column-wrapper--2 {
    width: 24.3%;

    @include viewport--xl {
      width: 24.9%;
      padding-left: 20px;
    }

    @include viewport--lg {
      width: 29.2%;
      padding-right: 26px;
      padding-left: 32px;
    }

    @include viewport--md {
      width: 50%;
      border-right: none;
    }

    @include viewport--sm {
      width: 100%;
      padding-top: 22px;
      padding-right: 23px;
      padding-left: 16px;
    }
  }

  &__title {
    margin-bottom: 16px;

    font-weight: $font-weight--extrabold;
    font-size: 16px;
    line-height: 22px;
    font-family: $font-family--Mont;
    color: $color-darkmain;

    @include viewport--lg {
      margin-bottom: 22px;
    }

    @include viewport--sm {
      margin-top: 0;
      margin-bottom: 11px;
    }
  }

  &__subcategoryes-list {
    padding: 0;

    @include viewport--lg {
      margin-top: 26px;
    }

    @include viewport--sm {
      margin-top: 0;
    }
  }

  &__subcategoryes-item {
    margin-bottom: 8px;

    @include viewport--lg {
      margin-bottom: 10px;
    }

    @include viewport--sm {
      margin-bottom: 8px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__column-wrapper--2 &__title {
    margin-bottom: 16px;

    @include viewport--sm {
      margin-bottom: 11px;
    }
  }

  &__subcategory-button {
    position: relative;

    display: none;
    width: 100%;
    margin: 0;

    border: none;

    @include viewport--sm {
      display: flex;
    }

    svg {
      transition: all $default-transition-settings;
      position: absolute;
      right: 5%;
      width: 18px;
      min-width: 18px;
      height: 11px;
      margin-left: auto;
    }

    &.open {

      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__text-categoryes-list {
    @include reset-list;
    margin-top: -5px;

    @include viewport--lg {
      margin-top: 15px;
    }

    @include viewport--sm {
      max-height: 100%;
      overflow: hidden;
      margin-top: -7px;
      margin-bottom: 10px;
    }

    &--sublist {
      padding-top: 20px;
    }


    &.is-closed {
      display: none;
    }
  }

  &__text-categoryes-item {
    @include reset-item;
  }

  &__text-categoryes-item.hidden {
    @include viewport--sm {
      display: none;
    }
  }

  &__text-categoryes-list--sublist {
    @media (min-width: 768px) {
      display: none;
    }
  }

  &__text-categoryes-list--open {
    @include viewport--sm {
      max-height: none;
      overflow: auto;
    }
  }

  &__text-categoryes-list a {
    @include reset-link;
    font-weight: $font-weight--regular;
    font-size: 14px;
    line-height: 36px;
    font-family: $font-family--Mont;
    color: $color-darkmain;
    letter-spacing: -0.02em;

    transition: color $default-transition-settings;

    @include viewport--lg {
      font-size: 13px;
      line-height: 34px;
    }

    &:hover {
      color: color $color-darkgray;

      transition: $default-transition-settings;
    }
  }

  &__column-wrapper--3 {
    display: flex;
    flex-direction: column;
    width: 25.6%;
    padding-right: 0;
    padding-left: 0;

    @include viewport--xl {
      width: 25%;
    }

    @include viewport--lg {
      width: 35.8%;
      border-right: none;
      padding-bottom: 35px;
    }

    @include viewport--md {
      flex-direction: row;
      width: 100%;
      padding-top: 0;
      padding-right: 26px;
      padding-bottom: 0;
      padding-left: 25px;

      border-top: 1px solid $color-beigelight;
    }

    @include viewport--sm {
      width: 100%;
      margin-bottom: 0;
      padding-top: 36px;
      padding-right: 23px;
      padding-bottom: 10px;
      padding-left: 19px;

      border-top: none;
    }
  }

  &__column-3-top-wrapper {
    margin-bottom: 18px;
    padding-right: 18px;
    padding-left: 24px;

    @include viewport--xl {
      padding-right: 12px;
      padding-left: 15px;
    }

    @include viewport--lg {
      margin-bottom: 29px;
      padding-right: 24px;
      padding-left: 36px;
    }

    @include viewport--md {
      width: calc(50% + 3px);
      margin-bottom: 0;
      padding-top: 25px;
      padding-right: 0;
      padding-bottom: 38px;
      padding-left: 0;

      border-right: 1px solid $color-beigelight;
    }

    @include viewport--sm {
      width: 100%;
      padding-top: 0;
      padding-bottom: 0;

      border-right: none;
    }
  }

  &__column-3-top-wrapper .collection__image-wrapper {
    position: relative;
    width: 300px;
    height: 162px;

    @include viewport--xl {
      width: 270px;
      height: 154px;
    }

    @include viewport--lg {
      width: 280px;
      height: 154px;
    }

    @include viewport--sm {
      width: 100%;
    }
  }

  &__column-3-top-wrapper img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__swiper-size-wrapper {
    position: relative;

    max-width: 300px;

    @include viewport--xl {
      max-width: 270px;
    }

    @include viewport--lg {
      max-width: 280px;
    }
  }

  &__swiper-button-next,
  &__swiper-button-prev {
    top: 50%;

    width: 32px;
    height: 32px;

    transform: translateY(-50%);
  }

  &__swiper-button-prev {
    left: 12px;
  }

  &__swiper-button-next {
    right: 12px;
  }

  &__swiper-button-prev.swiper-button-prev,
  &__swiper-button-next.swiper-button-next {
    margin-top: 0;

    &::after {
      content: "";

      width: 32px;
      height: 32px;

      background-image: url("../img/sprite/icon-arrow-swiper.svg");
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 32px 32px;

      transition: all $default-transition-settings;
    }

    &:hover::after {
      background-image: url("../img/sprite/icon-arrow-swiper-hover.svg");
    }

    &:active::after {
      background-image: url("../img/sprite/icon-arrow-swiper-active.svg");
    }
  }

  &__swiper-button-next.swiper-button-next::after {
    transform: rotate(180deg);
  }

  &__column-wrapper--3 hr {
    margin-bottom: 34px;
    background-color: $color-beigelight;

    @include viewport--xl {
      margin-bottom: 27px;
    }

    @include viewport--lg {
      margin-bottom: 10px;
    }
  }

  &__column-3-bottom-wrapper {
    padding-right: 15px;
    padding-left: 25px;

    @include viewport--xl {
      padding-right: 15px;
      padding-left: 18px;
    }

    @include viewport--lg {
      padding-top: 47px;
      padding-right: 3px;
      padding-left: 37px;
    }

    @include viewport--md {
      width: 50%;
      margin-bottom: 0;
      padding-top: 25px;
      padding-right: 0;
      padding-left: 32px;
    }

    @include viewport--sm {
      display: none;
      width: auto;
    }
  }

  &__column-3-bottom-wrapper &__title {
    margin-bottom: 11px;
  }

  .tags-list__item {
    margin-right: 12px;
    margin-bottom: 8px;

    a:not(.tag) {
      @include reset-link;
    }
  }

  &__column-wrapper--4 {
    width: 25.5%;
    padding-left: 25px;

    border: none;

    @include viewport--xl {
      padding-left: 20px;
    }

    @include viewport--lg {
      width: 100%;
      min-height: auto;
      padding-top: 0;
      padding-right: 30px;
      padding-left: 26px;

      border-top: 1px solid $color-beigelight;
    }

    @include viewport--sm {
      width: 100%;
      padding-top: 22px;
      padding-right: 23px;
      padding-left: 19px;

      border-top: none;
    }
  }

  &__column-wrapper--4>&__title {
    @include viewport--lg {
      margin-bottom: 25px;
    }

    @include viewport--sm {
      margin-bottom: 11px;
    }
  }

  &__categoryes-list {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;

    @include viewport--lg {
      margin-right: -14px;
      margin-left: -14px;
    }

    @media (max-width: 374px) {
      margin-right: 0;
    }
  }

  .category--small {
    @include viewport--xl {
      width: 260px;
    }

    @include viewport--lg {
      width: 260px;
    }

    @media (max-width: 374px) {
      width: 100%;
    }
  }

  &__categoryes-item {
    margin-bottom: 12px;
    width: 100%;

    @include viewport--lg {
      margin-left: 14px;
      margin-right: 14px;
      width: inherit;
    }

    @include viewport--sm {
      margin-bottom: 10px;
    }

    @media (max-width: 620px) {
      margin-right: 0;
      width: 100%;
    }
  }

  &__close-btn {
    position: absolute;
    top: -56px;
    left: 35px;
    z-index: 100000;

    display: block;
    width: 166px;
    height: 50px;

    @include viewport--sm {
      position: absolute;
      top: 20px;
      right: 2.5%;
      left: inherit;

      display: block;
      width: 30px;
      height: 30px;

      background-image: url("../img/sprite/icon-close-submenu.svg");
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 22px 22px;

      transition: all $modal-transition-settings;
      transition-duration: 0.2s;
    }
  }

  &.modal--active {
    z-index: 102;
    //height: 100vh;
    height: calc(var(--subMenuHeight) + 50px);
    will-change: transform;

    transform: translateZ(0);
    animation: showSub 0.15s linear;

    @include viewport--md {
      height: calc((var(--vh, 1vh) * 100));
    }

    #{$b}__content {
      position: relative;
      top: 0;
      z-index: 10;
      will-change: transform;
      transform: translateZ(0);
      min-height: 525px;
      overflow: auto;

      @include viewport--lg {
        min-height: calc((var(--vh, 1vh) * 97.5) - var(--headerHeight));
        max-height: calc((var(--vh, 1vh) * 95) - var(--headerHeight));
        overflow: auto;
      }

      @include viewport--md {
        min-height: calc((var(--vh, 1vh) * 97.5) - var(--headerHeight));
        max-height: calc((var(--vh, 1vh) * 95) - var(--headerHeight));
        overflow: auto;
      }

      @include viewport--sm {
        top: 0;

        @include viewport--md {
          min-height: calc((var(--vh, 1vh) * 100) - var(--headerHeight));
          max-height: calc((var(--vh, 1vh) * 100) - var(--headerHeight));
          overflow: auto;
        }
      }

      &::before {
        content: "";
        position: absolute;
        top: -15px;
        z-index: 10;

        width: 100%;
        height: 15px;
      }
    }

    #{$b}__overlay {
      top: 10px;
      z-index: 1;

      width: 100%;
      height: 100vh;
      opacity: 0.2;

      @include viewport--lg {
        height: 100%;
      }

      @include viewport--sm {
        top: 0;
      }
    }
  }

  &__wrapper {
    overflow: visible;
  }



  &.modal--hide {

    will-change: transform;
    transform: translateZ(0);
    animation: hideSub 0.15s linear;

    #{$b}__content {
      @include viewport--sm {
        top: calc(var(--modalScroll) * (-1));
      }
    }
  }
}



@keyframes showSub {
  0% {
    height: 0;
  }

  50% {
    //height: 50vh;
    height: calc((var(--subMenuHeight) + 50px) / 2);
  }

  100% {
    //height: 100vh;
    height: calc(var(--subMenuHeight) + 50px);
  }
}

@keyframes hideSub {
  0% {
    //height: 100vh;
    height: calc(var(--subMenuHeight) + 50px);
  }

  50% {
    //height: 50vh;
    height: calc((var(--subMenuHeight) + 50px) / 2);
  }

  100% {
    height: 0;
  }
}
