.collections-slider-small {
  overflow: hidden;
  position: relative;

  .swiper-slide {
    width: 104px;
    height: 104px;

    picture {
      font-size: 0;
      line-height: 0;
    }

    img {
      transition: transform $default-transition-settings;
      object-fit: cover;
      max-width: unset;
      min-height: 100%;
    }

    &:hover {
      img {
        transform: scale(1.1);
      }
    }

    a {
      display: block;
      overflow: hidden;
      border-radius: 4px;
      position: relative;
      height: 100%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    width: 69px;
    height: 100%;
    background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }
}

@include viewport--lg {
  .collections-slider-small {
    .swiper-slide {
      width: 90px;
      height: 90px;
    }

    .slider-button-fill-next {
      right: 7px;
    }
  }
}
