.wallpapers-in-project {
  padding: 20px 0 67px;

  @include viewport--xl {
    padding-top: 26px;
  }

  @include viewport--md {
    padding-top: 36px;
  }

  &__wrapper {
    display: flex;
    justify-content: center;

    @include viewport--md {
      flex-direction: column;
    }
  }

  &__description {
    max-width: 59%;
    padding-right: 59px;

    @include viewport--md {
      width: 100%;
      max-width: unset;
      padding-right: 0;
      padding-bottom: 31px;
    }
  }

  &__img {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;

    img {
      object-fit: cover;
      object-position: center;
      border-radius: 50%;
      width: 220px;
      height: 220px;
      min-width: 220px;
      min-height: 220px;
      opacity: 1;
      transition: opacity 0.3s;

      @include viewport--xl {
        width: 223px;
        height: 223px;
        min-width: 223px;
        min-height: 223px;
      }

      @include viewport--md {
        width: 304px;
        height: 304px;
        min-width: 304px;
        min-height: 304px;
      }
    }

    &:hover {
      img {
        opacity: 0.7;
      }

      .wallpapers-in-project__name,
      .wallpapers-in-project__art {
        color: $color-blue;
      }
    }
  }

  &__name {
    @include reset-item;
    font-weight: 800;
    font-size: 16px;
    line-height: 21px;
    text-align: center;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    margin-top: 10px;
    transition: color 0.3s;

    @include viewport--xl {
      font-size: 14px;
      line-height: 18px;
    }

    @include viewport--md {
      margin-top: 11px;
      font-size: 12px;
      line-height: 14px;
    }
  }

  &__art {
    @include reset-item;
    font-weight: 500;
    font-size: 12px;
    line-height: 21px;
    text-align: center;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    transition: color 0.3s;
  }

  &__title {
    @include reset-item;
    margin-bottom: 19px;
    font-weight: 800;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    @include viewport--xl {
      font-size: 34px;
      line-height: 46px;
    }

    @include viewport--md {
      font-size: 24px;
      line-height: 26px;
      margin-bottom: 17px;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
  }

  &__imgs {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    gap: 24px;
    min-width: 24%;

    @include viewport--md {
      width: 100%;
      min-width: unset;
      justify-content: center;
    }

    @include viewport--mm {
      flex-wrap: wrap;
    }

    &--two-rows {
      display: grid;
      grid-template-columns: repeat(2, auto);
      grid-template-rows: repeat(2, auto);
      min-width: unset;

      @include viewport--xl {
        gap: 20px;
      }

      @include viewport--md {
        width: 100%;
        gap: 17px;
      }

      @include viewport--mm  {
        display: flex;
      }

      img {
        width: 208px;
        height: 208px;
        min-width: 208px;
        min-height: 208px;

        @include viewport--xl {
          width: 180px;
          height: 180px;
          min-width: 180px;
          min-height: 180px;
        }

        @include viewport--md {
          width: 160px;
          height: 160px;
          min-width: 160px;
          min-height: 160px;
        }
      }
    }

    &--three {
      min-width: unset;

      @include viewport--xl {
        gap: 14px;
      }

      @include viewport--md {
        width: 100%;
        gap: 10px;
      }

      img {
        width: 208px;
        height: 208px;
        min-width: 208px;
        min-height: 208px;

        @include viewport--xl {
          width: 144px;
          height: 144px;
          min-width: 144px;
          min-height: 144px;
        }

        @include viewport--md {
          width: 106px;
          height: 106px;
          min-width: 106px;
          min-height: 106px;
        }
      }
    }
  }
}
