.form-calculate {
  position: absolute;
  left: 0;
  top: 0;

  width: 100%;
  padding: 115px 58px 32px 56px;

  visibility: hidden;
  overflow: hidden;
  height: 0;
  opacity: 0;

  transition: $default-transition-settings;

  &.is-active {
    height: auto;
    min-height: 100%;
    opacity: 1;
    z-index: 1000;
    visibility: visible;
    background-color: $color-default-white;
  }

  .input-wrapper--regular {
    margin-bottom: 24px;
  }

  .input-wrapper--checkbox {
    margin-top: 51px;
    margin-bottom: 0;
  }

  .btn {
    margin-top: 26px;
    padding: 17px 20px 11px 40px;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

.form-calculate__text {
  margin-bottom: 34px;
}

.form-calculate__success {
  position: absolute;
  left: 0;
  top: 0;

  display: flex;
  flex-direction: column;
  padding: 115px 56px 56px 56px;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: -1;

  transition: $default-transition-settings;
  background-color: #ffffff;

  .btn {
    padding: 14px 37px 8px 38px;
    align-self: start;
    margin-top: 38px;
    font-size: 14px;
    line-height: 32px;
  }

  &.is-active {
    opacity: 1;
    z-index: 1;
  }

  .title {
    margin-bottom: 27px;
  }
}

.form-calculate__success-wrapper {
  display: flex;

  svg {
    margin-right: 22px;
    flex-shrink: 0;
    width: 96px;
    height: 96px;
  }

  i {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.02em;
  }
}

@include viewport--sm {
  .form-calculate {
    padding: 71px 30px 30px 30px;

    .input-wrapper--regular {
      margin-bottom: 16px;
    }

    .input-wrapper--checkbox {
      margin-top: 39px;

      label {
        padding-right: 0;
        padding-left: 39px;
      }
    }

    .btn {
      margin-top: 22px;
      width: 100%;
    }
  }

  .form-calculate__text {
    margin-bottom: 19px;
  }

  .form-calculate__success {
    display: flex;
    flex-direction: column;
    padding: 71px 30px 56px;

    .btn {
      width: unset;
      align-self: start;
      padding: 9px 31px 3px 31px;

      font-size: 12px;
      line-height: 32px;
    }

    .title {
      margin-bottom: 16px;
    }
  }

  .form-calculate__success-wrapper {
    margin-bottom: 11px;

    svg {
      margin-right: 12px;
      width: 82px;
      height: 82px;
    }

    i {
      margin-top: 10px;

      font-size: 12px;
      line-height: 20px;
    }
  }
}
