.materials-list {
  @include reset-list;
}

.materials-item {
  @include reset-item;
  position: relative;
  z-index: 1001;

  border-radius: 4px;
  padding: 20px 50px 20px 17px;
  margin-bottom: 13px;
  cursor: pointer;
  transition: background-color $default-transition-settings;

  &:last-child {
    margin-bottom: 0;
  }

  label {
    position: absolute;
    z-index: 3;
    left: 0;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  .cost-calculate__header {
    margin-bottom: 8px;
  }

  &:hover {
    background-color: $beige-extralight;
  }

  &::after {
    content: "";
    position: absolute;
    right: -5px;
    top: -8px;

    opacity: 0;
    visibility: hidden;

    width: 34px;
    height: 34px;

    transition: opacity $default-transition-settings, visibility $default-transition-settings;
    background-image: url("../img/sprite/icon-check.svg");
    background-repeat: no-repeat;
  }

  &.is-active {
    background-color: $beige-extralight;

    &::after {
      opacity: 1;
      visibility: visible;
    }
  }
}

.materials-item__wrapper {
  display: flex;
  cursor: pointer;
}

.materials-item__img-wrapper {
  flex-shrink: 0;
  position: relative;

  img {
    width: 110px;
    height: 110px;
    object-fit: cover;
    border-radius: 8px;
  }

  svg {
    position: absolute;
    right: -5px;
    top: -11px;

    width: 36px;
    height: 36px;
  }
}

.materials-item__desc {
  margin: 0;

  margin-bottom: 17px;

  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: $color-darkgray;
}

.materials-item__text-wrapper {
  margin-left: 16px;
}

.materials-item__price-wrapper {
  display: flex;
  align-items: center;
}

.materials-item__price {
  margin: 0;

  margin-right: 18px;

  font-weight: 800;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: -0.02em;

  color: $color-darkmain;

  &.green {
    color: $color-green;
  }
}

.materials-item__discount {
  margin: 0;

  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: -0.02em;

  color: $color-mediumgray;
}

@include viewport--sm {
  .materials-item {
    margin-bottom: 4px;
    padding: 20px 31px 27px 14px;

    &::after {
      width: 28px;
      height: 28px;

      background-size: contain;
    }
  }

  .materials-item__img-wrapper {
    img {
      width: 60px;
      height: 60px;

      border-radius: 8px;
    }
  }

  .materials-item__desc {
    font-size: 11px;
    line-height: 16px;
  }

  .materials-item__price {
    margin-right: 24px;

    font-size: 15px;
    line-height: 15px;
  }

  .materials-item__discount {
    font-size: 15px;
    line-height: 15px;
  }
}
