.author-collection-card {
  $b: #{&};

  @include reset-link;

  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 306px;
  min-height: 344px;
  padding: 15px;

  &--big {
    width: 623px;
    max-width: 100%;

    @include viewport--sm {
      width: 306px;
    }
  }

  &--text-white {
    #{$b}__text-wrapper {
      #{$b}__title {
        color: $color-default-white;
      }

      #{$b}__text {
        color: $color-default-white;
        opacity: 0.8;
      }
    }
  }

  &:hover,
  &:focus {
    img {
      transform: scale(1.03);
    }
  }

  img {
    position: absolute;
    object-fit: cover;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transition: transform $default-transition-settings;
  }

  &__text-wrapper {
    z-index: 1;
    margin-top: 130px;
    width: 62%;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 18px;
    color: $color-darkmain;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-align: center;
    font-weight: 800;
  }

  &__text {
    margin: 0;
    color: $color-darkmain;
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    font-weight: 600;
    opacity: 0.7;
  }

  &__labels {
    position: absolute;
    top: 17px;
    right: 19px;
    width: auto;

    svg {
      width: 36px;
      min-width: 36px;
      height: 36px;
    }
  }
}
