.solution {
  padding: 74px 0 35px;
  max-width: 1139px;
  margin: 0 auto;

  @include viewport--lg {
    padding: 66px 0 20px 30px;
  }

  @include viewport--sm {
    padding: 45px 0 20px 0;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;

    @include viewport--sm {
      flex-direction: column-reverse;
      gap: 0;
    }
  }

  &__img {
    width: 270px;
    height: 270px;
    flex: 0 0 auto;
    background-color: $color-darkmain;
    border-radius: 100%;

    @include viewport--lg {
      width: 244px;
      height: 244px;
    }

    @include viewport--sm {
      width: 303px;
      height: 303px;
    }

    &--display-none {
      @include viewport--sm {
        display: none;
      }
    }
  }

  &__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 100%;
  }

  &__text {
    display: block;
    max-width: 790px;
    font-weight: 500;
    font-size: 26px;
    line-height: 40px;
    padding: 0 0 0 20px;
    margin: -30px 0 0;
    letter-spacing: -0.02em;

    @include viewport--lg {
      font-size: 20px;
      line-height: 30px;
      margin-top: 15px;
      padding-left: 20px;
    }

    @include viewport--sm {
      font-size: 18px;
      line-height: 28px;
      padding-left: 0;
      margin-bottom: 42px;
    }
  }

  &--showroom {

    @include viewport--sm {
      padding: 6px 0 40px;
    }
  }

  &--virtual {
    padding: 0;
    max-width: 100%;
    margin: 0 auto;

    .solution__inner {
      padding: 54px 0 66px;
      max-width: 1139px;
      margin: 0 auto;
      flex-direction: row-reverse;
      justify-content: flex-end;
      align-items: start;
      gap: 75px;

      @include viewport--sm {
        flex-direction: column;
        flex-direction: column-reverse;
        align-items: center;
        gap: 35px;
        padding: 54px 0 35px;
      }
    }

    .solution__title {
      margin-top: 20px;
      margin-bottom: 30px;

      @include viewport--sm {
        margin-bottom: 5px;
      }
    }

    .solution__text {
      font-size: 14px;
      line-height: 24px;
      margin: 0;
      padding: 0 20px 0 0;
      max-width: 675px;

      @include viewport--sm {
        margin-bottom: 10px;
      }
    }

    .solution__images {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 24px;

      @include viewport--sm {
        grid-template-columns: 1fr;
      }
    }

    .solution__image {
      overflow: hidden;
      max-height: 504px;
      cursor: pointer;
    }

  }
}
