.pay-now {
  padding: 57px 75px 59px 48px;

  background-color: $beige-extralight;

  .title {
    margin-top: 0;
    margin-bottom: 25px;
  }

  p {
    margin: 0;

    margin-bottom: 38px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    color: $color-darkgray;
  }

  .btn {
    width: 401px;
    padding: 17px 20px 11px 41px;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

.pay-now__wrapper {
  display: flex;
  align-items: center;

  svg {
    width: 28px;
    min-width: 28px;
    height: 20px;
  }
}

.pay-now__img-wrapper {
  margin-left: 45px;
  width: 244px;
  height: 37px;
}

@include viewport--lg {
  .pay-now {
    padding: 49px 64px 49px 50px;

    .title {
      margin-bottom: 17px;
      font-size: 20px;
      line-height: 24px;
    }

    p {
      margin-bottom: 35px;

      font-size: 12px;
      line-height: 22px;
    }
  }

  .pay-now__wrapper {
    flex-direction: column;
    align-items: start;

    .btn {
      margin-bottom: 20px;
    }
  }

  .pay-now__img-wrapper {
    margin-left: 0;
    width: 223px;
    height: 34px;
  }
}

@include viewport--md {
  .pay-now {
    padding: 39px 30px 45px 30px;

    .title {
      margin-bottom: 12px;

      font-size: 18px;
      line-height: 22px;
    }

    p {
      margin-bottom: 23px;

      font-size: 12px;
      line-height: 22px;
    }

    .btn {
      padding-right: 12px;
      margin-bottom: 27px;
      width: 282px;
      max-width: 100%;

      svg {
        width: 21px;
      }
    }
  }
}

@media (max-width: 375px) {
  .pay-now {
    .btn {
      padding-left: 21px;
    }
  }
}
