.tag {
  @include reset-link;

  display: inline-block;
  padding: 10px 15px;

  font-weight: $font-weight--medium;
  line-height: 14px;
  color: $color-blue;

  background-color: $color-extralightgray;
  border-radius: 4px;
  cursor: pointer;

  transition: all $default-transition-settings;

  &:hover {
    color: $color-lightblue;
  }

  &--s {
    min-height: 28px;
    padding: 9px 15px;

    font-size: 11px;
    line-height: 11px;
  }

  &--m {
    min-height: 40px;
    padding: 14px 15px;

    font-size: 12px;
    line-height: 12px;

    @include viewport--sm {
      font-size: 11px;
      line-height: 11px;
      min-height: 29px;
      padding: 9px 9px 7px;
    }
  }

  &--active {
    color: $color-default-white;
    background-color: $color-darkmain;
  }

  &--hero {
    background: rgba($color-default-white, 0.3);
    border-radius: 32px;
    font-weight: 700;
    font-size: 12px;
    line-height: 32px;
    color: $color-default-white;
    padding: 5px 25px;
    cursor: unset;

    &:hover {
      color: $color-default-white;
    }

    @include viewport--md {
      padding: 12px 20px;
      font-size: 10px;
      line-height: 10px;
    }
  }
}
