.projects {
  margin-top: 88px;
  margin-bottom: 61px;

  @include viewport--xl {
    margin-top: 92px;
  }

  @include viewport--md {
    margin-top: 56px;
    margin-bottom: 54px;
  }

  &__list {
    @include reset-list;
    display: flex;
    flex-wrap: wrap;
    grid-row-gap: 38px;
    grid-column-gap: 28px;
    margin-bottom: 93px;

    @include viewport--xl {
      grid-row-gap: 25px;
      grid-column-gap: 26px;
      margin-bottom: 83px;
    }

    @include viewport--md {
      grid-row-gap: 20px;
      grid-column-gap: 0;
      padding-right: 3px;
    }
  }

  &__list-element {
    width: calc((100% - 60px) / 3);

    @include viewport--xl {
      width: calc((100% - 32px) / 2);
    }

    @include viewport--md {
      width: 100%;
    }
  }
}
