.wallpaper-categories-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  margin-left: -24px;
  margin-top: -24px;

  h3 {
    margin: 0;
    margin-bottom: 7px;

    font-weight: bold;
    font-size: 26px;
    line-height: 28px;
    letter-spacing: -0.02em;

    color: $color-default-white;
  }

  p {
    margin: 0;

    font-size: 14px;
    line-height: 22px;
    color: $color-default-white;
  }

  li {
    @include reset-item;
    position: relative;
    z-index: 4;
    overflow: hidden;
    width: calc(100% / 3 - 24px);
    height: 280px;
    margin-left: 24px;
    margin-top: 24px;

    border-radius: 8px;

    &:hover {
      img {
        transform: scale(1.1);
        opacity: 0.7;
      }
    }

    a {
      @include reset-link;

      overflow: hidden;
      padding: 19px 38px;
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
    }

    img {
      position: absolute;
      z-index: 3;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;

      transition: transform $default-transition-settings, opacity $default-transition-settings;
      object-fit: cover;
    }

    &.big {
      width: calc((100% / 3) * 2 - 24px);
    }

    &.max {
      min-width: 90%;
      width: 100%;
    }

    &.half {
      min-width: calc(50% - 24px);
      width: calc(50% - 24px);
    }
  }
}

.wallpaper-categories-list__text-wrapper {
  position: relative;
  z-index: 3;
  margin-top: auto;

  &--text-dark {
    h3 {
      color: $color-darkmain;
    }

    p {
      color: $color-darkmain;
      opacity: 0.7;
    }
  }
}

@include viewport--lg {
  .wallpaper-categories-list {
    margin-bottom: 83px;
    margin-left: -20px;

    h3 {
      font-size: 24px;
      line-height: 28px;
    }

    p {
      font-size: 14px;
      line-height: 22px;
    }

    li {
      width: calc(100% / 3 - 20px);
      height: 270px;
      margin-left: 20px;

      a {
        padding: 21px 29px;
      }

      &.big {
        width: calc((100% / 3) * 2 - 20px);
      }

      &.half {
        min-width: calc(50% - 20px);
        width: calc(50% - 20px);
      }
    }
  }
}


@include viewport--md {
  .wallpaper-categories-list {
    margin-top: 0;

    h3 {
      font-size: 22px;
      line-height: 28px;
    }

    p {
      font-size: 14px;
      line-height: 24px;
    }

    li {
      width: 100%;
      height: 240px;
      margin-top: 0;
      margin-bottom: 20px;

      a {
        padding: 20px 19px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &.half {
        width: 100%;
      }

      &.big {
        width: 100%;
      }
    }
  }
}
