.instagram {
  padding-top: 75px;
  padding-bottom: 95px;

  @include viewport--md {
    padding-top: 50px;
    padding-bottom: 60px;
  }

  &__text-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    width: 74px;
    min-width: 74px;
    height: 74px;
    margin-bottom: 14px;
    stroke-width: 2.5;
    stroke-linecap: round;
    stroke: $color-darkred;
    stroke-dasharray: 73;
    stroke-dashoffset: 73;

    &--no-js {
      stroke-dashoffset: 0;
    }
  }

  &__title {
    margin-bottom: 30px;
    text-align: center;
  }

  p {
    margin: 0;

    width: 500px;
    margin-bottom: 32px;
    text-align: center;
    font-size: 14px;
    font-weight: 600;

    @include viewport--sm {
      width: 100%;
    }
  }

  &__slider {
    position: relative;
    margin-bottom: 35px;

    &::before {
      content: "";
      position: absolute;
      z-index: 2;
      left: 0;
      top: 0;
      width: 122px;
      height: 100%;
      background: linear-gradient(to right, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);

      @include viewport--sm {
        width: 60px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      right: 0;
      top: 0;
      width: 122px;
      height: 100%;
      background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);

      @include viewport--sm {
        width: 60px;
      }
    }
  }

  &__btn {
    @include viewport--sm {
      width: 100%;
      margin: 0 $mobile-padding;
    }
  }
}

.instagram_gallery {
  display: flex;
  width: auto;

  a {
    flex-shrink: 0;
    width: 230px;
    height: 230px;
    margin-right: 5px;
    overflow: hidden;
  }
}

.draw-animation {
  animation: stroke-dash 1s ease forwards;
}

@keyframes stroke-dash {
  0% {
    stroke-dashoffset: 73;
  }

  100% {
    stroke-dashoffset: 0;
  }
}
