.showroom-item {
  padding: 37px 116px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;

  @include viewport--xl {
    padding: 25px 30px;
  }

  @include viewport--md {
    flex-direction: column;
    padding: 32px 0;
  }

  &__wrapper {
    width: 43%;

    @include viewport--xl {
      width: 48%;
    }

    @include viewport--md {
      width: 100%;
    }
  }

  &__swiper {
    width: 46%;
    overflow: hidden;

    @include viewport--xl {
      width: 49%;
    }

    @include viewport--md {
      width: 100%;
    }
  }

  .swiper-slide {
    height: auto;
  }

  picture {
    line-height: 0;
    font-size: 0;
    width: 100%;
    height: 100%;
    display: flex;
  }

  img {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  &__title {
    @include reset-item;
    font-weight: 800;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-bottom: 17px;

    @include viewport--xl {
      margin-bottom: 18px;
      font-size: 30px;
      line-height: 40px;
    }

    @include viewport--md {
      font-size: 26px;
      line-height: 36px;
      margin-bottom: 20px;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;

    @include viewport--md {
      padding-bottom: 30px;
    }
  }

  &--reverse {
    flex-direction: row-reverse;

    @include viewport--md {
      flex-direction: column;
    }

    .showroom-item__wrapper {
      margin-right: 35px;

      @include viewport--xl {
        margin-right: 0;
      }
    }
  }
}
