.gallery-slider {
  overflow: hidden;
}

.gallery-slider__img-wrapper {
  width: 100%;
  position: relative;
  padding-bottom: percentage(835/1200);
  margin: 0;

  img {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    object-fit: scale-down;
  }
}

.gallery-slider__info {
  position: relative;
  padding: 32px 32px 34px 120px;

  background-color: $color-beigelight;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;

    height: 100%;
    width: 86px;

    background-color: $color-green;
    background-image: url("../img/svg/symbol.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 38px 31px;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
  }
}

@include viewport--xl {
  .gallery-slider__info {
    padding: 25px 130px 27px 130px;

    &::before {
      width: 93px;
    }
  }
}

@include viewport--lg {
  .gallery-slider__info {
    padding: 21px 45px 20px 117px;

    p {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
    }

    &::before {
      width: 82px;

      background-size: 33px 27px;
    }
  }
}

@include viewport--md {
  .gallery-slider__info {
    padding: 14px 23px 19px 23px;

    &::before {
      display: none;
    }

    p {
      font-size: 12px;
      line-height: 18px;
    }
  }
}
