.find-us {
  padding-top: 39px;
  padding-bottom: 90px;

  .container {
    display: flex;
  }

  .title {
    margin-bottom: 25px;
  }
}

.find-us__preview {
  position: relative;
  width: 635px;
  height: 409px;
  flex-shrink: 0;
  cursor: pointer;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 121px;
    height: 121px;
    background-image: url("../img/svg/play_icon.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    transition: opacity $default-transition-settings;
  }

  &:hover {
    &::before {
      opacity: 0.7;
    }
  }
}

.find-us__text-block {
  padding-left: 61px;
}

@include viewport--lg {
  .find-us {
    padding-top: 31px;
    padding-bottom: 76px;

    .title {
      margin-bottom: 12px;
    }
  }

  .find-us__preview {
    width: 451px;
    height: 291px;

    &::before {
      width: 90px;
      height: 90px;
    }
  }

  .find-us__text-block {
    padding-left: 36px;
  }
}

@include viewport--md {
  .find-us {
    padding-top: 14px;
    padding-bottom: 62px;

    .container {
      flex-direction: column;
    }
  }

  .find-us__preview {
    margin-bottom: 27px;
    width: 100%;
    height: auto;

    position: relative;
    padding-bottom: percentage(221/343);

    &::before {
      width: 68px;
      height: 68px;
    }

    img {
      position: absolute;
      z-index: -1;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }

  .find-us__text-block {
    padding-left: 0;
  }
}
