.header-nav {
  background-image: url("../img/svg/atw-pattern-summer.svg");
  background-repeat: repeat-x;
  position: relative;
  background-size: auto 100%;
  background-color: $color-darkmain;

  .container {
    justify-content: space-between;
    align-items: flex-start;
    min-height: 56px;

    @include viewport--lg {
      justify-content: flex-start;
      padding-right: 35px;
    }

    @include viewport--md {
      justify-content: space-between;
      padding-right: 25px;
    }

    @include viewport--md {
      justify-content: space-between;
    }

    @include viewport--sm {
      padding-right: 0;
      padding-left: 0;
    }

    @media (max-width: 550px) {
      justify-content: flex-end;
    }
  }

  &__all {
    display: flex;
    align-items: center;
    min-width: 166px;
    min-height: 56px;
    padding: 20px 18px 20px 22px;

    font-weight: $font-weight--bold;
    font-size: 12px;
    line-height: 15px;
    color: $color-beigelight;

    background-color: rgba(255, 255, 255, 0.2);

    svg {
      width: 18px;
      min-width: 18px;
      height: 10px;
      transform: rotate(0deg);
      margin-left: 15px;
      object-fit: contain;
      transition: $default-transition-settings;

      @include viewport--md {
        margin-left: 26px;
      }

      @media (max-width: 550px) {
        margin-left: 10px;
      }
    }

    @include viewport--sm {
      width: 50%;
      max-width: 203px;
      min-width: 172px;
    }

    @media (max-width: 550px) {
      position: absolute;
      left: 0;
      padding: 20px 14px 20px 14px;
    }

    span {
      width: 80%;

      @media (max-width: 550px) {
        width: 90%;
      }
    }
  }

  &__menu {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    width: 30%;
    padding: 0 0 0 31px;
    min-height: 56px;
    height: 1px;
    overflow: hidden;
    flex-grow: 3;

    @include viewport--lg {
      margin-right: 0;
    }

    @media (max-width: 550px) {
      display: none;
    }

    .header-nav__item {
      @include reset-item;
      margin-right: 34px;
      display: flex;
      min-height: 56px;

      .link {
        height: 100%;
        display: flex;
        align-items: center;
        font-weight: $font-weight--medium;
        font-size: 12px;
        line-height: 15px;
        color: $color-mediumgray;

        svg {
          width: 36px;
          min-width: 36px;
          height: 27px;
          margin-left: 4px;
        }

        &:hover {
          color: $color-beigelight;
        }
      }
    }
  }

  &__sale {
    display: flex;
    min-height: 56px;
    position: relative;

    color: $color-default-white;

    @include viewport--md {
      margin-left: 0;
    }

    .link {
      display: flex;
      align-items: center;
      position: relative;
      font-weight: $font-weight--bold;
      color: $color-default-white;
      font-size: 12px;

      svg {
        height: 24px;
        width: 24px;
        min-width: 24px;
        margin-right: 6px;
      }

      @include viewport--sm {
        svg {
          margin-right: 12px;
        }
      }
    }
  }

  &__order {
    @include reset-link;
    position: relative;

    display: flex;
    align-items: center;
    width: 172px;
    min-height: 56px;
    margin-left: 25px;
    padding: 20px 15px 20px 47px;
    font-weight: $font-weight--bold;
    color: $color-default-white;
    background-color: $color-green;
    font-size: 12px;
    line-height: 15px;

    @include viewport--xl {
      padding-right: 15px;
    }

    @include viewport--sm {
      width: 56px;
      padding-right: 10px;
      padding-left: 10px;

      font-size: 0;
    }

    @media (max-width: 370px) {
      margin-left: 4%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 15px;

      width: 20px;
      height: 20px;

      background-image: url("../img/sprite/icon-search.svg");
      background-repeat: no-repeat;
      transform: translateY(-50%);

      @include viewport--sm {
        left: 50%;

        transform: translateY(-50%) translateX(-50%);
      }
    }

    &:hover {
      color: $color-beigelight;
      background-color: $color-darkmain;
    }
  }
}
