.help-choose {
  padding: 40px 0;
  position: relative;

  @include viewport--lg {
    padding: 50px 0;
  }

  @include viewport--sm {
    padding: 75px 0 50px;
  }

  &__img {
    height: 680px;
    border-radius: 16px;
    background-color: $color-darkgray;

    @include viewport--lg {
      height: 610px;
    }

    @include viewport--sm {
      height: 570px;
    }
  }

  &__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 34% 50%;
    border-radius: 15px;

    @include viewport--sm {
      object-position: 45% 50%;
    }
  }

  &__description {
    position: absolute;
    height: 100%;
    top: 0;
    left: 0;
    padding: 124px 0 88px 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--lg {
      padding: 120px 0 80px 30px;
    }

    @include viewport--sm {
      justify-content: flex-end;
      padding: 20px 0 83px 17px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 54px;
    color: $color-default-white;
    z-index: 1;
    max-width: 400px;
    letter-spacing: -0.02em;
    margin-bottom: 37px;

    @include viewport--lg {
      font-size: 44px;
      line-height: 44px;
      margin-bottom: 23px;
    }

    @include viewport--sm {
      font-size: 32px;
      line-height: 38px;
      margin-bottom: 0;
      padding-right: 10px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
    letter-spacing: -0.02em;
    color: $color-default-white;
    max-width: 530px;

    @include viewport--lg {
      font-size: 18px;
      line-height: 28px;
    }

    @include viewport--sm {
      max-width: 250px;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 13px;
    }
  }

  &__mock {
    display: flex;
    align-items: center;
    margin-left: -4px;

    @include viewport--sm {
      padding-left: 33px;
    }

    &-percent {
      font-weight: 400;
      font-size: 72px;
      line-height: 24px;
      letter-spacing: -0.05em;
      color: $color-green;
      margin-right: 13px;

      @include viewport--sm {
        font-size: 60px;
        line-height: 24px;
      }
    }

    &-text {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      color: $color-default-white;
      opacity: 0.7;
      max-width: 243px;

      @include viewport--sm {
        font-size: 10px;
        line-height: 16px;
      }
    }
  }
}
