.find-us-gallery {
  @include reset-list;
}

.find-us-gallery__item {
  @include reset-item;
  cursor: pointer;
  margin-bottom: 24px;

  transition: background-color $default-transition-settings;

  &:last-child {
    margin-bottom: 0;
  }

  &:hover {
    background-color: $beige-extralight;
  }

  display: flex;
  align-items: center;

  p {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  img,
  picture {
    flex-shrink: 0;
  }

  img {
    margin-right: 20px;
    width: 70px;
    height: 70px;
  }
}

@include viewport--lg {
  .find-us-gallery__item {
    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}

@include viewport--md {
  .find-us-gallery__item {
    margin-bottom: 15px;

    picture {
      margin: 0;
    }
  }
}
