.quality {
  display: flex;
}

.quality__facts {
  display: flex;
}

.quality__wrapper {
  flex-shrink: 0;
  align-self: start;
  display: flex;
  flex-wrap: wrap;
  width: 233px;

  svg {
    width: 68px;
    min-width: 68px;
    height: 53px;
  }

  &--rating {
    margin-right: 75px;

    .quality__icon {
      align-self: center;
      margin-right: 10px;
    }
  }

  &--geography {
    width: 205px;

    .quality__number {
      margin-left: -38px;
      position: relative;
      min-width: 65px;
      text-align: right;
      justify-content: flex-end;

      &::before {
        position: absolute;
        content: "";
        top: 45%;
        left: -35px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: $color-green;
      }
    }

    .quality__desc {
      padding-left: 25px;
    }

    .quality__icon {
      align-self: center;
    }
  }
}

.quality__promo {
  margin: 0;

  width: 49%;
  margin-right: 95px;

  p {
    margin: 0;
    font-weight: 500;
    font-size: 26px;
    line-height: 40px;

    letter-spacing: -0.02em;
  }
}

.quality__number {
  margin: 0;
  font-weight: 500;
  font-size: 72px;
  line-height: 84px;

  display: flex;
  align-items: center;
  align-self: start;
  letter-spacing: -0.05em;
}

.quality__desc {
  margin: 0;

  width: 100%;

  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.05em;
}

@include viewport--lg {
  .quality__facts {
    margin-left: auto;
  }

  .quality__wrapper {
    &--rating {
      margin-right: 50px;
    }
  }

  .quality__promo {
    width: 46%;
    margin-right: 25px;

    p {
      font-size: 22px;
      line-height: 34px;
    }
  }
}

@include viewport--md {
  .quality {
    align-items: center;
    flex-direction: column;
  }

  .quality__promo {
    width: 80%;
    margin-right: 0;
    margin-bottom: 37px;

    p {
      text-align: center;
    }
  }

  .quality__facts {
    margin-left: 0;
  }

  .quality__wrapper {
    &--rating {
      margin: 0 30px 20px 0;
    }

    &--geography {
      margin: 0 auto;
      width: 233px;
    }
  }
}

@include viewport--sm {
  .quality {
    flex-direction: column;
  }

  .quality__facts {
    width: 100%;
    flex-direction: column;
    margin-left: 0;
  }

  .quality__promo {
    width: 100%;
    margin-right: 0;
    margin-bottom: 27px;

    p {
      font-size: 18px;
      line-height: 28px;
      text-align: left;
    }
  }

  .quality__wrapper {
    width: 100%;
    flex-wrap: nowrap;
    align-items: center;

    &--rating {
      margin-left: 0;
      margin-bottom: 30px;

      .quality__icon {
        width: 55px;
        height: 43px;
        transform: scale(0.8);
      }
    }

    &--geography {
      margin-left: 0;

      .quality__icon {
        width: 91px;
        height: 47px;
      }

      .quality__number {
        margin-left: -40px;
        margin-right: 25px;
        position: relative;

        &::before {
          position: absolute;
          content: "";
          top: 16px;
          left: -25px;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background-color: $color-green;
        }
      }

      .quality__desc {
        padding-left: 0;
      }
    }
  }

  .quality__number {
    align-self: center;
    flex-shrink: 0;
    margin-right: 25px;

    font-size: 42px;
    line-height: 47px;
  }

  .quality__desc {
    width: auto;
    padding-top: 0;
    font-size: 12px;
    line-height: 22px;
  }

  .quality__icon {
    flex-shrink: 0;
  }
}
