.modal-gallery {
  position: relative;
  margin: auto 0;
  width: 1200px;

  p {
    margin: 0;
  }

  ul.swiper-wrapper {
    @include reset-list;
  }

  .slider-arrow-left,
  .slider-arrow-right {
    position: absolute;
    display: block;
    width: 40px;
    height: 69px;
    top: 50%;
    cursor: pointer;
    border: 0;
    opacity: 0.3;
    background-color: transparent;
    background-image: url("../img/svg/arrow-white.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: 1;
    }
  }

  .slider-arrow-left {
    left: -85px;
    transform: translateY(-50%);
  }

  .slider-arrow-right {
    right: -85px;
    transform: translateY(-50%) rotate(-180deg);
  }

  &--review {
    .slider-arrow-left,
    .slider-arrow-right {
      top: 50%;
      transform: translate(-50%, -50%) rotate(-180deg);
    }

    .slider-arrow-right {
      transform: translate(100%, -50%) rotate(-180deg);
      right: -65px;
    }

    .slider-arrow-left {
      transform: translate(-100%, -50%);
      left: -65px;
    }

    .swiper-wrapper {
      align-items: center;
    }
  }
}

.close-btn {
  position: absolute;
  right: 0;
  top: -53px;

  width: 36px;
  height: 36px;

  display: block;

  cursor: pointer;
  opacity: 0.2;
  background-color: transparent;
  border: 0;
  background-image: url("../img/svg/close.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: $default-transition-settings;

  &:hover {
    opacity: 1;
  }
}

@include viewport--xl {
  .modal-gallery {
    width: 81.4%;

    .slider-arrow-left,
    .slider-arrow-right {
      top: 318px;
    }
  }
}

@include viewport--lg {
  .modal-gallery {
    width: 83.9%;

    .slider-arrow-left,
    .slider-arrow-right {
      width: 30px;
      height: 52px;
    }

    .slider-arrow-left {
      left: -47px;
    }

    .slider-arrow-right {
      right: -47px;
    }

    &--review {
      .slider-arrow-left {
        left: -47px;
      }

      .slider-arrow-right {
        right: -47px;
      }
    }
  }

  .close-btn {
    top: -48px;
    width: 32px;
    height: 32px;
  }
}

@include viewport--md {
  .modal-gallery {
    width: 91.5%;

    .slider-arrow-left,
    .slider-arrow-right {
      width: 32px;
      height: 50px;
      z-index: 2;
    }

    .slider-arrow-left {
      left: 14px;
    }

    .slider-arrow-right {
      right: 14px;
    }

    &--review {
      .slider-arrow-left {
        left: -7px;
      }

      .slider-arrow-right {
        right: -7px;
      }

      &.modal-gallery {
        width: 85%;
      }
    }
  }

  .close-btn {
    top: -36px;
    width: 24px;
    height: 24px;
  }
}

@include viewport--sm {
  .modal-gallery--review {
    .slider-arrow-left {
      left: -7px;
    }

    .slider-arrow-right {
      right: -7px;
    }

    &.modal-gallery {
      width: 80%;
    }
  }
}

@media (max-width: 400px) {
  .modal-gallery--review {
    .slider-arrow-left {
      left: 0;
    }

    .slider-arrow-right {
      right: 0;
    }
  }
}
