.subscribe {
  position: sticky;
  height: fit-content;
  overflow: auto;

  img,
  picture {
    flex-shrink: 0;
    width: 278px;
    height: 326px;
  }
}

.subscribe__text-wrapper {
  width: 235px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  h2 {
    font-weight: 800;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 12px;
    line-height: 20px;

    color: $color-darkgray;
  }

  .btn {
    align-self: start;
    margin-top: 23px;
    display: block;
    text-align: center;
    padding: 11px 36px 7px;

    font-weight: bold;
    font-size: 12px;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@include viewport--lg {
  .subscribe {
    img,
    picture {
      width: 241px;
      height: 283px;
    }
  }

  .subscribe__text-wrapper {
    width: 230px;
    margin: 0;
    margin-left: 31px;

    h2 {
      margin-bottom: 21px;
      font-size: 20px;
      line-height: 24px;
    }
  }
}

@include viewport--md {
  .subscribe {
    picture,
    img {
      margin: 0 auto;
    }
  }

  .subscribe__text-wrapper {
    width: 65%;
    margin: 0 auto;

    h2 {
      margin-bottom: 5px;
    }

    .btn {
      margin-top: 30px;
    }
  }
}
