.service-block {
  display: flex;
  justify-content: space-between;

  p {
    margin: 0;

    width: 47.7%;
    padding-top: 5px;

    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
  }
}

.service-block__svg-wrapper {
  display: flex;
  width: 44%;

  svg {
    flex-shrink: 0;
    margin-right: 23px;
    width: 102px;
    min-width: 102px;
    height: 80px;
  }

  b {
    margin-top: 5px;

    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.02em;
  }
}

@include viewport--lg {
  .service-block__svg-wrapper {
    b {
      font-size: 15px;
      line-height: 22px;
    }
  }
}
