.contacts-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  margin-right: -24px;
  margin-top: -24px;
}

.contacts-list__item {
  @include reset-item;
  padding: 30px 39px 55px 39px;

  background-color: #ffffff;
  border-radius: 8px;
  width: calc(100% / 3 - 24px);
  margin-right: 24px;
  margin-top: 24px;


  h2 {
    margin-top: 0;
    margin-bottom: 21px;

    font-weight: bold;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    color: $color-mediumgray;
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: $color-darkgray;
  }

  svg {
    flex-shrink: 0;
  }

  a {
    @include reset-link;
    transition: color $default-transition-settings;

    &:hover {
      color: $color-blue;
    }
  }
}

.contacts-list__socials-list {
  @include reset-list;

  display: flex;
  margin-right: 16px;
  padding-top: 7px;
  flex-direction: row-reverse;

  svg {
    width: 31px;
    min-width: 31px;
    height: 31px;
  }

  a {
    @include reset-link;
  }
}

.contacts-list__social-item {
  @include reset-item;
  margin-left: -4px;
  z-index: auto;

  &:last-child {
    margin-left: 0;
  }
}

.contacts-list__messenger {
  margin-bottom: 27px;
}

.contacts-list__messenger-number {
  display: flex;
  margin-bottom: 1px;

  a {
    @include reset-link;
    display: block;
    font-weight: 800;
    font-size: 26px;
    line-height: 46px;
    letter-spacing: -0.02em;

    color: $color-darkmain;
  }
}

.contacts-list__number {
  margin-bottom: 22px;

  &:last-child {
    margin-bottom: 0;
  }

  h3 {
    margin-top: 0;
    margin-bottom: -5px;

    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
  }

  a {
    @include reset-link;
    width: fit-content;
    display: block;
    margin-bottom: -5px;

    font-weight: bold;
    font-size: 20px;
    line-height: 46px;
    letter-spacing: -0.02em;

    color: $color-darkmain;
  }
}

.contacts-list__mail {
  margin-bottom: 18px;

  a {
    @include reset-link;
    display: block;
    width: fit-content;
    margin-bottom: -7px;

    font-weight: 600;
    font-size: 26px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
  }

  &--small {
    margin-bottom: 18px;

    &:last-child {
      margin-bottom: 0;
    }

    a {
      font-size: 16px;
      line-height: 46px;
    }
  }
}

.contacts-list__social-lead {
  display: flex;
  margin-bottom: 5px;

  a {
    @include reset-link;
    display: flex;
    align-items: center;

    &:hover {
      p {
        color: $color-blue;
      }
    }
  }

  svg {
    width: 48px;
    min-width: 48px;
    height: 48px;
    margin-right: 18px;
  }

  p {
    font-weight: 500;
    font-size: 26px;
    line-height: 22px;
    letter-spacing: -0.02em;

    transition: color $default-transition-settings;

    color: $color-darkmain;
  }
}

.contacts-list__socials-wrapper {
  margin-bottom: 33px;
}

.contacts-list__social {
  margin-bottom: 11px;

  &:last-child {
    margin-bottom: 0;
  }

  svg {
    margin-right: 18px;

    fill: $color-mediumgray;
    transition: fill $default-transition-settings;
  }

  a {
    width: max-content;
    display: flex;
    align-items: center;

    &:hover {
      svg {
        fill: $color-darkmain;
      }
    }
  }

  p {
    margin: 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
  }
}

@include viewport--lg {
  .contacts-list {
    margin-right: -20px;
    margin-top: -20px;
  }

  .contacts-list__item {
    padding: 30px 32px 34px;
    margin-right: 20px;
    margin-top: 20px;

    width: calc(100% / 3 - 20px);

    p {
      font-size: 12px;
      line-height: 16px;
    }
  }

  .contacts-list__messenger {
    margin-bottom: 25px;
  }

  .contacts-list__number {
    h3 {
      margin-bottom: -7px;
      font-size: 13px;
      line-height: 24px;
    }

    a {
      margin-bottom: -2px;
    }
  }

  .contacts-list__messenger-number {
    a {
      font-size: 22px;
      line-height: 46px;
    }
  }

  .contacts-list__socials-list {
    margin-right: 11px;

    svg {
      width: 27px;
      height: 27px;
    }
  }

  .contacts-list__social-item {
    margin-left: -2px;
  }

  .contacts-list__mail {
    margin-bottom: 22px;

    a {
      margin-bottom: -4px;
      font-size: 22px;
      line-height: 46px;
    }


    &--small {
      margin-bottom: -3px;

      a {
        margin-bottom: -8px;

        font-size: 16px;
        line-height: 46px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }
    }
  }

  .contacts-list__socials-wrapper {
    margin-bottom: 19px;

    p {
      font-size: 12px;
      line-height: 18px;
    }
  }

  .contacts-list__social-lead {
    margin-bottom: 11px;

    svg {
      margin-right: 13px;

      width: 42px;
      height: 42px;
    }

    p {
      font-size: 22px;
      line-height: 22px;
    }
  }

  .contacts-list__social {
    svg {
      margin-right: 7px;
    }

    p {
      font-size: 14px;
      line-height: 22px;
    }
  }
}

@include viewport--md {
  .contacts-list {
    margin: 0;
  }

  .contacts-list__item {
    padding: 30px 32px 54px;
    width: 100%;
    margin-right: 0;
    margin-top: 0;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media (max-width: 375px) {
  .contacts-list__messenger-number a {
    font-size: 20px;
  }
}
