.in-blog {
  padding: 94px 0 40px;
  overflow: hidden;

  @include viewport--lg {
    padding: 58px 0 40px;
  }

  @include viewport--sm {
    padding: 40px 0 5px;
  }

  &__title {
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
    margin-bottom: 30px;

    @include viewport--sm {
      font-size: 22px;
      line-height: 26px;
    }
  }

  &__slider {
    position: relative;
  }

  &__buttons-wrapper {
    display: inline-block;
    position: absolute;
    top: -72px;
    right: 0;

    @include viewport--sm {
      top: -66px;
    }
  }

  &__button-prev,
  &__button-next {
    padding: 0;
    border: none;
    background-color: transparent;
    fill: $color-lightgray;
    transition: fill $default-transition-settings;
    cursor: pointer;

    &:hover {
      fill: $color-darkmain;
    }

    svg {
      @include viewport--sm {
        width: 42px;
        height: 42px;
      }
    }

  }

  &__button-prev {
    margin-right: 7px;
  }


  .article {
    max-width: 32.3%;

    @include viewport--lg {
      max-width: 32%;
    }

    @include viewport--sm {
      max-width: 100%;
    }
  }

  .article__title {
    line-height: 26px;

    @include viewport--lg {
      font-size: 22px;
    }

    @include viewport--sm {
      font-size: 18px;
      line-height: 18px;
    }
  }

  .article__img img {
    width: 100%;
    height: 100%;

    @include viewport--lg {
      margin-bottom: 21;
    }
  }

  .article__about {
    width: 100%;
    margin-top: 18px;
    border-bottom: none;

    @include viewport--sm {
      margin-top: 16px;
      margin-bottom: 0;
    }
  }
}
