.filter-reset {
  display: block;
  font-weight: $font-weight--medium;
  color: $color-default-white;

  border: none;
  background-color: $color-blue;
  border-radius: 4px;
  cursor: pointer;
  opacity: 1;
  transition: opacity $default-transition-settings;

  min-height: 40px;
  padding: 14px 15px;

  font-size: 12px;
  line-height: 12px;

  @include viewport--sm {
    font-size: 11px;
    line-height: 11px;
    min-height: 29px;
    padding: 9px 9px 7px;
  }

  &:hover {
    opacity: 0.7;
  }
}
