.quote {
  position: relative;

  padding-left: 47px;
  margin: 0;
  margin-top: 45px;
  margin-bottom: 53px;

  font-weight: 600;
  font-size: 18px;
  line-height: 30px;

  &::before {
    content: "";
    position: absolute;

    left: 5px;
    top: 0;

    width: 8px;
    height: 100%;

    background: $color-beigelight;
  }
}

@include viewport--lg {
  .quote {
    padding-left: 45px;
    margin: 42px 0 37px;

    &::before {
      left: 0;
    }
  }
}

@include viewport--sm {
  .quote {
    padding-left: 32px;
    padding-right: 5px;
    margin: 27px 0 37px;

    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
  }
}
