.materials {
  padding-top: 81px;
  padding-bottom: 48px;

  background: linear-gradient(90deg, $extra-dark 0%, $color-darkmain 99.86%);
  color: $color-default-white;

  &__btn {
    margin: 0 auto;
  }
}


.materials__header-wrapper {
  padding: 0 14%;
  margin-bottom: 52px;

  .title {
    margin-bottom: 24px;
    text-align: center;
    margin-top: 0;
    font-weight: bold;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;

    color: $color-default-white;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
  }
}

@include viewport--xl {
  .materials {
    padding-bottom: 48px;
  }

  .materials__header-wrapper {
    padding: 0 9%;
  }
}

@include viewport--lg {
  .materials {
    padding-bottom: 40px;
  }

  .materials__header-wrapper {
    margin-bottom: 67px;
    padding: 0 7%;

    .title {
      margin-bottom: 22px;

      font-size: 36px;
      line-height: 40px;
    }

    p {
      font-size: 14px;
      line-height: 24px;
    }
  }
}

@include viewport--sm {
  .materials {
    padding-top: 51px;
    padding-bottom: 35px;
  }

  .materials__header-wrapper {
    padding: 0;
    margin-bottom: 54px;

    .title {
      font-size: 28px;
      line-height: 32px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}
