.promo__wrapper {
  display: flex;
  flex-direction: column;

  &--center {
    align-items: center;

    .promo__content {
      text-align: center;
    }

    .breadcrumbs__list {
      justify-content: center;
    }
  }
}

.promo__img {
  font-size: 0;
  line-height: 0;
}

.promo__content {
  padding-right: 15px;
}

.promo--bg {
  min-height: 380px;
  position: relative;

  .promo__img {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;

    img {
      width: 100%;
      max-height: 100%;
      height: 100%;
      object-fit: fill;
      object-position: center;

      @include viewport--xxl {
        object-position: left center;
      }

      @include viewport--xl {
        object-fit: cover;
      }
    }
  }

  .promo__wrapper {
    padding: 47px 0 101px;
  }

  .promo__content {
    position: relative;
    z-index: 1;
    max-width: 52%;
    padding-bottom: 106px;

    @include viewport--md {
      max-width: 90%;
    }

    p {
      margin: 27px 0 16px;
    }
  }
}

.promo--parallax {
  min-height: 600px;
  height: 620px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;

  @include viewport--lg {
    min-height: 480px;
    height: inherit;
  }

  @include viewport--sm {
    min-height: 352px;
  }

  .promo-rellax {
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 128%;
    top: 0;
    border-radius: 8px;

    @include viewport--sm {
      height: 140%;
    }
  }

  .promo__img {
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 100%;
    top: 0;
    border-radius: 8px;

    img {
      width: 100%;
      max-height: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 8px;

      @include viewport--xl {
        object-fit: cover;
      }

      @include viewport--sm {
        object-fit: fill;
      }
    }
  }

  .promo__wrapper {
    padding: 41px 35px 101px;

    @include viewport--md {
      padding: 41px 25px 40px;
    }

    @include viewport--sm {
      padding: 35px 17px 10px;
    }
  }

  .promo__content {
    position: relative;
    z-index: 1;
    max-width: 52%;
    padding-top: 145px;
    padding-bottom: 106px;

    @include viewport--md {
      max-width: 90%;
      padding-top: 100px;
      padding-bottom: 40px;
    }

    @include viewport--sm {
      padding-top: 44px;
      padding-bottom: 10px;
    }


    .title {
      text-transform: uppercase;
      letter-spacing: 0.1em;

      @include viewport--sm {
        font-size: 32px;
        line-height: 48px;
      }
    }

    p {
      margin: 29px 0 16px;
    }

    &--light {
      .title {
        color: $color-default-white;
      }

      p {
        color: $color-default-white;
      }
    }
  }
}

.promo--gradient {
  min-height: 380px;
  position: relative;

  .promo__img {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    background: linear-gradient(180deg, #acbcdc 0%, #fef3e1 100%);
  }

  .promo__wrapper {
    padding: 47px 0 20px;
  }

  .promo__content {
    position: relative;
    z-index: 1;
    max-width: 52%;
    padding-top: 15px;
    padding-bottom: 20px;

    @include viewport--md {
      max-width: 90%;
    }

    .paragraph {
      margin: 27px 0 16px;
    }
  }
}

.promo--small {
  padding: 52px 0 27px;

  .promo__wrapper {
    display: flex;
    flex-direction: row;

    @include viewport--sm {
      flex-direction: column;
    }
  }

  .promo__content {
    width: 60%;

    .title {
      max-width: 95%;
      padding-top: 6px;

      @include viewport--lg {
        max-width: 100%;
      }
    }

    .paragraph {
      margin-top: 27px;
      margin-bottom: 27px;
      max-width: 95%;

      @include viewport--lg {
        margin-top: 16px;
        margin-bottom: 16px;
        max-width: 100%;
        font-size: 12px;
        line-height: 22px;
      }
    }

    @include viewport--lg {
      width: 55%;
    }

    @include viewport--sm {
      width: 100%;
    }
  }

  .promo__img {
    width: 40%;
    display: flex;
    justify-content: flex-end;

    @include viewport--lg {
      width: 45%;
    }

    @include viewport--sm {
      width: 100%;
      display: block;
    }

    picture {
      width: 100%;
    }

    img {
      width: 100%;
      height: 320px;
      object-fit: cover;
      object-position: center;

      @include viewport--lg {
        width: auto;
        height: 239px;
        object-fit: cover;
        object-position: center;
      }

      @include viewport--sm {
        width: 100%;
        height: auto;
        object-fit: cover;
        object-position: center;
      }
    }
  }
}
