.collections-cards-slider {
  overflow: hidden;
  position: relative;
  margin-bottom: 85px;

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    width: 122px;
    height: 100%;
    background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  &__title {
    margin-bottom: 18px;
  }

  @include viewport--md {
    margin-bottom: 60px;
  }

  &--main {
    margin-top: 20px;

    @include viewport--sm {
      margin-top: 15px;
    }
  }
}
