.shop {
  padding-top: 49px;
  padding-bottom: 157px;

  @include viewport--xl {
    padding-bottom: 100px;
  }

  @include viewport--lg {
    padding-bottom: 62px;
  }

  @include viewport--md {
    padding-top: 25px;
  }

  @include viewport--sm {
    padding-bottom: 98px;
  }

  &__other-products {
    margin-bottom: 120px;

    @include viewport--lg {
      margin-bottom: 100px;
    }

    @include viewport--md {
      margin-bottom: 90px;
    }

    @include viewport--sm {
      margin-bottom: 70px;
    }
  }

  .breadcrumbs ul {
    padding-left: 6px;
    justify-content: center;
  }
}


.shop .title {
  margin: 0;
  margin-bottom: 28px;
  padding: 0;
  color: $color-darkmain;
  text-align: center;

  @include viewport--lg {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 25px;
  }

  @include viewport--md {
    margin-bottom: 20px;
  }

  @include viewport--sm {
    font-size: 28px;
    line-height: 36px;
    margin-bottom: 16px;
  }
}

.shop__wrapper {
  padding-left: 90px;
  width: 50%;

  @include viewport--sm {
    padding-left: 0;
    width: 100%;
  }
}

.shop__img-wrapper {
  width: 50%;

  img {
    width: 100%;
    height: auto;
  }

  @include viewport--sm {
    width: 100%;
  }
}

.shop__about {
  padding-right: 15px;
  padding-bottom: 20px;
  display: flex;
  flex-wrap: nowrap;

  @include viewport--sm {
    flex-direction: column;
    padding-left: 0;
    padding-right: 0;
  }

  .btn--m {
    padding-top: 15px;
    padding-bottom: 14px;
    line-height: 12px;

    @include viewport--sm {
      font-size: 12px;
      line-height: 18px;
      min-width: 190px;
    }
  }
}

.shop__about h2 {
  padding-top: 16px;
  margin: 0;
  margin-bottom: 21px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 42px;
  line-height: 46px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--lg {
    padding-top: 0;
    margin-top: -3px;
  }

  @include viewport--sm {
    font-size: 28px;
    line-height: 32px;
    padding-top: 12px;
    margin-top: 0;
  }
}

.shop__about p {
  margin: 0;

  margin-bottom: 30px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 16px;
  line-height: 28px;
  color: $color-darkmain;

  @include viewport--lg {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 29px;
  }
}
