.search-result {
  padding-top: 49px;
  padding-bottom: 96px;

  background: linear-gradient(180deg, $color-beigelight 21.61%, #f3f3f3 100%, #f3f3f3 100%);

  .title {
    margin-bottom: 44px;
  }
}

@include viewport--md {
  .search-result {
    padding-top: 25px;
  }
}

@include viewport--sm {
  .search-result {
    padding-bottom: 55px;

    .title {
      margin-bottom: 34px;
    }
  }
}
