.paragraph-columns {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  p {
    margin: 0;
  }
}

.paragraph-columns__column {
  font-family: $font-family--Mont;
  font-size: 14px;
  line-height: 24px;
  background-position: 0 0;
  background-repeat: no-repeat;

  svg {
    margin-bottom: 28px;
  }

  @include viewport--md {
    font-size: 14px;
    line-height: 18px;
  }

  @include viewport--sm {
    font-size: 13px;
    line-height: 18px;
  }
}

.paragraph-columns__wrapper {
  height: 100px;
}

.paragraph-columns--4-columns .paragraph-columns__column {
  width: 20%;

  &:not(:nth-child(4n)) {
    margin-right: 5%;
  }
}

.paragraph-columns--img-left {
  .paragraph {
    font-size: 12px;
    line-height: 22px;
  }

  .title {
    margin-bottom: 6px;
  }

  .paragraph-columns__wrapper {
    margin-right: 25px;
  }

  .paragraph-columns__column {
    margin-right: 16%;
    padding-top: 9px;
    padding-left: 9px;
    margin-top: 25px;
    display: flex;

    &:nth-child(-n + 2) {
      margin-top: 0;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}
