.find-order {
  max-width: 1005px;
  padding: 49px 15px 114px;
  margin: 0 auto;

  @include viewport--lg {
    max-width: none;
    padding-left: 37px;
    padding-right: 37px;
    padding-bottom: 125px;
  }

  @include viewport--md {
    padding-top: 25px;
  }

  @include viewport--sm {
    max-width: 650px;
    padding-left: 17px;
    padding-right: 17px;
    box-sizing: border-box;
    padding-bottom: 97px;
  }

  .breadcrumbs {
    padding-left: 22px;
    display: flex;
    justify-content: center;

    @include viewport--lg {
      padding-left: 0;
    }
  }

  .title {
    margin: 0;
    margin-bottom: 38px;
    text-align: center;
    letter-spacing: -0.02em;
  }
}

.find-order__search-wrapper {
  padding: 44px 33px;
  padding-right: 39px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  background-color: $color-beigelight;
  border-radius: 4px;

  @include viewport--lg {
    padding-top: 44px;
    padding-bottom: 45px;
    padding-left: 33px;
    padding-right: 42px;
  }

  @include viewport--sm {
    position: relative;
    margin-bottom: 16px;
    padding-bottom: 36px;
    padding-top: 28px;
    padding-left: 19px;
    padding-right: 20px;
  }
}

.input-wrapper--withbutton.find-order__order-id-input {
  margin-right: auto;
  width: 612px;
  height: 50px;
  margin-bottom: 0;

  @include viewport--lg {
    width: 74%;
  }

  @include viewport--md {
    width: 70%;
  }

  @include viewport--sm {
    width: 100%;
  }

  input {
    font-weight: $font-weight--regular;

    &::placeholder {
      font-weight: $font-weight--regular;
    }
  }
}

.find-order .find-order__search-btn {
  position: relative;
  padding: 0;
  padding-right: 76px;
  padding-left: 18px;
  padding-top: 5px;
  min-height: 0;
  z-index: 10;

  &::before {
    position: absolute;
    content: "";
    top: 28%;
    right: 5%;
    width: 22px;
    height: 20px;
    background-image: url("../img/sprite/icon-search-button.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 22px 20px;

    @include viewport--lg {
      top: 29%;
      right: 5%;
    }

    @include viewport--sm {
      width: 17px;
      height: 16px;
      background-size: 17px 16px;
      right: 21%;
    }
  }

  @include viewport--lg {
    padding-left: 12px;
    max-width: none;
    width: 201px;
  }

  @include viewport--md {
    padding-right: 55px;
    width: 25%;
  }

  @include viewport--sm {
    position: absolute;
    min-width: auto;
    font-size: 0;
    line-height: 0;
    padding: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    top: 29%;
    right: 29px;
  }
}

.find-order__about-id {
  margin: 0;

  margin-bottom: 61px;
  font-family: $font-family--Mont;
  font-style: italic;
  font-weight: $font-weight--regular;
  font-size: 14px;
  line-height: 24px;
  color: $color-mediumgray;

  @include viewport--sm {
    margin-bottom: 37px;
    font-size: 12px;
    line-height: 20px;
  }

  span {
    color: $color-darkmain;
  }
}

.find-order hr:first-of-type {
  margin-bottom: 60px;

  @include viewport--sm {
    margin-bottom: 52px;
  }
}

.find-order hr:last-of-type {
  margin-bottom: 73px;

  @include viewport--sm {
    margin-bottom: 50px;
  }
}

.find-order__safe-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 63px;

  @include viewport--sm {
    margin-bottom: 52px;
  }
}

.find-order__safe-wrapper-texts {
  max-width: 490px;
  margin-right: 12%;

  svg {
    width: 33px;
    min-width: 33px;
    height: 31px;
  }

  @include viewport--lg {
    margin-right: 8%;
  }

  @include viewport--md {
    max-width: 300px;
  }

  @include viewport--sm {
    max-width: none;
  }
}

.find-order__robokassa-img-wrapper {
  padding-top: 17px;

  img {
    width: 303px;
    height: auto;
  }

  @include viewport--sm {
    display: none;
  }
}

.find-order__safe-wrapper h3 {
  padding-top: 10px;
  margin-bottom: 17px;
  display: flex;
  align-items: baseline;

  svg {
    width: 33px;
    height: 33px;
    min-width: 33px;
    margin-left: 17px;
  }

  @include viewport--sm {
    margin-bottom: 15px;
    font-size: 20px;
    line-height: 24px;
  }
}

.find-order__safe-wrapper p {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 14px;
  line-height: 24px;
  color: $color-darkmain;

  &:not(:last-child) {
    margin-bottom: 15px;
  }

  @include viewport--sm {
    font-size: 12px;
    line-height: 20px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

.find-order__contacts-title {
  margin: 0;

  margin-bottom: 45px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--light;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--sm {
    margin-bottom: 30px;
    width: 80%;
    font-size: 20px;
    line-height: 24px;
  }
}

.find-order__contacts-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;

  @include viewport--md {
    justify-content: flex-start;
  }

  @include viewport--sm {
    justify-content: flex-start;
    flex-direction: column;
  }
}

.find-order__contacts-item {
  @include reset-item;
  margin-bottom: 25px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @include viewport--sm {
    margin-bottom: 35px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &:nth-child(1n) {
    margin-right: 20%;

    @include viewport--lg {
      margin-right: 16%;
    }

    @include viewport--md {
      width: 40%;
      margin-right: 4%;
    }

    @include viewport--sm {
      width: 100%;
      margin-right: 0;
    }
  }

  &:nth-child(2n) {
    margin-right: 17.6%;

    @include viewport--lg {
      margin-right: 14.6%;
    }

    @include viewport--md {
      width: 40%;
      margin-right: 4%;
    }

    @include viewport--sm {
      width: 100%;
      margin-right: 0;
    }
  }

  &:nth-child(3n) {
    margin-right: 0;

    @include viewport--md {
      width: 40%;
      margin-right: 4%;
    }

    @include viewport--sm {
      width: 100%;
      margin-right: 0;
    }
  }
}

.find-order__text {
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 18px;
  color: $color-darkgray;

  @include viewport--sm {
    font-size: 13px;
  }
}

.find-order__contacts-list a {
  padding-top: 6px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 22px;
  line-height: 28px;
  color: $color-darkmain;
}

.find-order__contacts-img-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  svg {
    width: 21px;
    min-width: 21px;
    height: 21px;
    margin-right: 13px;
  }
}
