.check {
  padding-top: 128px;
  padding-bottom: 141px;

  color: #ffffff;
  background-color: $color-darkmain;

  .container {
    padding: 0 267px;

    display: flex;
  }

  .title {
    margin: 0;
    margin-bottom: 27px;

    font-weight: bold;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

.check__text-wrapper {
  width: 49%;
}

.check__img-wrapper {
  margin-right: 3px;
  margin-left: auto;
  margin-top: -10px;
  width: 350px;
  height: 329px;
}

@include viewport--xl {
  .check {
    padding-bottom: 128px;

    .container {
      padding: 0 18.5%;
    }
  }

  .check__text-wrapper {
    width: 55%;
    padding-left: 4px;
  }

  .check__img-wrapper {
    margin-top: 1px;
    margin-right: -1px;
    width: 318px;
    height: 299px;
  }
}

@include viewport--lg {
  .check {
    padding-top: 95px;
    padding-bottom: 143px;

    .container {
      padding: 0 11.2%;
    }

    .title {
      margin-bottom: 39px;
      font-size: 36px;
      line-height: 42px;
      letter-spacing: -0.02em;
    }

    p {
      font-size: 14px;
      line-height: 24px;
    }
  }

  .check__text-wrapper {
    padding-left: 0;
  }
}

@include viewport--md {
  .check {
    padding-top: 42px;
    padding-bottom: 58px;

    .container {
      flex-direction: column;
      padding: 0 17px;
    }

    .title {
      margin-bottom: 20px;

      font-size: 28px;
      line-height: 36px;
    }

    p {
      margin-bottom: 45px;

      font-size: 12px;
      line-height: 22px;
    }
  }

  .check__text-wrapper {
    width: 100%;
  }

  .check__img-wrapper {
    margin: 0 auto;
    width: 345px;
    height: 324px;
  }
}

@media (max-width: 375px) {
  .check__img-wrapper {
    width: 100%;
  }
}
