.inspire {
  padding-top: 100px;
  padding-bottom: 93px;

  @include viewport--lg {
    padding-top: 50px;
    padding-bottom: 82px;
  }

  @include viewport--sm {
    padding-top: 19px;
    padding-bottom: 61px;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;

    @include viewport--sm {
      display: grid;
      flex-direction: column;
      gap: 20px;
    }
  }

  .inspire-card {
    width: calc(50% - 24px/2);

    @include viewport--md {
      width: 100%;
    }
  }

  .title {
    margin-bottom: 26px;

    @include viewport--lg {
      margin-bottom: 28px;
    }

    @include viewport--sm {
      margin-bottom: 15px;
    }
  }
}

.inspire-card {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 400px;
  padding: 30px;

  &:hover {
    img {
      transform: scale(1.1);
    }
  }

  @include viewport--sm {
    min-height: 350px;
  }

  &__img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 8px;
    -webkit-mask-image: -webkit-radial-gradient(#fff,#000);

    picture {
      width: 100%;
      height: 100%;
      display: flex;
    }

    img {
      transition: transform $default-transition-settings;
      object-fit: cover;
      height: auto;
      width: auto;
      min-width: 100%;
      min-height: 100%;
      object-position: center;
      border-radius: 8px;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(360deg, rgba(46, 51, 70, 0.48) 0%, rgba(46, 51, 70, 0) 100%);;
      z-index: 9;
      top: 0;
      left: 0;
      border-radius: 8px;
    }
  }

  .title,
  &__text {
    position: relative;
    z-index: 2;
    color: $color-default-white;
    text-align: center;
  }

  .title {
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 8px;

    @include viewport--lg {
      font-size: 52px;
      line-height: 66px;
      display: flex;
      letter-spacing: -0.02em;
    }

    @include viewport--sm {
      font-size: 38px;
      line-height: 32px;
    }

    @media (max-width: 360px) {
      right: 0;
      font-size: 30px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;

    @include viewport--sm {
      font-size: 14px;
    }
  }
}
