.info {
  padding: 16px 7.9% 11px 4.8%;

  display: flex;
  background-color: $beige-extralight;

  p {
    margin: 0;

    padding-right: 9%;
    padding-top: 47px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  img {
    object-fit: cover;
    height: 206px;

    max-width: unset;
  }
}

@include viewport--lg {
  .info {
    p {
      padding-top: 11px;
    }

    img {
      width: 168px;
      height: 170px;
    }
  }
}

@include viewport--md {
  .info {
    padding: 28px 4.9% 24px 9.4%;
    flex-direction: column;

    p {
      margin-bottom: 22px;
      padding-right: 0;
      font-size: 12px;
      line-height: 22px;
    }
  }
}
