.header-main {
  background-color: $color-default-white;

  .container {
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 26px;
    padding-bottom: 30px;

    @include viewport--xl {
      padding-top: 26px;
    }

    @include viewport--lg {
      padding-top: 25px;
      padding-bottom: 32px;
    }

    @include viewport--sm {
      padding-top: 27px;
      padding-bottom: 20px;
      align-items: center;
    }
  }

  &__logo {
    display: flex;
    width: 30%;
    margin-right: 10px;

    svg {
      width: 200px;
      min-width: 200px;
      height: 24px;
    }

    @include viewport--xl {
      margin-right: 17px;
    }

    @include viewport--lg {
      width: 40%;
    }

    @include viewport--md {
      margin-right: 0;

      svg {
        width: 180px;
        min-width: 180px;
        height: 29px;
      }
    }

    @include viewport--sm {
      width: 90%;
    }

    a {
      line-height: 1px;
    }

    .header-main__text {
      margin: 0;

      max-width: 25%;
      margin-left: 21px;

      font-size: 11px;
      line-height: 16px;
      color: $color-mediumgray;
      font-weight: $font-weight--regular;

      @include viewport--lg {
        margin-left: 32px;
      }

      @include viewport--md {
        max-width: 32%;
        margin-left: 10px;
      }

      @include viewport--sm {
        display: none;
      }
    }
  }

  &__whatsapp {
    position: relative;

    display: flex;
    flex-direction: column;
    width: 17%;
    margin-right: 15px;

    svg {
      width: 87px;
      min-width: 87px;
      height: 20px;
      position: relative;
      top: -3px;

      @include viewport--lg {
        left: 3px;
      }
    }

    @include viewport--xl {
      padding-left: 5px;
    }

    @include viewport--lg {
      width: 25%;
      margin-right: 5px;
    }

    @include viewport--sm {
      display: none;
    }
  }

  &__whatsapp-text {
    display: flex;
  }

  &__call {
    display: flex;
    flex-direction: column;
    width: 15%;
    margin-right: 10px;

    @include viewport--xl {
      padding-left: 15px;
    }

    @include viewport--lg {
      width: 25%;
      margin-right: 17px;
      padding-left: 2px;
    }

    @include viewport--sm {
      display: none;
    }

    .header-main__text {
      margin: 0;

      font-weight: $font-weight--regular;
      font-size: 12px;
      line-height: 20px;
      color: $color-mediumgray;
    }

    .link--tel {
      color: $color-darkgray;

      &:hover {
        color: $color-green;
      }
    }
  }

  &__cart {
    position: relative;
    top: -7px;
    padding-right: 3px;

    svg {
      width: 20px;
      min-width: 20px;
      height: 19px;
    }

    @include viewport--xl {
      padding-right: 8px;
    }

    @include viewport--lg {
      top: -4px;

      padding-right: 4px;
    }

    @include viewport--sm {
      top: 0;

      margin-right: 27px;
    }

    a {
      @include reset-link;

      display: flex;
      justify-content: center;
      align-items: flex-end;
      position: relative;
      width: 40px;
      height: 40px;

      @include viewport--sm {
        top: 0;
        width: 25px;
        height: 25px;
      }
    }

    span {
      position: absolute;
      top: 6px;
      right: -2px;

      display: inline-block;
      width: 16px;
      height: 16px;

      font-size: 11px;
      line-height: 18px;
      text-align: center;
      color: $color-beigelight;

      background: $color-green;
      border-radius: 50%;

      @include viewport--xl {
        right: -2px;
      }

      @include viewport--lg {
        right: 0;
      }

      @include viewport--sm {
        top: -10px;
        right: -11px;
      }
    }
  }

  &__button {
    display: none;

    svg {
      width: 26px;
      min-width: 26px;
      height: 20px;
    }

    @include viewport--sm {
      display: block;
      position: relative;
      top: 4px;
    }
  }
}
