.card-tags {
  @include reset-list;

  display: flex;
  z-index: 2;

  svg {
    width: 36px;
    min-width: 36px;
    height: 36px;
  }

  li {
    @include reset-item;
    margin-right: 7px;

    &:last-child {
      margin-right: 0;
    }
  }

  span {
    border-radius: 50%;
    background-color: rgba($color-default-white, 0.7);
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: -0.02em;
  }

  &--vertical {
    flex-direction: column;

    li {
      margin-right: 0;
      margin-bottom: 8px;
    }

    span {
      height: 42px;
      width: 42px;
      min-height: 42px;
      min-width: 42px;
    }
  }
}
