.landing-desc {
  .container {
    display: flex;
  }

  background-repeat: no-repeat;
  background-position: bottom;
}

.landing-desc__img-wrapper {
  margin-top: auto;
  flex-shrink: 0;
  width: 589px;
  height: 563px;
  margin-left: 40px;
}

.landing-desc__text-wrapper {
  margin-left: auto;
  padding-top: 79px;
  padding-bottom: 189px;
  width: 49.1%;
  padding-right: 10%;

  p {
    margin-bottom: 14px;
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .landing-desc__subtitle {
    margin-bottom: 22px;

    font-weight: 300;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }
}

@include viewport--xl {
  .landing-desc {
    background-position: -80px;
  }

  .landing-desc__text-wrapper {
    padding-right: 3%;
  }

  .landing-desc__img-wrapper {
    margin-left: -40px;
  }
}

@include viewport--lg {
  .landing-desc {
    background-position: -191px;
  }

  .landing-desc__text-wrapper {
    padding-top: 80px;
    padding-bottom: 211px;

    p {
      margin-bottom: 24px;

      font-size: 14px;
      line-height: 24px;
    }

    .landing-desc__subtitle {
      margin-bottom: 26px;

      font-size: 28px;
      line-height: 32px;
    }
  }

  .landing-desc__img-wrapper {
    margin-left: -150px;
  }
}

@include viewport--md {
  .landing-desc {
    background-position: bottom 0 left -500px;

    .container {
      flex-direction: column;
    }
  }

  .landing-desc__img-wrapper {
    margin-left: -90px;
    margin-top: -18px;
    order: 1;
    width: 446px;
    height: 426.31px;
  }

  .landing-desc__text-wrapper {
    padding-top: 39px;
    padding-bottom: 0;
    padding-right: 0;
    margin-left: 0;
    width: 100%;

    p {
      margin-bottom: 12px;

      font-size: 12px;
      line-height: 22px;
    }

    .landing-desc__subtitle {
      margin-bottom: 24px;

      font-size: 22px;
      line-height: 26px;
    }
  }
}
