.desc-block {
  padding-top: 49px;
  padding-bottom: 18px;

  .title {
    margin-bottom: 28px;
  }
}

.desc-block__wrapper {
  display: flex;
}

.desc-block__text-wrapper {
  width: 55%;

  font-weight: 500;
  font-size: 14px;
  line-height: 24px;

  .paragraph-columns__column {
    width: 40%;

    p {
      margin: 0;
    }
  }
}

.desc-block__img-wrapper {
  padding-top: 12px;
  margin-left: auto;
  width: 541px;

  img {
    width: 541px;
    height: 348px;
    border-radius: 4px;
    object-fit: cover;
  }
}

.desc-block__text {
  margin-bottom: 45px;

  p {
    margin: 0;

    margin-bottom: 14px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@include viewport--xl {
  .desc-block {
    padding-bottom: 30px;

    .title {
      margin-bottom: 27px;
    }
  }

  .desc-block__img-wrapper {
    padding-top: 11px;
    width: 522px;

    img {
      width: 521px;
      height: 335px;
    }
  }

  .desc-block__text {
    margin-bottom: 39px;
    padding-right: 55px;
  }

  .desc-block__columns {
    padding-right: 55px;
  }

  .desc-block__text-wrapper {
    .paragraph-columns__column {
      width: 45%;
      margin-right: 10%;
    }
  }
}

@include viewport--lg {
  .desc-block {
    padding-top: 25px;

    .title {
      margin-bottom: 18px;
    }
  }

  .desc-block__img-wrapper {
    padding-top: 0;
    width: 372px;

    img {
      height: 239px;
    }
  }

  .desc-block__text-wrapper {
    width: 61%;

    .paragraph-columns__column {
      .title {
        font-size: 14px;
        line-height: 20px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }
    }
  }

  .desc-block__text {
    margin-bottom: 31px;
    padding-right: 41px;

    font-size: 12px;
    line-height: 22px;

    p {
      margin-bottom: 13px;
    }
  }

  .desc-block__columns {
    padding-right: 41px;
  }
}

@include viewport--md {
  .desc-block {
    padding-bottom: 7px;

    .title {
      margin-bottom: 8px;
    }
  }

  .desc-block__wrapper {
    flex-direction: column;

    .paragraph-columns {
      margin-bottom: 40px;
    }

    .paragraph-columns__column {
      width: 100%;
      margin-bottom: 33px;
      min-height: 73px;

      &--latex {
        min-height: 80px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .desc-block__img-wrapper {
    width: 62.5vw;
    max-width: 100%;
    margin-right: auto;
    margin-left: 0;

    img {
      width: 62.5vw;
      height: auto;
    }
  }

  .desc-block__text-wrapper {
    width: 100%;
  }

  .desc-block__text {
    padding-right: 0;
    margin-bottom: 22px;

    p {
      margin-bottom: 14px;
    }
  }

  .desc-block__columns {
    padding-right: 0;
  }
}

@include viewport--sm {
  .desc-block__img-wrapper {
    width: 83.33vw;

    img {
      width: 83.33vw;
    }
  }
}

@media (max-width: 375px) {
  .desc-block__img-wrapper {
    width: 100%;

    img {
      width: 100%;
    }
  }
}
