.studio-header {
  padding: 60px 0 32px;
  text-align: center;
  color: $color-darkmain;

  @include viewport--lg {
    padding: 48px 0 32px;
  }

  @include viewport--sm {
    padding: 32px 0 20px;
  }

  &__title {
    font-weight: 800;
    font-size: 52px;
    line-height: 54px;
    letter-spacing: -0.02em;
    margin: 0 0 24px;

    @include viewport--lg {
      font-size: 36px;
      line-height: 42px;
    }

    @include viewport--sm {
      margin: 0 0 20px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    max-width: 1040px;
    margin: 0 auto;

    @include viewport--lg {
      font-size: 16px;
      line-height: 28px;
      max-width: 855px;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 24px;
    }
  }

}
