.contacts-form {
  padding-top: 67px;
  padding-bottom: 181px;

  &--reviews {
    padding-top: 90px;

    .container {
      justify-content: space-between;
    }
  }

  &--proectorum {
    padding-top: 27px;

    .title--h4-medium {
      margin-bottom: 23px;
    }

    .title {
      &.desktop-only {
        display: block;
      }

      &.mobile-only {
        display: none;
      }
    }
  }

  .container {
    display: flex;
  }

  .title {
    margin-bottom: 34px;
  }

  .delivery {
    padding: 0;
  }

  .delivery__form {
    margin-right: 67px;
  }

  .checkbox-label {
    font-size: 13px;
    line-height: 28px;

    @include viewport--mm {
      line-height: 18px;
    }
  }
}

.contacts-form__requisite {
  padding-top: 66px;

  h3 {
    margin-top: 0;
    margin-bottom: 9px;

    font-weight: bold;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: -0.02em;
    appearance: none;

    color: $color-darkgray;
  }
}

.contacts-form__info {
  width: 32%;
  padding-top: 66px;

  h3 {
    margin-top: 0;
    margin-bottom: 14px;

    font-weight: bold;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    margin-bottom: 12px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

@include viewport--lg {
  .contacts-form {
    padding-top: 60px;
    padding-bottom: 80px;

    &--reviews {
      padding-top: 60px;
    }

    &--proectorum {
      padding-top: 27px;

      .delivery__rightside-wrapper {
        max-width: 40%;
      }

      &.contacts-form .delivery {
        width: 100%;
      }
    }

    .title {
      margin-bottom: 24px;
    }

    .delivery__form {
      max-width: unset;
      margin-right: 0;
    }

    .delivery__form--reviews {
      margin-right: 50px;
    }

    .delivery {
      width: 71%;
    }
  }

  .contacts-form__requisite {
    padding-top: 53px;
    padding-left: 53px;
    width: 29%;

    p {
      font-size: 12px;
      line-height: 32px;
    }
  }

  .contacts-form__info {
    padding-top: 53px;
  }
}

@include viewport--md {
  .contacts-form {
    padding-top: 45px;
    padding-bottom: 104px;

    &--proectorum {
      .title {
        &.desktop-only {
          display: none;
        }

        &.mobile-only {
          display: block;
        }
      }

      .delivery__wrapper {
        flex-direction: column;
      }

      .delivery__rightside-wrapper {
        max-width: unset;
        width: 100%;
        order: -1;
      }

      .delivery__rightside-bottom-wrapper {
        display: block;
      }

      .delivery__form .delivery__oferta-checkbox {
        order: unset;
      }
    }

    .container {
      flex-direction: column;
    }

    .delivery {
      margin-bottom: 40px;
      width: 100%;
    }

    .delivery__form--reviews {
      padding-top: 45px;
      margin-right: 0;
    }
  }

  .contacts-form__requisite {
    width: 100%;
    padding-left: 0;
    padding-top: 0;
  }

  .contacts-form__info {
    width: 100%;
    padding-left: 0;
    padding-top: 0;
  }
}

@include viewport--sm {
  .contacts-form--proectorum .delivery__rightside-wrapper {
    order: -1;
  }
}
