.breadcrumbs {
  $b: #{&};
  padding-bottom: 8px;

  @include viewport--md {
    margin-bottom: 4px;
  }

  &__list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    @include reset-item;
    position: relative;
    margin-right: 25px;
    min-height: 20px;
    height: 1px;
    padding: 5px 0;

    &:last-of-type {
      margin-right: 13px;
    }

    .link {
      @include reset-link;
      color: $color-darkgray;
      height: 100%;
      font-weight: $font-weight--regular;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: -0.02em;

      display: flex;
      align-items: center;
    }

    &::after {
      content: "→";
      position: absolute;
      padding-left: 8px;
      top: 50%;
      right: -15px;
      transform: translateY(-50%);

      color: $color-darkgray;
      line-height: 20px;
      font-size: 12px;
    }

    &:hover {

      .link,
      &::after {
        color: $color-green;
        transition: all $default-transition-settings;
      }
    }
  }

  &--light {
    #{$b}__item {
      .link {
        color: $color-beigelight;
      }

      &::after {
        color: $color-beigelight;
      }
    }
  }

  &--gray {
    #{$b}__item {
      .link {
        color: $color-mediumgray;
      }

      &::after {
        color: $color-mediumgray;
      }
    }
  }

  &--studio {
    margin-bottom: 95px;

    @include viewport--lg {
      margin-bottom: 67px;
    }

    @include viewport--sm {
      margin-bottom: 79px;
    }

    .breadcrumbs__list {
      justify-content: center;
    }

    .breadcrumbs__item {
      &::after {
        content: "/";
      }
    }
  }

  &--main {
    margin-bottom: 15px;

    @include viewport--lg {
      margin-bottom: 5px;
    }

    @include viewport--sm {
      margin-bottom: 0;
    }
  }
}
