/* stylelint-disable */
.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden !important;
}
/* stylelint-enable */

.wrapper {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden;

  // используйте для страниц с min-height: 100vh для визуального скрытия скроллбара
  &--no-scrollbar {
    height: 100vh;

    -ms-overflow-style: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

li {
  list-style: none;
}

.sprite {
  width: 0;
  height: 0;
}

.scroll-lock {
  position: fixed;

  overflow-y: auto;

  // требует теста, должно улучшить поведение в ios ( скрол выше \ ниже контента body )
  overflow: hidden;
  -webkit-overflow-scrolling: none;

  overscroll-behavior: none;
  touch-action: none;
}

.open-submenu {
  padding: 0;

  header {
    z-index: 10;

    .header__nav .header-nav__all {
      svg {
        transform: rotate(180deg);
        transition: $default-transition-settings;
      }
    }
  }

  main,
  footer {
    filter: blur(1.5px);
    position: relative;
    z-index: 0;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      background-color: $color-beigelight;
      z-index: 101;
      opacity: 0.8;
    }
  }
}

.text-page-wrapper {
  .container {
    display: flex;
    position: relative;

    @include viewport--md {
      flex-direction: column;
    }
  }
}

.sidebar.bottom {
  position: absolute;
  bottom: 0;
  right: 35px;
  top: auto;
}

.sidebar.top {
  position: fixed;
  top: 0;
}

.clearfix {
  overflow: auto;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.grid-sizer {
  @include reset-item;
  width: 23.65%;
}

.gutter-sizer {
  @include reset-item;
  width: 1.8%;
}
