.order-success {
  padding-top: 49px;
  padding-bottom: 175px;

  .container {
    padding-right: 54px;
  }

  .title--h1-medium {
    position: relative;
    display: inline;

    span {
      color: $color-green;
    }
  }

  .pay-now {
    margin-bottom: 78px;
  }

  .subscribe {
    width: 304px;
    top: 75px;
    margin-top: 75px;
  }
}

.order-success__header-wrapper {
  margin-bottom: 34px;
}

.order-success__container {
  display: flex;
  justify-content: space-between;
}

.order-success__text {
  margin: 0;

  margin-bottom: 50px;

  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
}

.order-success__wrapper {
  width: 67%;
}

@include viewport--lg {
  .order-success {
    padding-bottom: 140px;

    .title--h1-medium {
      &::after {
        bottom: 6px;
        right: -42px;
        width: 36px;
        height: 42px;
      }
    }

    .pay-now {
      margin-bottom: 93px;
    }

    .subscribe {
      width: 264px;
      top: 60px;
      margin-top: 60px;
    }

    .container {
      padding-right: 29px;
    }
  }

  .order-success__header-wrapper {
    margin-bottom: 25px;
  }

  .order-success__wrapper {
    width: 65.5%;
  }

  .order-success__text {
    padding-right: 35px;
    margin-bottom: 33px;
  }
}

@include viewport--md {
  .order-success {
    padding-top: 25px;
    padding-bottom: 109px;

    .container {
      padding-right: 25px;
    }

    .title--h1-medium {
      margin-bottom: 20px;

      &::after {
        right: -33px;
        bottom: 1px;
        width: 28px;
        height: 36px;
      }
    }

    .pay-now {
      margin-bottom: 59px;
    }

    .subscribe {
      margin: 0 auto;
      width: 100%;
      margin-top: 74px;
    }
  }

  .order-success__header-wrapper {
    margin-bottom: 20px;
  }

  .order-success__text {
    margin-bottom: 41px;
    padding-right: 0;

    font-size: 12px;
    line-height: 22px;
  }

  .order-success__container {
    flex-direction: column;
  }

  .order-success__wrapper {
    width: 100%;
  }
}

@include viewport--sm {
  .order-success {
    .container {
      padding-right: 17px;
    }
  }
}
