.like {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;

  width: 48px;
  height: 48px;

  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 4px 12px rgba(45, 51, 68, 0.07);
  border: 0;
  border-radius: 50%;
  cursor: pointer;

  transition: background-color $default-transition-settings;

  svg {
    margin-top: 4px;
    flex-shrink: 0;
    transition: stroke $default-transition-settings, fill $default-transition-settings;
    width: 29px;
    height: 25px;
  }

  &:hover {
    background-color: $color-darkmain;

    svg {
      stroke: $color-beigelight;
    }
  }

  &.is-active {
    background-color: $color-darkmain;

    svg {
      fill: $color-beigelight;
      stroke: $color-beigelight;
    }

    &:hover {
      svg {
        fill: transparent;
      }
    }
  }
}
