.notice {
  border-radius: 4px;
  max-width: 1136px;

  svg {
    min-width: 24px;
    min-height: 24px;
    margin-top: 24px;

    @include viewport--sm {
      margin-top: 30px;
    }
  }
}

.notice__container {
  @include reset-link;
  display: flex;
  flex-wrap: nowrap;
  padding: 24px 63px 33px 48px;

  svg {
    width: 24px;
    height: 24px;
    min-width: 24px;
  }

  @include viewport--sm {
    padding: 24px 22px 33px;
  }
}

.notice__wrapper {
  padding-left: 24px;

  @include viewport--sm {
    padding-left: 15px;
  }
}

.notice__date {
  margin: 0;
  margin-bottom: 2px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 12px;
  line-height: 22px;
}

.notice__title {
  margin: 0;

  margin-bottom: 12px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.02em;

  @include viewport--lg {
    font-size: 20px;
    line-height: 24px;
  }

  @include viewport--sm {
    font-size: 18px;
    line-height: 22px;
    margin-top: 8px;
    margin-bottom: 15px;
  }
}

.notice__info {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 14px;
  line-height: 24px;

  @include viewport--sm {
    font-size: 12px;
    line-height: 22px;
    max-width: 90%;
  }
}

.notice--light {
  background-color: $beige-extralight;

  .notice__date {
    color: $color-mediumgray;
  }

  .notice__title {
    color: $color-darkmain;
  }

  .notice__info {
    color: $color-darkmain;
  }
}

.notice--dark {
  background-color: $color-darkmain;

  .notice__date {
    color: $color-mediumgray;
  }

  .notice__title {
    color: $color-beigelight;
  }

  .notice__info {
    color: $color-beigelight;
  }
}
