.form {
  position: relative;
  padding: 65px 65px 74px 63px;
  margin: auto 0;
  background-color: $color-default-white;
  width: 593px;
  pointer-events: auto;

  .input-wrapper {
    width: 100%;
  }

  .input-wrapper--regular {
    margin-bottom: 15px;
  }

  .btn {
    margin-top: 55px;
  }

  .input-wrapper--checkbox {
    margin: 32px 0;

    label {
      font-weight: 600;
      font-size: 13px;
      line-height: 16px;
      letter-spacing: -0.02em;
    }
  }
}

.form__header {
  font-size: 34px;
  line-height: 36px;
  margin-bottom: 27px;
}

.form__text {
  margin: 0;

  margin-bottom: 31px;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
}

@include viewport--lg {
  .form {
    padding-bottom: 64px;

    .btn {
      margin-top: 56px;
    }
  }

  .form__header {
    margin-bottom: 22px;
    font-size: 28px;
    line-height: 32px;
  }

  .form__text {
    margin-bottom: 26px;
  }
}

@include viewport--sm {
  .form {
    width: 342px;
    max-width: 100%;
    padding: 42px 23px 55px 25px;

    .btn {
      margin-top: 48px;

      font-size: 12px;
      letter-spacing: 0.05em;
      width: 181px;
      padding: 15px 10px 10px 10px;
    }

    .input-wrapper--checkbox {
      margin: 22px 0;

      label {
        padding-right: 45px;
      }
    }
  }

  .form__header {
    margin-bottom: 18px;
    font-size: 22px;
    line-height: 26px;
  }

  .form__text {
    margin-bottom: 23px;

    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
  }
}
