.ss-main {
  .ss-single-selected {
    height: 65px;
    padding: 0 25px;
    border-width: 2px;
    border-color: $color-lightgray;
    border-radius: 4px;

    .ss-arrow span {
      transition: all $default-transition-settings;
      padding: 6px;
      margin-top: -9px;
    }

    &:hover {
      .ss-arrow span {
        border-color: $color-lightblue;
      }
    }

    .placeholder {
      color: $color-darkgray;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: -0.02em;
      font-weight: $font-weight--medium;
      padding: 20px 0 10px;
      height: 100%;
    }
  }
}

.ss-content {
  border-width: 2px;
  border-color: $color-lightgray;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;

  .ss-list .ss-option {
    color: $color-darkmain;
    cursor: pointer;
    width: 100%;
    height: 46px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.02em;
    display: flex;
    align-items: center;
    padding: 0 26px 0 25px;
    font-weight: $font-weight--medium;

    &:hover,
    &:active {
      outline: none;
      background-color: $color-extralightgray;
      color: $color-darkmain;
    }
  }
}

.select {
  position: relative;
  width: 100%;

  &__label {
    z-index: 1;
    display: block;
    position: absolute;
    top: 12px;
    transform: translateY(0);
    left: 27px;
    font-weight: $font-weight--medium;
    font-size: 12px;
    line-height: 12px;
    color: $color-mediumgray;
    letter-spacing: -0.02em;
    transition: font-size $default-transition-settings, top $default-transition-settings;
  }

  &--gradient {
    .ss-main .ss-single-selected {
      color: $color-darkgray;

      background: $lightgray-to-extralightgray-select;
      background-origin: border-box;
      border: 2px solid transparent;
    }
  }

  &--text-dark {
    .ss-main .ss-single-selected .placeholder {
      color: $color-darkmain;
    }
  }
}

.checkbox-select {
  position: relative;

  &.is-active {
    .checkbox-select__button {
      background: $color-darkmain;
      border-radius: 4px 4px 0 0;

      b {
        color: #ffffff;
      }

      svg {
        transform: rotate(-180deg) translateY(50%);
      }
    }

    .checkbox-select__wrapper {
      height: auto;
    }
  }
}

.checkbox-select__button {
  position: relative;
  display: flex;
  align-items: baseline;

  background: linear-gradient(0deg, $color-beigelight 0%, #f3f3f3 100%, #f3f3f3 100%);
  border: 0;
  border-radius: 4px;
  padding: 24px 29px 20px 29px;
  width: 100%;
  transition: border-radius $default-transition-settings;

  cursor: pointer;

  svg {
    position: absolute;
    right: 18px;
    top: 50%;
    transform: translateY(-50%);

    transition: transform $default-transition-settings;
    width: 18px;
    height: 11px;
  }

  &:hover {
    background: $color-darkmain;

    p {
      color: #ffffff;
    }
  }
}

.checkbox-select__material {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;

  color: $color-darkmain;
}

.checkbox-select__qty {
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  margin-left: 15px;

  color: $color-darkgray;
}

.checkbox-select__wrapper {
  position: absolute;
  height: 0;
  overflow: hidden;

  width: 100%;

  transition: $default-transition-settings;
  background: linear-gradient(0deg, $color-beigelight 0%, #f3f3f3 100%, #f3f3f3 100%);
  border-radius: 0 0 4px 4px;
}

.checkbox-select__input-wrapper {
  border-bottom: 1px solid $color-lightgray;

  transition: background-color $default-transition-settings;

  &.is-active {
    background-color: $color-green;
    border-color: transparent;

    label {
      display: flex;
      color: #ffffff;

      &::before {
        display: block;
      }
    }

    &:hover {
      background-color: rgba($color: $color-green, $alpha: 0.8);
      border-color: transparent;
    }
  }

  &:hover {
    background-color: $color-lightgray;
  }

  &:last-child {
    border-bottom: 0;
    border-radius: 0 0 4px 4px;
  }

  label {
    position: relative;
    width: 100%;
    display: block;
    padding: 16px 68px 13px 29px;
    cursor: pointer;

    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.02em;

    span {
      position: relative;

      svg {
        position: absolute;
        right: 0;
        bottom: -4px;
      }
    }

    &::after {
      content: "";
      position: absolute;
      right: 14px;
      top: 50%;
      transform: translateY(-50%);

      width: 28px;
      height: 28px;

      border-radius: 2px;
      background-color: #ffffff;
    }

    &::before {
      content: "";
      position: absolute;
      display: none;
      z-index: 1;
      right: 14px;
      top: 50%;
      transform: translateY(-50%);

      width: 28px;
      height: 28px;

      background-size: 18px 14px;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("../img/sprite/icon-check-black.svg");
    }
  }
}

.checkbox-select__with-icon {
  span {
    padding-right: 36px;
  }

  svg {
    width: 28px;
    min-width: 28px;
    height: 28px;
  }
}

@include viewport--sm {
  .checkbox-select__input-wrapper {
    span {
      font-size: 12px;
      line-height: 20px;
    }

    label {
      padding-left: 25px;
    }
  }

  .checkbox-select__button {
    span {
      margin-left: 5px;
    }
  }
}
