.colors {
  padding: 50px 0 95px;

  @include viewport--sm {
    padding: 50px 0 45px;
  }

  @include viewport--sd {
    padding: 28px 0 70px;
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    padding: 0;
    margin: 0;
    color: $color-darkmain;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @include viewport--sm {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    overflow: hidden;
    max-height: 440px;
  }

  &__item img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  &__title {
    margin-bottom: 20px;

    @include viewport--sd {
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__text {
    margin-bottom: 35px;

    @include viewport--sd {
      margin-bottom: 20px;
    }
  }
}
