.examples {
  padding-top: 30px;
  padding-bottom: 75px;

  .container {
    display: flex;
  }

  .examples-slider {
    width: 66.1%;

    .examples-slider__img {
      width: 100%;
      position: relative;
      padding-bottom: percentage(293/440);
      margin-bottom: 13px;

      img {
        position: absolute;
        z-index: -1;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
  }

  a {
    @include reset-link;
  }

  ul.swiper-wrapper {
    @include reset-list;
  }
}

.examples__header-mobile {
  display: none;
}

.examples__text-wrapper {
  width: 33.9%;
  padding-top: 26px;
  padding-left: 77px;

  .title {
    margin-bottom: 19px;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  .btn {
    padding: 9px 28px 3px 29px;
    width: fit-content;
    display: block;
    margin-top: 32px;

    font-weight: bold;
    font-size: 12px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@include viewport--lg {
  .examples {
    padding-bottom: 73px;

    .examples-slider {
      .examples-slider__img {
        margin-bottom: 6px;
      }
    }
  }

  .examples__text-wrapper {
    padding-top: 0;
    padding-left: 58px;

    .title {
      margin-bottom: 23px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}

@include viewport--md {
  .examples {
    padding-bottom: 62px;

    .container {
      flex-direction: column;
    }

    .examples-slider {
      width: 100%;
      margin-bottom: 32px;
    }
  }

  .examples__text-wrapper {
    width: 100%;
    padding-left: 0;

    .btn {
      margin-top: 24px;
    }

    .title {
      display: none;
    }
  }

  .examples__header-mobile {
    display: block;
    margin-bottom: 10px;
  }
}
