.separator {
  $b: #{&};

  margin: 0;
  padding: 0;
  background-color: $color-beigelight;
  border: none;

  &--small {
    height: 1px;
  }

  &--medium {
    height: 2px;
  }

  &--big {
    height: 4px;
  }

  &--margin {
    width: 100%;
    background-color: $color-beigelight;
    border: none;
    margin-top: 40px;
    margin-bottom: 40px;
    height: 1px;

    #{$b}--medium {
      height: 2px;
    }

    #{$b}--big {
      height: 4px;
    }
  }

  &--big-margin {
    margin-top: 64px;
    margin-bottom: 64px;

    @include viewport--md {
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }

  &--no-desktop-margin {
    margin: 0;

    @include viewport--md {
      margin-top: 40px;
      margin-bottom: 40px;
    }
  }
}
