.result {
  background-color: $beige-extralight;

  &__wrapper {
    padding: 89px 154px;
    display: flex;
    max-width: 1440px;
    margin: 0 auto;

    @include viewport--xl {
      padding: 89px 36px;
    }

    @include viewport--md {
      flex-direction: column-reverse;
      padding: 57px 16px 78px 16px;
    }
  }

  &__description {
    padding-left: 72px;
    width: 50%;

    @include viewport--xl {
      padding-left: 49px;
    }

    @include viewport--md {
      padding-left: 0;
      padding-bottom: 41px;
      width: 100%;
    }
  }

  &__title {
    @include reset-item;
    margin-bottom: 28px;
    font-weight: 800;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-top: 12px;

    @include viewport--xl {
      font-size: 34px;
      line-height: 46px;
      margin-bottom: 18px;
    }

    @include viewport--md {
      font-size: 24px;
      line-height: 26px;
      margin-top: 8px;
      margin-bottom: 17px;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
  }

  .result-slider {
    width: 49.5%;

    @include viewport--md {
      width: 100%;
    }
  }
}
