.factoid-block {
  margin-top: 72px;
  margin-bottom: 171px;

  .container {
    display: flex;
    max-width: 1370px;
    padding-left: 51px;
    padding-right: 51px;
    padding-top: 94px;
    padding-bottom: 83px;
    background-color: $beige-extralight;
  }

  b {
    margin-right: 29px;
    display: block;
    width: 644px;

    font-weight: 800;
    font-size: 130px;
    line-height: 160px;
    letter-spacing: -0.03em;

    color: $color-blue;

    sup {
      top: -51px;
      font-size: 63%;
    }
  }

  p {
    margin: 0;

    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }
}

.factoid-block__text-wrapper {
  width: 78.1%;
}

.factoid-block__emblem {
  padding-top: 9px;
  align-self: start;
  flex-shrink: 0;

  img {
    height: 85px;
    width: 70px;
    object-fit: contain;
  }
}

.factoid-block__wrapper {
  padding-top: 26px;
  display: flex;

  p {
    margin-bottom: 36px;
  }

  .btn {
    display: block;
    width: 250px;
    padding: 17px 10px 11px 10px;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@include viewport--xl {
  .factoid-block {
    margin-bottom: 186px;

    .container {
      width: 94.6%;
    }

    b {
      width: 616px;

      font-size: 109px;

      sup {
        top: -45px;
      }
    }
  }

  .factoid-block__text-wrapper {
    width: 88.5%;
  }
}

@include viewport--lg {
  .factoid-block {
    margin-bottom: 135px;

    .container {
      padding: 69px 60px 70px;
      width: 92.8%;
      flex-direction: column;
    }

    b {
      width: 100%;

      font-size: 140px;

      sup {
        top: -55px;
      }
    }
  }

  .factoid-block__wrapper {
    padding-top: 16px;

    p {
      margin-bottom: 60px;
    }
  }

  .factoid-block__emblem {
    padding-top: 0;
  }

  .factoid-block__text-wrapper {
    width: 78.6%;
  }
}

@include viewport--md {
  .factoid-block {
    margin-bottom: 93px;

    .container {
      padding: 52px 5% 63px 6.8%;
    }

    p {
      padding-right: 20px;
      margin-bottom: 33px;
      font-size: 16px;
      line-height: 28px;
    }

    b {
      margin-bottom: 8px;
      font-size: 68px;
      line-height: 68px;

      sup {
        top: -26px;
      }
    }
  }

  .factoid-block__emblem {
    width: 59px;
    height: 72px;
  }


  .factoid-block__wrapper {
    .btn {
      padding: 12px 10px 6px;
      width: 223px;

      font-size: 12px;
      line-height: 32px;
    }
  }
}

@media (max-width: 374px) {
  .factoid-block {
    b {
      font-size: 57px;
    }
  }
}
