.btn {
  $b: #{&};

  @include reset-link;
  transition: all $default-transition-settings;
  padding: 0;

  background: $color-transparent;
  border: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &--general {
    position: relative;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 235px;
    min-height: 60px;
    padding: 20px 47px;

    font-weight: $font-weight--bold;
    font-size: 14px;
    vertical-align: middle;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    word-wrap: break-word;
    word-break: normal;
    /* для Opera и FireFox */
    /* stylelint-disable */
    word-break: break-word;
    /* для Chrome */
    /* stylelint-enable */

    border: 3px solid transparent;

    @include viewport--md {
      max-width: 217px;
      min-height: 55px;
      padding-right: 45px;
      padding-left: 45px;

      font-size: 13px;
    }

    @include viewport--sm {
      max-width: 206px;
      min-height: 50px;
      padding-right: 40px;
      padding-left: 40px;
    }
  }

  &--light {
    display: block;
    padding: 17px 30px 17px 30px;
    width: fit-content;

    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.02em;

    background-color: #ffffff;
    border-radius: 4px;

    box-shadow: (0 4px 12px rgba(45, 51, 68, 0.07));
    color: $color-darkmain;

    &:hover {
      opacity: 0.7;
    }
  }

  &--video {
    position: relative;

    display: block;
    padding: 17px 29px 17px 65px;
    width: fit-content;

    font-weight: 600;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: -0.02em;

    background-color: #ffffff;
    border-radius: 4px;
    box-shadow: (0 4px 12px rgba(45, 51, 68, 0.07));
    color: $color-darkmain;

    &:hover {
      opacity: 0.7;
    }

    &::before {
      content: "";
      position: absolute;
      width: 44px;
      height: 44px;
      left: 11px;
      top: 50%;
      transform: translateY(-50%);

      background-image: url("../img/svg/play_icon.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &--dark {
    color: $color-default-white;

    background-color: $color-darkmain;

    &:hover {
      background-color: $color-green;
    }
  }

  &--green {
    color: $color-default-white;

    background-color: $color-green;

    &:hover {
      background-color: $color-darkmain;
    }
  }

  &--blue {
    color: $color-default-white;

    background-color: $color-blue;

    &:hover {
      background-color: $color-lightblue;
    }
  }

  &--white {
    color: $color-darkmain;

    background: $color-beigelight;

    &:hover {
      background-color: $color-default-white;
    }
  }

  &--gradient {
    position: relative;
    z-index: 1;

    color: $color-darkgray;

    background: $lightgray-to-extralightgray-button;
    background-origin: border-box;

    &::after {
      content: "";
      position: absolute;
      top: -3px;
      left: -3px;
      z-index: 2;

      box-sizing: content-box;
      width: 100%;
      height: 100%;

      background-image: $green-to-lightgreen-button;
      background-origin: border-box;
      border: 3px solid transparent;
      opacity: 0;

      transition: opacity 0.3s;
    }

    &:hover::after {
      opacity: 1;
    }

    &:hover span {
      z-index: 3;

      color: $color-default-white;
    }
  }

  &--disabled {
    color: $color-mediumgray;

    background: $color-beigelight;
    cursor: text;

    pointer-events: none;
  }

  &--xxl {
    width: 100%;
    max-width: 427px;

    @include viewport--md {
      font-size: 13px;
    }
  }

  &--xl {
    min-width: 228px;

    @include viewport--md {
      min-width: 216px;
      min-height: 55px;
      padding-right: 45px;
      padding-left: 45px;

      font-size: 13px;
    }

    @include viewport--sm {
      min-width: 206px;
      min-height: 50px;
      padding-right: 40px;
      padding-left: 40px;
    }
  }

  &--l {
    min-width: 290px;
    padding: 19px 20px;

    @include viewport--md {
      min-width: 280px;
      min-height: 55px;
      font-size: 13px;
    }

    @include viewport--sm {
      min-width: 270px;
      min-height: 50px;
    }
  }

  &--m {
    min-width: 190px;
    min-height: 50px;
    padding: 16px 35px;

    font-size: 12px;
    line-height: 18px;

    @include viewport--md {
      min-width: 170px;
      min-height: 45px;
      padding-right: 35px;
      padding-left: 35px;

      font-size: 11px;
    }

    @include viewport--sm {
      min-width: 152px;
      min-height: 40px;
      padding-right: 30px;
      padding-left: 30px;

      font-size: 10px;
    }
  }

  &--s {
    min-width: 170px;
    min-height: 40px;
    padding: 11px 25px 9px 25px;

    font-size: 10px;
    line-height: 14px;

    @include viewport--md {
      min-width: 149px;
      padding-right: 20px;
      padding-left: 20px;

      font-size: 9px;
    }
  }

  &--bordered {
    color: $color-darkmain;

    background-color: transparent;
    border: 3px solid $color-darkmain;

    &:hover {
      color: $color-green;

      background-color: transparent;
      border: 3px solid $color-green;
    }
  }

  &--bordered-white {
    color: $color-default-white;

    background-color: transparent;
    border: 3px solid $color-default-white;

    &:hover {
      color: $color-darkmain;

      background-color: transparent;
      border: 3px solid $color-darkmain;
    }
  }

  &--bordered-white-green {
    color: $color-beigelight;

    background-color: transparent;
    border: 3px solid $color-beigelight;

    &:hover {
      color: $color-green;

      background-color: transparent;
      border: 3px solid $color-green;
    }
  }

  &--rounded {
    border-radius: 4px;

    &::after {
      border-radius: 4px;
    }

    &:hover {
      border-radius: 4px;
    }
  }

  &--search {
    max-width: 123px;
    margin-left: 10px;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.05em;
  }

  &--search-circle {
    position: absolute;
    top: 50%;
    right: 5px;

    width: 40px;
    height: 40px;

    border-radius: 50%;
    transform: translateY(-50%);

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;

      width: 17px;
      height: 16px;

      background: url("../img/sprite/icon-search-button.svg") no-repeat;
      transform: translateY(-50%) translateX(-50%);
    }
  }

  &.btn--icon {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding-right: 14px;
    padding-left: 40px;
    flex-wrap: nowrap;
    flex-direction: row;
    white-space: nowrap;

    text-align: left;

    span {
      width: 86%;
    }

    svg {
      position: relative;

      object-fit: contain;
    }

    #{$b}--xl {
      max-width: 275px;
      padding-right: 14px;
      padding-left: 30px;

      svg {
        width: 30px;
        min-width: 30px;
        height: 30px;
      }
    }

    #{$b}--l {
      svg {
        width: 30px;
        min-width: 30px;
        height: 30px;
      }
    }

    #{$b}--m {
      max-width: 219px;
      padding-right: 11px;
      padding-left: 20px;

      font-size: 12px;

      svg {
        width: 25px;
        min-width: 25px;
        height: 25px;
      }
    }

    #{$b}--s {
      max-width: unset;
      padding-right: 11px;
      padding-left: 20px;
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 12px;

      svg {
        width: 18px;
        min-width: 18px;
        height: 18px;
      }

      span {
        padding-top: 4px;
      }
    }
  }

  &--green-cart {
    display: flex;
    flex-direction: column;
    padding-top: 10px;
    padding-bottom: 10px;

    color: $color-default-white;

    background-color: $color-green;

    span {
      font-weight: $font-weight--medium;
      font-size: 12px;
      line-height: 16px;
      text-transform: capitalize;
    }

    &:hover {
      background: rgba(112, 175, 96, 0.8);
    }
  }

  &--checkbox {
    position: relative;

    justify-content: flex-start;
    padding: 0;

    color: $color-default-white;

    border: none;

    input[type="checkbox"] {
      display: none;
    }

    label {
      display: block;
      width: 100%;
      min-height: 54px;
      padding: 13px 15px 10px 30px;

      text-align: left;

      border: 3px solid $color-blue;
      background-color: $color-blue;
      cursor: pointer;

      user-select: none;
      transition: all $default-transition-settings;
    }

    label::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 15px;

      width: 28px;
      height: 28px;

      background-color: #ffffff;
      border-radius: 2px;
      transform: translateY(-50%);
    }

    input[type="checkbox"]:checked+label::before {
      content: "";
      position: absolute;
      top: 37%;
      right: 20px;
      z-index: 1;

      width: 18px;
      height: 14px;

      background: url("../img/sprite/icon-check-black.svg");
    }

    input[type="checkbox"]:checked+label {
      border: 3px solid $color-green;
      background-color: $color-green;
    }

    &:hover label {
      background-color: $color-lightblue;
      border: 3px solid $color-lightblue;
    }
  }

  &--menu {
    min-height: 20px;
    padding: 0;

    border: none;
  }

  &--order {
    transition: background-color $default-transition-settings;

    &.is-active {
      padding-top: 17px;
      padding-bottom: 12.6px;

      background-color: $color-green;
      transition: opacity $default-transition-settings;

      .btn__order {
        display: none;
      }

      .btn__in-order {
        display: block;
      }

      &:hover {
        opacity: 0.7;
      }
    }
  }

  &--smaller-padding {
    padding-right: 20px;
    padding-left: 20px;

    @include viewport--md {
      padding-right: 20px;
      padding-left: 20px;
    }

    @include viewport--sm {
      padding-right: 20px;
      padding-left: 20px;
    }
  }

  &--cases {
    display: inline-flex;
    text-align: center;
    font-weight: 600;
    font-size: 13px;
    line-height: 32px;
    color: $color-blue;
    background-color: $color-extralightgray;
    border-radius: 32px;
    padding: 12px 31px;
    user-select: none;
    margin-right: 7.5px;
    margin-left: 7.5px;
    margin-bottom: 10px;

    @include viewport--md {
      padding: 5px 17px;
    }

    @include viewport--sm {
      padding: 5px 27px;
      font-size: 11px;
      margin-right: 4px;
      margin-left: 4px;
    }

    &-active {
      background-color: $color-darkmain;
      color: $color-beigelight;
    }

    &-hover {
      opacity: 0.7;
      color: $color-lightblue;
    }

    &:hover {
      opacity: 0.7;
      color: $color-lightblue;
    }
  }

  &--virtual {
    font-weight: 700;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 10px 10px 10px 18px;
    max-width: 100%;
    min-height: 100%;
    margin-right: 24px;
    margin-bottom: 24px;

    span {
      margin-top: 3px;
      margin-right: 29px;
    }

    @include viewport--mm {
      padding: 15px 15px 15px 10px;

      span {
        margin-right: 10px;
      }
    }
  }

  &--dressing {
    font-weight: 700;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 15px 15px 15px 30px;
    max-width: 100%;
    min-height: 100%;
    margin-right: 24px;
    margin-bottom: 24px;

    span {
      margin-top: 3px;
      margin-right: 32px;
    }

    @include viewport--mm {
      padding: 15px 15px 15px 10px;

      span {
        margin-right: 10px;
      }
    }
  }
}

.btn__order {
  font-weight: bold;
  font-size: 15px;
  line-height: 32px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.btn__in-order {
  display: none;

  span {
    margin-bottom: 4px;
    display: block;
    font-weight: bold;
    font-size: 15px;
    line-height: 15px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 12px;
    line-height: 12px;
    text-align: center;
    text-transform: capitalize;
  }
}

.btn--circle {
  width: 160px;
  height: 160px;
  border-radius: 100%;
  transition: all $default-transition-settings;

  @include viewport--lg {
    width: 140px;
    height: 140px;

    svg {
      width: 40px;
      height: 40px;
    }
  }

  @include viewport--sm {
    width: 78px;
    height: 78px;
    padding: 0;
    margin-bottom: 5px;

    svg {
      width: 25px;
      height: 25px;
    }
  }
}
