.basket {
  padding-top: 49px;
  padding-bottom: 144px;

  @include viewport--lg {
    padding-bottom: 121px;
  }

  @include viewport--md {
    padding-top: 25px;
  }

  @include viewport--sm {
    padding-bottom: 102px;
    margin: 0 auto;
    max-width: 600px;
  }
}

.basket .title {
  margin-bottom: 23px;
}

.basket__top-orders-wrapper {
  margin-bottom: 117px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;

  @include viewport--lg {
    margin-bottom: 52px;
    flex-wrap: wrap;
  }

  @include viewport--sm {
    margin-bottom: 31px;
  }
}

.basket__client-orders-list {
  @include reset-list;

  margin-right: 25px;
  margin-bottom: 42px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  max-width: 928px;
  flex-grow: 2;
  box-sizing: border-box;

  @include viewport--lg {
    margin-right: 0;
    max-width: none;
    width: 100%;
  }

  @include viewport--md {
    margin-bottom: 27px;
    width: auto;
  }
}

.basket__client-orders-item {
  @include reset-item;
  padding-top: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid $color-beigelight;
  border-top: 1px solid $color-beigelight;

  &:last-of-type {
    margin-top: -1px;
  }

  @include viewport--md {
    padding-top: 15px;
    padding-bottom: 19px;
  }
}

.basket__price-promo-wrapper {
  display: flex;
  flex-direction: column;
  width: 372px;

  @include viewport--lg {
    width: 680px;
  }
}

.basket__price-promo-top-wrapper {
  padding: 40px 30px 44px 28px;
  margin-bottom: 28px;
  background-color: $color-beigelight;
  border-radius: 4px;

  @include viewport--lg {
    max-width: 600px;
    margin-bottom: 23px;
    padding: 27px 44px 26px 46px;
  }

  @include viewport--sm {
    max-width: none;
    margin-bottom: 23px;
    padding: 30px 19px 34px;
  }
}

.basket__total-price-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: $font-family--Mont;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--xl {
    max-width: 421px;
  }
}

.basket__total-text {
  padding-right: 20px;
  font-weight: $font-weight--bold;
  font-size: 26px;
  line-height: 32px;

  @include viewport--sm {
    font-size: 22px;
    line-height: 26px;
  }
}

.basket__total-summ {
  font-weight: $font-weight--extrabold;
  font-size: 36px;
  line-height: 36px;
  white-space: nowrap;

  @include viewport--sm {
    font-size: 28px;
    line-height: 32px;
  }
}

.basket__no-delivery-price {
  margin: 0;

  margin-bottom: 27px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--md {
    margin-bottom: 17px;
    font-size: 12px;
  }
}

.basket .basket__input-promocode {
  margin-bottom: 15px;
  width: 100%;

  @include viewport--sm {
    margin-bottom: 20px;
  }

  input {
    height: 54px;
  }

  input:placeholder-shown ~ label {
    padding-left: 21px;

    @include viewport--lg {
      padding-left: 34px;
    }

    @include viewport--sm {
      padding-left: 19px;
    }
  }
}

.basket__discount-wrapper {
  padding-right: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include viewport--xl {
    max-width: 466px;
  }

  @include viewport--md {
    padding-right: 0;
    max-width: none;
    width: 100%;
  }
}

.basket__discount-text {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}

.basket__discount-summ {
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-green;

  @include viewport--md {
    letter-spacing: -0.05em;
  }
}

.basket__to-delivery-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @include viewport--lg {
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  @include viewport--sm {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.basket .basket__to-delivery-button {
  padding-left: 37px;
  padding-bottom: 16px;
  margin-bottom: 23px;
  background-color: $color-darkmain;

  svg {
    width: 24px;
    min-width: 24px;
    height: 18px;
    top: 0;
    right: 3px;
  }

  @include viewport--xl {
    margin-bottom: 10px;
    margin-right: auto;
    padding-bottom: 14px;
    max-width: 400px;
  }

  @include viewport--lg {
    margin-bottom: 0;
  }

  @include viewport--sm {
    padding-bottom: 16px;
    padding-left: 37px;
    margin-bottom: 17px;
  }

  &:hover {
    background-color: $color-green;
  }

  span {
    @include viewport--sm {
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
    }
  }
}

.basket__to-delivery-wrapper p {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--lg {
    max-width: 249px;
  }
}

.basket hr {
  margin-bottom: 47px;

  @include viewport--lg {
    margin-bottom: 56px;
  }

  @include viewport--md {
    margin-bottom: 66px;
  }
}

.basket__add-to-interior {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--lg {
    font-size: 24px;
    line-height: 28px;
  }

  @include viewport--md {
    font-size: 20px;
    line-height: 24px;
  }
}

.basket__extra-goods-list {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.basket__extra-goods-list.swiper-wrapper {
  -webkit-transform: translate3d(0, 0, 0); /* stylelint-disable-line */
}

.basket__extra-goods-item.swiper-slide {
  -webkit-transform: translate3d(0, 0, 0); /* stylelint-disable-line */

  @include viewport--sm {
    width: 375px;
    margin: 0 auto;
  }
}

.basket__title-arrows-wrapper {
  margin-bottom: 36px;
  position: relative;
  display: flex;
  justify-content: space-between;

  @include viewport--lg {
    margin-bottom: 22px;
  }

  @include viewport--md {
    margin-bottom: 20px;
  }
}

.basket__arrows-wrapper {
  position: relative;
  display: flex;
  width: 140px;

  @include viewport--md {
    width: 110px;
  }
}

.basket__swiper-button-next.swiper-button-next {
  margin-top: 0;
  width: 50px;
  height: 50px;
  top: -7px;
  left: auto;
  right: 0;
  outline: none;

  @include viewport--xl {
    top: -14px;
  }

  @include viewport--md {
    top: -10px;
    width: 42px;
    height: 42px;
  }

  &::after {
    content: "";
    width: 50px;
    height: 50px;
    background-image: url("../img/sprite/icon-basket-arrow-swiper.svg");
    background-repeat: no-repeat;
    background-size: 48px 48px;
    background-position: 0 0;
    transform: rotate(180deg);

    @include viewport--md {
      width: 42px;
      height: 42px;
      background-size: 42px 42px;
    }
  }

  &:hover {
    &::after {
      background-image: url("../img/sprite/icon-basket-arrow-swiper-hover.svg");
    }
  }
}

.basket__swiper-button-prev.swiper-button-prev {
  margin-top: 0;
  width: 50px;
  height: 50px;
  top: -5px;
  left: auto;
  right: 61px;
  outline: none;

  @include viewport--xl {
    top: -12px;
  }

  @include viewport--md {
    top: -10px;
    right: 54px;
    width: 42px;
    height: 42px;
  }

  &::after {
    content: "";
    width: 50px;
    height: 50px;
    background-image: url("../img/sprite/icon-basket-arrow-swiper.svg");
    background-repeat: no-repeat;
    background-size: 48px 48px;
    background-position: 0 0;

    @include viewport--md {
      width: 42px;
      height: 42px;
      background-size: 42px 42px;
    }
  }

  &:hover {
    &::after {
      background-image: url("../img/sprite/icon-basket-arrow-swiper-hover.svg");
    }
  }
}

.basket--empty {
  .title {
    margin-bottom: 32px;
  }
}

.basket__text {
  margin: 0;

  font-size: 22px;
  line-height: 25px;
  font-weight: bold;
  margin-bottom: 15px;
  letter-spacing: -0.02em;

  span {
    color: $color-darkred;
  }
}

.basket__text-wrapper {
  .link {
    font-size: inherit;
  }

  .paragraph {
    font-weight: 600;
  }
}
