.swiper-with-tips {
  position: relative;
  padding-bottom: 55px;
  padding-top: 55px;

  @include viewport--xl {
    padding-bottom: 40px;
    padding-top: 40px;
  }

  @include viewport--md {
    padding-bottom: 20px;
    padding-top: 20px;
  }

  .goods-slider-prev,
  .goods-slider-next {
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);

    @include viewport--xl {
      width: 33px;
      height: 33px;
    }

    //@include viewport--xl {
    //  width: 33px;
    //  height: 33px;
    //}
  }

  .goods-slider-prev {
    left: 32px;

    @include viewport--sm {
      left: 20px;
    }
  }

  .slider-buttons {
    height: 0;
  }

  img {
    min-height: 956px;

    @include viewport--xl {
      min-height: 666px;
    }

    @include viewport--md {
      min-height: 266px;
    }
  }

  .goods-slider-next {
    right: 32px;
    transform: translateY(-50%) rotate(180deg);
    margin: 0;

    @include viewport--sm {
      right: 20px;
    }
  }

  .swiper-container {
    overflow: visible;
  }

  .swiper-slide {
    opacity: 0;
    visibility: hidden;

    &-active {
      opacity: 1;
      visibility: visible;
    }
  }

  .tippy-content {
    padding: 0;
  }

  .tippy-box {
    background-color: transparent;

    .close-btn {
      //display: none;
    }
  }

  .btn {
    font-weight: 800;
  }

  [data-tippy-element] {
    position: absolute;
    appearance: none;
    width: 32px;
    height: 32px;
    border: 4px solid $color-default-white;
    border-radius: 50%;
    background-color: $color-green;
    z-index: 2;
    cursor: pointer;
    padding: 0;

    &:after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: 1;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: $color-default-white;
      transform-origin: 0 0;
      animation-name: scaleOut;
      animation-duration: 1s;
      animation-iteration-count: infinite;
    }

    @include viewport--xl {
      width: 22px;
      height: 22px;
    }

    //@include viewport--sm {
    //  width: 8px;
    //  height: 8px;
    //  border: 2px solid $color-default-white;
    //}
  }

  //[data-tippy-root] {
  //  inset: -40px auto auto 50px !important;
  //
  //  @include viewport--md {
  //    inset: -20px auto auto 10px !important;
  //  }
  //}

  .tippy-svg-arrow {
    display: none;
  }

  .swiper-pagination {
    position: absolute;
    width: 100%;
    bottom: 86px;
    z-index: 2;
    display: flex;
    justify-content: center;
    gap: 8px;

    @include viewport--xl {
      bottom: 64px;
    }

    @include viewport--md {
      bottom: 34px;
    }
  }

  .swiper-pagination-bullet {
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background-color: $color-default-white;
    opacity: 0.3;
    transition: opacity 0.3s;

    &-active {
      opacity: 0.9;
    }
  }
}

@keyframes scaleOut {
  from {
    transform: scale(1) translate(-50%, -50%);
    opacity: 1;
  }

  to {
    transform: scale(2) translate(-50%, -50%);
    opacity: 0;
  }
}
