.equipment {
  position: relative;
  display: block;
  padding: 0 0 25px;
  color: $color-default-white;
  margin-top: 37px;
  margin-bottom: 43px;

  @include viewport--lg {
    margin-top: 55px;
    margin-bottom: 43px;
  }

  @include viewport--sm {
    margin-top: 70px;
    margin-bottom: 48px;
  }

  &__img {
    height: 100%;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 8px;

    img {
      border-radius: 8px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__description {
    padding: 89px 10px 0 114px;
    z-index: 1;
    min-height: 656px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--lg {
      padding: 43px 10px 5px 30px;
      min-height: 575px;
    }

    @include viewport--sm {
      padding: 48px 10px 10px 21px;
      min-height: 633px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 54px;
    letter-spacing: -0.02em;
    max-width: 560px;
    margin: 0 0 35px;

    @include viewport--lg {
      font-size: 44px;
      line-height: 54px;
    }

    @include viewport--sm {
      font-size: 26px;
      line-height: 36px;
      margin: 0 0 20px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
    letter-spacing: -0.02em;
    max-width: 650px;
    margin: 0;

    @include viewport--lg {
      font-size: 18px;
      line-height: 28px;
      max-width: 360px;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 20px;
      max-width: 250px;
    }
  }

  &__list {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;

    @include viewport--lg {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      column-gap: 65px;
    }

    @include viewport--md {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 10px;
      row-gap: 30px;
    }

    @include viewport--sm {
      grid-template-columns: 1fr;
      row-gap: 12px;
    }
  }

  &__item {
    display: flex;
    margin: 0 50px 38px 0;

    @include viewport--lg {
      margin: 0 0 15px 0;
    }

    @include viewport--sm {
      margin: 0;
    }
  }

  &__item-img svg {
    @include viewport--lg {
      width: 55px;
      height: 55px;
    }
  }

  &__item-text {
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    opacity: 0.7;
    max-width: 220px;
    margin: 0 0 0 16px;

    @include viewport--lg {
      max-width: 190px;
    }
  }
}
