.wallpaper-promo {
  padding-top: 97px;
  padding-bottom: 103px;

  background-color: $beige-extralight;

  .container {
    display: flex;
    padding: 0 150px 0 94px;
  }

  p {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  &__italics {
    display: block;
    margin-bottom: 32px;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
  }

  img {
    flex-shrink: 0;
    width: 582px;
    height: 441px;
    max-width: unset;
    object-fit: cover;
  }

  &__text-wrapper {
    padding-top: 11px;
    margin-left: 127px;

    .title {
      margin-bottom: 24px;
    }

    p {
      margin: 0;
    }
  }
}

@include viewport--xl {
  .wallpaper-promo {
    padding-bottom: 106px;

    .container {
      padding: 0 68px 0 74px;
    }

    &__text-wrapper {
      margin-left: 84px;
    }
  }
}

@include viewport--lg {
  .wallpaper-promo {
    padding-top: 81px;
    padding-bottom: 131px;


    .container {
      padding: 0 40px 0 36px;
    }

    picture,
    img {
      width: 489px;
      height: 391px;
    }

    &__text-wrapper {
      margin-left: 58px;
      padding-top: 28px;

      .title {
        font-size: 36px;
        line-height: 40px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }

      .wallpaper-promo__italics {
        font-size: 14px;
        line-height: 26px;
      }
    }
  }
}

@include viewport--md {
  .wallpaper-promo {
    padding-top: 53px;
    padding-bottom: 78px;

    .container {
      padding: 0 17px;
      flex-direction: column;
    }

    picture,
    img {
      margin: 0 auto;
      max-width: 100%;
      width: 343px;
      height: 310px;

      object-fit: contain;
    }

    &__text-wrapper {
      margin-left: 0;
      padding-top: 63px;

      .title {
        margin-bottom: 23px;
        font-size: 28px;
        line-height: 32px;
      }

      .wallpaper-promo__italics {
        margin-bottom: 17px;
      }
    }
  }
}
