.order-list {
  padding-top: 49px;
  padding-bottom: 141px;

  @include viewport--lg {
    padding-bottom: 112px;
  }

  @include viewport--md {
    padding-top: 25px;
  }

  @include viewport--sm {
    padding-bottom: 85px;
  }

  .breadcrumbs__list {
    justify-content: center;

    @include viewport--sm {
      padding-left: 11px;
    }
  }
}

.order-list .container {
  max-width: 987px;
  padding-left: 41px;
  padding-right: 20px;
  box-sizing: border-box;

  @include viewport--lg {
    padding-left: 18px;
  }

  @include viewport--sm {
    padding-left: 16px;
    padding-right: 16px;
  }
}

.order-list .title {
  margin: 0;
  margin-bottom: 44px;
  text-align: center;
}

.order-list__title span {
  color: $color-green;

  @include viewport--lg {
    display: none;
  }
}

.order-list__user-img-wrapper {
  margin-bottom: 38px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  @include viewport--lg {
    margin-bottom: 35px;
  }

  @include viewport--sm {
    margin-bottom: 24px;
  }

  img {
    width: 36px;
    height: 36px;
  }
}

.order-list__contact-user {
  margin: 0;

  padding-left: 14px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;

  @include viewport--sm {
    font-size: 12px;
    line-height: 18px;
  }
}

.order-list__contact-user span {
  font-weight: $font-weight--bold;

  @include viewport--sm {
    width: 100%;
    display: block;
  }
}

.order-list .title--h2-medium {
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  text-align: left;

  @include viewport--lg {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.02em;
  }

  @include viewport--sm {
    margin-bottom: 11px;
    font-size: 18px;
  }
}

.order-list__order-list {
  @include reset-list;

  margin-bottom: 12px;
  display: flex;
  flex-direction: column;

  @include viewport--lg {
    margin-bottom: 14px;
  }
}

.order-list__order-item {
  @include reset-item;
  padding-top: 26px;
  padding-bottom: 28px;
  padding-right: 23px;
  display: flex;
  justify-content: space-between;
  margin-top: -1px;
  border-top: 1px solid $color-beigelight;
  border-bottom: 1px solid $color-beigelight;

  @include viewport--sm {
    padding-top: 17px;
    padding-bottom: 15px;
    padding-right: 17px;
  }

  &:first-of-type {
    margin-top: 0;
  }

  &:last-of-type {
    border-bottom: none;
  }
}

.order-list__order-item p {
  font-family: $font-family--Mont;
  letter-spacing: -0.02em;
  line-height: 22px;
  color: $color-darkmain;

  @include viewport--sm {
    line-height: 20px;
  }
}

.order-list__item-title {
  margin: 0;

  padding-right: 20px;
  font-weight: $font-weight--medium;
  font-size: 14px;

  @include viewport--sm {
    max-width: 70%;
    font-size: 12px;
  }
}

.order-list__item-price {
  margin: 0;

  font-weight: $font-weight--bold;
  font-size: 18px;
  white-space: nowrap;

  @include viewport--sm {
    font-size: 14px;
  }
}

.order-list__order-item--discount p {
  color: $color-green;
}

.order-list__total-wrapper {
  margin-bottom: 13px;
  padding: 26px 22px 20px 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: baseline;
  background-color: $color-beigelight;
  border-radius: 4px;

  @include viewport--sm {
    margin-bottom: 11px;
    padding-top: 22px;
    padding-bottom: 16px;
    padding-right: 16px;
  }
}

.order-list__total-wrapper p {
  font-family: $font-family--Mont;
  line-height: 44px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}

.order-list__total {
  margin-right: 35px;
  font-size: 16px;
  font-weight: $font-weight--medium;

  @include viewport--sm {
    margin-right: 27px;
    font-size: 14px;
  }
}

.order-list__total-price {
  font-size: 24px;
  font-weight: $font-weight--extrabold;
  white-space: nowrap;

  @include viewport--sm {
    font-size: 20px;
  }
}

.order-list .order-list__go-button {
  padding-top: 22px;
  padding-right: 17px;
  padding-bottom: 14px;
  max-width: 400px;

  @include viewport--lg {
    padding-left: 37px;
    padding-top: 20px;
    padding-right: 18px;
  }

  @include viewport--sm {
    width: 100%;
    max-width: none;
    padding-top: 22px;
    padding-right: 12px;
    padding-left: 37px;
    letter-spacing: 0.1em;
  }

  svg {
    width: 27px;
    min-width: 27px;
    height: 18px;

    @include viewport--sm {
      top: -1px;
    }
  }
}
