.not-found {
  padding-top: 109px;
  padding-bottom: 155px;

  .title {
    text-align: center;
    margin-bottom: 25px;
  }

  img {
    margin: 0 auto 28px;
  }

  .btn {
    padding: 11px 10px;
    display: block;
    margin: 0 auto;
    margin-bottom: 101px;
    margin-top: 37px;
    width: 204px;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

.not-found__text {
  margin: 0;
  padding: 0 20%;
  text-align: center;

  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
}

@include viewport--lg {
  .not-found {
    padding-top: 98px;
    padding-bottom: 120px;

    .title {
      margin-bottom: 16px;
    }

    img {
      width: 78%;
      margin-bottom: 25px;
    }

    .btn {
      margin-top: 39px;
      margin-bottom: 97px;
    }
  }

  .not-found__text {
    padding: 0 17%;
  }
}

@include viewport--md {
  .not-found {
    overflow: hidden;
    padding-top: 45px;
    padding-bottom: 79px;

    .title {
      padding: 0 5%;
      margin-bottom: 12px;
    }

    img {
      width: 744px;
      margin-bottom: 17px;
      max-width: unset;
    }

    .btn {
      margin-bottom: 73px;
      width: 151px;
      padding: 9px 5px 3px;
      margin-top: 40px;
      font-size: 12px;
      line-height: 32px;
    }
  }

  .not-found__text {
    padding: 0;
    font-size: 14px;
    line-height: 24px;
  }
}
