.author-collections-block {

  padding-top: 62px;
  padding-bottom: 62px;

  &__link-wrapper {
    margin-bottom: 15px;

    @include viewport--md {
      a {
        @include reset-link;

        position: absolute;
        bottom: 40px;
      }
    }
  }

  &__slider {
    position: relative;
    overflow: hidden;

    &::after {
      content: "";
      position: absolute;
      z-index: 2;

      right: 0;
      top: 0;

      width: 122px;
      height: 100%;

      background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
    }
  }

  .swiper-slide {
    border-radius: 8px;
    overflow: hidden;
    width: auto;
    max-width: 100%;
  }

  @include viewport--md {
    position: relative;
    padding-top: 40px;
    padding-bottom: 110px;
  }

  &--no-top-padding {
    padding-top: 0;

    @include viewport--md {
      padding-top: 0;
    }
  }
}
