.principles {
  padding: 96px 0 86px 115px;
  color: $color-darkmain;

  @include viewport--lg {
    padding: 84px 0 86px 0;
  }

  @include viewport--sm {
    padding: 52px 0 80px;
  }

  &__img {
    width: 115px;
    height: 115px;
    flex: 0 0 auto;
    margin: -10px 32px 0 -34px;
    background-color: $color-darkmain;
    border-radius: 100%;

    @include viewport--lg {
      margin: -2px 26px 0 21px;
    }

    @include viewport--sm {
      width: 85px;
      height: 85px;
      margin: -2px 17px 0 17px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    margin-bottom: 42px;

    @include viewport--lg {
      font-size: 36px;
      line-height: 46px;
      margin-bottom: 38px;
    }

    @include viewport--sm {
      font-size: 22px;
      line-height: 46px;
      margin-bottom: 5px;
    }
  }

  &__list {
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    padding: 0;
    margin: 0;

    @include viewport--sm {
      grid-template-columns: 1fr;
      gap: 8px;
    }
  }

  &__item {
    padding-top: 25px;
    max-width: 505px;
    list-style-type: decimal;
    list-style-position: inside;

    &:nth-child(even) {
      padding-left: 55px;

      @include viewport--lg {
        padding-left: 0;
      }

    }

    @include viewport--sm {
      font-size: 18px;
      line-height: 26px;
    }

  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    padding-top: 10px;
    margin: 0;
    letter-spacing: 0.05px;

    @include viewport--sm {
      line-height: 22px;
    }
  }

  &__item--brandbook {
    list-style-type: none;
    border: 2px dashed $color-lightgray;
    border-radius: 16px;
    padding: 40px 17px 28px 23px;
    display: flex;
    justify-content: space-between;
    max-width: 556px;
    margin-left: 54px;
    margin-top: 10px;

    @include viewport--lg {
      margin-left: -6px;
      padding: 32px 45px 28px 23px;
    }

    @include viewport--sm {
      margin-left: 0;
      margin-top: 52px;
    }

    .principles__item-brandbook-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: -0.02em;
      margin: 0 0 5px 0;
    }

    .principles__item-brandbook-text {
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      color: $color-darkgray;
      margin: 0;
    }
  }
}
