.product-card-block {
  $b: #{&};
  padding-top: 49px;
  padding-bottom: 30px;

  &__price-wrapper {
    display: none;
    align-items: center;
    margin-bottom: 6px;
  }

  &__price {
    margin-right: 40px;

    font-weight: 900;
    font-size: 32px;
    line-height: 44px;
    letter-spacing: -0.02em;
  }

  &__discount-wrapper {
    display: flex;
    align-items: center;
    margin-right: auto;

    svg {
      margin-right: 18px;
      width: 32px;
      min-width: 32px;
      height: 32px;
    }
  }

  &__discount {
    margin: 0;

    position: relative;
    font-weight: 800;
    font-size: 24px;
    line-height: 44px;
    letter-spacing: -0.02em;

    color: $color-mediumgray;

    &::after {
      width: 81px;
      transform: translate(-50%, -50%) rotate(-15deg);
    }
  }

  &__collection-title a {
    @include reset-link;
  }

  a.product-card-block__cart-btn {
    display: none;
    font-weight: 800;

    p {
      margin: 0;
    }
  }

  &__size-select {
    display: none;
    margin-bottom: 20px;
  }

  &--no-calculation {
    button.product-card-block__calculate-btn {
      display: none;
    }

    a.product-card-block__cart-btn {
      display: block;

      .btn__order {
        font-size: 14px;
      }
    }

    #{$b}__collections-title {
      display: none;
    }

    .collections-slider-small {
      display: none;
    }

    .tags-list {
      display: none;
      margin-bottom: 37px;
    }

    #{$b}__menu-container {
      margin-bottom: 0;
    }

    #{$b}__price-wrapper {
      display: flex;
      margin-bottom: 45px;
    }

    #{$b}__size-select {
      display: block;
      margin-bottom: 20px;
    }

    #{$b}__prices {
      display: flex;
      flex-grow: 1;
      flex-wrap: wrap;
    }

    #{$b}__price {
      margin-right: 25px;
    }

    #{$b}__menu-wrapper {
      &.is-active {
        #{$b}__price-wrapper {
          display: none;
        }

        #{$b}__size-select {
          display: none;
        }
      }
    }

    @include viewport--md {
      #{$b}__menu-wrapper &.is-active #{$b}__cart-btn {
        .btn__in-order,
        .btn__order {
          display: none;
        }
      }
    }
  }

  .product-card-slider {
    margin-bottom: 20px;

    .slider-button-fill-next,
    .slider-button-fill-prev {
      opacity: 0.7;

      &:hover {
        opacity: 1;
      }
    }

    .swiper-slide {
      width: 100%;
      position: relative;
      padding-bottom: percentage(560/840);
      overflow: hidden;

      img {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        transition: transform $default-transition-settings;
        cursor: pointer;

        object-fit: cover;

        &:hover {
          transform: scale(1.03);
        }
      }
    }
  }

  .title--h1-extrasmall {
    margin-bottom: 20px;
  }

  &__collections-title {
    margin-bottom: 5px;

    font-weight: bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: -0.02em;

    .link {
      @include reset-link;
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      letter-spacing: inherit;
      color: $color-blue;

      transition: color $default-transition-settings;

      &:hover {
        color: $color-lightblue;
      }
    }
  }

  &__mobile {
    display: none;
  }

  &__slider-wrapper {
    width: 61.4%;
    margin-right: 61px;
  }

  &__desc-wrapper {
    width: calc(38.6% - 64px);
    flex-grow: 1;

    .btn {
      padding: 17px 33px 11px 33px;
      display: block;
      text-align: center;
      width: 100%;

      font-weight: bold;
      font-size: 14px;
      line-height: 32px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 4px;

      transition: background-color $default-transition-settings;
    }
  }

  &__menu-container {
    margin-bottom: 40px;
  }

  &__wrapper {
    display: flex;
  }

  &__article-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 17px;
  }

  &__author {
    margin: 0;

    margin-left: 20px;
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: -0.02em;

    color: $color-darkgray;

    .link {
      @include reset-link;

      color: inherit;
      font-weight: 500;

      &:hover {
        opacity: 0.7;
      }
    }
  }

  &__desc {
    margin: 0;

    margin-bottom: 32px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  .tags-list {
    margin-bottom: 83px;
    margin-left: -10px;
    margin-top: -10px;
  }

  .tags-list__item {
    @include reset-item;
    margin-top: 10px;
    margin-left: 10px;
  }

  &__menu-wrapper {
    transition: none;

    .color-proof-open {
      display: none;
    }

    &.init {
      & * {
        transition: none;
      }

      transform: translateY(-100%);
    }

    &.is-active {
      position: fixed;
      z-index: 12;
      top: 0;
      left: 0;
      width: 100%;

      transform: translateY(0);
      box-shadow: 0 2px 32px rgba(45, 51, 68, 0.15);
      transition: transform $modal-transition-settings;
      background-color: #ffffff;

      #{$b}__container {
        width: 100%;
        height: 100%;
        max-width: 1440px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        padding: 15px 36px 15px 36px;
      }

      #{$b}__price-wrapper {
        margin-bottom: 0;
        margin-left: 60px;
      }

      #{$b}__price {
        margin-right: 25px;
      }

      #{$b}__cart-btn {
        margin-left: 60px;
      }

      .title {
        max-width: 57%;
        margin-bottom: 0;

        font-weight: 800;
        font-size: 14px;
        line-height: 36px;
        margin-right: auto;
      }

      #{$b}__article-wrapper {
        margin-bottom: 0;
        display: block;
      }

      .article-number {
        margin-right: 27px;
      }

      .color-proof-open {
        position: relative;
        margin-left: auto;
        padding-left: 42px;
        display: block;

        background-color: transparent;
        color: $color-darkmain;
        border: 0;
        cursor: pointer;

        span {
          font-weight: 700;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: -0.02em;
          transition: color $default-transition-settings;
        }

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 50%;

          width: 32px;
          height: 32px;

          transform: translateY(-50%);

          background-image: url("../img/sprite/icon-colors.svg");
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
        }

        &:hover {
          span {
            color: $color-blue;
          }
        }
      }

      .btn {
        width: fit-content;
        margin-bottom: 0;
        margin-left: 53px;

        font-size: 10px;
        line-height: 32px;
        font-weight: 700;

        padding: 6px 28px 2px 28px;
        border-radius: 0;
      }

      .tags-list {
        display: none;
      }

      #{$b}__desc {
        display: none;
      }

      #{$b}__author {
        display: none;
      }
    }
  }
}

@include viewport--xl {
  .product-card-block__menu-wrapper {
    &.is-active {
      .btn {
        margin-left: 28px;
      }

      .product-card-block__price-wrapper {
        margin-bottom: 0;
        margin-left: 30px;
      }

      .product-card-block__cart-btn {
        margin-left: 40px;
      }
    }
  }
}

@include viewport--lg {
  .product-card-block {
    padding-bottom: 30px;

    .title {
      margin-bottom: 28px;

      font-size: 26px;
      line-height: 32px;
    }

    &__collections-title {
      font-size: 13px;
      line-height: 32px;
    }

    .product-card-slider {
      margin-bottom: 20px;

      .card-tags {
        right: 20px;
        top: 20px;
      }

      .swiper-slide {
        padding-bottom: percentage(560/840);
      }

      .slider-button-fill-prev,
      .slider-button-fill-next {
        width: 42px;
        height: 42px;
      }

      .slider-button-fill-prev {
        left: 21px;
      }

      .slider-button-fill-next {
        right: 21px;
      }

      .like {
        right: 20px;
        bottom: 20px;

        width: 42px;
        height: 42px;

        svg {
          width: 27px;
          height: 22px;
        }
      }
    }
  }

  .product-card-block__menu-wrapper {
    &.is-active {
      .color-proof-open span {
        display: none;
      }

      .title {
        font-size: 12px;
        line-height: 36px;
      }

      .btn {
        margin-left: 8px;
      }

      .product-card-block__price-wrapper {
        margin-left: 20px;
      }

      .product-card-block__cart-btn {
        margin-left: 30px;
      }
    }
  }

  .product-card-block__desc-wrapper {
    .btn {
      text-align: center;
    }
  }

  .product-card-block__menu-container {
    margin-bottom: 45px;
  }

  .product-card-block__desc {
    margin-bottom: 30px;

    font-size: 12px;
    line-height: 22px;
  }

  .tags-list {
    margin-bottom: 43px;
  }

  .product-card-block__slider-wrapper {
    padding-top: 8px;
    margin-right: 43px;

    .service-block {
      p {
        width: 40.5%;
      }
    }

    .service-block__svg-wrapper {
      width: 53%;
    }
  }
}

@include viewport--md {
  .product-card-block__mobile {
    display: block;

    .title {
      margin-bottom: 6px;
      font-size: 26px;
      line-height: 32px;
    }

    .article-number {
      display: inline-block;
      position: relative;
      top: -3px;
      padding: 7px 14px 9px;
      font-size: 11px;
      line-height: 14px;
    }

    .product-card-block__article-wrapper {
      margin-bottom: 8px;
    }

    .product-card-block__author {
      margin-left: 0;
    }
  }

  .product-card-block {
    padding-top: 25px;
    padding-bottom: 30px;

    &__collection-title {
      margin-top: 30px;
      margin-bottom: 13px;
    }
  }

  .product-card-block__wrapper {
    flex-direction: column;
  }

  .product-card-block__menu-wrapper {
    .product-card-block__article-wrapper {
      display: none;
    }

    .title {
      display: none;
    }

    &.is-active {
      .product-card-block__container {
        padding: 17px 15px;
        padding-bottom: 12px;
      }

      .title {
        display: block;
        position: relative;
        width: 65%;
        max-width: unset;
        overflow: hidden;
        white-space: nowrap;
        line-height: 16px;

        &::after {
          content: "";
          position: absolute;
          right: 0;
          top: 50%;
          transform: translateY(-50%);

          width: 81px;
          height: 32px;

          background: linear-gradient(to left, #ffffff 11.48%, rgba(255, 255, 255, 0) 94.41%);
        }
      }

      .article-number {
        margin-right: 15px;
        padding: 5px 19px 7px;

        span {
          display: none;
        }
      }

      .color-proof-open {
        display: none;
      }

      .btn span {
        display: none;
      }

      .btn {
        padding: 0;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin-left: auto;
        flex-shrink: 0;
      }
    }
  }

  .product-card-block__slider-wrapper {
    margin-bottom: 25px;
    width: 100%;

    .service-block__svg-wrapper {
      width: 46%;

      svg {
        width: 83px;
        height: 65px;
      }

      b {
        font-size: 14px;
        line-height: 20px;
      }
    }

    .service-block {
      p {
        width: 50%;
      }
    }

    .product-card-slider {
      margin-bottom: 23px;

      .swiper-slide {
        height: 250px;
      }

      .card-tags {
        right: 16px;
      }

      .slider-button-fill-prev,
      .slider-button-fill-next {
        width: 36px;
        height: 36px;
      }

      .slider-button-fill-prev {
        left: 16px;
      }

      .slider-button-fill-next {
        right: 16px;
      }

      .like {
        right: 15px;
        bottom: 15px;

        width: 36px;
        height: 36px;

        svg {
          width: 22px;
          height: 19px;
          margin-left: 1px;
          margin-top: 3px;
        }
      }
    }
  }

  .tags-list {
    margin-bottom: 46px;
  }

  .product-card-block__desc {
    margin-bottom: 29px;
  }

  .product-card-block__desc-wrapper {
    width: 100%;
    padding-top: 31px;
    border-top: 1px solid $beige-extralight;
  }

  .product-card-block__menu-container {
    margin-bottom: 20px;
  }
}

@include viewport--sm {
  .product-card-block {
    &__collection-title {
      margin-bottom: 4px;
    }
  }

  .product-card-block__slider-wrapper {
    width: calc(100% + 34px);
    margin-left: -17px;
    margin-bottom: 25px;

    .service-block {
      padding: 0 13px;

      p {
        display: none;
      }
    }

    .product-card-slider {
      margin-bottom: 13px;
    }

    .service-block__svg-wrapper {
      width: 100%;
    }
  }

  .product-card-block__desc-wrapper {
    padding-top: 0;
    border-top: 0;
  }

  .product-card-block__desc {
    margin-bottom: 22px;
  }

  .product-card-block__mobile {
    .title {
      font-size: 22px;
      line-height: 28px;
    }
  }
}

.product-card-block__counter {
  margin-right: 25px;
}
