.cards-block {
  padding-top: 62px;
  padding-bottom: 112px;

  background-color: $beige-extralight;

  .title {
    margin-bottom: 33px;
  }
}

@include viewport--xl {
  .cards-block {
    padding-top: 60px;
    padding-bottom: 102px;

    .title {
      margin-bottom: 39px;
    }
  }
}


@include viewport--lg {
  .cards-block {
    padding-bottom: 72px;

    .title {
      margin-bottom: 27px;
    }
  }
}

@include viewport--md {
  .cards-block {
    padding-bottom: 55px;
    padding-top: 39px;

    .title {
      margin-bottom: 16px;
    }
  }
}
