.similar {
  padding: 29px 10px 120px 0;
  margin-right: -10px;
  overflow: hidden;

  @include viewport--xl {
    padding: 23px 10px 120px 0;
  }

  @include viewport--md {
    padding: 14px 10px 100px 0;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 29px;

    @include viewport--xl {
      margin-bottom: 13px;
    }

    @include viewport--md {
      margin-bottom: 20px;
    }
  }

  .slider-buttons.hidden {
    display: none;
  }

  &__title {
    @include reset-item;
    font-weight: 500;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    @include viewport--xl {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 18px;
    }

    @include viewport--md {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 7px;
    }
  }

  &.swiper-container-initialized {
    //padding-right: 0;
    //margin-right: 0;

    .similar__title {
      margin-bottom: 0;
    }
  }

  .swiper-wrapper:not(.similar__slider) .project-card__img {
    height: auto;

    @include viewport--xl {
      height: auto;
    }

    @include viewport--md {
      height: 218px;
    }
  }

  &__slider {
    display: flex;
    gap: 28px;

    @include viewport--xl {
      gap: 19px;
    }

    .card-tags span {
      @include viewport--xl {
        height: 36px;
        width: 36px;
        min-height: 36px;
        min-width: 36px;
      }
    }

    .project-card__wrapper {
      @include viewport--xl {
        padding: 12px 23px 15px 19px;
      }
    }

    //.project-card__title {
    //  font-size: 14px;
    //  line-height: 16px;
    //}

    .swiper-slide {
      width: calc((100% - 56px) / 3);

      @include viewport--xl {
        .project-card__img {
          height: 222px;
        }
      }

      @include viewport--md {
        width: calc((100% - 32px) / 2);
      }
    }
  }
}
