.cases-card {
  display: block;
  text-decoration: none;
  position: relative;

  &:hover {
    img {
      transform: scale(1.1);
    }

    .cases-card__inner {
      opacity: 1;
    }
  }

  &__img {
    border-radius: 16px;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    -webkit-mask-image: -webkit-radial-gradient(white, black);

    @include viewport--sm {
      height: 240px;
      margin-bottom: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      height: 44%;
      width: 100%;
      background: linear-gradient(360deg, rgba(46, 51, 70, 0.48) 0%, rgba(46, 51, 70, 0) 100%);
      z-index: 1;
    }

    img {
      max-height: 420px;
      min-height: 240px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      transition: transform $default-transition-settings;
    }
  }

  &__img--460 {
    max-height: 460px;

    img {
      max-height: 460px;
    }
  }

  &__inner {
    position: absolute;
    left: 20px;
    bottom: 24px;
    display: flex;
    align-items: center;
    z-index: 2;
    opacity: 0.7;
    transition: opacity $default-transition-settings;

    @include viewport--md {
      left: 15px;
      bottom: 16px;
    }
  }

  &__tag {
    font-weight: 600;
    font-size: 12px;
    line-height: 32px;
    color: $color-darkmain;
    background: $color-extralightgray;
    border-radius: 32px;
    padding: 4px 24px;
    margin-right: 30px;
  }

  &__addres {
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: $color-beigelight;
    position: relative;
    padding-left: 20px;

    svg {
      position: absolute;
      top: 2px;
      left: 0;
    }
  }

  &__header {
    display: flex;
    align-items: center;
    margin-bottom: -10px;

    svg {
      min-width: 36px;
      align-self: flex-start;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-right: 10px;

    @include viewport--md {
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkgray;
    margin: 0;
    margin-top: 10px;


    @include viewport--md {
      margin-top: 10px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 18px;
      margin-top: 17px;
    }
  }
}
