.faq {
  padding-top: 113px;
  padding-bottom: 118px;

  background-color: $beige-extralight;

  .container {
    max-width: 1204px;
  }

  .title {
    margin-top: 0;
    margin-bottom: 34px;

    font-weight: 400;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  &--studio {
    padding-top: 76px;

    .container {
      padding: 0 32px;
    }

    h2 {
      font-weight: 400;
      font-size: 34px;
      line-height: 36px;
      letter-spacing: -0.02em;
      color: $color-darkmain;
    }
  }
}

@include viewport--xl {
  .faq {
    padding-bottom: 117px;

    .container {
      max-width: unset;
      padding: 0 10.7%;
    }
  }
}

@include viewport--lg {
  .faq {
    padding-top: 74px;
    padding-bottom: 96px;

    .container {
      padding: 0 3.4%;
    }

    .title {
      margin-bottom: 34px;

      font-size: 28px;
      line-height: 32px;
    }

    &--studio {
      padding-top: 76px;

      .container {
        padding: 0 35px;
      }

      h2 {
        font-size: 28px;
        line-height: 32px;
        margin: 0 0 34px 0;
      }

      button b {
        font-size: 16px;
        line-height: 22px;
      }
    }

    &--info {
      padding-right: 88px;
      padding-left: 88px;
      padding-top: 89px;
      padding-bottom: 125px;
    }
  }
}

@include viewport--md {
  .faq {
    padding-top: 50px;
    padding-bottom: 65px;

    .container {
      padding: 0 17px;
    }

    .title {
      display: none;
    }
  }

  .faq--info .title {
    display: block;
  }
}

@include viewport--sm {
  .faq--info .title {
    font-size: 24px;
    line-height: 36px;
    display: block;
    margin-bottom: 21px;
  }

  .faq--info {
    padding: 44px 0 75px;
  }

  .faq--studio {
    padding-top: 64px;

    h2 {
      font-size: 22px;
      line-height: 32px;
      margin-bottom: 40px;
    }

    button {
      padding: 33px 75px 30px 35px;
    }

    button b {
      font-size: 14px;
      line-height: 20px;
    }

    .accordeon li {
      margin-bottom: 16px;
    }

    .accordeon button::after {
      top: 38px;
    }
  }
}
