.made {
  position: relative;
  padding-top: 95px;
  padding-bottom: 119px;

  background-color: $beige-extralight;

  h2 {
    margin: 0;
    margin-bottom: 44px;
    padding: 0 10%;

    text-align: center;
    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
    width: 143px;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    width: 143px;
    height: 100%;
    background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  .reviews-slider .swiper-slide img {
    height: 246px;
    width: 350px;
  }

  .reviews-slider .swiper-wrapper {
    .swiper-slide {
      width: 350px;
      height: auto;
    }

    margin-left: -21px;
  }
}

@include viewport--lg {
  .made {
    padding-top: 61px;
    padding-bottom: 98px;

    h2 {
      margin-bottom: 40px;

      font-size: 28px;
      line-height: 32px;
    }

    .reviews-slider .swiper-wrapper {
      margin-left: 21px;
    }
  }
}

@include viewport--md {
  .made {
    padding-top: 46px;
    padding-bottom: 71px;

    h2 {
      margin-bottom: 31px;

      font-size: 22px;
      line-height: 26px;
    }

    .reviews-slider .swiper-slide img {
      width: 260px;
      height: 166px;
    }

    .reviews-slider .swiper-wrapper .swiper-slide {
      width: 260px;
    }

    &::before {
      width: 50px;
      height: 100%;
    }

    &::after {
      width: 50px;
      height: 100%;
    }
  }
}
