.reviews {
  position: relative;
  padding-top: 65px;
  padding-bottom: 71px;
  background-color: $beige-extralight;

  .title {
    margin: 0;
    margin-bottom: 8px;

    text-align: center;
    letter-spacing: -0.02em;
  }

  &__text {
    margin: 0;
    margin-bottom: 34px;

    text-align: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  &__number {
    font-weight: bold;
  }

  .btn {
    display: block;
    margin: 54px auto 0;
    padding: 22px 10px 10px 10px;
  }
}

@include viewport--lg {
  .reviews {
    padding-top: 62px;

    &__text {
      font-size: 16px;
      margin-bottom: 41px;
    }
  }
}

@include viewport--md {
  .reviews {
    padding-top: 51px;
    padding-bottom: 52px;

    .btn {
      min-width: 195px;
      margin: 43px auto 0;
      padding: 16px 12px 11px;
      max-width: 190px;

      font-size: 12px;

      letter-spacing: 0.05em;
    }

    &__text {
      margin-bottom: 36px;
      font-size: 14px;
      line-height: 36px;
    }
  }
}

@include viewport--sm {
  .reviews {
    .btn {
      margin-top: 53px;
    }
  }
}
