.tippy-popup {
  max-width: 580px;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  padding: 32px 21px 30px;
  background-color: $color-default-white;
  box-shadow: 0 2px 16px rgba(45, 51, 68, 0.15);
  border-radius: 8px;
  overflow: hidden;
  position: relative;

  @include viewport--xl {
    max-width: 485px;
    padding-top: 35px;
    padding-bottom: 30px;
  }

  @include viewport--sm {
    max-width: 178px;
    padding: 13px 8px;
  }

  .tippy-svg-arrow {
    display: none;
  }

  .close-btn {
    top: 12px;
    right: 17px;
    height: 18px;
    width: 18px;
    padding: 5px;
    background-image: url(../img/svg/close-dark.svg);
    opacity: 1;

    &:hover {
      opacity: 0.7;
    }

    @include viewport--xl {
      top: 18px;
      right: 18px;
    }

    @include viewport--sm {
      height: 15px;
      width: 15px;
      top: 8px;
      right: 8px;
    }
  }

  &__wrapper {
    display: grid;
    grid-column-gap: 20px;
    justify-content: start;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(3, auto);
    grid-template-areas:
      "A B"
      "A C"
      "A D"
      "A E";

    @include viewport--sm {
      grid-template-areas:
        "B B"
        "C C"
        "D D"
        "E E";
    }
  }

  .tag {
    grid-area: C;
    background: $beige-extralight;
    border-radius: 24px;
    display: inline-block;
    width: auto;
    color: $color-darkmain;
    justify-self: start;
    margin-bottom: 6px;

    @include viewport--sm {
      display: none;
    }
  }

  &__img {
    grid-area: A;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    overflow: hidden;

    @include viewport--sm {
      display: none;
    }

    img {
      min-height: 100%;
    }

    @include viewport--xl {
      width: 142px;
      height: 142px;
    }

    @include viewport--sm {
      width: 35px;
      height: 35px;
    }
  }

  &__title {
    grid-area: B;
    @include reset-item;
    font-weight: 800;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-bottom: 11px;
    margin-top: 8px;

    @include viewport--xl {
      margin-top: -4px;
      margin-bottom: 13px;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 26px;
      align-self: start;
      margin-top: 3px;
      margin-bottom: 0;
    }
  }

  &__text {
    grid-area: D;
    @include reset-item;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: $color-default-black;
    margin-bottom: 16px;

    @include viewport--xl {
      line-height: 19px;
    }

    @include viewport--sm {
      font-size: 10px;
      line-height: 14px;
      padding-top: 8px;
    }
  }

  .btn {
    grid-area: E;
    justify-self: start;
  }
}

.tippy-box {
  background-color: transparent;
}
