.landing-promo {
  padding-top: 49px;

  .breadcrumbs {
    display: flex;
    justify-content: center;
  }
}

.landing-promo__text-wrapper {
  padding-bottom: 80px;

  h1 {
    padding: 0 26%;
    margin: 0;
    margin-bottom: 28px;

    font-weight: 800;
    font-size: 42px;
    line-height: 48px;
    text-align: center;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;
    padding: 0 23%;

    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
  }
}

@include viewport--xl {
  .landing-promo__text-wrapper {
    h1 {
      padding: 0 23%;
    }

    p {
      padding: 0 20%;
    }
  }
}

@include viewport--lg {
  .landing-promo__text-wrapper {
    padding-bottom: 64px;

    h1 {
      padding: 0 20%;
      margin-bottom: 29px;

      font-size: 36px;
      line-height: 42px;
    }

    p {
      padding: 0 12%;

      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
    }
  }
}

@include viewport--md {
  .landing-promo {
    padding-top: 25px;
  }

  .landing-promo__text-wrapper {
    padding-bottom: 41px;

    h1 {
      padding: 0 5%;
      margin-bottom: 23px;

      font-size: 28px;
      line-height: 36px;
    }

    p {
      padding: 0 5%;
    }
  }
}
