*,
*::before,
*::after {
  box-sizing: border-box;
}

// ! Не используйте scroll-behavior: smooth вместе с текущим utils/scroll-lock.js
html {
  height:  -webkit-fill-available;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height:  -webkit-fill-available;
  min-height: 100vh;
  padding-bottom: env(safe-area-inset-bottom);
}

body {
  width: 100%;
  display: flex;
  flex-direction: column;

  background-color: $color-default-white;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// если вы убираете outline - убедитесь, что для всех интерактивных элементов есть кастомный стиль фокуса
:focus {
  outline: none;
}

// chrome autofill background removal
// Если на проекте у инпутов используются разные цвета фонов\текста -
// удалите это из глобала и используйте локально с нужными цветами
// rgba не подойдет, сконвертируйте цвет в hex без прозрачности
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}
/* stylelint-disable */
// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

//safari - hide input shadow
input {
  -webkit-appearance: none;
}
/* stylelint-enable */

//safari - hide user's icon
::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
  right: 0;
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;

  opacity: 0;
  z-index: -999;
  overflow-y: auto;

  background-color: rgba($color: $color-darkmain, $alpha: 0.8);

  &.is-active {
    z-index: 999;
    opacity: 1;
  }
}

.overlay-open {
  position: fixed;
  overflow-y: auto;
}
