.quality-icon {
  display: inline-flex;
  align-items: center;

  &__number {
    @include reset-item;
    font-weight: 500;
    font-size: 64px;
    line-height: 76px;
    color: $color-default-white;
    letter-spacing: -0.05em;
    margin-left: 7px;

    @include viewport--md {
      font-size: 48px;
      line-height: 64px;
      margin-left: 13px;
    }
  }

  &__icon {
    @include viewport--md {
      width: 49px;
      height: 38px;
    }
  }
}
