.delivery {
  padding-top: 49px;
  padding-bottom: 126px;

  @include viewport--lg {
    padding-bottom: 142px;
  }

  @include viewport--md {
    padding-top: 25px;
  }

  @include viewport--sm {
    padding-bottom: 55px;
  }

  &__input-city--open-checkbox {
    @include viewport--sm {
      margin-bottom: 330px;
    }

    &::after {
      display: block;
    }
  }

  .title {
    margin-bottom: 29px;
  }
}

.delivery__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @include viewport--sm {
    flex-direction: column;
  }
}

.delivery__form {
  margin-right: 30px;
  display: flex;
  flex-wrap: wrap;
  max-width: 904px;

  &--reviews {
    max-width: 813px;
  }

  @include viewport--lg {
    margin-right: 20px;
    max-width: 629px;
  }

  @include viewport--sm {
    order: 5;
    margin-right: 0;
    max-width: 100%;
  }
}

.delivery__upload-wrapper,
.delivery__input-textarea,
.delivery__submit-checkbox-wrapper,
.delivery__form-block {
  width: 100%;
}

.delivery__input-city {
  position: relative;
  margin-bottom: 17px;

  @include viewport--sm {
    margin-bottom: 21px;
  }

  &::after {
    position: absolute;
    content: "";
    width: 14px;
    height: 14px;
    background-color: $color-green;
    top: 42%;
    right: -6px;
    border-radius: 50%;
    display: none;

    @include viewport--sm {
      top: 77%;
      left: 24px;
    }
  }
}

.delivery__input-address {
  margin-bottom: 22px;

  @include viewport--sm {
    margin-bottom: 16px;
  }
}

.delivery__form-block {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 24px;

  @include viewport--sm {
    margin-bottom: 15px;
  }
}

.delivery__form-block + .delivery__form-block {
  margin-bottom: 24px;

  @include viewport--sm {
    margin-bottom: 15px;
  }
}

.delivery__form-block .input-wrapper {
  width: 48%;
  margin-right: 4%;
  margin-bottom: 0;

  &:last-of-type {
    margin-right: 0;
  }

  @include viewport--lg {
    width: 48.5%;
    margin-right: 3%;
  }

  @include viewport--sm {
    margin-right: 0;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.delivery__form .delivery__input-textarea {
  margin-bottom: 24px;

  @include viewport--sm {
    margin-bottom: 18px;
  }
}

.delivery__upload-wrapper {
  margin-bottom: 42px;
  display: flex;
  align-items: flex-start;

  @include viewport--lg {
    margin-bottom: 26px;
  }

  @include viewport--sm {
    flex-direction: column;
    align-items: flex-start;
  }

  p {
    margin: 0;
  }
}

.delivery__form .delivery__input-upload {
  margin-right: 34px;
  width: 70.8%;

  @include viewport--lg {
    margin-right: 19px;
    width: 61.4%;
  }

  @include viewport--sm {
    margin-right: 0;
    margin-bottom: 17px;
    width: 100%;
  }

  & + p {
    padding-top: 17px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    max-width: 197px;

    @include viewport--lg {
      padding-top: 12px;
    }

    @include viewport--sm {
      padding-top: 0;
      max-width: none;
    }
  }
}

.delivery__submit-checkbox-wrapper {
  display: flex;
  flex-wrap: wrap;

  @include viewport--md {
    flex-direction: column;
  }

  &--centered {
    align-items: center;

    .input-wrapper {
      margin-bottom: 0;
    }

    @include viewport--xl {
      flex-direction: column;
      align-items: flex-start;

      .input-wrapper--checkbox.delivery__oferta-checkbox {
        margin-top: 20px;
        width: 100%;
      }
    }
  }
}

.delivery__form .delivery__make-order-button {
  padding-top: 15px;
  padding-bottom: 13px;
  margin-right: 9%;
  max-width: 420px;

  svg {
    width: 24px;
    min-width: 24px;
    height: 18px;
  }

  @include viewport--lg {
    margin-right: auto;
  }

  @include viewport--sm {
    padding-right: 20px;
    margin-right: 0;
    width: 100%;
  }

  span {
    font-family: $font-family--Mont;
    font-weight: $font-weight--bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.05em;
    color: $color-default-white;
  }
}

.delivery__form--reviews .delivery__make-order-button {
  max-width: 378px;
  margin-right: 9%;

  @include viewport--xl {
    margin-right: auto;
  }

  @include viewport--lg {
    margin-right: auto;
  }

  @include viewport--sm {
    margin-right: 0;
  }
}

.delivery__form .delivery__oferta-checkbox {
  max-width: 337px;

  @include viewport--lg {
    width: calc(100% - 450px);
  }

  @include viewport--md {
    width: 100%;
    order: -1;
    margin-bottom: 24px;
  }

  @include viewport--sm {
    margin-bottom: 15px;
  }

  label {
    font-weight: $font-weight--medium;
  }
}

.delivery__form--reviews .delivery__oferta-checkbox {
  @include viewport--lg {
    width: calc(100% - 408px);
  }

  @include viewport--md {
    width: 100%;
  }
}

.delivery__form--order .delivery__oferta-checkbox {
  max-width: 337px;

  @include viewport--lg {
    width: 100%;
    order: -1;
    margin-bottom: 24px;
  }

  @include viewport--sm {
    margin-bottom: 15px;
  }
}

.delivery__rightside-wrapper {
  max-width: 32%;
  flex-grow: 1;

  @include viewport--sm {
    order: 0;
  }
}

.delivery__rightside-bottom-wrapper {
  @include viewport--sm {
    display: none;
  }
}

.contacts-form--reviews .delivery__rightside-bottom-wrapper {
  @include viewport--sm {
    display: block;
  }
}

.contacts-form--reviews .delivery__rightside-wrapper {
  @include viewport--sm {
    max-width: 100%;
    order: 6;
  }
}

.delivery__need-montage-wrapper {
  position: relative;
  margin-bottom: 48px;
  display: none;

  @include viewport--sm {
    position: absolute;
    margin: 0;
    width: calc(100% - 34px);
    transform: translateX(-50%);
    left: 50%;
    top: 312px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 200px;
    height: 1px;
    top: 34px;
    left: -120px;
    background-color: $color-beigelight;
    z-index: -1;

    @include viewport--lg {
      width: 400px;
      left: -300px;
    }

    @include viewport--sm {
      width: 2px;
      left: 30px;
      top: -22px;
      height: 80px;
    }
  }
}

.delivery__need-montage-wrapper--open-checkbox {
  display: block;
}

.delivery__need-montage-img {
  margin-bottom: 12px;
  padding-top: 37px;
  padding-bottom: 61px;
  padding-left: 45px;
  background-image: url("../img/delivery-montage-desktop.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-color: $color-beigelight;
  z-index: 4;

  @include viewport--lg {
    padding-left: 34px;
    padding-bottom: 62px;
    background-image: url("../img/delivery-montage-tablet.jpg");
    background-size: cover;
  }

  @include viewport--md {
    padding-right: 15px;
    padding-left: 15px;
  }

  @include viewport--sm {
    padding-top: 29px;
    padding-left: 27px;
    padding-bottom: 33px;
    padding-right: 35px;
    background-image: url("../img/delivery-montage-mobile.jpg");
    background-size: cover;
  }
}

.delivery__need-montage-img span {
  display: inline-block;
  margin-bottom: 5px;
  font-family: $font-family--Mont;
  font-weight: $font-weight--bold;
  font-size: 34px;
  line-height: 36px;
  letter-spacing: -0.02em;
  color: $color-darkmain;

  @include viewport--lg {
    margin-bottom: 0;
    font-size: 28px;
    line-height: 32px;
  }

  @include viewport--sm {
    font-size: 24px;
    line-height: 28px;
  }
}

.delivery__need-montage-img p {
  margin: 0;

  margin-bottom: 24px;
  font-family: $font-family--Mont;
  color: $color-darkmain;
  font-weight: $font-weight--regular;
  font-size: 14px;
  line-height: 24px;

  @include viewport--sm {
    margin-bottom: 8px;
    font-size: 13px;
    line-height: 20px;
  }

  &:first-of-type {
    margin-bottom: 20px;
    font-weight: $font-weight--light;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.02em;

    @include viewport--lg {
      margin-bottom: 27px;
      font-size: 24px;
    }

    @include viewport--sm {
      margin-bottom: 9px;
      font-size: 20px;
      line-height: 24px;
    }
  }

  &:last-of-type {
    @include viewport--sm {
      min-height: 40px;
    }
  }
}

.delivery__montage-checkbox {
  max-width: 248px;

  label {
    padding-top: 13px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--bold;
    font-size: 14px;
    line-height: 32px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $color-default-white;
  }
}

.delivery__need-montage-wrapper > p {
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: -0.02em;
  color: $color-mediumgray;
  max-width: 270px;
}

.delivery__services-wrapper .title {
  margin-bottom: 25px;
  letter-spacing: -0.015em;

  @include viewport--lg {
    padding-right: 10px;
    margin-bottom: 22px;
  }
}

.delivery__partner {
  margin-bottom: 24px;
  padding-left: 0;
  display: flex;

  @include viewport--lg {
    margin-bottom: 25px;
  }

  .delivery__name {
    margin: 0;

    padding-top: 2px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
    flex-grow: 0;

    @include viewport--lg {
      padding-top: 0;
    }
  }
}

.delivery__sdek-img-wrapper {
  margin-right: 29px;
  min-width: 152px;

  @include viewport--lg {
    margin-right: 27px;
    min-width: 127px;
    height: 37px;
  }
}

.delivery__sdek-img {
  @include viewport--lg {
    width: 127px;
    height: 37px;
  }
}

.delivery .paragraph {
  margin: 0;
  margin-bottom: 22px;
  padding-right: 15px;

  @include viewport--lg {
    margin-bottom: 32px;
    padding-right: 0;
    letter-spacing: -0.042em;
    line-height: 22px;
  }
}

.contacts-form--reviews .paragraph {
  margin: 0;
  margin-bottom: 12px;
  padding-right: 0;

  @include viewport--lg {
    margin-bottom: 12px;
    padding-right: 0;
    letter-spacing: -0.042em;
    line-height: 22px;
  }
}

.delivery hr {
  margin-bottom: 24px;

  @include viewport--lg {
    margin-bottom: 23px;
  }
}

.delivery__post-logos {
  display: flex;
  flex-wrap: wrap;

  @include viewport--md {
    flex-direction: column;
  }
}

.delivery__post-img-wrapper {
  margin-bottom: 13px;

  @include viewport--lg {
    margin-bottom: 9px;
  }

  @include viewport--md {
    margin-bottom: 25px;
  }
}

.delivery__post-img {
  @include viewport--lg {
    width: 89px;
    height: 44px;
  }
}

.delivery__dostavista-img-wrapper {
  margin-right: 27px;
  padding-top: 12px;

  @include viewport--lg {
    padding-top: 9px;
    margin-right: 24px;
  }

  @include viewport--md {
    padding-top: 0;
    margin-right: 0;
    margin-bottom: 25px;
  }
}

.delivery__dostavista-img {
  @include viewport--lg {
    width: 140px;
    height: 20px;
  }
}

.delivery__dellin-img-wrapper {
  padding-left: 3px;
}

.delivery__dellin-img {
  @include viewport--lg {
    width: 227px;
    height: 35px;
  }
}
