.contacts {
  background-color: $beige-extralight;
  padding-top: 49px;

  .title {
    text-align: center;
    margin-bottom: 29px;
  }

  .breadcrumbs {
    .breadcrumbs__list {
      justify-content: center;
    }
  }
}

.contacts__desc {
  margin: 0;

  margin-bottom: 27px;

  text-align: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
}

@include viewport--lg {
  .contacts__desc {
    margin-bottom: 30px;

    font-size: 14px;
    line-height: 24px;
  }
}

@include viewport--md {
  .contacts {
    padding-top: 25px;

    .title {
      margin-bottom: 19px;
    }
  }

  .contacts__desc {
    margin-bottom: 31px;
  }
}
