.container {
  box-sizing: border-box;
  width: 100%;
  max-width: $content + $desktop-xxl-padding * 2;
  margin: 0 auto;
  padding: 0 $desktop-xxl-padding;

  @include viewport--lg {
    // padding: 0 $desktop-lg-padding;
  }

  @include viewport--md {
    padding: 0 $tablet-md-padding;
  }

  @include viewport--sm {
    padding: 0 $mobile-padding;
  }

  &--no-padding {
    @include viewport--sm {
      padding: 0;
    }
  }

  &-md {
    @include viewport--md {
      padding: 0 35px;
    }

    @include viewport--sm {
      padding: 0 15px;
    }
  }
}
