.search-result-list {
  padding-top: 57px;

  .title {
    margin-bottom: 29px;
    margin-top: 64px;

    &:first-child {
      margin-top: 0;
    }

    span {
      margin-left: 10px;

      font-weight: 500;
      font-size: 28px;
      line-height: 20px;
      letter-spacing: -0.02em;

      color: $color-blue;
    }
  }

  .collections-cards-slider {
    margin-bottom: 72px;
  }
}

@include viewport--sm {
  .search-result-list {
    padding-top: 42px;

    .collections-cards-slider {
      margin-bottom: 42px;
    }

    .title {
      margin-top: 36px;
      margin-bottom: 19px;

      padding-right: 35px;

      span {
        margin-left: 5px;
        font-size: 22px;
        line-height: 20px;
      }
    }
  }
}
