.team {
  margin: 0 35px;

  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 611px;
    padding-top: 85px;
    padding-left: 85px;
    padding-right: 85px;
    padding-bottom: 85px;

    background-color: $beige-extralight;
  }

  &__title {
    margin: 0;
    margin-bottom: 17px;
    margin-right: 28px;

    font-weight: 800;
    font-size: 100px;
    line-height: 100px;
    letter-spacing: -0.02em;
    color: $color-default-white;

    span {
      color: $color-green;
    }
  }

  &__subtitle {
    width: 200px;
    margin: 0;
    align-self: center;
    margin-bottom: 17px;

    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
    letter-spacing: -0.02em;
    color: $color-default-white;
  }

  p {
    margin: 0;

    width: 100%;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-default-white;
  }

  .btn {
    min-width: auto;
    width: 205px;
    padding: 17px 10px 10px;
    margin-top: 65px;
  }

  img {
    position: absolute;
    object-fit: cover;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }

  &__text-wrapper {
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    width: 59%;
  }

  &--studio {
    padding: 70px 0 100px;

    .container {
      min-height: 560px;
      padding: 47px 80px 33px;
    }

    .team__text-wrapper {
      flex-direction: column;
      justify-content: space-between;
      min-height: 472px;
    }

    .team__text-inner {
      display: flex;
      flex-wrap: wrap;
    }

    .team__title {
      font-weight: 950;
      font-size: 120px;
      line-height: 120px;
      letter-spacing: -0.02em;
      text-transform: uppercase;
      margin: 0 70px 7px 0;
    }

    .team__subtitle {
      font-weight: 600;
      font-size: 14px;
      line-height: 22px;
      color: $color-mediumgray;
      margin: 0 0 80px;
    }

    p {
      width: 82%;
    }

    .btn-general {
      min-height: 50px;
    }

    .btn {
      width: 218px;
      padding: 5px 10px 4px;
      margin-top: 15px;
    }

    img {
      object-position: 14% 50%;
    }
  }

  &--cases {
    padding: 14px 0 0 0;
    border-radius: 8px;

    a {
      text-decoration: none;
      z-index: 1;
    }

    .team__title {
      margin: 0 38px 13px 0;
    }

    p {
      font-size: 18px;
      line-height: 28px;
      width: 91%;
      margin-bottom: 25px;
    }

    img {
      border-radius: 8px;
    }
  }

}

@include viewport--xl {
  .team {
    .container {
      padding-bottom: 68px;
    }

    &--cases {
      margin: 0;
    }
  }
}

@include viewport--lg {
  .team {
    .container {
      padding: 65px 67px 67px 38px;
    }

    &__title {
      //font-size: 64px;
      font-size: 24px;
      line-height: 28px;
    }

    .btn {
      margin-top: 50px;
    }

    &--studio {
      margin: 0;
      padding: 7px 0 95px;

      .container {
        min-height: 560px;
        padding: 47px 80px 33px;
      }

      .team__text-wrapper {
        width: 100%;
      }

      p {
        width: 74%;
      }
    }

    &--cases {
      padding-bottom: 0;

      .container {
        padding: 47px 55px 33px;
        min-height: 480px;
      }

      .team__title {
        font-size: 94px;
        line-height: 100px;
        margin-right: 30px;
      }

      .team__subtitle {
        margin: 7px 0 61px;
      }

      p {
        font-size: 16px;
        line-height: 26px;
        margin-bottom: 25px;
        width: 60%;
      }

      .team__text-wrapper {
        min-height: 400px;
      }

    }
  }
}

@include viewport--md {
  .team {
    margin: 0 25px;

    &__title {
      //margin-bottom: 3px;
      //font-size: 56px;
      //width: 170px;
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 22px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
      margin-bottom: 15px;
    }

    .container {
      padding: 44px 20px 51px 22px;
      flex-direction: column;
      min-height: 450px;
    }

    .btn {
      font-size: 12px;
      margin-top: 26px;
      max-width: 174px;
    }

    &__text-wrapper {
      width: 60%;
    }

    &--studio {
      margin: 0;
      padding: 7px 0 46px;

      .container {
        min-height: 560px;
        padding: 47px 80px 33px;
      }

      .team__title {
        font-size: 72px;
        line-height: 72px;
      }

      .team__subtitle {
        margin: 0 0 35px;
      }

      .team__text-wrapper {
        width: 100%;
      }

      p {
        width: 74%;
      }

      .btn {
        max-width: 190px;
      }
    }

    &--cases {
      padding-bottom: 0;

      .container {
        min-height: 480px;
      }
    }

  }
}

@include viewport--sm {
  .team {
    margin: 0 17px;

    &__text-wrapper {
      width: 90%;
    }

    &--studio {
      margin: 0;
      margin-top: -20px;
      padding: 7px 0 77px;

      .container {
        min-height: 263px;
        padding: 30px 20px 10px;
      }

      .team__title {
        width: 140px;
        font-size: 48px;
        line-height: 24px;
        margin: 10px 17px 7px 0;
      }

      .team__subtitle {
        font-size: 10px;
        line-height: 14px;
        margin: 0 0 20px;
        width: 150px;
      }

      .team__text-wrapper {
        width: 100%;
        min-height: 222px;
      }

      p {
        width: 60%;
        line-height: 18px;
      }

      .btn {
        max-width: 218px;
      }
    }

    &--cases {
      padding: 7px 0 0;
    }
  }
}

@media(max-width: 528px) {
  .team--studio {
    p {
      width: 92%;
    }
  }
}

@include viewport--mm {

  .team--cases {
    p {
      width: 100%;
      margin-bottom: 10px;
    }
  }
}


@media (min-width: 1440px) {
  .team {
    margin: 0;

    .container {
      max-width: 1371px;
      margin: 0 auto;
    }
  }
}
