.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 27px;
  flex-wrap: wrap;

  .title {
    margin: 0;
    margin-right: 20px;
    font-size: 34px;
    line-height: 36px;
    font-weight: 500;
    letter-spacing: -0.02em;
  }

  .slider-buttons {
    margin-left: auto;
  }
}

@include viewport--lg {
  .button-wrapper {
    margin-bottom: 24px;

    .title {
      font-size: 28px;
      line-height: 32px;
    }
  }
}

@include viewport--sm {
  .button-wrapper {
    margin-bottom: 9px;

    .title {
      font-size: 22px;
      line-height: 26px;
    }
  }
}
