.parallax-block {
  position: relative;
  height: 90px;
  overflow: hidden;
}

.parallax-block__img-wrapper {
  position: absolute;
  left: 50%;
  min-width: 1440px;
  width: 100%;
}

@include viewport--lg {
  .parallax-block {
    height: 70px;
  }
}

@include viewport--md {
  .parallax-block {
    height: 60px;
  }
}
