.categories-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  margin-left: -24px;

  &__item {
    @include reset-item;
    margin-left: 24px;
    margin-bottom: 20px;
  }

  &--6-items {
    .category-list__item {
      width: 205.16px;
    }
  }
}

.categories-list__wrapper {
  margin-bottom: 18px;
  display: flex;
  align-items: center;

  color: $color-darkgray;

  .title {
    color: $color-darkgray;
    text-transform: uppercase;
    position: relative;
  }

  hr {
    margin-left: 13px;
    display: block;
    border: 0;
    height: 1px;
    flex-grow: 1;

    opacity: 0.3;
    background-color: $color-darkgray;
  }
}

@include viewport--xl {
  .categories-list {
    margin-left: -1.87vw;

    &__item {
      margin-left: 1.87vw;
    }

    &--6-items {
      .category-list_item {
        width: 13.97vw;
      }
    }
  }

  .categories-list__wrapper {
    margin-bottom: 22px;
  }
}

@include viewport--lg {
  .categories-list {
    margin-left: -1.65vw;

    &__item {
      margin-left: 1.65vw;
    }

    &--6-items {
      .category-list__item {
        width: 13.875vw;
      }
    }
  }
}

@include viewport--md {
  .categories-list__wrapper {
    margin-bottom: 20px;

    .title {
      font-size: 12px;
      line-height: 18px;
    }
  }

  .categories-list {
    margin-left: 0;

    &__item {
      margin-left: 0;
      margin-bottom: 0;
    }

    &--6-items {
      .category-list__item {
        width: 47%;
      }
    }
  }
}
