.infoblock-list {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 24px);
  margin-left: -12px;
}

.infoblock {
  $b: #{&};

  @include reset-link;

  padding: 0 12px;
  margin-bottom: 24px;

  &__title,
  &__text {
    margin: 0;
    position: relative;
    z-index: 2;
  }

  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    height: 100%;
    width: 100%;

    img {
      object-fit: cover;
      object-position: center;
      max-width: unset;
    }
  }

  .btn {
    margin-top: auto;
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__link {
    @include reset-link;

    height: 100%;
    min-height: 244px;
    padding: 52px;
    display: flex;
    flex-direction: column;
    position: relative;

    #{$b}__title {
      margin-bottom: 24px;
    }

    svg {
      width: 72px;
      height: 72px;
      min-width: 72px;
      margin-bottom: 24px;
    }
  }

  &--col-1 {
    width: calc(100% / 12);
  }

  &--col-2 {
    width: calc(100% / 6);
  }

  &--col-3 {
    width: calc(100% / 4);
  }

  &--col-4 {
    width: calc(100% / 3);
  }

  &--col-5 {
    width: calc(100% / 2.4);
  }

  &--col-6 {
    width: calc(100% / 2);
  }

  &--col-7 {
    width: calc(100% / 1.7);
  }

  &--col-8 {
    width: calc(100% / 1.5);
  }

  &--col-9 {
    width: calc(100% / 1.3);
  }

  &--col-10 {
    width: calc(100% / 1.2);
  }

  &--col-12 {
    width: calc(100% / 1);
  }

  &--darktheme {
    #{$b}__link {
      background-color: $color-darkmain;
    }

    #{$b}__title {
      color: $color-default-white;
    }

    #{$b}__text {
      color: $color-beigelight;
    }

    svg {
      fill: $color-default-white;
    }
  }

  &--lighttheme {
    #{$b}__link {
      background-color: $color-beigelight;
    }

    #{$b}__title {
      color: $color-darkmain;
    }

    #{$b}__text {
      color: $color-darkgray;
    }

    svg {
      fill: $color-darkmain;
    }
  }

  &--bg {
    #{$b}__link {
      overflow: hidden;
    }
  }

  &--center {
    #{$b}__link {
      align-items: center;
    }
  }

  &--right {
    #{$b}__link {
      align-items: right;
    }
  }

  &--icon {
    #{$b}__link {
      padding: 60px 49px 56px;
    }
  }

  &--gradient-blue {
    #{$b}__link {
      background: $blue-to-lightblue;
      background-color: transparent;
    }
  }

  &--gradient-red {
    #{$b}__link {
      background: $darkred-to-red;
      background-color: transparent;
    }
  }

  &--gradient-green {
    #{$b}__link {
      background: $blue-to-green;
      background-color: transparent;
    }
  }

  &--pic-right {
    margin-top: 33px;

    #{$b}__link {
      display: flex;
      flex-direction: row;
      position: relative;
      min-height: 368px;
    }

    #{$b}__content {
      width: 50%;

      #{$b}__title {
        width: 90%;
      }

      #{$b}__text {
        width: 50%;
      }
    }

    #{$b}__img {
      width: 50%;
      position: absolute;
      bottom: 0;
      right: 0;

      img {
        max-height: 450px;
        height: 100%;
        width: auto;
        margin: 0 auto;

        object-fit: contain;
        object-position: center;
      }
    }
  }

  &--pic-left {
    margin-top: 33px;

    #{$b}__link {
      display: flex;
      flex-direction: row-reverse;
      position: relative;
      min-height: 368px;
    }

    #{$b}__content {
      width: 50%;
      padding-left: 20px;

      #{$b}__title {
        width: 100%;
      }

      #{$b}__text {
        width: 90%;
      }
    }

    #{$b}__img {
      width: 50%;
      position: absolute;
      bottom: 0;
      left: 0;

      img {
        max-height: 450px;
        height: 100%;
        margin: 0 auto;

        object-fit: contain;
        object-position: center;
      }
    }
  }

  &--big-title {
    #{$b}__title {
      font-size: 200px;
      line-height: 160px;
      font-weight: 700;
      letter-spacing: -0.05em;

      @include viewport--xl {
        font-size: 180px;
      }

      @include viewport--lg {
        font-size: 150px;
      }

      @include viewport--md {
        font-size: 100px;
        line-height: 90px;
        word-break: break-word;
      }

      @include viewport--sm {
        font-size: 80px;
        line-height: 68px;
      }
    }

    #{$b}__text {
      font-size: 20px;
      line-height: 34px;
      font-weight: 500;
      letter-spacing: -0.05em;

      @include viewport--lg {
        font-size: 18px;
      }

      @include viewport--sm {
        font-size: 16px;
        line-height: 28px;
      }
    }
  }

  &--text-dark-main {
    #{$b}__title {
      color: $color-darkmain;
    }

    #{$b}__text {
      color: $color-darkmain;
    }
  }

  &--bg-grey {
    background-color: $color-beigelight;
  }

  &--main {
    display: flex;
    width: 100%;
    padding: 60px;
    margin-bottom: 75px;

    @include viewport--lg {
      padding: 55px 30px 55px 45px;
    }

    @include viewport--md {
      padding: 40px;
      margin-bottom: 50px;

      #{$b}__title {
        margin: 0 auto;
      }
    }

    @include viewport--sm {
      padding: 30px 10px 50px 18px;
    }
  }

  &--text-float {
    #{$b}__text {
      width: 700px;
      padding-right: 120px;
      padding-top: 33px;
      float: right;

      @include viewport--xl {
        width: calc(100% - 500px);
        padding-right: 7%;
      }

      @include viewport--lg {
        width: calc(100% - 410px);
        padding-right: 0;
      }

      @include viewport--md {
        width: 100%;
        float: none;
        padding: 0 10px;
        margin: 20px auto 0;
      }

      @include viewport--sm {
        width: 100%;
        padding: 0;
      }
    }
  }
}
