.material-slider-wrapper {
  position: relative;

  @media (max-width: 375px) {
    width: 100%;
  }
}

.materials-slider {
  position: relative;
  width: 1200px;
  max-width: 100%;
  background-color: $color-default-white;
  height: 600px;
  overflow-x: hidden;

  @include viewport--lg {
    width: 952px;
    height: 476px;
  }

  @include viewport--md {
    width: 600px;
    height: auto;
  }

  @include viewport--sm {
    width: 476px;
  }

  @media (max-width: 520px) {
    width: 342px;
  }

  @media (max-width: 375px) {
    width: 100%;
  }

  .swiper-wrapper {
    width: 100%;
    height: 100%;
  }

  .slider-buttons {
    z-index: 2;
    position: absolute;
    display: flex;
    justify-content: flex-start;
    top: 48px;
    right: 48px;
    min-width: unset;
    height: auto;

    &__prev {
      width: 42px;
      height: 42px;
      margin-right: 6px;

      @include viewport--sm {
        width: 38px;
        height: 38px;
      }
    }

    &__next {
      width: 42px;
      height: 42px;
      margin: 0;

      @include viewport--sm {
        width: 38px;
        height: 38px;
      }
    }

    @include viewport--lg {
      top: 42px;
      right: 32px;
    }

    @include viewport--md {
      top: 642px;
      right: 28px;
    }

    @include viewport--sm {
      top: 506px;
    }

    @media (max-width: 520px) {
      top: 362px;
    }

    @media (max-width: 375px) {
      top: calc(100vw + 20px);
    }
  }

  &__slide {
    display: flex;
    width: 100%;
    height: 100%;
    position: relative;

    &::after {
      z-index: 1;
      content: "";
      position: absolute;
      bottom: 0;
      right: 20px;
      width: calc(50% - 20px);
      height: 80px;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, $color-default-white 100%);
    }

    @include viewport--md {
      flex-direction: column;

      &::after {
        display: none;
      }
    }
  }

  &__text-wrapper {
    flex-grow: 1;
    overflow-y: auto;

    p {
      margin: 0;
    }
  }

  &__description {
    width: 50%;
    height: 100%;
    padding: 48px 48px 48px 60px;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    @include viewport--lg {
      padding: 46px 32px 46px 46px;
    }

    @include viewport--md {
      width: 100%;
      height: auto;
      padding: 46px 46px 68px;
    }

    @include viewport--sm {
      padding: 35px 35px 68px;
    }

    @media (max-width: 520px) {
      padding: 27px 28px 68px;
    }

    h3 {
      margin-top: 0;
      margin-bottom: 18px;
      padding-right: 90px;
      font-weight: 800;
      font-size: 42px;
      line-height: 46px;
      letter-spacing: -0.02em;
      text-transform: uppercase;

      @include viewport--lg {
        font-size: 36px;
        line-height: 40px;
        margin-bottom: 12px;
      }

      @include viewport--sm {
        font-size: 28px;
        line-height: 32px;
      }

      @media (max-width: 520px) {
        margin-bottom: 7px;
      }
    }

    p {
      margin-top: 0;
      margin-bottom: 13px;
      font-weight: 600;
      font-size: 12px;
      line-height: 22px;
    }

    table {
      margin-top: 50px;
      border-collapse: collapse;

      @include viewport--xl {
        margin-top: 25px;
      }

      @include viewport--lg {
        margin-top: 20px;
      }

      @media (max-width: 520px) {
        margin-top: 15px;
      }
    }

    th {
      width: 160px;
      padding: 8px 0;
      text-align: left;
      color: $color-darkgray;
      font-weight: 600;
      font-size: 12px;
      line-height: 22px;
    }

    td {
      width: 114px;
      font-weight: bold;
      font-size: 12px;
      line-height: 22px;
    }

    tr {
      border-bottom: 2px solid $color-lightgray;
    }
  }
}
