.blog-slider {
  $b: #{&};
  overflow: hidden;

  &--news {
    .swiper-slide {
      background-color: $beige-extralight;

      a {
        @include reset-link;

        transition: $default-transition-settings;

        &:hover {
          opacity: 0.7;
        }
      }
    }

    .article__title,
    .title {
      //display: block;
      margin: 0;
      margin-bottom: 20px;
      font-weight: bold;
      font-size: 22px;
      line-height: 26px;

      letter-spacing: -0.02em;
      color: #2e3346;
    }

    .btn {
      margin-top: 61px;
    }

    .article__about {
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;

      color: #2e3346;
    }
  }
}

@media (min-width: 1440px) {
  .blog--slider {
    margin: 0 auto;

    .container {
      padding-left: 0;
      padding-right: 0;
      max-width: 1368px;
    }
  }
}
