.preview-cards {
  display: flex;
  flex-wrap: wrap;
  margin-left: -24px;
  margin-top: -24px;

  li {
    position: relative;
    overflow: hidden;
    height: 360px;
    margin-top: 24px;

    -webkit-mask-image: -webkit-radial-gradient(#ffffff, #000000); /* stylelint-disable-line */

    width: calc(100% / 3 - 24px);
    margin-left: 24px;
    border-radius: 8px;

    &.big {
      width: calc((100% / 3) * 2 - 24px);
    }

    &.half {
      width: calc(50% - 24px);
    }

    &.max {
      width: 100%;
    }

    a {
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;
    }

    &:hover {
      img {
        transform: scale(1.1);
        opacity: 0.7;
      }
    }
  }

  img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;

    transition: transform $default-transition-settings, opacity $default-transition-settings;
    object-fit: cover;
  }
}

.preview-cards__text-wrapper {
  padding: 0 8%;
  margin: auto 0;
  position: relative;
  z-index: 3;

  text-align: center;
  color: $color-darkmain;

  h3 {
    margin: 0;
    margin-bottom: 9px;
    font-weight: bold;
    font-size: 42px;
    line-height: 32px;
    letter-spacing: 0.1em;
  }

  p {
    margin: 0;
    min-height: 32px;

    font-weight: 600;
    font-size: 16px;
    line-height: 32px;
  }
}

@include viewport--lg {
  .preview-cards {
    li {
      min-width: calc(50% - 22px);
      width: calc(50% - 22px);
      height: 340px;

      margin-left: 22px;

      &.big {
        width: calc(50% - 22px);
      }
    }
  }
}
