.materials-select {
  position: absolute;
  top: 0;
  left: 0;

  visibility: hidden;
  overflow: hidden;
  height: 0;

  opacity: 0;
  width: 100%;

  min-height: 100%;
  padding: 115px 38px 35px;

  background-color: #ffffff;
  transition: $default-transition-settings;

  .title {
    padding-left: 18px;
    margin-bottom: 33px;
  }

  &.is-active {
    height: auto;
    opacity: 1;
    z-index: 1000;
    visibility: visible;
  }
}

@include viewport--sm {
  .materials-select {
    padding: 71px 15px 35px;

    .title {
      margin-bottom: 18px;
      padding-left: 15px;
    }
  }
}
