.team-gallery {
  overflow: hidden;

  &__slider {
    margin-bottom: 17px;

    @include viewport--md {
      margin-bottom: 20px;
    }

    @include viewport--sm {
      min-height: 414px;
      margin-bottom: 10px;

      img {
        width: 100%;
        height: 414px;
        object-fit: cover;
        object-position: 32% 50%;
      }
    }
  }

  &__thumb {
    position: relative;

    .swiper-slide {
      height: unset;
    }

    .swiper-slide img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &:after {
      content: '';
      position: absolute;
      display: block;
      right: -2px;
      top: -2px;
      height: calc(100% + 4px);
      width: 124px;
      background-image: linear-gradient(to left,rgba(255,255,255,.8) 0%,rgba(255,255,255,.1) 80%,rgba(255,255,255,0) 100%);
      z-index: 1;
    }

    @include viewport--xl {
      &:after {
        width: 88px;
      }
    }

    @include viewport--md {
      &:after {
        width: 32px;
      }
    }
  }

  &-button-prev,
  &-button-next {
    position: absolute;
    top: 50%;
    width: 36px;
    height: 36px;
    background-color: $color-default-white;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 3;
    transform: translateY(-50%);
    transition: opacity $default-transition-settings;

    &:hover {
      opacity: 0.8;
    }


    @include viewport--lg {
      width: 26px;
      height: 26px;
    }

    @media(max-width: 425px) {
      width: 18px;
      height: 18px;

      svg {
        width: 10px;
        height: 10px;
      }
    }

    @include viewport--mm {
      width: 18px;
      height: 18px;

    }
  }

  &-button-prev {
    left: 20px;

    svg {
      margin-left: -2px;
    }

    @include viewport--lg {
      left: 17px;
    }

    @include viewport--sm {
      left: 11px;
    }

    @include viewport--mm {
      top: 50%;
      left: 11px;
    }
  }

  &-button-next {
    right: 23px;
    top: 47%;

    svg {
      margin-left: 2px;
    }

    @include viewport--lg {
      right: 18px;
    }

    @media(max-width: 425px) {
      right: 10px;
      top: 50%;
    }
  }

  .swiper-slide-thumb-active {
    transition: opacity $default-transition-settings;
    opacity: 0.6;
  }
}
