.check-result {
  padding: 97px 0 95px;
  background: $bg-gradient;

  @include viewport--md {
    padding: 47px 0 70px;
  }

  @include viewport--sd {
    padding: 47px 0 70px;
  }



  &__inner {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 60px;

    @include viewport--md {
      grid-template-columns: 1fr;
      gap: 30px;
    }
  }

  &__item--reverse {
    @include viewport--md {
      order: 1;
    }
  }

  &__title {
    margin-top: 46px;
    margin-bottom: 25px;
    max-width: 386px;

    @include viewport--lg {
      margin-top: 0;
    }

    @include viewport--md {
      max-width: 100%;
    }

    @include viewport--sd {
      margin-bottom: 18px;
    }

  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
    max-width: 386px;
    margin: 0;

    @include viewport--md {
      max-width: 100%;
    }
  }
}

.beer-slider[data-beer-label]:after {
  display: none;
}

.beer-reveal[data-beer-label]:after {
  display: none;
}

.beer-slider {
  max-height: 650px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.beer-handle {
  display: none;
}

.beer-reveal::before {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 4px;
  background-color: $color-default-white;
  z-index: 99;
}


.icv__theme-wrapper {
  display: none;
}
