.slider-buttons {
  min-width: 110.2px;

  button {
    position: relative;

    padding: 0;
    width: 48px;
    height: 48px;

    border: 0;
    background-color: transparent;
    cursor: pointer;

    background-image: url("../img/sprite/icon-basket-arrow-swiper.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    transition: background-color $default-transition-settings;


    &:hover {
      background-image: url("../img/sprite/icon-basket-arrow-swiper-hover.svg");
    }
  }
}

.slider-button {
  position: relative;

  padding: 0;
  width: 48px;
  height: 48px;

  border: 0;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    svg {
      fill-opacity: 1;
    }
  }

  &--next {
    transform: rotate(180deg);
  }
}

.slider-buttons--fill {
  button {
    opacity: 0.4;

    border-radius: 50%;
    border: 2px solid #ffffff;
    background-image: url("../img/svg/arrow-white.svg");
    background-size: 12px 24px;

    &:hover {
      opacity: 1;
      background-color: #ffffff;
      background-image: url("../img/svg/arrow.svg");
    }
  }
}

.goods-slider-next {
  transform: rotate(180deg);
  margin-left: 10px;
}

.slider-button-fill-next,
.slider-button-fill-prev {
  position: relative;
  width: 36px;
  height: 36px;
  background-color: transparent;
  border: 0;
  border-radius: 50%;
  filter: drop-shadow(0 2px 16px rgba(45, 51, 68, 0.15));
  transition: opacity $default-transition-settings;

  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &::before {
    content: "";

    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    background-image: url("../img/svg/button-small.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }

  &.hidden {
    display: none;
  }
}

.slider-pagination {
  bottom: 14px;
  left: 50%;
  transform: translateX(-50%);

  .swiper-pagination-bullet {
    margin: 0;
    margin-right: 8px;
    width: 16px;
    height: 16px;

    cursor: pointer;
    background-color: #ffffff;
    opacity: 0.3;
    transition: $default-transition-settings;

    &:hover {
      opacity: 1;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  .swiper-pagination-bullet-active {
    background-color: #ffffff;
    opacity: 0.9;
  }
}

@include viewport--md {
  .slider-buttons {
    min-width: 98.2px;
  }
}

.swiper-button-disabled {
  pointer-events: none;
  opacity: 0.4;
}

@include viewport--sm {
  .slider-buttons {
    display: flex;
    justify-content: flex-end;
    height: 42px;

    button {
      width: 42px;
      height: 42px;
    }
  }
}
