.tabs {
  display: flex;
  justify-content: center;
}

.tabs__button {
  @include reset-link;

  display: inline-block;
  min-height: 40px;
  padding: 12px 15px;

  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: $color-blue;

  background-color: $color-extralightgray;
  border-radius: 4px;
  cursor: pointer;

  transition: all $default-transition-settings;

  svg {
    width: 10px;
    height: 10px;
    min-width: 10px;
  }

  &:hover {
    color: $color-lightblue;

    background: rgba(235, 239, 242, 0.7);
  }

  &--active {
    color: $color-default-white;

    background: none;
    background-color: $color-darkmain;

    &:hover {
      color: $color-default-white;

      background: none;
      background-color: $color-darkmain;
    }
  }
}

.tabs__count {
  margin-left: 15px;

  font-size: 12px;
  line-height: 16px;
  color: $color-mediumgray;
  text-transform: capitalize;
}

.tabs__reset {
  line-height: 16px;
  color: $color-extralightgray;

  background-color: $color-blue;

  svg {
    position: relative;

    margin-left: 24px;

    vertical-align: middle;

    transition: all $default-transition-settings;

    fill-opacity: 0.4;
  }

  &:hover {
    color: $color-default-white;

    background-color: $color-lightblue;

    svg {
      fill-opacity: 1;
    }
  }
}

.tabs__mark {
  @include reset-list;

  position: relative;

  display: flex;
  flex-wrap: wrap;
}

.tabs__mark-item {
  @include reset-item;

  z-index: 1;

  padding: 10px 20px;

  border-bottom: 3px solid $color-beigelight;
  cursor: pointer;

  @include viewport--lg {
    padding: 5px 14px;
  }

  @include viewport--sm {
    padding: 5px 14px;
  }

  a {
    @include reset-link;
    position: relative;

    font-weight: $font-weight--bold;
    font-size: 16px;
    line-height: 18px;
    color: $color-mediumgray;
    text-transform: capitalize;

    .tabs__mark-count {
      position: relative;
      top: -2px;

      display: inline-block;
      width: 22px;
      height: 22px;
      margin-left: 5px;

      font-size: 10px;
      line-height: 22px;
      text-align: center;
      color: $color-default-white;

      background-color: $color-darkmain;
      border-radius: 50%;
    }
  }

  &--blue:hover {
    border-bottom: 3px solid $color-blue;
  }

  &--green:hover {
    border-bottom: 3px solid $color-green;
  }
}

.tabs__mark-item--current.tabs__mark-item--blue {
  border-bottom: 3px solid $color-blue;

  a {
    color: $color-darkmain;
  }
}

.tabs__mark-item--current.tabs__mark-item--green {
  border-bottom: 3px solid $color-green;

  a {
    color: $color-darkmain;
  }
}
