.subcat {
  $b: #{&};
  padding-bottom: 30px;

  @include viewport--xl {
    padding-bottom: 70px;
  }

  @include viewport--sm {
    padding-bottom: 20px;
  }

  &__promo {
    min-height: 380px;
    position: relative;
  }

  &__promo-img {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;

    img {
      width: 100%;
      max-height: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;

      @include viewport--xxl {
        object-position: left center;
      }
    }
  }

  &__promo-wrapper {
    padding: 49px 0 99px;

    @include viewport--xl {
      padding: 49px 0 113px;
    }

    @include viewport--lg {
      padding: 49px 0 50px;
    }

    @include viewport--md {
      padding-top: 25px;
    }

    @include viewport--sm {
      padding: 25px 0 61px;
    }
  }

  &__promo-content {
    position: relative;
    z-index: 1;
    max-width: 52%;
    padding-bottom: 106px;

    @include viewport--xl {
      max-width: 58%;
      padding-bottom: 90px;
    }

    @include viewport--lg {
      max-width: 55%;
      padding-bottom: 90px;
    }

    @include viewport--md {
      max-width: 675px;
    }

    @include viewport--sm {
      padding-bottom: 70px;
    }

    .title {
      color: $color-default-white;
    }

    .paragraph {
      margin: 27px 0 16px;
      color: $color-default-white;

      @include viewport--xl {
        margin: 27px 0 16px;
      }

      @include viewport--lg {
        margin: 17px 0 16px;
      }

      @include viewport--sm {
        margin: 20px 0 16px;
      }
    }

    &--text-dark {
      .title {
        color: $color-darkmain;
      }

      .paragraph {
        color: $color-darkmain;
      }
    }
  }

  &__swiper-slide {
    width: 280px;
    height: auto;

    a {
      height: 100%;
    }
  }

  &__subcategoryes-wrapper {
    position: relative;
    padding-top: 35px;
    margin-bottom: 43px;
    border-radius: 8px;
    overflow: visible;

    @include viewport--xl {
      padding-top: 30px;
    }

    p.title {
      position: relative;
      z-index: 11;
      margin-bottom: 15px;
    }
  }

  &__subcategory {
    padding: 15px;

    @include viewport--sm {
      min-height: 70px;

      img {
        height: 45px;
        width: 72px;
      }
    }
  }

  &__swiper-container {
    position: relative;
    overflow: hidden;

    &::after {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      width: 184px;
      height: 100%;
      background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, $color-default-white 70%);
      z-index: 10;

      @include viewport--lg {
        width: 125px;
      }

      @include viewport--sm {
        width: 59px;
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    margin-top: 0;
    z-index: 100;
    width: 36px;
    height: 36px;
    bottom: 24px;
    top: auto;
    background-color: $color-default-white;
    border-radius: 50%;
    box-shadow: 0 2px 10px rgba(45, 51, 68, 0.25);
    transition: box-shadow $default-transition-settings;

    &::after {
      content: "";
      background-image: url("../img/sprite/icon-arrow-swiper.svg");
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: 36px 36px;
      width: 36px;
      height: 36px;
      transition: all $default-transition-settings;
    }

    &:hover {
      box-shadow: 1px 1px 10px rgba(46, 51, 70, 0.4);

      &::after {
        background-image: url("../img/sprite/icon-arrow-swiper-hover.svg");
      }
    }

    &:active::after {
      background-image: url("../img/sprite/icon-arrow-swiper-active.svg");
    }


    @include viewport--sm {
      bottom: 19.5px;
    }
  }

  .swiper-button-next {
    display: none;
    left: 0;
    right: 0;
  }

  .swiper-button-next::after {
    transform: rotate(180deg);
  }

  &__categoryes-list {
    margin-bottom: 62px;
    display: flex;
    flex-wrap: wrap;
  }

  &__categoryes-item {
    margin-right: 24px;
    margin-bottom: 30px;

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  &__categoryes-list img {
    height: 472px;
  }

  &__about {
    padding-left: 695px;
    padding-right: 15px;
    padding-bottom: 20px;
    background-image: url("../img/subcat-about-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 603px 380px;
    background-position: 0 0;

    @include retina {
      background-image: url("../img/subcat-about-desktop@2x.jpg");
    }
  }

  &__about p:first-of-type {
    padding-top: 16px;
    margin-bottom: 21px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--bold;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
  }

  &__about p:last-of-type {
    margin-bottom: 25px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 16px;
    line-height: 28px;
    color: $color-darkmain;
  }

  .tags-list {
    padding-bottom: 14px;

    @include viewport--sm {
      padding-bottom: 9px;
    }
  }

  .tags-list__item {
    margin-right: 10px;
    margin-bottom: 11px;

    @include viewport--sm {
      margin-bottom: 8px;
    }
  }

  &__separator {
    margin-bottom: 55px;

    @include viewport--sm {
      margin-bottom: 40px;
    }
  }

  &__goods {
    padding-bottom: 75px;

    @include viewport--xl {
      padding-bottom: 100px;
    }

    @include viewport--lg {
      padding-bottom: 70px;
    }

    @include viewport--sm {
      padding-bottom: 40px;
    }
  }

  &__card-list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;

    @include viewport--sm {
      margin-left: 0;
      margin-right: 0;
    }

    .product-card__img-wrapper-in-extra {
      margin-bottom: 16px;

      @include viewport--xl {
        margin-bottom: 20px;
      }

      @include viewport--lg {
        height: auto;
        width: initial;
        margin-bottom: 22px;
      }

      @include viewport--md {
        width: initial;
        height: auto;
        margin-bottom: 15px;
      }

      @include viewport--sm {
        width: 100%;
        height: auto;
        margin-bottom: 15px;
      }

      img {
        width: 100%;
        object-fit: cover;
        object-position: center;
        max-height: 100%;
        height: 100%;
        transition: opacity $default-transition-settings;
      }
    }

    .product-card__article-in-extra {
      width: 100%;

      @include viewport--xl {
        margin-bottom: 10px;
      }
    }

    .product-card__discount-price-wrapper {
      width: auto;
    }

    .product-card__buttons-wrapper-in-extra {
      align-items: flex-start;
    }

    .product-card__regular-price {
      margin-right: 18px;

      span {
        font-size: 14px;
        line-height: 16px;
        padding-right: 2px;
      }
    }

    .product-card__price-with-discount {
      margin-left: 10px;
      margin-right: 18px;
    }

    .product-card__img-icons {
      @include viewport--xl {
        right: 16px;
      }

      @include viewport--sm {
        top: 18px;
      }
    }
  }

  &__extra-goods-item {
    @include reset-item;
    width: calc((100% / 4) - 24px);
    margin-left: 12px;
    margin-right: 12px;
    margin-bottom: 24px;
    height: 100%;
    padding-bottom: 12px;

    @include viewport--xl {
      margin-bottom: 50px;
    }

    @include viewport--lg {
      width: calc((100% / 3) - 24px);
      margin-bottom: 40px;
    }

    @include viewport--sm {
      width: 100%;
      margin-bottom: 25px;
      margin-left: 0;
      margin-right: 0;
    }

    a {
      width: 100%;
    }

    a:hover {
      .product-card__title-in-extra {
        border-bottom: 2px solid $color-lightblue;
      }

      img {
        opacity: 0.8;
      }
    }
  }

  &__more {
    margin: 0 auto;
    width: 427px;
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include viewport--xl {
      width: 418px;

      button {
        padding-top: 20px;
        padding-bottom: 17px;
        min-height: 56px;
      }
    }

    @include viewport--lg {
      width: 418px;

      button {
        line-height: 14px;
        padding-top: 18px;
        padding-bottom: 18px;
        min-height: 56px;
      }
    }

    @include viewport--sm {
      width: 100%;
      margin-top: 0;

      button {
        line-height: 14px;
        padding-top: 18px;
        padding-bottom: 18px;
        min-height: 56px;
      }
    }
  }

  &__summary-note {
    align-self: center;
    font-weight: $font-weight--bold;
    font-size: 13px;
    line-height: 22px;
    margin-top: 10px;

    @include viewport--xl {
      margin-top: 12px;
    }
  }

  &__info {
    background: rgba(239, 238, 238, 0.6);
    padding: 82px 0 75px;

    @include viewport--xl {
      padding: 82px 0 116px;
    }

    @include viewport--lg {
      padding: 80px 0 73px;
    }

    @include viewport--sm {
      padding: 50px 0 25px;
    }
  }

  &__info-wrapper {
    display: flex;

    @include viewport--sm {
      flex-direction: column;
    }
  }

  &__info-img {
    position: relative;
    width: 50%;
    height: 0;
    padding-top: 36.65%;

    @include viewport--xl {
      width: 47%;
      padding-top: 34.45%;
    }

    @include viewport--sm {
      width: 100%;
      padding-top: 73.3%;
    }

    img {
      position: absolute;
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }
  }

  &__info-content {
    width: 50%;
    padding: 28px 0 0 100px;

    p {
      margin: 0;
    }

    @include viewport--xxl {
      padding: 57px 0 0 50px;
    }

    @include viewport--xl {
      width: 53%;
      padding: 49px 0 0 50px;
    }

    @include viewport--lg {
      width: 53%;
      padding: 0 0 0 40px;
    }

    @include viewport--sm {
      width: 100%;
      padding: 28px 0 0;
    }

    .title {
      margin-bottom: 25px;

      @include viewport--xl {
        margin-bottom: 26px;
      }

      @include viewport--lg {
        margin-bottom: 14px;
      }

      @include viewport--sm {
        margin-bottom: 22px;
      }
    }

    .paragraph--big {
      margin-bottom: 30px;

      @include viewport--xxl {
        margin-bottom: 60px;
      }

      @include viewport--xl {
        margin-bottom: 37px;
      }

      @include viewport--lg {
        margin-bottom: 23px;
      }
    }
  }

  &__adv {
    padding: 73px 0 40px;
    margin-left: -13px;
    margin-right: -13px;
    flex-wrap: wrap;

    @include viewport--xl {
      padding: 68px 0 20px;
    }

    @include viewport--lg {
      padding: 65px 0 20px;
    }

    @include viewport--sm {
      padding: 45px 0 17px;
      flex-direction: column;
      margin-left: 0;
      margin-right: 0;
    }

    .paragraph-columns__column {
      margin: 0 13px;
      width: calc(100% / 4 - 30px);
      padding-bottom: 20px;

      @include viewport--sm {
        padding-top: 88px;
        margin: 0 auto;
        width: 90%;
        text-align: center;
        background-position: top center;
        padding-bottom: 38px;
      }

      p.title {
        @include viewport--sm {
          font-size: 16px;
          line-height: 20px;
        }
      }

      p.paragraph {
        margin: 8px 0;
        width: 95%;

        @include viewport--xl {
          width: 90%;
        }

        @include viewport--lg {
          font-size: 12px;
          line-height: 22px;
          width: 97%;
        }

        @include viewport--sm {
          width: 90%;
          margin: 10px auto;
        }
      }
    }
  }

  &__addition {
    @include reset-list;

    padding: 50px 0 43px;
    display: flex;
    flex-wrap: wrap;
    margin-left: -44px;
    margin-right: -44px;

    @include viewport--xl {
      margin-left: -31px;
      margin-right: -31px;
    }

    @include viewport--lg {
      padding: 22px 0 40px;
      margin-left: -10px;
      margin-right: -10px;
    }

    @include viewport--sm {
      padding: 0 0 20px;
      margin-left: 0;
      margin-right: 0;
    }
  }

  &__item {
    @include reset-item;
    display: flex;
    flex-direction: column;
    width: calc(100% / 3 - 95px);
    margin-left: 44px;
    margin-right: 44px;
    margin-bottom: 43px;

    @include viewport--xl {
      width: calc(100% / 3 - 69px);
      margin-left: 31px;
      margin-right: 31px;
    }

    @include viewport--lg {
      width: calc(100% / 3 - 20px);
      margin-left: 10px;
      margin-right: 10px;
    }

    @include viewport--sm {
      width: 100%;
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 28px;
    }

    img {
      margin-bottom: 24px;
      height: 225px;
      object-fit: cover;
      object-position: center;
      width: 100%;

      @include viewport--lg {
        height: 193px;
        margin-bottom: 16px;
      }

      @include viewport--sm {
        height: 217px;
        margin-bottom: 20px;
      }
    }

    h3 {
      @include viewport--sm {
        font-size: 24px;
        line-height: 28px;
      }
    }

    p {
      margin: 16px 0;

      @include viewport--xl {
        max-width: 93%;
      }

      @include viewport--lg {
        margin: 9px 0;
        max-width: 98%;
      }

      @include viewport--sm {
        font-size: 12px;
        line-height: 22px;
        max-width: 100%;
      }
    }

    a {
      margin-top: 10px;
      max-width: 208px;
      line-height: 12px;
      padding-top: 12px;

      @include viewport--sm {
        margin-top: 0;
        letter-spacing: 0.05em;
        font-size: 10px;
        margin-bottom: 25px;
      }
    }
  }

  &__filter {
    display: flex;
    margin-bottom: 28px;
    flex-wrap: wrap;
    align-items: center;

    @include viewport--lg {
      margin-bottom: 25px;
    }

    @include viewport--sm {
      margin-bottom: 22px;
    }
  }

  &__filter-sum {
    margin: 0;

    width: auto;
    padding-right: 50px;
    margin-right: auto;
    font-size: 26px;
    line-height: 20px;

    letter-spacing: -0.02em;

    @include viewport--sm {
      padding-right: 0;
      width: 100%;
      margin-bottom: 5px;
    }

    span {
      font-weight: $font-weight--bold;
      padding-right: 3px;
    }
  }

  &__filter-wrapper {
    display: flex;
    align-items: baseline;

    p {
      margin: 0;

      padding-right: 48px;
      font-weight: $font-weight--medium;
      font-size: 13px;
      line-height: 20px;

      letter-spacing: -0.02em;

      color: $color-mediumgray;

      @include viewport--xxl {
        padding-right: 18px;
      }
    }
  }

  &__filter-list {
    @include reset-list;

    display: flex;
  }

  &__filter-item {
    @include reset-item;

    width: 100%;

    &::after {
      content: "/";
      position: relative;
      color: $color-lightgray;
    }

    &:last-of-type {
      &::after {
        display: none;
      }
    }
  }

  &__filter-link {
    @include reset-link;

    font-weight: $font-weight--medium;
    font-size: 13px;
    line-height: 20px;

    text-align: center;
    letter-spacing: -0.02em;
    padding: 0 6px;
    color: $color-blue;
    text-transform: lowercase;

    @include viewport--xl {
      padding: 0 4px;
    }

    &.active {
      color: $color-darkmain;
      pointer-events: none;
    }

    &:hover {
      color: $color-darkmain;
    }
  }

  &__extra-goods-item--hide {
    display: none;
  }
}
