.article {
  $b: #{&};

  @include reset-item;
  display: flex;
  flex-direction: column;
  max-width: 440px;

  a {
    @include reset-link;
  }

  a:hover {
    #{$b}__title {
      border-bottom: 2px solid #73b8de;
    }

    #{$b}__img img {
      opacity: 0.8;
      transform: scale(1.02);
    }
  }

  &__news-date {
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;

    color: #a7adb8;
    display: block;
  }

  &__container {
    display: flex;
    flex-wrap: nowrap;
    padding: 26px 19% 37px 12%;

    svg {
      width: 24px;
      min-width: 24px;
      height: 24px;
      margin-right: 25px;
      margin-top: 25px;
    }
  }

  &__title {
    margin: 0;
    display: inline;
    font-family: $font-family--Mont;
    font-weight: $font-weight--bold;
    font-size: 22px;
    line-height: 30px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    border-bottom: 2px solid transparent;
  }

  &__about {
    margin: 0;

    margin-top: 14px;
    padding-bottom: 20px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
    border-bottom: 1px solid $color-beigelight;
    max-width: 400px;
  }

  &__bottom-wrapper {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding-top: 27px;
  }

  &__avatar {
    width: 42px;
    height: 42px;
    margin-right: 15px;
    object-fit: cover;
    object-position: center;
  }

  &__about-author,
  &__publication-date {
    margin: 0;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 12px;
    line-height: 22px;
    color: $color-mediumgray;
  }

  &__publication-date {
    color: $color-darkmain;
  }

  &__img {
    margin-bottom: 28px;
    overflow: hidden;

    img {
      transition: all $default-transition-settings;
      width: 440px;
      max-height: 258px;
      object-fit: cover;
      object-position: center;
    }
  }

  @include viewport--xl {
    &__img {
      margin-bottom: 20px;
    }

    &__title {
      font-size: 20px;
      line-height: 24px;
    }

    &__about {
      padding-bottom: 15px;
      max-width: 98%;
    }
  }

  @include viewport--lg {
    max-width: inherit;

    &__img {
      margin-bottom: 18px;

      img {
        width: 100%;
        max-height: 100%;
      }
    }

    &__title {
      width: 100%;
      padding-right: 0;
      margin-bottom: 10px;
    }

    &__about {
      padding-bottom: 23px;
      max-width: 84%;
    }
  }

  @include viewport--md {
    max-width: inherit;

    &__img {
      margin-bottom: 22px;
    }
  }

  @include viewport--sm {
    max-width: inherit;

    &__img {
      margin-bottom: 23px;

      img {
        width: 100%;
        height: auto;
      }
    }

    &__title {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 12px;
    }

    &__avatar {
      margin-right: 21px;
      width: 36px;
      height: 36px;
    }

    &__about {
      padding-bottom: 14px;
      font-size: 12px;
      line-height: 22px;
      max-width: 100%;
      margin-bottom: 18px;
    }
  }

  &--medium {
    max-width: 376px;

    #{$b}__img {
      margin-bottom: 22px;
    }

    #{$b}__title {
      font-size: 20px;
      line-height: 24px;
    }

    #{$b}__about {
      padding-bottom: 22px;
      max-width: 336px;
    }
  }

  &--small {
    max-width: 345px;

    #{$b}__img {
      margin-bottom: 23px;
    }

    #{$b}__title {
      font-size: 18px;
      line-height: 22px;
    }

    #{$b}__avatar {
      margin-right: 21px;
      width: 36px;
      height: 36px;
    }

    #{$b}__about {
      padding-bottom: 14px;
      max-width: 310px;
    }
  }
}
