.blog {
  $b: #{&};

  .container {
    padding-top: 49px;
    padding-bottom: 152px;
    display: flex;
    flex-direction: column;

    @include viewport--md {
      padding-top: 25px;
    }

    @include viewport--sm {
      padding-bottom: 50px;
    }
  }

  .link {
    font-size: inherit;
    line-height: inherit;
    font-weight: inherit;
  }

  .breadcrumbs__list {
    justify-content: center;
  }

  .title {
    text-align: center;
  }

  &__tabs {
    @include reset-list;

    align-self: start;
    margin: 25px auto 34px;

    @include viewport--lg {
      margin: 18px auto 40px;
    }

    @include viewport--sm {
      padding-bottom: 2px;
      margin: 11px auto 26px;
    }

    span {
      font-size: 18px;
      line-height: 32px;

      @include viewport--lg {
        font-size: 16px;
        line-height: 32px;
      }

      @include viewport--sm {
        font-size: 14px;
        line-height: 32px;
      }
    }
  }

  &__tab-wrapper {
    display: none;

    &.show {
      display: block;
    }
  }

  &__article {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;

    @include viewport--lg {
      margin-left: -10px;
      margin-right: -10px;
    }

    @include viewport--sm {
      margin-left: 0;
      margin-right: 0;
    }

    .article {
      @include reset-item;
      margin-left: 12px;
      margin-right: 12px;
      margin-bottom: 73px;
      width: calc(100% / 3 - 24px);

      @include viewport--lg {
        width: calc(100% / 2 - 20px);
        margin-left: 10px;
        margin-right: 10px;
      }


      @include viewport--sm {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 48px;
      }
    }
  }

  &__title-wrapper {
    display: flex;
    margin-bottom: 20px;

    @include viewport--lg {
      align-items: center;
    }

    .title {
      margin-right: auto;
    }
  }

  &__notice {
    @include reset-list;

    padding-bottom: 37px;

    .article {
      @include reset-item;
      margin: 0 auto 39px;
      position: relative;
      will-change: transform;
      transition: transform $default-transition-settings;

      &:hover {
        transform: translateX(20px);
      }

      @include viewport--sm {
        &:hover {
          transform: translateX(0);
        }
      }
    }

    .notice {
      margin: 0 auto;
    }

    .notice__container {
      @include reset-link;
    }
  }

  &__more {
    margin: 0 auto;
    width: 427px;
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 7px;

    @include viewport--xl {
      width: 418px;
      margin-top: 25px;

      button {
        padding-top: 20px;
        padding-bottom: 17px;
        min-height: 56px;
      }
    }

    @include viewport--lg {
      width: 418px;
      margin-top: 35px;

      button {
        line-height: 14px;
        padding-top: 20px;
        padding-bottom: 18px;
        min-height: 60px;
      }
    }

    @include viewport--sm {
      width: 100%;
      margin-top: 22px;

      button {
        line-height: 14px;
        padding-top: 18px;
        padding-bottom: 18px;
        min-height: 56px;
      }
    }

    span {
      align-self: center;
      font-weight: $font-weight--bold;
      font-size: 13px;
      line-height: 22px;
      margin-top: 10px;

      @include viewport--xl {
        margin-top: 12px;
      }

      @include viewport--sm {
        margin-top: 8px;
      }
    }
  }

  &--list {
    padding-bottom: 56px;

    .article__about {
      margin-bottom: 0;
      padding-bottom: 0;
      border-bottom: 0;
    }

    .article__img {
      position: relative;
      height: 258px;

      img {
        aspect-ratio: unset;
        position: absolute;
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
      }

      @include viewport--lg {
        height: 227px;
      }

      @include viewport--md {
        height: 272px;
      }

      @media (max-width: 375px) {
        height: 200px;
      }
    }

    #{$b}__article {
      overflow: hidden;
      margin-left: 0;
      margin-right: 0;

      .article {
        margin-bottom: 55px;
      }
    }

    .container {
      padding: 95px 0 0;
      max-width: 1368px;
    }

    .slider-buttons {
      height: 48px;
    }
  }

  .swiper-wrapper {
    flex-wrap: nowrap;
  }

  .swiper-wrapper .article {
    margin: 0;
  }

  &--wallpaper {
    padding-bottom: 5px;

    .container {
      padding-top: 46px;
    }
  }

  &--slider {
    margin: 0 35px;

    .swiper-wrapper {
      margin: 0;
      width: auto;
      flex-wrap: nowrap;

      .article {
        width: auto;
        margin: 0;
      }
    }

    .article__img {
      height: 259px;
    }

    .btn {
      width: 267px;
      margin: 77px auto 0;
      padding-top: 14px;
      padding-bottom: 8px;
      text-align: center;

      font-weight: bold;
      font-size: 14px;
      line-height: 32px;

      letter-spacing: 0.05em;
      text-transform: uppercase;
    }

    .container {
      border-top: 1px solid $color-beigelight;
      border-bottom: 1px solid $color-beigelight;
      padding-top: 39px;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &--slider-2 {
    .title {
      font-weight: bold;
    }

    .container {
      padding-top: 89px;
    }

    .article__about {
      border-bottom: 0;
      margin-bottom: 0;
      padding-bottom: 0;
    }

    @include viewport--xl {
      .container {
        padding-top: 76px;
      }
    }
  }

  &--news {
    .container {
      padding-top: 71px;
      padding-bottom: 188px;

      border-bottom: 0;
    }

    .swiper-slide {
      height: auto;
    }
  }
}


@include viewport--lg {
  .blog--slider {
    .container {
      padding-top: 48px;
      padding-bottom: 344px;
    }

    .btn {
      display: none;
    }
  }

  .blog--slider-2 {
    .container {
      padding-top: 83px;
      padding-bottom: 83px;
    }
  }

  .button-wrapper {
    margin-bottom: 24px;

    .title {
      font-size: 28px;
      line-height: 32px;
    }
  }
}

@include viewport--sm {
  .blog--slider {
    margin: 0 16px;

    .container {
      padding-bottom: 68px;
      padding-top: 37px;
    }

    .cite__avatar {
      margin-right: 21px;

      width: 36px;
      height: 36px;
    }
  }

  .blog--slider-2 {
    .container {
      padding-top: 68px;
    }

    .button-wrapper {
      margin-bottom: 16px;

      .title {
        width: 60%;
      }
    }
  }

  .button-wrapper {
    margin-bottom: 9px;

    .title {
      font-size: 22px;
      line-height: 26px;
    }
  }

  .slider-buttons {
    display: flex;
    justify-content: flex-end;
    height: 42px;

    button {
      width: 42px;
      height: 42px;
    }
  }
}

@include viewport--xl {
  .blog--list {
    padding-bottom: 72px;

    .container {
      padding-top: 82px;
    }

    .blog__article {
      .article {
        margin-bottom: 72px;
      }
    }
  }

  .blog--wallpaper {
    padding-bottom: 51px;

    .container {
      padding-top: 36px;
    }

    .article__img {
      height: 227px;
    }
  }
}

@include viewport--lg {
  .blog--list {
    padding-bottom: 92px;

    .container {
      padding-top: 67px;
    }
  }

  .blog--wallpaper {
    padding-bottom: 68px;

    .container {
      padding-top: 54px;
    }

    .article__img {
      height: 272px;
    }
  }
}

@include viewport--md {
  .blog--list {
    padding-bottom: 57px;

    .container {
      padding-top: 45px;
    }

    .blog__article {
      .article {
        margin-bottom: 57px;
      }
    }

    .slider-buttons {
      display: flex;
      height: 42px;
      justify-content: flex-end;
    }
  }

  .blog--wallpaper {
    padding-bottom: 0;

    .container {
      padding-top: 15px;
    }

    .article__img {
      height: 201px;
    }
  }
}
