.cost-calculate {
  position: absolute;
  z-index: 1001;
  transform: translateX(100%);

  height: auto;
  min-height: 100%;
  width: 555px;
  padding: 115px 56px 35px 56px;

  transition: transform $default-transition-settings;
  background-color: #ffffff;

  .overlay-open & {
    transform: translateX(0);
    right: 0;
  }

  .title {
    margin: 0;
    margin-bottom: 13px;

    font-weight: 800;
  }
}

.cost-calculate__header {
  margin-top: 0;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.02em;
}

.cost-calculate__wrapper {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1002;
  padding: 33px 58px 10px 54px;
  align-items: start;

  display: flex;
  width: 100%;

  button {
    padding: 0;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
}

.close {
  display: block;
  margin-left: auto;
  margin-top: 2px;

  svg {
    width: 35px;
    min-width: 35px;
    height: 35px;
    transition: fill $default-transition-settings;
  }

  &:hover {
    svg {
      fill: $color-darkmain;
    }
  }
}

.comeback {
  transition: opacity $default-transition-settings;
  opacity: 0;
  visibility: hidden;

  svg {
    width: 44px;
    min-width: 44px;
    height: 40px;
  }

  &.is-active {
    visibility: visible;
    opacity: 1;

    &:hover {
      opacity: 0.7;
    }
  }
}

.cost-calculate__form {
  margin-bottom: 21px;
}

.cost-calculate__desc {
  margin-bottom: 34px;
  display: block;

  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.02em;
}

.cost-calculate__inputs {
  position: relative;
  display: flex;

  .input-wrapper {
    width: 130px;
    margin-right: 23px;

    input {
      padding-top: 28px;
      font-weight: 600;
      font-size: 22px;
      letter-spacing: -0.02em;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &::before {
    content: "×";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    font-weight: 500;
    font-size: 16px;
    letter-spacing: -0.02em;
    color: $color-mediumgray;
  }
}

.cost-calculate__form-wrapper {
  display: flex;
  margin-bottom: 42px;
}

.cost-calculate__size {
  margin-left: auto;
  display: flex;
  align-items: center;

  svg {
    width: 30px;
    min-width: 30px;
    height: 30px;
    margin-right: 10px;
  }

  p {
    margin: 0;

    font-weight: 300;
    font-size: 26px;
    line-height: 44px;
    letter-spacing: -0.02em;
  }
}

.cost-calculate__select-text-wrapper {
  display: flex;
  margin-bottom: 12px;
  justify-content: space-between;

  .cost-calculate__header {
    margin-bottom: 0;
  }
}

.cost-calculate__about {
  @include reset-link;

  display: block;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;

  color: $color-mediumgray;
  transition: color $default-transition-settings;

  &:hover {
    color: $color-darkgray;
  }
}

.cost-calculate__color-proof-button {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 40px;
  padding: 25px 55px 20px 27px;

  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  text-align: left;

  border: 0;
  color: $color-darkmain;
  background: linear-gradient(0deg, $color-beigelight 0%, #f3f3f3 100%, #f3f3f3 100%);
  border-radius: 4px;
  cursor: pointer;
  transition: opacity $default-transition-settings;

  &:hover {
    opacity: 0.7;
  }

  svg {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 18px;
    min-width: 18px;
    height: 11px;
  }
}

.cost-calculate__price-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 6px;
}

.cost-calculate__price {
  margin-right: 30px;

  font-weight: 800;
  font-size: 36px;
  line-height: 44px;
  letter-spacing: -0.02em;
}

.cost-calculate__discount {
  margin: 0;

  position: relative;
  font-weight: bold;
  font-size: 24px;
  line-height: 44px;
  letter-spacing: -0.02em;

  color: $color-mediumgray;

  &::after {
    width: 83px;
  }
}

.cost-calculate__discount-wrapper {
  display: flex;
  align-items: center;
  margin-right: auto;

  svg {
    width: 32px;
    min-width: 32px;
    height: 32px;
    margin-right: 18px;
  }
}

.cost-calculate__submit {
  @include reset-link;

  display: flex;
  padding: 17px 10px 11px 10px;
  justify-content: center;
  color: $color-default-white;

  font-weight: bold;
  font-size: 14px;
  line-height: 32px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 4px;
  width: 100%;

  @include viewport--sm {
    span {
      font-size: 12px;
      line-height: 32px;
    }

    .btn__in-order {
      span {
        line-height: 12px;
      }
    }

    &.is-active {
      padding-bottom: 15px;
    }
  }
}

.cost-calculate__fast-order {
  margin-bottom: 36px;

  a {
    @include reset-link;

    margin-bottom: 5px;
    display: block;
    width: fit-content;

    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.02em;

    color: $color-blue;
    transition: color $default-transition-settings;

    &:hover {
      color: $color-lightblue;
    }
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: -0.02em;

    color: $color-mediumgray;
  }
}

@include viewport--sm {
  .cost-calculate {
    width: 359px;
    padding: 71px 25px 35px;

    .title {
      margin-bottom: 10px;
      font-size: 20px;
      line-height: 24px;
    }
  }

  .cost-calculate__desc {
    margin-bottom: 21px;

    font-size: 12px;
    line-height: 24px;
  }

  .cost-calculate__inputs {
    align-self: start;

    .input-wrapper {
      margin-right: 21px;
      width: 100px;

      input {
        height: 61px;
        padding-top: 29px;

        font-size: 20px;
      }
    }

    .input-wrapper--regular input ~ label {
      padding-left: 25px;
      padding-right: 25px;
      top: 31px;
      font-size: 12px;
    }

    .input-wrapper--regular .input--focused ~ label {
      font-size: 11px;
      top: 16px;
    }

    .input-wrapper--regular .input--valued ~ label {
      font-size: 11px;
      top: 16px;
    }
  }

  .cost-calculate__price {
    margin-right: 14px;

    font-size: 28px;
    line-height: 44px;
  }

  .cost-calculate__discount-wrapper {
    svg {
      margin-right: 13px;

      width: 28px;
      height: 28px;
    }
  }

  .cost-calculate__discount {
    font-size: 20px;
    line-height: 44px;

    &::after {
      width: 66px;
    }
  }

  .cost-calculate__form-wrapper {
    margin-bottom: 20px;
  }

  .cost-calculate__about {
    font-size: 11px;
  }

  .cost-calculate__color-proof-button {
    margin-bottom: 35px;
    padding: 18px 55px 20px 24px;

    font-size: 12px;
  }

  .cost-calculate__size {
    flex-direction: column;
    margin-left: auto;
    align-items: start;

    svg {
      margin-right: 0;

      width: 28px;
      height: 28px;
    }

    p {
      font-size: 22px;
      line-height: 44px;

      sup {
        top: -0.6em;

        font-size: 58%;
      }
    }
  }

  .cost-calculate__submit {
    span {
      font-size: 12px;
      line-height: 32px;
    }

    .btn__in-order {
      span {
        line-height: 12px;
      }
    }

    &.is-active {
      padding-bottom: 15px;
    }
  }

  .cost-calculate__header {
    font-size: 14px;
    line-height: 20px;
  }

  .cost-calculate__fast-order {
    margin-bottom: 24px;

    a {
      font-size: 13px;
      line-height: 20px;
    }

    p {
      font-size: 11px;
      line-height: 18px;
    }
  }

  .cost-calculate__wrapper {
    padding: 25px 30px 0 30px;

    .close {
      width: 28px;
      height: 28px;

      svg {
        width: 28px;
        height: 28px;
      }
    }

    .comeback {
      svg {
        width: 30px;
        height: 28px;
      }
    }
  }
}

@media (max-width: 375px) {
  .cost-calculate {
    max-width: 100%;
  }

  .cost-calculate__size {
    flex-shrink: 0;
  }

  .cost-calculate__inputs {
    width: 72%;

    .input-wrapper {
      width: 50%;
    }

    .input-wrapper--regular input ~ label {
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  .cost-calculate__price {
    font-size: 24px;
  }
}
