.interior {
  padding-top: 90px;
  padding-bottom: 78px;
  margin-right: 35px;
  margin-left: 35px;

  .container {
    display: flex;
    position: relative;
    padding-right: 0;
    padding-left: 0;
  }

  .interior-slider {
    position: absolute;
    right: 0;
    top: 0;
    width: 74.5%;
  }

  .swiper-slide {
    img {
      transition: $default-transition-settings;
    }

    a {
      &:hover {
        img {
          opacity: 0.7;
        }
      }
    }
  }
}

.interior__text-wrapper {
  width: 21.85%;

  .slider-buttons {
    margin-bottom: 22px;
  }

  .btn {
    max-width: 228px;
    padding: 0 10px;
    padding-top: 20px;
    padding-bottom: 16px;
  }

  h2 {
    margin: 0;
    margin-bottom: 25px;

    font-weight: 800;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  p {
    margin-bottom: 42px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

.interior-slider {
  overflow: hidden;

  .category .category--medium p:last-of-type {
    margin: 0;
  }
}

.interior-slider__img-wrapper {
  margin-bottom: 28px;
  width: 100%;
  position: relative;

  padding-bottom: percentage(472/324);

  img {
    margin: 0;
    position: absolute;
    object-fit: cover;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
}

@include viewport--xl {
  .interior {
    padding-top: 67px;
    padding-bottom: 76px;

    .category .category--medium {
      max-width: unset;
    }
  }
}

@include viewport--lg {
  .interior {
    padding-top: 58px;
    padding-bottom: 63px;

    h2 {
      font-size: 28px;
      line-height: 32px;
    }

    .interior-slider {
      width: 65.8%;
    }
  }

  .interior__text-wrapper {
    width: 29.75%;
  }

  .interior-slider__img-wrapper {
    padding-bottom: percentage(443/304);
  }
}

@include viewport--md {
  .interior {
    padding-bottom: 69px;
    margin: 0;

    .interior-slider {
      width: 100%;
      position: static;

      .category .category--medium {
        max-width: unset;
      }
    }

    .container {
      padding: 0 25px;
    }

    h2 {
      margin-bottom: 20px;
      font-size: 22px;
      line-height: 26px;
    }
  }

  .interior-slider__img-wrapper {
    padding-bottom: 0;
    height: 443px;
  }

  .interior__slider-wrapper {
    position: relative;

    .slider-buttons {
      button {
        position: absolute;
        z-index: 2;
        top: 43%;

        opacity: 0.4;

        border-radius: 50%;
        border: 2px solid #ffffff;
        background-image: url("../img/svg/arrow-white.svg");
        background-size: 12px 24px;

        &:hover {
          opacity: 1;
          background-color: #ffffff;
          background-image: url("../img/svg/arrow.svg");
        }
      }

      .goods-slider-prev {
        left: 13px;
        transform: translateY(-50%);
      }

      .goods-slider-next {
        right: 13px;
        transform: translateY(-50%) rotate(180deg);
      }
    }
  }

  .interior__text-wrapper {
    width: 100%;

    p {
      margin-bottom: 37px;
    }

    .btn {
      max-width: 189px;
      min-width: 189px;
      padding: 16px 10px 12px;
    }
  }
}

@include viewport--sm {
  .interior {
    .container {
      padding: 0 17px;
    }

    .category .category--medium {
      margin-bottom: 46px;

      p {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .interior-slider__img-wrapper {
    margin-bottom: 24px;
  }

  .interior__text-wrapper {
    p {
      margin-bottom: 34px;
    }
  }

  .interior__slider-wrapper {
    .slider-buttons {
      button {
        width: 48px;
        height: 48px;

        top: 222px;
      }
    }
  }
}


@media (min-width: 1440px) {
  .interior {
    .container {
      max-width: 1368px;
      margin: 0 auto;
    }
  }
}
