.recomendation {
  h2 {
    margin-top: 0;
    margin-bottom: 45px;

    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  & > p {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.recomendation__flex-block {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 45px;

  p {
    margin: 0;

    width: 47%;
    margin-top: 20px;
    margin-right: 28px;
    color: $color-darkgray;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:nth-child(-n + 2) {
      margin-top: 0;
    }
  }
}

@include viewport--xl {
  .recomendation__flex-block {
    p {
      width: 46.4%;
    }
  }
}

@include viewport--lg {
  .recomendation {
    h2 {
      margin-bottom: 27px;

      font-size: 28px;
      line-height: 32px;
    }
  }

  .recomendation__flex-block {
    margin-bottom: 28px;

    p {
      width: 46.4%;
      margin-right: auto;
    }
  }
}

@include viewport--md {
  .recomendation {
    border-bottom: none;

    h2 {
      margin-bottom: 22px;

      font-size: 22px;
      line-height: 26px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }

    & > p {
      margin-bottom: 12px;
    }
  }

  .recomendation__flex-block {
    flex-direction: column;
    margin-bottom: 21px;

    p {
      margin-bottom: 17px;
      width: 100%;
      padding-right: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
