.counter-list {
  @include reset-list;

  counter-reset: counter-list;

  li {
    @include reset-item;
    padding-left: 20px;
    position: relative;

    &::before {
      position: absolute;
      left: 0;
      content: counter(counter-list) ".";
      counter-increment: counter-list;
    }
  }
}

@include viewport--md {
  .counter-list {
    li {
      margin-bottom: 10px;
      padding-left: 16px;
      font-size: 12px;
      line-height: 24px;
    }
  }
}
