.social-block {
  padding: 38px 7% 35px 11.9%;
  display: flex;
  width: 65.7%;
  margin: 0 auto;

  border-radius: 4px;
  background-color: $beige-extralight;
}

.social-block__wrapper {
  position: relative;

  a {
    @include reset-link;

    display: flex;
    margin-right: 49px;
    transition: $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  svg,
  img {
    width: 50px;
    height: 50px;
    flex-shrink: 0;
  }
}

.social-block__social {
  margin: 0;

  padding-top: 13px;
  margin-left: 12px;

  font-weight: 500;
  font-size: 30px;
  line-height: 22px;
  letter-spacing: -0.02em;
}

.social-block__text {
  margin: 0;

  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.02em;
  color: $color-darkgray;
}

@include viewport--lg {
  .social-block {
    width: 82.8%;
    padding: 38px 7% 36px 10.2%;
  }
}

@include viewport--md {
  .social-block {
    padding: 20px 7% 31px 18.6%;
    width: 100%;
    flex-direction: column;
  }

  .social-block__social {
    margin-right: 0;
  }

  .social-block__wrapper {
    margin-bottom: 11px;

    a {
      margin-right: 0;
    }
  }

  .social-block__text {
    padding-right: 25px;
    font-size: 12px;
    line-height: 20px;
  }
}

@media (max-width: 375px) {
  .social-block {
    padding: 20px 12.6% 31px 12.6%;
  }
}
