.metro-list {
  @include reset-list;

  display: flex;
  justify-content: center;

  svg {
    width: 18px;
    min-width: 18px;
    height: 18px;
    margin-right: 8px;
  }

  p {
    margin: 0;

    font-weight: bold;
    font-size: 14px;
    line-height: 28px;
  }

  &__item {
    @include reset-item;
    margin-right: 34px;

    &:last-child {
      margin-right: 0;
    }

    display: flex;
    align-items: center;
  }
}

@include viewport--md {
  .metro-list {
    p {
      font-size: 12px;
      line-height: 28px;
    }
  }
}
