.floats {
  img {
    float: left;
    margin-bottom: 35px;
    margin-right: 50px;
    width: 100%;
    max-width: 600px;
    height: auto;
  }

  &--left {
    img {
      float: right;
      margin-right: 0;
      margin-left: 50px;
    }
  }

  .paragraph {
    margin: 0 0 35px;
  }

  @include viewport--md {
    display: flex;
    flex-direction: column;
    justify-content: center;

    img {
      float: none;
      margin: 0 auto 15px;
    }

    .paragraph {
      margin-bottom: 15px;
    }
  }
}
