.index-popup {
  transition: 0.3s;
  position: fixed;
  z-index: 999;

  margin: 0;

  height: 300px;
  width: 616px;

  overflow-y: auto;
  overflow-x: visible;

  padding: 15px;
  padding-left: 40px;
  left: 20px;
  bottom: 20px;

  background-color: #ffffff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.3);
  border-radius: 10px;

  li {
    width: max-content;
    list-style: decimal;
  }

  p {
    position: absolute;
    z-index: 9999;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: none;
    color: $color-blue;
    text-align: center;
    font-size: 12px;
    line-height: 11px;
  }

  a {
    font-size: 14px;
    color: $color-blue;
    font-weight: 600;

    &:hover {
      opacity: 0.7;
    }
  }

  &.hidden {
    cursor: pointer;
    overflow: hidden;

    margin: 0;
    padding: 0;

    width: 60px;
    height: 60px;

    li {
      visibility: hidden;
    }

    p {
      display: block;
    }
  }
}

@include viewport--sm {
  .index-popup {
    height: 300px;
    width: 87%;

    &.hidden {
      width: 50px;
      height: 50px;
    }

    p {
      font-size: 10px;
    }
  }
}
