.account {
  display: flex;
  padding: 20px 11px 14px;

  object-fit: cover;

  svg {
    width: 31px;
    min-width: 31px;
    height: 31px;
    margin-left: auto;
    align-self: center;
  }
}

.account__name {
  margin: 0;
  padding-top: 6px;
}

.account__first-name {
  display: block;
  margin: 0;
  margin-bottom: 4px;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;

  color: $color-darkmain;
}

.account__city {
  margin: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  color: $color-mediumgray;
}

.account__avatar {
  margin: 0;
  margin-right: 17px;
  padding-top: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background-color: $color-darkmain;

  font-weight: 700;
  font-size: 24px;
  line-height: 23px;
  color: $color-beigelight;

  border-radius: 50%;

  img {
    width: 45px;
    height: 45px;

    border-radius: 50%;
    object-fit: cover;
  }
}

@include viewport--md {
  .account__avatar {
    margin-right: 15px;
    font-size: 18px;
    line-height: 14px;

    img {
      width: 36px;
      height: 36px;
    }
  }

  .account {
    padding: 17px 11px 17px;
    padding-right: 30px;
  }

  .account__name {
    padding-top: 4px;
  }

  .account__first-name {
    margin: 0;

    margin-bottom: 2px;

    font-size: 11px;
    line-height: 14px;
  }

  .account__city {
    font-weight: 600;
    font-size: 11px;
    line-height: 14px;
  }
}
