.collections-cards {
  @include reset-list;

  display: flex;
}

.collection-card {
  @include reset-item;
  width: 300px;
  height: auto;
  margin-right: 24px;
  background: linear-gradient(0deg, #efeeee 0%, #f3f3f3 100%, #f3f3f3 100%);
  border-radius: 8px;

  transition: $default-transition-settings;

  .title {
    margin: 0;
    margin-top: 20px;
    margin-bottom: 7px;
  }

  &__text {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 22px;

    color: $color-darkgray;
    transition: color $default-transition-settings;
  }

  .paragraph {
    margin: 0;
    color: $color-darkgray;
    line-height: 22px;
    transition: color $default-transition-settings;
  }

  &:hover {
    background: linear-gradient(0deg, $beige-extralight 0%, $beige-extralight 100%);

    .paragraph {
      color: $color-darkmain;
    }

    .collection-card__img {
      margin-left: -30px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  a {
    @include reset-link;

    display: flex;
    min-height: 344px;
    height: 100%;
    padding: 40px 36px 36px 36px;
    flex-direction: column;

    color: $color-darkmain;
  }

  &--img {
    overflow: hidden;

    a {
      padding-top: 23px;

      p {
        color: $color-darkmain;
        opacity: 0.7;
      }

      span {
        color: $color-darkmain;
      }

      .title {
        color: $color-darkmain;
      }
    }

    &:hover {
      p {
        color: $color-darkmain;
      }

      .collection-card__img-wrapper {
        transform: scale(1.1);
      }
    }
  }

  &--white {
    &:hover {
      p {
        color: #ffffff;
      }
    }

    a {
      p {
        color: #ffffff;
        opacity: 0.7;
      }

      .title,
      span {
        color: #ffffff;
      }
    }
  }
}

.collection-card__img-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px;

  transition: transform $default-transition-settings;

  overflow: hidden;
}

.collection-card__text-wrapper {
  margin-top: auto;
  z-index: 2;

  p {
    margin: 0;
  }
}

.collection-card__category {
  z-index: 2;
  font-weight: bold;
  font-size: 12px;
  line-height: 28px;
  letter-spacing: 0.1em;
  text-transform: uppercase;

  color: #ffffff;
}

.collection-card__number {
  position: absolute;
  right: 17px;
  top: 12px;

  font-weight: 500;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.02em;

  color: $color-mediumgray;
}

.collection-card__img-list {
  @include reset-list;

  display: flex;
  margin-bottom: auto;
}

.collection-card__img {
  @include reset-item;
  flex-shrink: 0;
  margin-left: -49px;

  transition: $default-transition-settings;

  img {
    border-radius: 50%;
    width: 96px;
    height: 96px;
    object-fit: cover;
  }

  &:first-child {
    margin-left: 0;
  }

  &:nth-child(1) {
    z-index: 3;
  }

  &:nth-child(2) {
    z-index: 2;
  }
}

.slider-button-fill-prev,
.slider-button-fill-next {
  z-index: 3;
  position: absolute;
}

.slider-button-fill-prev {
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
}

.slider-button-fill-next {
  right: 15px;
  top: 50%;
  transform: translateY(-50%) rotate(-180deg);
}

@include viewport--md {
  .collection-card {
    width: 240px;
    margin-right: 18px;

    .title {
      margin: 37px 0 8px;
      font-size: 18px;
      line-height: 22px;
    }

    a {
      min-height: 290px;
      padding: 25px 22px 20px 25px;
    }

    p {
      font-size: 12px;
      line-height: 18px;
    }

    &:hover {
      .collection-card__img {
        margin-left: -20px;
      }
    }
  }

  .collection-card__number {
    display: none;
    font-size: 20px;
    line-height: 26px;
  }

  .collection-card__img {
    margin-left: -32px;

    img {
      width: 80px;
      height: 80px;
    }
  }
}
