.notification {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: $color-beigelight;
  overflow: hidden;

  transition: height $default-transition-settings, padding-top $default-transition-settings, padding-bottom $default-transition-settings;

  .container {
    position: relative;
    padding: 26px 66px 26px 77px;

    &::before {
      content: "";
      position: absolute;
      left: 31px;
      top: 23px;

      width: 24px;
      height: 24px;

      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;

      transition: top $default-transition-settings;
    }

    .close {
      position: absolute;
      right: 26px;
      top: 26px;

      transition: opacity $default-transition-settings;
      width: 18px;
      height: 18px;
      opacity: 0.2;
      background-image: url("../img/svg/close.svg");

      display: block;
      cursor: pointer;
      border: 0;
      background-color: transparent;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;

      &:hover {
        opacity: 1;
      }
    }
  }

  &.hidden {
    padding-top: 0;
    padding-bottom: 0;

    &::before {
      top: 0;
    }
  }

  p {
    margin: 0;
  }

  &--warning {
    .container {
      &::before {
        background-image: url("../img/sprite/icon-lightning.svg");
      }
    }
  }

  &--info {
    .container {
      &::before {
        background-image: url("../img/sprite/icon-i.svg");
      }
    }
  }
}

@include viewport--lg {
  .notification {
    font-size: 12px;
    line-height: 22px;

    .container {
      padding: 26px 66px 22px 63px;

      &::before {
        left: 32px;
        top: 27px;
        width: 20px;
        height: 20px;
      }

      .close {
        right: 36px;
        top: 27px;
      }
    }
  }
}

@include viewport--sm {
  .notification {
    font-size: 12px;
    line-height: 18px;

    .container {
      padding: 21px 45px 20px;

      &::before {
        left: 13px;
        top: 24px;
      }

      .close {
        right: 19px;
        top: 24px;
      }
    }
  }
}
