.see-also {
  $b: #{&};
  padding: 16px 0 190px;

  @include viewport--lg {
    padding: 16px 0 105px;
  }

  @include viewport--sm {
    padding: 37px 0 90px;
  }

  &--info {
    padding-top: 43px;
    padding-bottom: 0;

    #{$b}__list {
      grid-template-columns: repeat(4, 1fr);
      margin-bottom: 23px;
      gap: 24px;

      @include viewport--lg {
        margin-bottom: 24px;
      }

      @include viewport--sm {
        grid-template-columns: 1fr;
        flex-direction: column;
        gap: 19px;
      }
    }

    #{$b}__item {
      min-height: 400px;
      border-radius: 8px;

      @include viewport--sm {
        width: 100%;
        min-height: 350px;
      }
    }

    #{$b}__item--tags {
      border-radius: 0;

      @include viewport--sm {
        min-height: unset;
      }
    }

    #{$b}__description {
      padding-top: 80px;
      font-weight: 800;

      @include viewport--lg {
        padding: 28px;
        padding-top: 80px;
      }

      @include viewport--sm {
        padding: 57px 77px 20px 25px;
      }

      @include viewport--mm {
        padding: 10px;
      }
    }

    #{$b}__description-title {
      margin-bottom: 21px;
      text-transform: unset;
      font-weight: 700;

      @include viewport--sm {
        margin-bottom: 12px;
      }
    }

    #{$b}__text {
      max-width: 700px;
      font-weight: 500;
      color: $color-beigelight;
    }

    .tag {
      font-weight: 600;
      padding: 21px 34px;
      margin-right: 6px;
      margin-bottom: 12px;

      @include viewport--lg {
        padding: 21px 27px;
        margin-bottom: 8px;
      }

      @include viewport--sm {
        padding: 19px 25px;
      }
    }

    @include viewport--lg {
      padding-top: 31px;
    }

    @include viewport--sm {
      padding-bottom: 15px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
    margin-bottom: 33px;

    @include viewport--lg {
      font-size: 24px;
      line-height: 36px;
      margin-bottom: 25px;
    }

    @include viewport--sm {
      font-size: 20px;
      line-height: 32px;
      margin-bottom: 24px;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 24px;
    margin-bottom: 47px;

    @include viewport--sm {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      gap: 10px;
      margin-bottom: 53px;
    }
  }

  &__item {
    position: relative;
    overflow: hidden;
    min-height: 480px;
    border-radius: 16px;
    -webkit-mask-image: -webkit-radial-gradient(white, black);

    @include viewport--lg {
      min-height: 333px;
    }

    @include viewport--sm {
      min-height: 245px;
    }
  }

  &__item--width-100 {
    grid-column: 1/3;
    max-height: 480px;

    @include viewport--sm {
      grid-column: 1/1;
    }
  }

  &__item--width-75 {
    grid-column: 1/4;
  }

  &__item--tags {
    border-radius: unset;
    -webkit-mask-image: none;

    @include viewport--sm {
      width: 100%;
      min-height: unset;
    }
  }

  &__link-wrapper {
    text-decoration: none;

    &:hover {
      img {
        transform: scale(1.1);
      }

      .see-also__img::after {
        opacity: 0.2;
      }
    }
  }

  &__img {
    border-radius: 8px;
    background-color: $color-darkgray;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      transition: transform $default-transition-settings;
    }

    &::after {
      content: '';
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 8px;
      background-color: $color-darkmain;
      opacity: 0.3;
      transition: opacity $default-transition-settings;
    }
  }

  &__description {
    position: relative;
    z-index: 1;
    padding: 46px 10px 10px 42px;

    @include viewport--lg {
      padding: 28px;
    }

    @include viewport--mm {
      padding: 10px;
    }
  }

  &__description-title {
    font-weight: 850;
    font-size: 72px;
    line-height: 68px;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    max-width: 442px;
    display: inline-block;
    word-wrap: break-word;
    color: $color-default-white;
    margin-bottom: 10px;

    @include viewport--lg {
      font-size: 52px;
      line-height: 47px;
      max-width: 325px;
    }

    @include viewport--sm {
      max-width: 100%;
      font-size: 32px;
      margin-bottom: 5px;
    }
  }

  &__text {
    color: $color-beigelight;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    margin: 0;
    max-width: 500px;

    @include viewport--lg {
      font-size: 14px;
      line-height: 24px;
      max-width: 320px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 20px;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 34px;
    line-height: 36px;
    padding-left: 4px;

    @include viewport--sm {
      position: relative;
      font-size: 24px;
      line-height: 32px;
      display: inline-block;
      padding-left: 23px;
    }

    &-link {
      font-weight: 700;
      margin-left: 5px;
      margin-top: 3px;

      @include viewport--sm {
        margin-left: 0;
      }

    }

    img {
      margin-right: 17px;

      @include viewport--sm {
        position: absolute;
        top: 2px;
        left: -3px;
      }
    }
  }

  &--studio {
    padding: 16px 0 0px;

    .see-also__list {
      @include viewport--sm {
        margin-bottom: 30px;
      }
    }
  }

  svg {
    fill: transparent;
    margin-right: 20px;
    min-width: 24px;
    min-height: 24px;

    @include viewport--xl {
      margin-right: 10px;
    }

    @include viewport--sm {
      min-width: 16px;
      min-height: 16px;
      max-width: 16px;
      max-height: 16px;
      position: absolute;
      top: 9px;
      left: -3px;
    }
  }

  &--small-padding {
    padding: 70px 3px 9px;

    @include viewport--xl {
      padding: 64px 1px 9px;
    }

    @include viewport--md {
      padding: 53px 1px 13px;
    }
  }
}
