.review {
  @include reset-item;
  border-radius: 8px;
  transform: translateX(0);
  width: 337px;
  //overflow: hidden;

  @include viewport--sm {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &--slider {
    box-shadow: 0 2px 16px rgba(45, 51, 68, 0.15);
  }

  &--large {
    grid-column: 1 / 3;
  }

  &__top {
    position: relative;
    height: 228px;
    overflow: hidden;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }

  &__btn-enlarge {
    z-index: 2;
    position: absolute;
    bottom: 8px;
    left: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    padding: 0;
    background-color: $color-transparent;
    border: none;
    cursor: pointer;

    svg {
      width: 16px;
      height: 16px;

      fill: $color-darkgray;
      transition: fill $default-transition-settings;
    }

    &:hover,
    &:focus {
      svg {
        fill: $color-darkmain;
      }
    }
  }

  &__btn-play {
    z-index: 3;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 72px;
    height: 72px;
    padding: 0;
    background-color: $color-transparent;
    background-image: url("../img/svg/play_icon.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 72px 72px;
    border: none;
    cursor: pointer;
    opacity: 1;
    transition: opacity $default-transition-settings;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  &__product-list {
    @include reset-list;

    z-index: 3;
    position: absolute;
    bottom: 13px;
    right: 16px;
    display: flex;
    flex-direction: row-reverse;

    &:hover,
    &:focus {
      li {
        margin-left: 5px;
      }

      &~.review-slider .swiper-slide-active::before {
        opacity: 0.4;
      }

      &~.review-img-wrapper::before {
        opacity: 0.4;
      }
    }
  }

  &__product-item {
    @include reset-item;
    width: 48px;
    height: 48px;
    margin-left: -18px;
    transition: margin $default-transition-settings;

    @include viewport--sm {
      margin-left: 5px;
    }

    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__top-link {
    @include reset-link;

    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    z-index: 4;
    box-shadow: 0 2px 16px rgba(45, 51, 68, 0.15);

    &:hover,
    &:focus {
      &::after {
        border-color: $color-default-white;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border: 4px solid rgba(255, 255, 255, 0.8);
      border-radius: 50%;
      transition: border-color $default-transition-settings;
    }
  }

  &__slider {
    height: 100%;

    &:hover,
    &:focus {
      .slider-button-fill-prev {
        left: 16px;
      }

      .slider-button-fill-next {
        right: 16px;
      }
    }

    .slider-button-fill-prev {
      left: -50px;
      transition: left $default-transition-settings;
      opacity: 0.6;

      &:hover {
        opacity: 1;
      }
    }

    .slider-button-fill-next {
      right: -50px;
      opacity: 0.6;
      transition: right $default-transition-settings;

      &:hover {
        opacity: 1;
      }
    }

    .swiper-slide-active {
      position: relative;

      &::before {
        z-index: 2;
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(360deg, $color-darkmain 0%, rgba(46, 51, 70, 0) 89.37%);
        opacity: 0;
        transition: opacity $default-transition-settings;
      }
    }
  }

  &__slider-pagination {
    z-index: 2;
    position: absolute;
    top: 16px;
    bottom: auto;
    left: 50%;
    transform: translateX(-50%);

    &.hidden {
      display: none;
    }

    .swiper-pagination-bullet {
      margin: 0;
      margin-right: 5px;
      width: 16px;
      height: 16px;

      cursor: pointer;
      background-color: $color-default-white;
      opacity: 0.3;
      transition: background-color $default-transition-settings;

      &:hover {
        opacity: 1;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    .swiper-pagination-bullet-active {
      background-color: $color-default-white;
      opacity: 0.8;
    }
  }

  &__slide {
    position: relative;

    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__img-wrapper {
    height: 100%;
    position: relative;

    &::before {
      z-index: 2;
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: linear-gradient(360deg, $color-darkmain 0%, rgba(46, 51, 70, 0) 89.37%);
      opacity: 0;
      transition: opacity $default-transition-settings;
    }

    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  &__bottom {
    overflow: hidden;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: $color-default-white;
  }

  &__bottom-link {
    @include reset-link;

    display: block;
    width: 100%;

    &:hover,
    &:focus {
      svg {
        fill: $color-darkred;
      }
    }
  }

  .account {
    border-bottom: 1px solid $color-beigelight;
  }

  &__link {
    @include reset-link;
    height: 100%;
    box-shadow: 0 2px 16px rgba(45, 51, 68, 0.15);
    border-radius: 8px;
    display: block;
    background-color: #ffffff;
    transition: $default-transition-settings;

    &:hover {
      transform: translateY(-20px);

      img {
        opacity: 0.7;
      }
    }
  }

  &__user {
    margin: 0;

    margin-right: auto;
    padding-top: 6px;
    flex-grow: 1;
  }

  &__username {
    display: block;
    margin-bottom: 4px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: $color-darkmain;
  }

  &__location {
    font-weight: bold;
    font-size: 11px;
    line-height: 14px;
    color: $color-mediumgray;
  }

  &__text {
    height: 116px;
    padding: 18px 16px 26px 16px;
    overflow: hidden;

    svg {
      width: 74px;
      min-width: 74px;
      height: 64px;
      opacity: 0.1;
    }

    &--empty {
      text-align: center;
    }

    p {
      margin: 0;

      display: -webkit-box;
      /* stylelint-disable-line */
      -webkit-box-orient: vertical;
      /* stylelint-disable-line */
      overflow: hidden;
      -webkit-line-clamp: 4;
      /* stylelint-disable-line */
      text-overflow: ellipsis;
      font-weight: 500;
      font-size: 11px;
      line-height: 18px;
      color: $color-darkmain;
      letter-spacing: -0.01em;
    }
  }

}

.reviews--studio {
  padding-top: 100px;

  @include viewport--lg {
    padding-top: 75px;
  }

  .reviews__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    @include viewport--lg {
      font-size: 36px;
      line-height: 46px;
    }

    @include viewport--sm {
      font-size: 22px;
      line-height: 26px;
      margin-bottom: -2px;
    }
  }

  .review__link img {

    @include viewport--lg {
      height: 168px;
    }
  }

  .account {
    @include viewport--lg {
      padding: 5px 11px 5px;
    }
  }

  .reviews__text {
    @include viewport--lg {
      margin-bottom: 54px;
    }

    @include viewport--sm {
      font-size: 12px;
      margin-bottom: 30px;
    }
  }

  .btn {
    margin: 64px auto 0;

    @include viewport--lg {
      margin: 30px auto 0;
    }
  }

  .slider-button-fill-prev {
    @include viewport--sm {
      top: 45%;
    }
  }

  .slider-button-fill-next {
    @include viewport--sm {
      top: 45%;
    }
  }
}
