.collections {
  position: relative;
  color: $color-beigelight;
  overflow: hidden;


  border-radius: 8px;

  text-align: center;

  img {
    position: absolute;
    left: 0;
    top: 0;

    width: 100%;
    height: 100%;


    transition: $default-transition-settings;
    z-index: -1;
    border-radius: 8px;
    object-fit: cover;
  }

  a {
    padding-top: 220px;
    padding-bottom: 221px;
    display: block;
    width: 100%;
    height: 100%;

    color: #ffffff;
  }

  &:hover {
    img {
      transform: scale(1.05);
    }
  }
}

.collections__subtitle {
  position: absolute;
  top: 55px;
  left: 50%;

  transform: translateX(-50%);

  font-weight: bold;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.25em;
}

.collections__text-wrapper {
  max-width: 670px;
  margin: 0 auto;

  h2 {
    text-align: center;
    margin-top: 0;
    margin-bottom: 35px;

    font-weight: 800;
    font-size: 64px;
    line-height: 48px;
    letter-spacing: 0.1em;
  }

  p {
    text-align: left;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

@include viewport--xl {
  .collections {
    a {
      padding-top: 201px;
      padding-bottom: 195px;
    }
  }

  .collections__text-wrapper {
    max-width: 56%;

    h2 {
      margin-bottom: 27px;
    }
  }

  .collections__subtitle {
    top: 52px;
  }
}

@include viewport--lg {
  .collections {
    a {
      padding-top: 179px;
      padding-bottom: 140px;
    }
  }

  .collections__subtitle {
    top: 47px;
  }

  .collections__text-wrapper {
    h2 {
      margin-bottom: 19px;
      font-size: 56px;
      line-height: 48px;
      letter-spacing: 0.1em;
    }

    p {
      text-align: center;
      font-size: 14px;
      line-height: 24px;
    }
  }
}

@include viewport--md {
  .collections {
    a {
      padding-top: 105px;
      padding-bottom: 43px;
    }
  }

  .collections__subtitle {
    top: 33px;
    width: 100%;
  }

  .collections__text-wrapper {
    max-width: 83%;

    h2 {
      margin-bottom: 9px;

      font-size: 32px;
      line-height: 48px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}
