.author-collections {
  padding-top: 47px;
  padding-bottom: 34px;

  .link-wrapper {
    margin-bottom: 19px;
  }

  .main-slider {
    position: relative;
    margin-bottom: 25px;
    border-radius: 8px;

    .swiper-slide {
      height: 360px;
    }

    a {
      position: relative;

      &:hover {
        transform: none;

        img {
          transform: scale(1.1);
        }
      }
    }

    img {
      position: absolute;
      left: 0;
      top: 0;
      height: 360px;
      transition: $default-transition-settings;
    }
  }

  &--no-padding {
    padding-top: 0;
    padding-bottom: 0;

    .link-wrapper {
      margin-bottom: 15px;
    }

    .main-slider {
      margin-bottom: 14px;
    }
  }
}

@include viewport--xl {
  .author-collections {
    padding-top: 51px;

    &--no-padding {
      padding-top: 0;
      padding-bottom: 0;
    }
  }
}

@include viewport--lg {
  .author-collections {
    padding-top: 56px;
    padding-bottom: 28px;

    .link-wrapper {
      margin-bottom: 8px;
    }

    .main-slider {
      margin-bottom: 28px;

      .swiper-slide {
        height: 340px;
      }
    }

    &--no-padding {
      padding-top: 0;
      padding-bottom: 0;

      .link-wrapper {
        margin-bottom: 15px;
      }

      .main-slider {
        margin-bottom: 14px;
      }
    }
  }
}

@include viewport--md {
  .author-collections {
    position: relative;
    padding-top: 0;
    padding-bottom: 55px;

    .link-wrapper {
      margin-bottom: 18px;

      .title {
        margin-top: 0;
      }

      a {
        position: absolute;
        bottom: 9px;
      }
    }

    .preview-cards {
      display: none;
    }

    .main-slider {
      margin-bottom: 28px;
      margin-left: 0;
      max-width: 100%;

      .slider-buttons {
        .goods-slider-prev {
          left: 24px;
        }

        .goods-slider-next {
          right: 24px;
        }
      }

      .swiper-slide {
        height: 320px;

        img {
          height: 320px;
        }
      }
    }

    &--no-padding {
      padding-top: 0;
      padding-bottom: 0;

      .main-slider {
        margin-bottom: 14px;
      }
    }
  }
}
