.subcategory {
  @include reset-link;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  background-color: $beige-extralight;
  color: $color-darkmain;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  border-radius: 4px;
  max-width: 280px;
  box-sizing: border-box;
  cursor: pointer;
  min-height: 100%;

  &:hover {
    color: $color-default-white;
    background-color: $color-darkmain;
    transition: $default-transition-settings;
  }

  p {
    margin-left: 14px;
  }

  span {
    margin-left: 14px;
  }

  svg {
    width: 34px;
    min-width: 34px;
    height: 32px;
    margin-left: auto;
  }
}

.subcategory__img-list,
.subcat__img-list {
  @include reset-list;

  display: flex;
  flex-shrink: 0;
  flex-direction: row-reverse;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
  }
}

.subcategory__item {
  @include reset-item;

  position: relative;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;

  &:not(:last-child) {
    margin-left: -27px;

    @include viewport--sm {
      margin-left: -23px;
    }
  }

  @include viewport--sm {
    width: 45px;
    height: 45px;
  }
}

.subcategory.subcategory--icon {
  padding-right: 7px;
}

.subcategory--small {
  min-height: 54px;
  padding-left: 7px;
  padding-right: 50px;
  padding-top: 7px;
  padding-bottom: 5px;

  .subcategory__item {
    width: 40px;
    height: 40px;

    &:not(:last-child) {
      margin-left: -20px;

      @include viewport--sm {
        margin-left: -18px;
      }
    }

    @include viewport--sm {
      width: 36px;
      height: 36px;
    }
  }

  @include viewport--sm {
    min-height: 48px;
  }
}

.subcategory--extrasmall {
  min-height: 48px;
  padding-left: 6px;
  padding-right: 75px;
  padding-top: 5px;
  padding-bottom: 3px;
}

.subcategory--in-filter {
  background-color: $beige-extralight;

  &:hover {
    box-shadow: none;
    transition: $default-transition-settings;
  }
}

.subcategory__medium-wrapper {
  margin-left: 14px;

  p {
    margin: 0;
    line-height: 14px;

    span {
      margin-left: 0;
    }
  }

  p:first-child {
    margin-bottom: 6px;
  }
}

.subcategory--in-filter-deletable {
  padding-right: 15px;
}

.subcategory--in-filter-deletable p {
  margin-right: auto;
}

.subcategory__close-button {
  font-size: 0;
  line-height: 0;
  cursor: pointer;

  & svg {
    &:hover {
      transform: scale(1.1);
      transition: $default-transition-settings;
    }
  }
}

.subcategory--medium {
  min-height: 84px;
  padding-left: 15px;
  padding-right: 75px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.subcategory--medium p:first-of-type,
.subcategory--medium span {
  font-weight: $font-weight--bold;
}

.subcategory--medium p:last-of-type {
  font-weight: $font-weight--regular;
  color: $color-darkgray;
}

.subcategory--medium-small {
  min-height: 70px;
  padding-left: 11px;
  padding-right: 75px;
}

.subcategory--medium-small p:first-of-type,
.subcategory--medium-small span {
  font-weight: $font-weight--bold;
}

.subcategory--medium-small p:last-of-type {
  font-weight: $font-weight--regular;
  color: $color-darkgray;
}

.subcategory--medium.subcategory--in-filter-deletable,
.subcategory--medium-small.subcategory--in-filter-deletable {
  padding-right: 12px;
}

.subcategory--medium.subcategory--in-filter-deletable .subcategory__medium-wrapper,
.subcategory--medium-small.subcategory--in-filter-deletable .subcategory__medium-wrapper {
  margin-right: auto;
}

.subcategory--medium .subcategory__close-button,
.subcategory--medium-small .subcategory__close-button {
  align-self: flex-start;
  padding-top: 15px;
}
