.text-extrabold,
.text-wrapper .text-extrabold,
.text-wrapper h1:not([class]) {
  font-weight: $font-weight--extrabold;
}

.text-bold {
  font-weight: $font-weight--bold;
}

.text-medium {
  font-weight: $font-weight--medium;
}

.text-regular {
  font-weight: $font-weight--regular;
}

.text-light {
  font-weight: $font-weight--light;
}

.title,
.text-wrapper .title,
.text-wrapper h1:not([class]) {
  margin: 0;
  padding: 0;
  font-family: $font-family--Mont;
  color: $color-darkmain;
  letter-spacing: -0.02em;
}

.title--h1-extrasmall {
  font-size: 30px;
  line-height: 36px;

  @include viewport--md {
    font-size: 26px;
    line-height: 32px;
  }

  @include viewport--sm {
    font-size: 22px;
    line-height: 28px;
  }
}

.title--h1-small {
  font-size: 36px;
  line-height: 42px;

  @include viewport--md {
    font-size: 28px;
    line-height: 36px;
  }

  @include viewport--sm {
    font-size: 24px;
    line-height: 30px;
  }
}

.title--h1-medium,
.text-wrapper .title--h1-medium,
.text-wrapper h1:not([class]) {
  font-size: 42px;
  line-height: 48px;

  @include viewport--lg {
    font-size: 36px;
    line-height: 42px;
  }

  @include viewport--sm {
    font-size: 28px;
    line-height: 36px;
  }
}

.title--h1-big {
  font-size: 52px;
  line-height: 54px;

  @include viewport--md {
    font-size: 46px;
    line-height: 50px;
  }

  @include viewport--sm {
    font-size: 38px;
    line-height: 42px;
  }
}

.text-wrapper h1:not([class]),
.text-wrapper h2:not([class]),
.text-wrapper h3:not([class]),
.text-wrapper h4:not([class]),
.text-wrapper h5:not([class]),
.text-wrapper h6:not([class]) {
  letter-spacing: -0.02em;
  margin-top: 0;
}

.title--h1-extrabig {
  font-size: 64px;
  line-height: 66px;

  @include viewport--md {
    font-size: 56px;
    line-height: 60px;
  }

  @include viewport--sm {
    font-size: 48px;
    line-height: 52px;
  }
}

.title--h2-medium,
.text-wrapper h2:not([class]) {
  font-size: 34px;
  line-height: 36px;
  margin-bottom: 16px;

  @include viewport--lg {
    margin-bottom: 12px;
    font-size: 28px;
    line-height: 32px;
  }

  @include viewport--sm {
    font-size: 22px;
    line-height: 26px;
  }
}

.title--h2-big {
  font-size: 42px;
  line-height: 46px;

  @include viewport--md {
    font-size: 36px;
    line-height: 40px;
  }

  @include viewport--sm {
    font-size: 28px;
    line-height: 32px;
  }
}

.title--h3-medium,
.text-wrapper h3:not([class]) {
  font-size: 28px;
  line-height: 32px;
  margin-bottom: 16px;

  @include viewport--lg {
    margin-bottom: 12px;
    font-size: 24px;
    line-height: 28px;
  }

  @include viewport--sm {
    font-size: 20px;
    line-height: 24px;
  }
}

.title--h3-big {
  font-size: 36px;
  line-height: 40px;

  @include viewport--md {
    font-size: 30px;
    line-height: 34px;
  }

  @include viewport--sm {
    font-size: 24px;
    line-height: 28px;
  }
}

.title--h4-medium,
.text-wrapper h4:not([class]) {
  font-size: 22px;
  line-height: 26px;
  margin-bottom: 16px;

  @include viewport--md {
    margin-bottom: 12px;
    font-size: 20px;
    line-height: 24px;
  }

  @include viewport--sm {
    font-size: 18px;
    line-height: 22px;
  }
}

.title--h4-big {
  font-size: 32px;
  line-height: 36px;

  @include viewport--md {
    font-size: 26px;
    line-height: 32px;
  }

  @include viewport--sm {
    font-size: 20px;
    line-height: 24px;
  }
}

.title--h5-medium,
.text-wrapper h5:not([class]) {
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 16px;

  @include viewport--md {
    margin-bottom: 12px;
    font-size: 16px;
    line-height: 20px;
  }

  @include viewport--sm {
    font-size: 14px;
    line-height: 18px;
  }
}

.title--h5-big {
  font-size: 26px;
  line-height: 32px;

  @include viewport--md {
    font-size: 22px;
    line-height: 26px;
  }

  @include viewport--sm {
    font-size: 16px;
    line-height: 20px;
  }
}

.title--h6-medium,
.text-wrapper h6:not([class]) {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 16px;

  @include viewport--md {
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 18px;
  }

  @include viewport--sm {
    font-size: 12px;
    line-height: 16px;
  }
}

.title--incut {
  font-size: 120px;
  line-height: 100px;

  @include viewport--md {
    font-size: 100px;
    line-height: 90px;
  }

  @include viewport--sm {
    font-size: 84px;
    line-height: 72px;
  }
}

.title--capitello-small {
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1em;
  text-transform: capitalize;

  @include viewport--md {
    font-size: 11px;
    line-height: 16px;
  }

  @include viewport--sm {
    font-size: 10px;
    line-height: 14px;
  }
}

.title--capitello-medium {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.1em;
  text-transform: capitalize;

  @include viewport--md {
    font-size: 12px;
    line-height: 18px;
  }

  @include viewport--sm {
    font-size: 11px;
    line-height: 16px;
  }
}

.title--capitello-big {
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.1em;
  text-transform: capitalize;

  @include viewport--md {
    font-size: 16px;
    line-height: 22px;
  }

  @include viewport--sm {
    font-size: 14px;
    line-height: 20px;
  }
}

.title--h2-md {
  font-weight: 700;
  font-size: 28px;
  line-height: 32px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}
