.simple-images {
  padding-top: 16px;

  .col {
    min-width: 221px;
    height: auto;
  }

  picture {
    width: 100%;
    height: 100%;
    display: flex;
  }

  .img-fluid {
    min-width: 100%;
    max-width: unset;
    min-height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  .row.swiper-wrapper {
    width: auto;
  }

  &.swiper-container-initialized {
    .row {
      margin-right: 0;
      margin-left: 0;
    }

    .col,
    [class^="col-"] {
      padding: 0;
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: 100%;
    }

    .row.swiper-wrapper {
      width: 100%;
    }

    .swiper-slide {
      &:last-of-type {
        margin-right: 0 !important;
      }

      @include viewport--md {
        min-width: 221px;
      }
    }

    @include viewport--md {
      position: relative;

      &:after {
        content: '';
        position: absolute;
        display: block;
        right: -25px;
        top: 7px;
        height: 100%;
        width: 77px;
        background: linear-gradient(270deg, $color-default-white 0%, rgba(255, 255, 255, 0) 108.18%);
        z-index: 5;
      }
    }

    @include viewport--sm {
      &:after {
        right: -17px;
      }
    }
  }

  [data-modal-button] {
    cursor: pointer;
    opacity: 1;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }
}
