.cards-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;

  &__item {
    @include reset-item;
    position: relative;

    width: 320px;
    margin-right: 24px;

    background-color: $color-default-white;
    border-radius: 8px;
    margin-top: 40px;
    transition-property: box-shadow, transform;
    transition-duration: 0.3s;
    transition-timing-function: ease;

    &:hover {
      box-shadow: 0 2px 16px rgba(45, 51, 68, 0.15);
      transform: translateY(-15px);
    }

    a {
      @include reset-link;

      display: block;
      width: 100%;
      height: 100%;
      padding: 37px 35px 45px 35px;
    }

    svg,
    img {
      height: 117px;
      width: 180px;
      margin-bottom: 40px;

      object-fit: cover;
    }

    &:nth-child(-n + 4) {
      margin-top: 0;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  h3 {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkgray;
  }
}

@include viewport--xl {
  .cards-list__item {
    width: 23.49%;
  }
}

@include viewport--lg {
  .cards-list {
    .title {
      font-size: 20px;
      line-height: 24px;
    }

    &__item {
      width: 48.9%;
      margin-top: 22px;
      margin-right: 20px;
      padding-bottom: 51px;
      min-height: 336px;

      &:hover {
        transform: translateY(-10px);
      }

      &:nth-child(-n + 4) {
        margin-top: 22px;
      }

      &:nth-child(-n + 2) {
        margin-top: 0;
      }

      &:nth-child(4n) {
        margin-right: unset;
      }

      &:nth-child(2n) {
        margin-right: 0;
      }
    }
  }
}

@include viewport--md {
  .cards-list {
    &__item {
      width: 100%;
      margin-right: 0;
      padding-bottom: 39px;
      min-height: 325px;

      margin-top: 0;
      margin-bottom: 24px;

      &:nth-child(-n + 4) {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }

      svg,
      img {
        margin-bottom: 22px;
      }
    }

    h3 {
      margin-bottom: 17px;

      font-size: 18px;
      line-height: 22px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}
