.category {
  transition: $default-transition-settings;

  & a {
    @include reset-link;

    display: flex;
    align-items: center;
    min-height: 100%;
  }

  &:hover {
    background-color: $color-beigelight;
    transition: $default-transition-settings;
  }

  p {
    margin: 0;
  }
}

.category--small {
  margin-right: 20px;
  min-height: 74px;
  background-color: $beige-extralight;
  border-radius: 4px;
  overflow: hidden;

  @include viewport--sm {
    min-height: 68px;
  }
}

.category--small img {
  margin-right: 17px;
  height: 74px;
  width: 76px;

  @include viewport--sm {
    width: 69px;
    min-width: 69px;
    height: 68px;
  }
}

.category--small span {
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}

.category--extrasmall {
  padding-right: 37px;
  max-width: 280px;
  min-height: 68px;
  background-color: $color-beigelight;
  border-radius: 4px;
  overflow: hidden;
}

.category--extrasmall img {
  width: 69px;
  height: 68px;
  margin-right: 16px;
}

.category--extrasmall span {
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: $color-darkmain;
}
