.geography {
  $b: #{&};

  @include reset-link;
  position: relative;

  display: flex;
  width: 31%;
  margin-right: 18px;

  color: $color-lightgray;
  opacity: 1;
  transition: opacity $default-transition-settings;

  svg {
    width: 122px;
    min-width: 122px;
    height: 64px;
    position: absolute;
    top: -12px;

    @include viewport--xl {
      top: -15px;
    }
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  @include viewport--xl {
    margin-right: 0;
    width: 30.5%;
  }

  @include viewport--lg {
    display: none;
  }

  &::after {
    content: "";
    position: absolute;
    top: 28%;
    left: 11%;

    width: 20px;
    height: 20px;

    background: $color-green;
    border-radius: 50%;

    @include viewport--xl {
      top: 20%;
      left: 47px;
    }
  }

  &__number {
    margin: 0 19px 0 74px;
    z-index: 2;

    font-weight: $font-weight--light;
    font-size: 48px;
    line-height: 44px;
    color: $color-darkmain;
  }

  &__count {
    font-weight: 700;
    color: #000000;

    span {
      font-weight: $font-weight--bold;
      font-size: 14px;
      line-height: 18px;
      color: $color-darkmain;
    }

    p {
      margin: 0;

      font-weight: $font-weight--regular;
      font-size: 12px;
      line-height: 15px;
      color: $color-mediumgray;
    }
  }

  &--big {
    flex-direction: column;
    width: 205px;
    flex-shrink: 0;

    #{$b}__count p {
      margin: 0;

      width: 100%;
      color: $color-darkmain;

      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: -0.05em;
      padding-left: 25px;
    }

    svg {
      top: 0;
      left: 0;
    }

    #{$b}__number {
      margin: 0;
      font-weight: 500;
      font-size: 72px;
      line-height: 84px;
      letter-spacing: -0.05em;
      margin-left: -38px;
      position: relative;
      min-width: 65px;
      text-align: right;
      justify-content: flex-end;
    }

    &::after {
      top: 32px;
      left: 52px;
    }
  }
}
