.reviews-page {
  &__map {
    position: relative;
    min-height: 560px;
    background-image: url("../img/world-map.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 48%;
      background: linear-gradient(to top, rgba(255, 255, 255, 0) 0%, $color-default-white 100%);
    }
  }

  .breadcrumbs {
    padding-left: 6px;

    &__list {
      justify-content: center;
    }
  }

  &__title-wrapper {
    position: relative;
    z-index: 2;
    padding-top: 49px;

    .title {
      text-align: center;
    }

    p {
      margin: 0;
      font-family: $font-family--Mont;
      font-weight: 500;
      font-size: 22px;
      line-height: 48px;
      letter-spacing: -0.02em;
      text-align: center;
    }

    @include viewport--md {
      padding-top: 25px;

      p {
        font-size: 20px;
        line-height: 36px;
      }
    }

    @include viewport--sm {
      p {
        font-size: 16px;
        line-height: 22px;
      }
    }
  }

  &__factoids {
    padding-top: 50px;
    padding-bottom: 73px;

    @include viewport--md {
      padding-top: 33px;
      padding-bottom: 60px;
    }

    @include viewport--sm {
      padding-top: 23px;
      padding-bottom: 50px;
    }
  }

  &__factoid-list {
    @include reset-list;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 94%;
    margin: 0 auto;

    @include viewport--lg {
      max-width: 100%;
    }
  }

  &__factoid {
    @include reset-item;
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding-left: 50px;

    &--countries {
      background-image: url("../img/sprite/icon-globe.svg");
      background-repeat: no-repeat;
      background-size: 110px 113px;
      background-position: bottom left;
    }

    &--cities {
      width: 230px;
      background-image: url("../img/sprite/icon-map.svg");
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: top left;
    }

    &--reviews {
      background-image: url("../img/sprite/icon-quote.svg");
      background-repeat: no-repeat;
      background-size: 140px 114px;
      background-position: bottom left;
    }
  }

  &__factoid-number {
    font-family: $font-family--Mont;
    font-weight: 800;
    font-size: 94px;
    line-height: 120px;
    letter-spacing: -0.01em;
  }

  &__factoid-text {
    margin-top: -7px;
    font-family: $font-family--Mont;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: -0.01em;
  }

  &__reviews {
    padding-bottom: 65px;
    background: linear-gradient(180deg, $color-default-white 0%, $color-white-smoke 16.15%);

    @include viewport--sm {
      padding-bottom: 40px;
    }
  }

  &__review-list {
    @include reset-list;

    display: grid;
    grid-row-gap: 24px;
    grid-column-gap: 24px;
    grid-template-columns: repeat(4, 1fr);
    margin-bottom: 65px;
    grid-auto-flow: row dense;

    @include viewport--lg {
      grid-template-columns: repeat(3, 1fr);
    }

    @include viewport--md {
      grid-template-columns: repeat(2, 1fr);
    }

    @include viewport--sm {
      display: block;
      margin-bottom: 40px;
    }
  }

  &__btn {
    max-width: 228px;
    margin: 0 auto;
  }

  .delivery__input-surname {
    margin-bottom: 24px;

    @include viewport--sm {
      margin-bottom: 18px;
    }
  }

  .delivery__input-city {
    margin-bottom: 24px;

    @include viewport--sm {
      margin-bottom: 18px;
    }
  }
}
