.accordeon {
  @include reset-list;

  button {
    position: relative;
    display: block;
    width: 100%;
    padding: 31px 80px 27px 39px;

    border: 0;
    background-color: #ffffff;
    border-radius: 4px;
    color: $color-darkmain;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      right: 38px;
      top: 50%;
      transform: translateY(-50%);

      width: 24px;
      height: 14px;

      transition: transform $default-transition-settings;
      background-image: url("../img/svg/accordeon-arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }

    &:hover {
      &::after {
        background-image: url("../img/svg/accordeon-arrow-blue.svg");
      }
    }

    &.is-active {
      &::after {
        transform: translateY(-50%) rotate(-180deg);
        background-image: url("../img/svg/accordeon-arrow-blue.svg");
      }
    }
  }

  b {
    margin: 0;
    display: block;

    text-align: left;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.02em;

    transition: $default-transition-settings;
  }

  li {
    @include reset-item;
    margin-bottom: 22px;
    box-shadow: none;

    transition: box-shadow $default-transition-settings;

    &:hover {
      box-shadow: 0 4px 12px rgba(45, 51, 68, 0.07);
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.accordeon__content {
  overflow: hidden;
  height: 0;
  margin-top: -2px;

  transition: $default-transition-settings;

  &.is-active {
    height: auto;
  }
}

.accordeon__wrapper {
  background-color: #ffffff;
  padding: 0 80px 27px 39px;

  border-radius: 0 0 4px 4px;
  border-top: 1px solid $color-default-white;

  p {
    margin: 0;

    margin-bottom: 20px;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@include viewport--lg {
  .accordeon {
    li {
      margin-bottom: 20px;
    }

    button {
      padding: 27px 80px 23px 37px;

      b {
        letter-spacing: -0.02em;
        font-size: 15px;
      }

      &::after {
        width: 20px;
        height: 12px;
      }
    }
  }
}

@include viewport--md {
  .accordeon {
    li {
      margin-bottom: 18px;
    }

    button {
      padding: 23px 75px 22px 35px;

      b {
        font-size: 14px;
        line-height: 20px;
      }

      &.is-active {
        &::after {
          top: 28px;
          transform: translateY(0) rotate(-180deg);
        }
      }

      &::after {
        top: 28px;
        transform: translateY(0);
        right: 35px;
      }
    }
  }

  .accordeon__wrapper {
    padding: 0 35px 21px 35px;

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }
}
