.main-slider {
  position: relative;
  overflow: hidden;

  img {
    height: 420px;
    object-fit: cover;
  }

  .swiper-slide {
    a {
      @include reset-link;

      display: flex;
      flex-direction: column;
      height: 100%;
      transition: $default-transition-settings;

      &:hover {
        transform: scale(1.03);
      }
    }
  }

  .slider-buttons {
    display: flex;
    justify-content: space-between;
    left: 0;
    top: 0;

    width: 100%;
    height: 100%;
    padding: 0 32px;

    button {
      z-index: 2;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }

    .goods-slider-prev {
      left: 32px;

      @include viewport--md {
        left: 17px;
      }
    }

    .goods-slider-next {
      right: 32px;
      transform: rotate(180deg) translateY(50%);

      @include viewport--md {
        right: 17px;
      }
    }
  }

  p {
    margin: 0;
  }
}

.main-slider__text-wrapper {
  padding: 0 8%;
  margin: auto 0;
  position: relative;
  z-index: 3;
  text-align: center;

  color: $color-default-white;

  p {
    margin: 0;

    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }

  h3 {
    margin: 0;
    margin-bottom: 14px;
    font-weight: bold;
    font-size: 48px;
    line-height: 32px;
    letter-spacing: 0.1em;
  }

  &--text-dark {
    color: $color-darkmain;
  }
}

.sections-slider {
  position: relative;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    z-index: 2;

    right: 0;
    top: 0;

    width: 122px;
    height: 100%;

    background: linear-gradient(to left, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 100%);
  }

  .slider-button-fill-next,
  .slider-button-fill-prev {
    position: absolute;
    z-index: 3;

    top: 50%;
  }

  .slider-button-fill-next {
    right: 16px;

    transform: translateY(-50%) rotate(180deg);
  }

  .slider-button-fill-prev {
    left: 16px;
    transform: translateY(-50%);
  }

  .swiper-slide {
    position: relative;
    height: 300px;
    width: 39.3%;

    a {
      @include reset-link;

      display: block;
      transition: $default-transition-settings;

      &:hover {
        opacity: 0.7;
      }
    }

    img {
      height: 300px;
      width: 100%;
      object-fit: cover;
      border-radius: 8px;
    }
  }

  p {
    margin: 0;
    position: absolute;
    left: 38px;
    bottom: 20px;
    width: 86%;

    font-weight: bold;
    font-size: 26px;
    line-height: 28px;
    letter-spacing: -0.02em;

    color: $color-default-white;
  }

  &__slide {
    &--text-dark {
      p {
        margin: 0;
        color: $color-darkmain;
      }
    }
  }
}

.goods-slider-wrapper {
  padding-bottom: 53px;

  &__flex {
    margin-bottom: 16px;
    display: flex;
    align-items: center;
  }

  .slider-buttons {
    margin-left: auto;
    height: 48px;
  }

  &__title {
    margin-right: 15px;
  }
}

.product-card__img-wrapper {
  width: 100%;
  position: relative;

  padding-bottom: percentage(1/1);

  img {
    position: absolute;
    object-fit: cover;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
}

.goods-slider {
  overflow: hidden;

  .product-card__img-icons svg {
    display: block;
  }

  .product-card__price-with-discount {
    margin-left: 0;
    margin-right: 13px;
  }

  .product-card__regular-price {
    margin-right: 13px;
  }

  .swiper-slide {
    width: calc((100% - 44px) / 3);
    margin-right: 22px;

    @include viewport--md {
      width: calc((100% - 22px) / 2);
    }

    @include viewport--sm {
      width: 100%;
    }
  }

  a {
    @include reset-link;
  }
}

.categories {
  position: relative;
  z-index: 5;

  button {
    position: relative;
    cursor: pointer;

    padding: 0;
    padding-right: 46px;

    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.02em;

    background-color: transparent;
    color: $color-blue;
    border: 0;

    &:hover {
      opacity: 0.7;
    }

    &::after {
      transition: none;
      content: "/";
      position: absolute;
      right: 15px;
      top: 0;

      color: $color-lightgray;
    }

    &.is-active {
      padding-right: 45px;
      font-weight: bold;
      color: $color-darkmain;

      cursor: unset;

      &:hover {
        opacity: 1;
      }

      &::after {
        font-weight: 500;
      }
    }

    &:first-child {
      padding-left: 0;
      padding-right: 39px;

      &.is-active {
        padding-right: 39px;
      }

      &::after {
        right: 10px;
      }
    }

    &:last-child {
      padding-right: 0;

      &.is-active {
        padding-right: 0;
      }

      &::after {
        display: none;
      }
    }
  }
}

.examples-slider {
  position: relative;
  overflow: hidden;

  a {
    display: block;

    &:hover {
      .examples-slider__text {
        color: $color-green;
      }
    }
  }

  &__text {
    margin: 0;

    font-weight: 600;
    font-size: 12px;
    line-height: 22px;
    text-align: center;

    transition: color $default-transition-settings;
  }

  img {
    width: 440px;
    height: 293px;
    object-fit: cover;
    margin-bottom: 15px;
  }

  .slider-button {
    position: absolute;
    z-index: 11;

    &--prev {
      left: 20px;
      transform: translateY(-50%);
    }

    &--next {
      right: 20px;
      transform: translateY(-50%) rotate(-180deg);
    }
  }

  .article-number {
    position: absolute;
    bottom: 18px;
    left: 50%;
    transform: translateX(-50%);

    color: $color-darkgray;
    background: rgba(255, 255, 255, 0.8);
  }

  @include viewport--lg {
    .article-number {
      bottom: 14px;
    }
  }
}

@include viewport--xl {
  .sections-slider {
    .swiper-slide {
      width: 41.5%;
    }
  }

  .main-slider__text-wrapper {
    h3 {
      font-size: 42px;
      line-height: 32px;
    }
  }

  .goods-slider-wrapper {
    padding-bottom: 61px;
  }
}

@include viewport--lg {
  .sections-slider {
    p {
      left: 34px;
      bottom: 15px;
    }

    .swiper-slide {
      width: 43%;
      height: 270px;

      img {
        height: 270px;
      }
    }
  }

  .main-slider__text-wrapper {
    font-size: 40px;
    line-height: 32px;
  }

  .goods-slider-wrapper {
    padding-bottom: 67px;

    &__flex {
      margin-bottom: 20px;
    }

    .slider-buttons {
      display: block;
    }
  }
}

@include viewport--md {
  .sections-slider {
    .swiper-slide {
      width: 270px;
      height: 230px;

      img {
        height: 230px;
      }
    }

    p {
      left: 19px;
      bottom: 20px;

      font-size: 22px;
      line-height: 28px;
    }
  }

  .main-slider__text-wrapper {
    h3 {
      margin-bottom: 4px;
      font-size: 34px;
      line-height: 32px;
    }
  }

  .categories {
    button {
      padding-right: 38px;
      padding-left: 0;
      font-size: 20px;
      line-height: 32px;

      &::after {
        right: 13px;
      }

      &:first-child {
        padding-right: 36px;

        &.is-active {
          padding-right: 36px;
        }
      }

      &.is-active {
        padding-right: 37px;
      }
    }
  }

  .goods-slider-wrapper {
    padding-bottom: 18px;
  }

  .goods-slider {
    .product-card__img-icons {
      top: 14px;
      right: 14px;
    }
  }
}

@include viewport--sm {
  .main-slider {
    width: 100vw;
    margin-left: -17px;
  }

  .goods-slider-wrapper {
    .slider-buttons {
      height: 42px;
      display: flex;
      justify-content: flex-end;
    }

    &__flex {
      margin-bottom: 15px;
    }
  }
}
