.sidebar {
  position: sticky;
  top: 4.8vh;
  padding: 56px 43px 62px 42px;

  margin-top: 85px;
  margin-bottom: 188px;
  align-self: flex-start;
  width: 25.5%;
  max-height: 90vh;

  overflow: auto;

  hr {
    background-color: $color-lightgray;
  }

  h2 {
    margin: 0;
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    &:last-child {
      margin-bottom: 0;
    }
  }

  ul {
    @include reset-list;

    li {
      @include reset-item;
      margin-bottom: 14px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      @include reset-link;
      display: block;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;

      color: $color-darkgray;
      transition: $default-transition-settings;

      &:hover {
        opacity: 0.7;
      }
    }
  }

  background-color: $beige-extralight;
}

@include viewport--xl {
  .sidebar {
    width: 29%;
  }
}

@include viewport--lg {
  .sidebar {
    width: 28.4%;
    padding: 57px 26px 57px 33px;

    ul {
      a {
        font-size: 13px;
      }
    }
  }
}

@include viewport--md {
  .sidebar {
    margin-bottom: 130px;
    padding: 56px 26px 63px 40px;
    max-height: unset;
    position: static;
    overflow: visible;
    height: auto;
    margin-top: 4px;
    width: 100%;
  }
}
