.calculate {
  padding-top: 95px;
  padding-bottom: 140px;

  .container {
    padding: 0;
    padding-left: 152px;
    padding-right: 35px;
    display: flex;
  }

  .delivery__input-surname {
    width: 100%;
  }

  .delivery__input-address {
    display: none;
  }

  .delivery__form {
    margin-right: auto;
    width: 64.9%;
    align-self: start;

    .delivery__input-textarea {
      margin-bottom: 37px;
    }
  }

  .delivery__upload-wrapper {
    display: none;
  }

  .title {
    margin: 0;
    margin-bottom: 37px;

    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  .delivery__make-order-button {
    align-self: start;
    padding-top: 14px;
    padding-bottom: 8px;
    margin-right: 11.6%;
    max-width: 377px;
  }

  .delivery__submit-checkbox-wrapper {
    flex-wrap: nowrap;
  }

  .checkbox-label {
    font-size: 13px;
    line-height: 16px;

    user-select: none;
  }
}

.calculate__text-wrapper {
  padding-top: 73px;
  width: 25.9%;

  .title {
    display: block;
    margin-bottom: 23px;

    font-weight: bold;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 26px;

    color: $color-darkgray;
  }
}

@include viewport--xl {
  .calculate {
    padding-top: 111px;
    padding-bottom: 288px;

    .container {
      padding: 0 2.6% 0 10.9%;
    }

    .delivery__form .delivery__oferta-checkbox {
      max-width: 302px;
    }

    .delivery__make-order-button {
      margin-right: 5.3%;
    }
  }

  .calculate__text-wrapper {
    p {
      padding-right: 7px;
    }
  }
}

@include viewport--lg {
  .calculate {
    padding-bottom: 163px;
    padding-top: 90px;

    .container {
      padding: 0 0.4% 0 3.6%;
    }

    .delivery__form {
      width: 68.9%;
      max-width: unset;
    }

    .title {
      margin-bottom: 34px;
      font-size: 28px;
      line-height: 32px;
    }

    .delivery__make-order-button {
      margin-right: 8.4%;
    }

    .delivery__form .delivery__oferta-checkbox {
      max-width: 243px;
    }

    .delivery__oferta-checkbox {
      order: 1;
    }

    .checkbox-label {
      padding-top: 15px;

      &::before {
        top: 16px;
        transform: none;
      }

      &::after {
        top: 16px;
        transform: none;
      }
    }
  }

  .calculate__text-wrapper {
    .title {
      margin-bottom: 15px;
      font-size: 24px;
      line-height: 28px;
    }

    p {
      padding-right: 30px;
      font-size: 12px;
      line-height: 22px;
    }
  }
}

@include viewport--md {
  .calculate {
    padding-top: 63px;
    padding-bottom: 200px;

    .title {
      margin-bottom: 29px;
      font-size: 22px;
      line-height: 26px;
    }

    .delivery__submit-checkbox-wrapper {
      flex-wrap: wrap;
    }

    .checkbox-label {
      padding-top: 0;

      &::before {
        top: 50%;
        transform: translateY(-50%);
      }

      &::after {
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .delivery__make-order-button {
      font-size: 14px;
      line-height: 32px;
      order: 6;
      margin-right: 0;
      max-width: unset;
      width: 100%;
    }

    .delivery__upload-wrapper {
      display: block;
      margin-bottom: 38px;
    }

    .delivery__input-address {
      display: flex;
    }

    .delivery__form .delivery__input-textarea {
      margin-bottom: 13px;
    }

    .delivery__form .delivery__oferta-checkbox {
      max-width: unset;
      margin-bottom: 12px;
    }

    .container {
      flex-direction: column;
      padding: 0 17px;
    }

    .delivery__form {
      width: 100%;
    }
  }

  .calculate__text-wrapper {
    padding-top: 56px;
    width: 100%;
    order: 6;

    .title {
      margin-bottom: 17px;
      padding-right: 70px;
      font-size: 20px;
      line-height: 24px;
    }

    p {
      padding-right: 0;
      font-size: 12px;
      line-height: 22px;
    }
  }
}
