.category-medium {
  $b: #{&};
  max-width: 324px;
  display: flex;
  flex-direction: column;

  &__img-wrapper {
    max-height: 460px;
    margin-bottom: 28px;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  img {
    width: 100%;
    max-width: unset;
    object-fit: cover;
    object-position: center;
  }

  &--medium #{$b}__img-wrapper {
    max-height: 360px;
  }

  &--small #{$b}__img-wrapper {
    max-height: 200px;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    padding-right: 40px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--extrabold;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
  }

  &__text {
    margin: 0;
    padding-right: 40px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 12px;
    line-height: 20px;
    color: $color-darkgray;
  }
}
