.about-project {
  &__wrapper {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
    padding-top: 54px;
    padding-bottom: 54px;
    padding-right: 36px;

    @include viewport--xl {
      padding-top: 49px;
      padding-right: 8px;
      padding-bottom: 61px;
    }

    @include viewport--md {
      flex-wrap: wrap;
      padding-top: 21px;
      padding-right: 0;
      padding-bottom: 54px;
      justify-content: flex-start;
    }

    @include viewport--mm {
      justify-content: center;
    }
  }

  picture {
    line-height: 0;
    font-size: 0;
    width: 100%;
    height: 100%;
    display: flex;
  }

  &__img {
    width: 220px;
    min-width: 220px;
    height: 210px;

    @include viewport--xl {
      width: 192px;
      height: 183px;
      min-width: 192px;
    }

    @include viewport--md {
      width: 202px;
      height: 191px;
      min-width: 202px;
      //margin-right: 10px;
      //margin-left: 10px;
    }
  }

  img {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center center;
  }

  &__title {
    @include reset-item;
    font-weight: 800;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-bottom: 25px;
    margin-top: 13px;

    @include viewport--xl {
      margin-bottom: 22px;
    }

    @include viewport--md {
      font-size: 24px;
      line-height: 26px;
      margin-bottom: 20px;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
  }

  &__promo {
    @include reset-item;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-mediumgray;
    max-width: 288px;
    align-self: center;
    min-width: 187px;

    @include viewport--md {
      font-size: 10px;
      line-height: 16px;
      min-width: 101px;
      max-width: calc(100% - 202px);
      padding-left: 20px;
      align-self: flex-start;
      padding-top: 18px;
    }

    @include viewport--mm {
      max-width: 100%;
      padding-left: 0;
    }
  }

  &__description {
    margin-left: 22px;
    margin-right: 40px;
    max-width: 684px;

    @include viewport--xl {
      margin-left: 50px;
      margin-right: 49px;
      //margin-left: 0;
      //margin-right: 0;
    }

    @include viewport--md {
      order: -1;
      width: 100%;
      max-width: 100%;
      padding-right: 0;
      padding-left: 0;
      padding-bottom: 33px;
      margin-right: 0;
      margin-left: 0;
    }
  }
}
