.btw {
  display: flex;
  padding: 0 192px;

  font-weight: 300;
  font-size: 26px;
  line-height: 40px;
  letter-spacing: -0.02em;

  picture,
  img {
    flex-shrink: 0;
  }

  img {
    margin-right: 47px;
    width: 165.56px;
    height: 124.25px;

    object-fit: cover;
  }

  p {
    margin: 0;

    margin-top: 8px;
  }
}

@include viewport--xl {
  .btw {
    padding: 0 146px;
  }
}

@include viewport--lg {
  .btw {
    padding: 0 113px;

    img {
      width: 159px;
      height: 119px;
    }

    p {
      margin-top: 12px;

      letter-spacing: -0.02em;
      font-size: 22px;
      line-height: 36px;
    }
  }
}

@include viewport--md {
  .btw {
    padding: 0;
    flex-direction: column;

    img {
      margin-left: 15px;
      margin-right: 0;
    }

    p {
      margin-top: 18px;
      font-size: 18px;
      line-height: 30px;
    }
  }
}
