.other-products-card {
  @include reset-link;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 20px;
  border-radius: 8px;
  transform: translate3d(0, 0, 0);
  z-index: 2;

  @include viewport--md {
    padding: 15px;
  }

  @include viewport--sm {
    padding: 20px;
  }

  &::after {
    z-index: 2;
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: 0;
    height: 146px;
    background: linear-gradient(180deg, rgba(45, 51, 68, 0) 0%, rgba(45, 51, 68, 0.3) 100%);

    @include viewport--md {
      height: 114px;
    }
  }

  &__title {
    z-index: 3;
    width: 70%;
    margin: 0;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
    color: $color-default-white;
    font-weight: 700;

    @include viewport--lg {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__text {
    margin: 0;
    z-index: 3;
    width: 90%;
    margin-top: 15px;
    font-size: 14px;
    line-height: 20px;
    color: $color-default-white;
    font-weight: 600;

    @include viewport--lg {
      font-size: 13px;
      line-height: 18px;
    }
  }

  img {
    z-index: 1;
    position: absolute;
    object-fit: cover;
    object-position: left;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    transition: transform $default-transition-settings, opacity $default-transition-settings;
  }

  &--big {
    padding-left: 30px;

    @include viewport--lg {
      padding-left: 25px;
    }

    @include viewport--md {
      padding-left: 20px;
    }

    .other-products-card__title {
      font-size: 28px;
      line-height: 28px;
      font-weight: 900;
    }
  }

  &:hover,
  &:focus {
    img {
      transform: scale(1.03);
      opacity: 0.9;
    }
  }
}

#cdek-block { /* stylelint-disable-line */
  height: 600px;
}
