.category-list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;

  &__item {
    @include reset-item;
    position: relative;
    margin-bottom: 30px;
    width: 254.4px;
    margin-right: 24px;

    transition: $default-transition-settings;

    a {
      @include reset-link;

      display: block;
      width: 100%;
      height: 100%;

      color: #ffffff;
    }

    h4 {
      margin-top: 0;
      margin-bottom: 6px;

      font-weight: bold;
      font-size: 18px;
      line-height: 22px;
      letter-spacing: -0.02em;
    }

    p {
      margin: 0;

      color: $color-darkgray;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
    }

    &:hover {
      opacity: 0.7;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.category-list__img-wrapper {
  position: relative;

  padding-bottom: percentage(254/254);
  margin-bottom: 22px;

  img {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    border-radius: 8px;
    object-fit: cover;
  }
}

.category-list svg {
  position: absolute;
  z-index: 2;
  right: -10px;
  top: -7px;

  width: 48px;
  height: 48px;

  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

@include viewport--xl {
  .category-list {
    &__item {
      width: 17.14vw;
      margin-right: 1.87vw;

      h4 {
        font-size: 18px;
        line-height: 22px;
      }

      p {
        padding-right: 5px;
      }
    }
  }

  .category-list__mark {
    right: -14px;
  }

  .category-list__img-wrapper {
    margin-bottom: 25px;

    padding-bottom: percentage(223/223);
  }
}

@include viewport--lg {
  .category-list {
    &__item {
      margin-right: 1.65vw;
      width: 16.98vw;

      h4 {
        margin-bottom: 13px;

        font-size: 18px;
        line-height: 22px;
      }
    }
  }

  .category-list__img-wrapper {
    margin-bottom: 23px;

    padding-bottom: percentage(177/177);
  }

  .category-list__mark {
    right: -8px;
    top: -9px;

    width: 36px;
    height: 36px;
  }
}

@include viewport--md {
  .category-list {
    &__item {
      margin-right: 6%;
      width: 47%;
      margin-bottom: 55px;

      p {
        padding-right: 0;
      }

      &:nth-child(2n) {
        margin-right: 0;
      }

      h4 {
        margin-bottom: 6px;
      }
    }
  }

  .category-list__img-wrapper {
    margin-bottom: 22px;

    padding-bottom: percentage(161/161);
  }
}
