.guarantees {
  padding-top: 84px;
  padding-bottom: 120px;

  &__title {
    margin: 0;
    margin-bottom: 67px;
    padding: 0 10%;

    text-align: center;
    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  .title {
    margin-bottom: 7px;
  }

  .paragraph-columns__column {
    width: 20.4%;
  }

  .paragraph {
    padding-right: 15px;
  }
}

.guarantees--wt {
  padding-top: 80px;
  padding-bottom: 42px;
}

@include viewport--xl {
  .guarantees {
    .paragraph {
      padding-right: 0;
    }
  }
}

@include viewport--lg {
  .guarantees {
    padding-top: 76px;
    padding-bottom: 90px;

    &__title {
      margin-bottom: 64px;

      font-size: 28px;
      line-height: 32px;
    }

    .paragraph {
      font-size: 12px;
      line-height: 22px;
    }

    .paragraph-columns__column {
      width: 23.4%;
    }

    .paragraph-columns__column:not(:nth-child(4n)) {
      margin-right: 2.1%;
    }
  }
}

@include viewport--md {
  .guarantees {
    padding-top: 45px;
    padding-bottom: 67px;

    .paragraph-columns {
      flex-direction: column;
    }

    &__title {
      margin-bottom: 32px;
      padding: 0;
      font-size: 22px;
      line-height: 26px;
    }

    .title {
      margin-bottom: 10px;

      font-size: 16px;
      line-height: 20px;
    }

    .paragraph {
      padding: 0 10%;
    }

    .paragraph-columns__column {
      width: 100%;
      padding-top: 89px;
      margin-bottom: 40px;

      text-align: center;

      background-position: center top;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .paragraph-columns__column:not(:nth-child(4n)) {
      margin-right: 0;
    }
  }

  .guarantees--wt {
    padding-bottom: 0;
  }
}
