.text-color-darkgray {
  color: $color-darkgray;
}

.text-color-gray {
  color: $color-mediumgray;
}

.text-color-lightgray {
  color: $color-lightgray;
}

.text-wrapper p:not([class]) {
  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  color: inherit;
  font-size: 14px;
  line-height: 24px;
  margin-top: 1em;
  margin-bottom: 2em;

  &:last-child {
    margin-bottom: 0;
  }

  @include viewport--md {
    font-size: 13px;
    line-height: 22px;
  }

  @include viewport--sm {
    font-size: 12px;
    line-height: 22px;
  }
}

.text-wrapper .paragraph {
  margin-top: 1em;
  margin-bottom: 2em;

  &:last-child {
    margin-bottom: 0;
  }
}

p.paragraph {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  color: $color-darkmain;
  margin-top: 0.6em;
  margin-bottom: 0.6em;

  &--norm { // deprecated, use .text-wrapper p
    font-size: 14px;
    line-height: 24px;

    @include viewport--md {
      font-size: 13px;
      line-height: 22px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 22px;
    }
  }

  &--big {
    font-size: 16px;
    line-height: 28px;

    @include viewport--md {
      font-size: 15px;
      line-height: 26px;
    }

    @include viewport--sm {
      font-size: 14px;
      line-height: 24px;
    }
  }

  &--small {
    font-size: 12px;
    line-height: 22px;

    @include viewport--md {
      font-size: 10px;
      line-height: 18px;
    }

    @include viewport--sm {
      font-size: 8px;
      line-height: 14px;
    }
  }

  @include viewport--md {
    margin-top: 0.6em;
    margin-bottom: 0.6em;
    font-size: 13px;
    line-height: 22px;
  }

  @include viewport--sm {
    margin-top: 0.6em;
    margin-bottom: 0.6em;
    font-size: 12px;
    line-height: 22px;
  }
}

p.paragraph--italic {
  font-style: italic;
}

p.paragraph-border,
blockquote.paragraph-border {
  padding-left: 42px;
  margin: 0;
  font-family: $font-family--Mont;
  font-size: 18px;
  line-height: 30px;
  font-weight: $font-weight--medium;
  color: $color-darkmain;

  &--green {
    border-left: 8px solid $color-green;
  }

  &--red {
    border-left: 8px solid $color-red;
  }

  &--beigelight {
    border-left: 8px solid $color-beigelight;
  }

  &--mediumgray {
    border-left: 8px solid $color-mediumgray;
  }

  @include viewport--md {
    font-size: 16px;
    line-height: 26px;
  }

  @include viewport--sm {
    font-size: 15px;
    line-height: 24px;
  }
}

p.paragraph-bg {
  margin: 0;

  padding: 35px 50px;
  font-weight: $font-weight--medium;

  &--green {
    background-color: $color-lightgreen;
  }

  &--extralightgray {
    background-color: $color-extralightgray;
  }

  &--beigelight {
    background-color: $color-beigelight;
  }

  &--extralightred {
    background-color: $color-extralightred;
  }

  @include viewport--md {
    font-size: 15px;
    line-height: 26px;
  }

  @include viewport--sm {
    font-size: 14px;
    line-height: 24px;
  }
}

p.paragraph-italic {
  margin: 0;

  font-family: $font-family--Mont;
  font-size: 22px;
  line-height: 28px;
  font-style: italic;
  font-weight: $font-weight--light;

  @include viewport--md {
    font-size: 15px;
    line-height: 26px;
  }

  @include viewport--sm {
    font-size: 14px;
    line-height: 24px;
  }
}

.paragraph-icon {
  margin: 0;

  position: relative;
  padding: 7px;
  min-height: 66px;
  display: flex;
  flex-wrap: nowrap;

  p {
    font-weight: $font-weight--bold;
  }

  svg {
    margin-right: 25px;
    width: 72px;
    min-width: 72px;
    height: 69px;
  }

  @include viewport--md {
    font-size: 15px;
    line-height: 24px;
  }

  @include viewport--sm {
    font-size: 14px;
    line-height: 22px;
  }
}

.paragraph-icon--bg {
  min-height: 172px;
  padding: 40px 42px;
  position: relative;
  font-family: $font-family--Mont;
  font-size: 18px;
  line-height: 26px;
  font-weight: $font-weight--bold;
  background-color: $color-extralightred;
  border-radius: 5px;

  svg {
    margin-right: 30px;
    width: 100px;
    min-width: 100px;
    height: 92px;
  }

  @include viewport--md {
    font-size: 16px;
    line-height: 22px;
  }

  @include viewport--sm {
    padding-left: 97px;
    font-size: 14px;
    line-height: 20px;

    &::before {
      width: 72px;
      height: 69px;
      background-size: 72px 69px;
      left: 10px;
    }
  }
}

p.highlight,
span.highlight {
  color: $color-green;
}


p.comment {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 12px;
  line-height: 22px;
  font-style: italic;

  @include viewport--sm {
    font-size: 10px;
    line-height: 18px;
  }
}

ol.list,
ul.list {
  padding: 0;
  margin: 0;
}

ol.list li {
  @include reset-item;
  font-family: $font-family--Mont;
  font-weight: $font-weight--medium;
  font-size: 14px;
  line-height: 24px;
  color: $color-darkmain;
  list-style-type: decimal;
  list-style-position: inside;
}

ul.list-mark li,
.text-wrapper ul:not([class]) li {
  @include reset-item;
  padding-left: 24px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 6px;
    left: 0;
    width: 10px;
    height: 10px;
    border-radius: 50%;
  }
}

ul.list-mark--darkmain li {
  &::before {
    background-color: $color-darkmain;
  }
}

.text-wrapper ul:not([class]) {
  @include reset-list;

  padding-right: 0;
  margin-top: 24px;
  margin-bottom: 24px;

  li {
    @include reset-item;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    color: inherit;
    font-size: 14px;
    line-height: 24px;
    padding-left: 24px;

    @include viewport--md {
      font-size: 13px;
      line-height: 22px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 22px;
    }

    &::before {
      position: absolute;
      content: "";
      top: 6px;
      left: 0;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: $color-darkmain;
    }
  }
}

.text-wrapper ul.tick-list {
  @include reset-list;
  list-style: none;
  padding-left: 2.2em;
  padding-right: 0;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}

.text-wrapper ol:not([class]) {
  @include reset-list;
  //counter-reset: counter-list;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  padding-left: 0;

  li {
    @include reset-item;
    position: relative;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
    list-style-type: decimal;
    list-style-position: inside;

    //&::before {
    //  position: absolute;
    //  left: 0;
    //  content: counter(counter-list) ".";
    //  counter-increment: counter-list;
    //}
  }
}

ul.list-mark--green li {
  &::before {
    background-color: $color-green;
  }
}

ul.list-mark--checkmark li {
  &::before {
    top: 4px;
    width: 16px;
    height: 13px;
    background: url("../img/sprite/icon-checkmark.svg") 0 0 no-repeat;
  }
}

p.factoid {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--extrabold;
  color: $color-darkmain;
  letter-spacing: -0.01em;

  & + p {
    margin: 0;

    font-family: $font-family--Mont;
    font-weight: $font-weight--medium;
    color: $color-darkmain;
    letter-spacing: -0.01em;
  }
}

p.factoid--extrabig {
  font-size: 200px;
  line-height: 256px;

  @include viewport--md {
    display: none;
  }

  @include viewport--sm {
    display: none;
  }

  & + p {
    margin-top: -55px;
    margin-bottom: 55px;
    font-size: 20px;
    line-height: 26px;
  }
}

p.factoid--big {
  font-size: 120px;
  line-height: 153px;

  @include viewport--md {
    font-size: 100px;
    line-height: 128px;
  }

  @include viewport--sm {
    font-size: 92px;
    line-height: 118px;
  }

  & + p {
    margin-top: -30px;
    margin-bottom: 30px;
    font-size: 18px;
    line-height: 23px;

    @include viewport--md {
      margin-top: -27px;
      margin-bottom: 27px;
      font-size: 16px;
      line-height: 20px;
    }

    @include viewport--sm {
      margin-top: -25px;
      margin-bottom: 25px;
      font-size: 14px;
      line-height: 18px;
    }
  }
}

p.factoid--medium {
  font-size: 92px;
  line-height: 118px;

  @include viewport--md {
    font-size: 84px;
    line-height: 107px;
  }

  @include viewport--sm {
    font-size: 72px;
    line-height: 92px;
  }

  & + p {
    margin-top: -30px;
    margin-bottom: 30px;
    font-size: 18px;
    line-height: 23px;

    @include viewport--md {
      margin-top: -27px;
      margin-bottom: 27px;
      font-size: 16px;
      line-height: 20px;
    }

    @include viewport--sm {
      margin-top: -25px;
      margin-bottom: 25px;
      font-size: 14px;
      line-height: 18px;
    }
  }
}

p.factoid--small {
  display: inline-block;
  margin-right: 5px;
  font-size: 48px;
  line-height: 61px;

  @include viewport--md {
    font-size: 44px;
    line-height: 56px;
  }

  @include viewport--sm {
    font-size: 42px;
    line-height: 54px;
  }

  & + p {
    display: inline-block;
    vertical-align: text-bottom;
    font-size: 14px;
    line-height: 20px;
    max-width: 90px;

    @include viewport--md {
      font-size: 13px;
      line-height: 18px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 16px;
    }
  }
}

.service-spreadsheet {
  border: 3px solid $color-darkmain;
  box-sizing: border-box;
  border-collapse: collapse;

  & td,
  th {
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 13px;
    line-height: 42px;
    letter-spacing: -0.01em;
    border: 2px solid $color-beigelight;
    text-align: left;
  }

  & th {
    padding-left: 29px;
    padding-right: 29px;
    color: $color-darkgray;
  }

  & td {
    padding-left: 38px;
    padding-right: 38px;
  }
}

.main-spreadsheet {
  border-collapse: collapse;

  & tr:first-child {
    background-color: $color-darkmain;
  }

  & tr:nth-child(even):not(:first-child) {
    background-color: $color-default-white;
  }

  & tr:nth-child(odd):not(:first-child) {
    background-color: $color-beigelight;
  }

  & td,
  th {
    padding-left: 30px;
    padding-right: 30px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--regular;
    font-size: 13px;
    line-height: 60px;
  }

  & td {
    color: $color-darkmain;
  }

  & th {
    color: $color-mediumgray;
  }
}

.main-spreadsheet--green {
  & tr:first-child {
    background-color: $color-green;
  }

  & th {
    color: $color-beigelight;
  }
}

.content-wrapper {
  & > div {
    padding: 0;

    &:not(:last-child) {
      margin-bottom: 50px;

      @include viewport--md {
        margin-bottom: 40px;
      }

      @include viewport--sm {
        margin-bottom: 30px;
      }
    }
  }
}

.text-block {
  font-family: $font-family--Mont;
  color: $color-darkmain;
  font-weight: $font-weight--regular;

  p {
    font-size: 14px;
    line-height: 24px;
    margin-top: 0;
    margin-bottom: 1em;

    @include viewport--md {
      font-size: 13px;
      line-height: 22px;
    }

    @include viewport--sm {
      font-size: 12px;
      line-height: 22px;
    }
  }
}

.text-center {
  text-align: center;
}
