.article-number {
  margin: 0;

  padding: 9px 21px 7px 21px;

  background-color: $beige-extralight;
  border-radius: 24px;

  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  letter-spacing: -0.02em;

  &--main {
    font-weight: 500;
    margin-left: 15px;
    padding: 0;
    font-size: 14px;
    line-height: 32px;
    color: $color-mediumgray;
    background-color: $color-transparent;
    border-radius: 0;
  }
}
