.themes {
  display: flex;
  margin-top: 63px;
  margin-bottom: 47px;

  padding-top: 35px;
  padding-bottom: 35px;

  border-top: 2px solid $color-beigelight;
  border-bottom: 2px solid $color-beigelight;

  picture,
  img {
    flex-shrink: 0;
  }

  img {
    margin-right: 60px;
    width: 100%;
    height: auto;
    max-width: 350px;
  }
}

.themes__text-wrapper {
  .text-color-gray {
    margin-bottom: 10px;

    font-size: 12px;
    line-height: 18px;

    letter-spacing: 0.1em;
    text-transform: uppercase;
  }

  p {
    margin: 0;

    margin-bottom: 42px;
    padding-right: 20px;

    font-size: 14px;
    line-height: 24px;
  }

  h2 {
    display: block;
    margin-top: 0;
    margin-bottom: 11px;

    font-weight: bold;
    font-size: 28px;
    line-height: 32px;

    letter-spacing: -0.02em;
  }

  .btn {
    padding: 19px 35px 16px 31px;

    font-weight: bold;
    padding-top: 20px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
  }
}

@include viewport--lg {
  .themes {
    margin-top: 52px;
    margin-bottom: 47px;
    padding-top: 35px;
    padding-bottom: 36px;

    img {
      margin-right: 56px;
    }

    p {
      margin-bottom: 37px;
    }

    h2 {
      margin-bottom: 14px;

      font-weight: bold;
      font-size: 24px;
      line-height: 28px;

      letter-spacing: -0.02em;
    }
  }
}

@include viewport--sm {
  .themes {
    margin-top: 41px;
    margin-bottom: 36px;
    padding-top: 28px;

    flex-direction: column;

    h2 {
      font-size: 20px;
      line-height: 24px;
    }

    p {
      margin-bottom: 34px;
      font-size: 12px;
      line-height: 22px;
    }

    img {
      margin-right: 0;
      margin-bottom: 28px;
    }
  }

  .themes__text-wrapper {
    .btn {
      font-size: 12px;
      line-height: 32px;

      text-align: center;
      letter-spacing: 0.05em;
    }
  }
}
