@mixin retina {

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first
@mixin viewport--xxl {
  @media (max-width: $viewport--xxxl - 1) {
    @content;
  }
}

@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 1) {
    @content;
  }
}

@mixin viewport--lg {
  @media (max-width: $viewport--xl - 1) {
    @content;
  }
}

@mixin viewport--md {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

@mixin viewport--sm {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

@mixin viewport--sd {
  @media (max-width: $viewport--sd - 1) {
    @content;
  }
}

@mixin viewport--mm {
  @media (max-width: $viewport--mm - 1) {
    @content;
  }
}

@mixin reset-link {
  color: inherit;
  text-decoration: none;
  font-size: 11px;
  line-height: 16px;
}

@mixin reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

@mixin reset-item {
  padding: 0;
  margin: 0;
}

@mixin link {
  font-weight: $font-weight--medium;
  font-size: inherit;
  line-height: inherit;
  letter-spacing: -0.02em;
}

