.main-page {
  overflow: hidden; //fix for flickering scrolls in WOW

  .main-slider {
    margin-top: 16px;
    margin-bottom: 20px;
  }

  .sections-slider {
    margin-bottom: 71px;

    .swiper-slide {
      margin-right: 17px;
    }
  }

  .quality {
    margin-bottom: 45px;
  }
}

@include viewport--xl {
  .main-page {
    .quality {
      margin-bottom: 32px;
    }

    .main-slider {
      margin-top: 15px;
      margin-bottom: 21px;

      img {
        height: 420px;
      }
    }
  }
}

@include viewport--lg {
  .main-page {
    .main-slider {
      margin-top: 11px;
      margin-bottom: 17px;

      img {
        height: 391px;
      }
    }

    .sections-slider {
      margin-bottom: 58px;
    }

    .quality {
      margin-bottom: 67px;
    }
  }
}

@include viewport--md {
  .main-page {
    .main-slider {
      margin-top: 8px;
      margin-bottom: 12px;

      img {
        height: 280px;
      }
    }

    .sections-slider {
      margin-bottom: 45px;
    }

    .quality {
      margin-bottom: 40px;
    }
  }
}

@include viewport--sm {
  .main-page {
    .sections-slider {
      margin-bottom: 35px;

      .swiper-slide {
        margin-right: 13px;
      }
    }
  }
}
