.search-input-wrapper {
  display: flex;
  max-width: 1019px;

  input {
    height: 50px;

    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -0.02em;

    &::placeholder {
      font-weight: 500;
      font-size: 14px;
      letter-spacing: -0.02em;
    }
  }

  button {
    padding: 11px 38px 7px 40px;
    margin-left: 10px;

    font-weight: bold;
    font-size: 12px;
    line-height: 32px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

@include viewport--sm {
  .search-input-wrapper {
    input {
      padding-right: 50px;
    }

    button {
      padding: 11px 28px 6px;
    }
  }
}
