.about {
  $b: #{&};
  background-color: $color-darkmain;
  color: $color-beigelight;

  .container {
    padding-top: 105px;
    padding-bottom: 142px;
  }

  .title {
    margin: 0;
    margin-bottom: 21px;
    letter-spacing: -0.02em;
    color: $color-beigelight;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  .btn {
    margin-top: -65px;
    padding: 20px 10px;
    padding-bottom: 16px;
    border-color: $color-beigelight;
    color: $color-beigelight;

    &:hover {
      color: $color-green;
      border-color: $color-green;
    }
  }

  &__desc {
    margin-bottom: 29px;

    font-weight: 500;
    font-size: 22px;
    line-height: 32px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__text-wrapper {
    width: 44%;
    padding-bottom: 118px;
  }

  &__img-wrapper {
    position: relative;
    align-self: start;
    width: 48%;
    height: 393px;

    a {
      @include reset-link;
    }

    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }

    &--rounded {
      border-radius: 50%;
      width: auto;
      height: auto;
    }
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

    width: 121px;
    height: 120px;

    border: 0;
    background-color: transparent;
    cursor: pointer;

    transition: $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;

      width: 100%;
      height: 100%;

      background-image: url("../img/svg/play_icon.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }

  &--white {
    background-color: transparent;
    color: $color-darkmain;

    .title {
      font-size: 34px;
      line-height: 36px;
    }

    p {
      font-size: 16px;
      line-height: 28px;
    }

    #{$b}__img-wrapper {
      width: 48.2%;
      height: 313px;
      padding-top: 0;
    }

    #{$b}__text-wrapper {
      order: 1;
      padding-bottom: 87px;
      margin-right: 3.7%;
      padding-top: 29px;
    }
  }

  &--main {
    background-color: transparent;
    color: $color-darkmain;
    padding: 61px 0 100px;

    .about__wrapper {
      max-width: 1124px;
      margin: 0 auto;
    }

    .title {
      color: $color-darkmain;
      margin: 0 0 14px;
    }

    .about__text-wrapper {
      padding: 13px 0 32px 0;
      width: 46%;
    }

    .about__img-wrapper {
      width: 49%;
      height: 360px;
      border-radius: 8px;
      background-color: $color-lightgray;

      img {
        border-radius: 8px;
      }
    }

    .about__description {
      font-weight: 500;
      font-size: 22px;
      line-height: 32px;
      margin-bottom: 30px;
    }

    .about__text {
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
    }
  }

  &--studio {
    padding: 21px 0 100px;

    .about__wrapper {
      max-width: 1200px;
      margin: 0 auto;
    }


    .about__text-wrapper {
      padding: 20px 5px 0 32px;

      @include viewport--lg {
        padding: 0px 5px 0 0px
      }
    }

    .about__img-wrapper {
      width: 49%;
      height: 398px;
    }

    .about--main .title {
      margin: 0 0 25px;
    }
  }
}

@include viewport--xl {
  .about {
    $b: #{&};

    p {
      padding-right: 60px;
    }

    .btn {
      margin-top: -82px;
    }

    &__desc {
      margin-bottom: 18px;
      padding-right: 0;
    }

    &__img-wrapper {
      width: 50.5%;
      height: 341px;
    }

    &__play {
      width: 107px;
      height: 108px;
    }

    &--white {
      .container {
        padding: 110px 6.1% 119px 10.8%;
      }

      #{$b}__img-wrapper {
        width: 48%;
        height: 291px;
      }

      #{$b}__text-wrapper {
        width: 44%;
        padding-bottom: 65px;
        margin-right: 0.4%;

        p {
          padding-right: 0;
        }
      }
    }
  }
}

@include viewport--lg {
  .about {
    $b: #{&};

    .container {
      padding-bottom: 141px;
    }

    .title {
      margin-bottom: 19px;

      font-size: 36px;
      line-height: 40px;
    }

    p {
      font-size: 14px;
      line-height: 24px;
    }

    .btn {
      margin-top: -79px;
    }

    &__desc {
      margin-bottom: 28px;
      padding-right: 40px;

      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
    }

    &__img-wrapper {
      width: 48%;
      height: 331px;
    }

    &__play {
      width: 97px;
      height: 98px;
    }

    &__text-wrapper {
      width: 48%;
    }

    &--white {
      .container {
        padding-top: 75px;
        padding-bottom: 95px;
      }

      #{$b}__img-wrapper {
        height: 262px;
      }

      #{$b}__text-wrapper {
        padding-bottom: 19px;
        padding-top: 24px;
        margin-right: 2.1%;
      }

      .title {
        font-size: 28px;
        line-height: 32px;
      }

      p {
        font-size: 16px;
        line-height: 28px;
      }
    }

    &--main {
      .about__wrapper {
        flex-direction: row;
        max-width: 896px;
        margin: 0;
        padding-left: 35px;
      }

      .about__text-wrapper {
        width: 50%;
      }

      .about__img-wrapper {
        height: 297px;
        width: 49%;
        border-radius: 8px;
      }

      .title {
        margin-bottom: 24px;
        font-size: 34px;
        line-height: 40px;
      }

      .about__description {
        font-weight: 600;
        font-size: 18px;
        line-height: 27px;
        margin-bottom: 23px;
        padding-right: 10px;
      }

      .about__text {
        padding-right: 10px;
      }
    }

    &--studio {

      .about__wrapper {
        margin: 0 auto;
        max-width: 1080px;
        padding: 0;
      }

      .about__img-wrapper {
        height: 290px;
      }
    }
  }
}

@include viewport--md {
  .about {
    $b: #{&};

    padding-top: 53px;
    padding-bottom: 64px;

    .container {
      padding: 0 25px;
    }

    .title {
      margin-bottom: 13px;

      font-size: 28px;
      line-height: 32px;
    }

    p {
      padding-right: 0;

      font-size: 12px;
      line-height: 22px;
    }

    .btn {
      margin-top: 51px;
      padding: 9px 10px 3px 10px;
      font-size: 12px;
      line-height: 32px;
      min-width: 100px;
      max-width: 215px;
    }

    &__desc {
      padding-right: 30px;
      margin-bottom: 27px;

      font-size: 18px;
      line-height: 26px;
    }

    &__text-wrapper {
      margin-bottom: 33px;
      width: 100%;
      padding-bottom: 0;
    }

    &__img-wrapper {
      width: 100%;
      height: 230px;

      img {
        width: 100%;
      }

      &__play {
        width: 70px;
        height: 71px;
      }
    }

    &__wrapper {
      flex-direction: column;
    }

    &--white {
      padding-top: 48px;
      padding-bottom: 45px;

      #{$b}__text-wrapper {
        width: 100%;
        padding-top: 29px;
        padding-bottom: 0;
        margin-bottom: 0;
      }

      .title {
        margin-bottom: 11px;
        font-size: 22px;
        line-height: 26px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }

      #{$b}__img-wrapper {
        margin: 0 auto;
        width: 100%;
        overflow: hidden;

        img {
          object-fit: cover;
        }

        height: 300px;
      }
    }

    &--main {
      .about__wrapper {
        padding-left: 23px;
      }
    }

    &--studio {
      .about__wrapper {
        padding-left: 0;
      }
    }
  }
}

@include viewport--sm {
  .about {
    .container {
      padding: 0 17px;
    }

    &--main {
      padding-top: 33px;
      padding-bottom: 93px;

      .about__wrapper {
        flex-direction: column;
        padding-left: 0;
      }

      .about__text-wrapper {
        width: 100%;
        margin-bottom: 20px;
      }

      .title {
        font-size: 28px;
        margin-bottom: 27px;
      }

      .about__description {
        margin-bottom: 11px;
      }

      .about__img-wrapper {
        width: 100%;
        height: 240px;
      }
    }

    &--studio {
      .about__text-wrapper {
        margin-bottom: 55px;
      }

      .about__img-wrapper {
        height: 230px;
      }
    }
  }

  @media (min-width: 1440px) {
    .about--white {
      .container {
        padding-bottom: 102px;
      }
    }
  }
}
