.counter-list-steps {
  @include reset-list;

  counter-reset: counter-list-steps;

  li {
    @include reset-item;
  }

  .warning {
    margin-top: 44px;
  }

  .info {
    margin-top: 65px;
    margin-bottom: 65px;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }

  h3 {
    margin: 0;
    margin-bottom: 20px;

    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;

    &::before {
      content: counter(counter-list-steps) ". ";
      counter-increment: counter-list-steps;
    }
  }
}

.counter-list-steps__flex {
  display: flex;

  img {
    height: 284px;
    align-self: flex-start;
    object-fit: cover;
  }

  p {
    margin: 0;

    width: 50%;
    margin-left: auto;
    margin-top: 31px;
  }
}

.counter-list-steps__img-flex {
  flex-wrap: wrap;
  margin-top: 65px;
  display: flex;
  justify-content: space-between;
  padding-right: 88px;

  img {
    margin-top: 30px;
    object-fit: contain;
    max-height: 100%;
    height: 344px;
    align-self: start;

    &:nth-child(-n + 2) {
      margin-top: 0;
    }
  }

  &--3-col {
    padding-right: 51px;

    img {
      height: 281px;
      margin-top: 30px;

      &:nth-child(-n + 3) {
        margin-top: 0;
      }
    }
  }
}

@include viewport--xl {
  .counter-list-steps {
    .warning {
      margin-top: 20px;
    }

    .info {
      padding: 16px 5.7% 11px 4.9%;

      p {
        padding-right: 5%;
      }
    }
  }

  .counter-list-steps__img-flex {
    padding-right: 0;

    &--3-col {
      padding-right: 0;
      margin-top: 32px;
    }
  }

  .counter-list-steps__flex {
    p {
      width: 47%;
      margin-left: 6.2%;
    }
  }
}

@include viewport--lg {
  .counter-list-steps {
    h3 {
      margin-bottom: 23px;
    }

    .warning {
      margin-top: 33px;
    }

    .info {
      margin-bottom: 49px;
      padding: 28px 4.9% 39px 7.6%;
      margin-right: 0;
    }
  }

  .counter-list-steps__flex {
    display: block;

    img {
      margin-right: 8px;
      float: left;
      height: 224px;
      width: 317px;
      object-fit: contain;
    }

    p {
      width: 100%;
      margin-top: 38px;
      margin-left: 0;
    }
  }

  .counter-list-steps__img-flex {
    padding-right: 0;

    img {
      width: auto;
      height: 283px;
    }

    &--3-col {
      margin-top: 42px;

      img {
        height: 246px;
        object-fit: contain;
      }
    }
  }

  .counter-list-steps b,
  .counter-list-steps h3 {
    font-size: 28px;
    line-height: 32px;
  }
}

@include viewport--md {
  .counter-list-steps {
    .warning {
      margin-top: 22px;
      margin-bottom: 5px;
    }

    p {
      font-size: 12px;
      line-height: 22px;
    }

    h3 {
      margin-bottom: 13px;

      font-size: 22px;
      line-height: 26px;
    }

    .info {
      margin-bottom: 44px;
      margin-top: 33px;
      padding: 28px 4.9% 24px 9.4%;

      p {
        padding-right: 0;
      }
    }
  }

  .counter-list-steps__flex {
    img {
      margin: 0 auto;
      width: 340px;
      height: 240px;
      float: none;
    }

    p {
      margin-top: 12px;
      margin-bottom: 12px;
    }
  }

  .counter-list-steps__img-flex {
    margin-top: 42px;
    justify-content: space-around;

    img {
      margin-bottom: 22px;

      width: 340px;
      height: auto;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &--3-col {
      justify-content: space-between;
      margin-top: 22px;

      img {
        height: 214px;
        width: 170px;

        object-fit: contain;
      }
    }
  }
}
