.hero {
  $b: #{&};
  width: 100%;
  position: relative;
  border-radius: 16px;
  overflow: hidden;
  margin: 12px 0;
  min-height: 560px;
  display: flex;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 140px;
  align-items: stretch;
  -webkit-mask-image: -webkit-radial-gradient(#fff, #000);

  .hero__footer,
  .breadcrumbs {
    align-self: flex-start;
  }

  @include viewport--xl {
    min-height: 429px;
  }

  @include viewport--md {
    min-height: 331px;
    margin: 10px 0 24px;
  }

  @include viewport--sm {
    padding-bottom: 54px;
  }

  &:hover {
    img {
      transform: scale(1.1);
    }

    .hero__img::after {
      opacity: 0.4;
    }
  }

  &__img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 8px;

    picture {
      width: 100%;
      height: 100%;
      display: flex;
    }

    img {
      transition: transform $default-transition-settings;
      object-fit: cover;
      height: auto;
      width: auto;
      min-width: 100%;
      min-height: 100%;
      object-position: center;
      border-radius: 8px;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      width: calc(100% + 2px);
      height: calc(100% + 2px);
      background: $color-darkmain;
      opacity: 0.5;
      z-index: 9;
      top: -1px;
      left: -1px;
      border-radius: 16px;
      transition: opacity $default-transition-settings;
    }
  }

  &__wrapper {
    z-index: 1;
    width: 100%;
    padding: 0 35px;
    position: relative;
    max-width: 696px;
    margin: 0 auto;
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  &__title,
  &__text {
    color: $color-default-white;
    text-align: center;
  }

  &__title {
    font-weight: 900;
    font-size: 92px;
    line-height: 92px;
    //letter-spacing: -0.02em;
    @include reset-item;
    margin-bottom: 21px;

    @include viewport--xl {
      font-size: 64px;
      line-height: 82px;
    }

    @include viewport--md {
      font-size: 34px;
      line-height: 43px;
    }

    @include viewport--mm {
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    @include reset-item;

    @include viewport--xl {
      font-size: 18px;
      line-height: 26px;
    }

    @include viewport--md {
      font-size: 12px;
      line-height: 15px;
    }
  }

  &__author {
    @include reset-item;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: -0.02em;
    color: $color-beigelight;
    opacity: 0.6;
  }

  &__description {
    margin-top: auto;
    margin-bottom: auto;
  }

  &--big {
    padding: 47px 0 25px 82px;
    justify-content: flex-start;
    align-items: stretch;
    min-height: 620px;

    @include viewport--xl {
      padding: 29px 0 23px 45px;
      min-height: 486px;
    }

    @include viewport--md {
      padding: 30px 17px 18px 17px;
      min-height: 540px;
    }

    .hero-tags {
      margin-bottom: 25px;

      @include viewport--xl {
        margin-bottom: 14px;
      }

      @include viewport--md {
        margin-bottom: 21px;
      }
    }

    #{$b}__wrapper {
      max-width: 988px;
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include viewport--md {
        padding: 0;
      }
    }

    #{$b}__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 28px;
      text-align: left;
      @include reset-item;

      @include viewport--md {
        font-size: 12px;
        line-height: 22px;
      }
    }

    #{$b}__title {
      font-weight: 800;
      font-size: 52px;
      line-height: 54px;
      letter-spacing: -0.02em;
      text-align: left;
      margin-bottom: 45px;

      @include viewport--xl {
        font-size: 40px;
        line-height: 43px;
        margin-bottom: 29px;
      }

      @include viewport--md {
        font-size: 28px;
        line-height: 36px;
        margin-bottom: 21px;
      }

      @include viewport--mm {
        font-size: 24px;
        line-height: 30px;
      }
    }
  }

  &--team {
    padding-top: 33px;

    .breadcrumbs {
      align-self: center;
    }

    .hero__wrapper {
      margin: 0 auto;
      padding: 0;
    }

    .hero__img {
      border-radius: 16px;
    }

    .hero__img img {
      @include viewport--sm {
        object-position: 73% 50%;
      }
    }

    .hero__img::after {
      border-radius: 16px;

      @include viewport--xl {
        border-radius: 8px;
      }
    }

    .hero__title {
      margin: 0 0 5px 0;
      text-transform: uppercase;
      font-weight: 950;
      font-size: 92px;
      line-height: 120px;
      letter-spacing: 1.5px;

      @include viewport--lg {
        margin: 0px 0 5px 5px;
        letter-spacing: 2.5px;
      }

      @include viewport--md {
        font-size: 52px;
        line-height: 60px;
        letter-spacing: 0.5px;
        margin: 0 auto 9px;
      }

      @include viewport--mm {
        word-break: break-all;
        font-size: 50px;
        line-height: 52px;
      }
    }

    .hero__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 28px;

      @include viewport--sm {
        font-size: 12px;
        line-height: 18px;
        padding: 0 20px;
      }
    }

    @include viewport--lg {
      min-height: 502px;
    }

    @include viewport--sm {
      min-height: 394px;
      margin-top: 21px;
    }
  }

  &--production {
    .breadcrumbs {
      align-self: center;
    }

    .hero__wrapper {
      max-width: 100%;
    }

    .hero__title {
      margin: 0 auto;

      @include viewport--lg {
        font-size: 90px;
        line-height: 120px;
        margin: 0 auto;
      }

      @include viewport--md {
        font-size: 75px;
        line-height: 100px;
        margin: 0 auto;
      }

      @include viewport--sm {
        font-size: 36px;
        line-height: 60px;
      }

      @include viewport--mm {
        font-size: 32px;
        line-height: 60px;
      }
    }

    .hero__text {
      max-width: 726px;
      margin: 0 auto;
    }
  }
}
