.communication {
  padding-top: 88px;
  padding-bottom: 143px;

  .container {
    padding: 0 265px;
  }

  p {
    margin: 0;

    font-size: 16px;
    line-height: 28px;
  }

  &__title {
    margin: 0;
    font-size: 54px;
    line-height: 60px;
    font-weight: normal;
    letter-spacing: -0.02em;
  }

  &__link {
    @include reset-link;

    position: relative;
    font-size: 54px;
    line-height: 60px;
    font-weight: bold;
    padding-right: 76px;
    letter-spacing: -0.02em;

    color: $color-blue;
    transition: $default-transition-settings;

    &:hover {
      color: $color-lightblue;
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 9px;

      width: 60px;
      height: 60px;

      background-image: url("../img/sprite/whatsapp.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.communication--fill {
  padding: 75px 35px 169px;

  .communication__fill-wrapper {
    padding: 89px 27% 88px 91px;
    background-color: $beige-extralight;
  }
}

.communication__wrapper {
  margin-bottom: 25px;
}

@include viewport--xl {
  .communication {
    padding-top: 98px;
    padding-bottom: 123px;

    .container {
      padding: 0 234px;
    }
  }
}

@include viewport--lg {
  .communication {
    padding-top: 69px;
    padding-bottom: 139px;

    .container {
      padding: 0 115px;
    }

    &__title {
      margin-bottom: 0;
    }

    &__link {
      @include reset-link;

      padding-right: 70px;

      &::after {
        bottom: 7px;
        width: 54px;
        height: 54px;
      }
    }

    &__title,
    &__link {
      letter-spacing: -0.02em;
      font-size: 48px;
      line-height: 54px;
    }
  }

  .communication--fill {
    .communication__fill-wrapper {
      padding: 89px 130px 88px 91px;
    }
  }

  .communication__wrapper {
    margin-bottom: 22px;
  }
}

@include viewport--md {
  .communication {
    padding-top: 50px;
    padding-bottom: 107px;

    .container {
      padding: 0 16px;
    }

    p {
      font-size: 14px;
      line-height: 24px;
    }

    a {
      padding-right: 0;

      &::after {
        right: -40px;
        bottom: 3px;

        width: 35px;
        height: 35px;
      }
    }

    &__title,
    &__link {
      font-size: 32px;
      line-height: 38px;
    }
  }

  .communication--fill {
    padding: 75px 25px 100px;

    .communication__fill-wrapper {
      padding: 50px 50px 50px;
    }
  }

  .communication__wrapper {
    padding-right: 38px;
  }
}

@include viewport--sm {
  .communication--fill {
    padding: 75px 17px 100px;

    .communication__fill-wrapper {
      padding: 50px 17px 50px;
    }
  }
}
