.material-images-slider {
  position: relative;
  width: 50%;
  overflow: hidden;

  @include viewport--md {
    width: 100%;
    height: 600px;
  }

  @include viewport--sm {
    height: 476px;
  }

  @media (max-width: 520px) {
    height: auto;
  }

  &__pagination {
    z-index: 2;
    position: absolute;
    top: auto;
    bottom: 24px;
    left: 50%;
    transform: translateX(-50%);

    @include viewport--md {
      bottom: 20px;
    }

    @include viewport--sm {
      bottom: 15px;
    }

    &__slide {
      position: relative;

      @media (max-width: 520px) {
        padding-top: 100%;
      }

      img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    .swiper-pagination-bullet {
      margin: 0;
      margin-right: 8px;
      width: 16px;
      height: 16px;

      cursor: pointer;
      background-color: $color-default-white;
      opacity: 0.3;
      transition: background-color $default-transition-settings;

      &:hover {
        opacity: 1;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    .swiper-pagination-bullet-active {
      background-color: $color-default-white;
      opacity: 0.9;
    }
  }

  .slider-button-fill-prev {
    left: 24px;

    @include viewport--sm {
      width: 32px;
      height: 32px;
      left: 16px;
    }
  }

  .slider-button-fill-next {
    right: 24px;

    @include viewport--sm {
      width: 32px;
      height: 32px;
      right: 16px;
    }
  }
}
