.modal-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  min-height: 100vh;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  //костыль паддинга для firefox
  // &::after {
  //   content: "";
  //   padding-bottom: 100px;
  //   display: block;
  // }
}

.modal-wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding: 100px 0;
}

.modal {
  $b: #{&};
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include viewport--sm {
      padding: 60px 16px;
    }
  }

  &__title {
    margin: 0;
  }

  &__overlay {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;


    background-color: $color-default-white;

    @include viewport--sm {
      width: 100%;
      padding: 20px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
  }

  &--responsive {
    #{$b}__content {
      width: 100%;
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  &--menu {
    transform: scale(1);
    opacity: 1;
    transition: visibility 0.2s ease;

    #{$b}__content {
      left: -279px;
      transition: left $default-transition-settings;
    }

    &#{$b}--active {
      #{$b}__content {
        left: 0;
        transition: left $default-transition-settings;
      }
    }
  }

  &--submenu {
    transform: scale(1);
    opacity: 1;
    transition: visibility 0.2s ease;

    #{$b}__content {
      left: -279px;
      transition: left $default-transition-settings;
    }

    &#{$b}--active {
      #{$b}__content {
        left: 0;
        transition: left $default-transition-settings;
      }
    }
  }

  &--darkblue {
    background-color: rgba($color-darkmain, 0.8);
  }

  &__subcategoryes-list {
    @include reset-list;
  }

  &__subcategoryes-item {
    @include reset-item;

    li {
      @include reset-item;
    }
  }
}

.modal-img {
  position: relative;
  width: 1200px;

  @include viewport--xl {
    width: 81.4%;
  }

  @include viewport--lg {
    width: 83.9%;
  }

  @include viewport--md {
    width: 91.5%;
  }
}

.modal-img__img {
  position: relative;
  padding-bottom: percentage(835/1200);
  margin: 0;

  img {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    object-fit: scale-down;
  }

}
