.info-list {
  @include reset-list;
  margin-top: 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 23px;
  grid-row-gap: 40px;

  @include viewport--lg {
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 25px;
  }

  @include viewport--md {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 25px;
  }

  &.swiper-wrapper {
    display: flex;
    justify-content: flex-start;
    grid-column-gap: 0;
    grid-row-gap: 0;

    .swiper-slide,
    .info-list__item:not(:first-of-type) {
      height: 100%;
      margin-top: 0;
    }

    .info-list__card {
      @include viewport--lg {
        padding: 44px;
        padding-bottom: 0;
      }

      @include viewport--sm {
        padding-left: 26px;
        padding-top: 41px;
      }
    }

    .info-list__card--mount p {
      color: $color-darkgray;
    }

    .info-list__item {
      width: 324px;
      margin: 0;

      &--width-50 {
        width: 680px;

        h2,
        p {
          max-width: 290px;
        }
      }
    }

    @include viewport--sm {
      .swiper-slide {
        flex-wrap: wrap;
      }

      .info-list__item {
        width: 100%;

        &--width-50 {
          width: 100%;
        }
      }

      .info-list__card {
        padding-left: 25px;
        padding-top: 46px;

        h2 {
          margin-bottom: 17px;
        }

        p {
          font-size: 14px;
          line-height: 22px;
        }
      }
    }
  }

  &__bg-img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 8px;
    -webkit-mask-image: -webkit-radial-gradient(#fff, #000);

    picture {
      width: 100%;
      height: 100%;
      display: flex;
    }

    img {
      transition: transform $default-transition-settings;
      object-fit: cover;
      height: auto;
      width: auto;
      min-width: 100%;
      min-height: 100%;
      object-position: center;
      border-radius: 8px;
    }
  }

  &__item {
    @include reset-item;
  }

  @include viewport--sm {
    display: block;

    .info-list__item:not(:first-of-type) {
      margin-top: 20px;
    }
  }

  &__card {
    @include reset-link;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: 400px;
    padding-top: 45px;
    padding-left: 42px;
    padding-right: 42px;
    border-radius: 8px;
    opacity: 1;
    transition: opacity $default-transition-settings;

    &:hover,
    &:focus {
      opacity: 0.7;
    }

    @include viewport--lg {
      padding-left: 28px;
      padding-top: 39px;
    }

    @include viewport--sm {
      padding-left: 25px;
      min-height: 350px;
      height: auto;
    }

    &--wallpapers {
      color: $color-default-white;
      background-color: $color-darkmain;

      .info-list__img-wrapper {
        height: 169px;
      }
    }

    &--delivery {
      color: $color-default-white;
      background-color: $color-green;

      .info-list__img-wrapper {
        height: 169px;
      }
    }

    &--payment {
      color: $color-default-white;
      background-color: $color-blue;

      .info-list__img-wrapper {
        height: 202px;
      }
    }

    &--mount {
      background-color: $color-beigelight;

      .info-list__img-wrapper {
        height: 267px;
      }

      @include viewport--sm {
        .info-list__img-wrapper {
          height: 212px;
        }
      }
    }

    &--info {
      color: $color-darkmain;
      background-color: $color-beigelight;
    }

    h2 {
      margin-top: 0;
      margin-bottom: 10px;
      font-weight: 700;
      font-size: 28px;
      line-height: 32px;
      letter-spacing: -0.02em;
      position: relative;
      z-index: 3;

      @include viewport--lg {
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 12px;
      }

      @include viewport--sm {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 12px;
      }
    }

    p {
      margin: 0;
      position: relative;
      z-index: 3;
      font-weight: $font-weight--regular;
      font-size: 14px;
      line-height: 22px;
      margin-bottom: 5px;
      color: $color-beigelight;

      @include viewport--sm {
        font-size: 12px;
        line-height: 20px;
      }
    }

    a {
      @include reset-link;
    }
  }

  &__img-wrapper {
    max-width: 100%;
    margin-top: auto;
    margin-bottom: 0;
    position: relative;
    z-index: 2;

    svg {
      width: 236px;
      min-width: 236px;
      height: 224px;
    }

    img {
      position: absolute;
      margin: 0;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: left bottom;
    }
  }
}

.info-list__links {
  @include reset-list;

  margin-top: 20px;

  @include viewport--sm {
    margin-top: 9px;
  }

  li {
    @include reset-item;
    margin-bottom: 11px;

    @include viewport--sm {
      margin-bottom: 5px;
    }
  }

  a {
    @include reset-link;

    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.02em;
    color: $color-darkgray;
    transition: color $default-transition-settings;

    &:hover,
    &:focus {
      color: $color-darkmain;
    }

    @include viewport--lg {
      font-size: 12px;
    }
  }
}

a.info-list__link {
  display: inline-block;
  font-weight: 700;
  font-size: 13px;
  line-height: 32px;
  letter-spacing: 0.02em;
  color: $color-mediumgray;
  margin-top: auto;
  margin-bottom: 24px;
  position: relative;
  width: auto;
  align-self: flex-start;
  transition: color $default-transition-settings;

  &:hover {
    color: $color-darkmain;
  }

  &::after {
    content: "→";
    position: absolute;
    padding-left: 8px;
    top: 50%;
    right: -11px;
    transform: translateY(-50%);

    color: $color-darkgray;
    line-height: 20px;
    font-size: 12px;
  }
}
