.learn-more {
  padding: 60px 0 57px 116px;

  @include viewport--lg {
    padding: 50px 0 40px;
  }

  @include viewport--sm {
    padding: 20px 0;
  }

  &__descriptions {
    font-weight: 500;
    font-size: 54px;
    line-height: 60px;
    letter-spacing: -0.02em;
    max-width: 950px;
    padding: 0;
    margin: 0 0 30px 0;

    a {
      font-weight: 500;
    }

    @include viewport--lg {
      font-size: 48px;
      line-height: 60px;
    }

    @include viewport--sm {
      font-size: 30px;
      line-height: 40px;
      margin-bottom: 20px;
    }
  }

  &__text {
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 28px;
    max-width: 920px;

    @include viewport--sm {
      font-size: 14px;
      line-height: 24px;
    }
  }

  &--studio {
    padding: 106px 0 162px 116px;

    @include viewport--lg {
      padding: 76px 0 162px 0px;
    }

    @include viewport--sm {
      padding: 56px 0 92px 0px;
    }

    .learn-more__descriptions {
      @include viewport--lg {
        margin-top: 25px;
      }
    }
  }

  &--virtual {
    padding: 106px 0 162px 116px;

    @include viewport--lg {
      padding: 101px 0 162px 0px;
    }


    @include viewport--sm {
      padding: 81px 0 92px 0px;
    }

    .container {
      @media (min-width: 1460px) {
        padding-left: 83px;
      }
    }
  }

  &--cases {
    padding: 67px 0 162px 116px;

    @include viewport--lg {
      padding: 0 0 92px 0px;
    }

    @include viewport--sm {
      padding: 0 0 92px 0px;
    }
  }
}
