.tick-list {
  @include reset-list;

  padding-right: 47%;

  li {
    @include reset-item;
    position: relative;

    padding-left: 30px;
    margin-bottom: 16px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 4px;

      width: 16px;
      height: 13px;

      background-image: url("../img/svg/tick.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

@include viewport--md {
  .tick-list {
    li {
      padding-left: 23px;

      font-size: 12px;
      line-height: 22px;
    }
  }
}
