.location {
  padding: 95px 0 46px;

  @include viewport--sd {
    padding: 45px 0 46px;
  }


  &__row:not(:last-child) {
    margin-bottom: 96px;

    @include viewport--lg {
      margin-bottom: 45px;
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @include viewport--lg {
      grid-template-columns: repeat(2, 1fr);
    }

    @include viewport--sm {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    overflow: hidden;
    max-height: 440px;

    &--heigh {
      @include viewport--sd {
        max-height: 465px;
      }
    }
  }

  &__item img {
    transition: transform $default-transition-settings;

    @include viewport--sm {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item--w-100 {
    @include viewport--lg {
      grid-column: 1 / -1;
      grid-row: 1 / 2;
    }
  }

  &__title {
    margin-top: 25px;
    margin-bottom: 30px;
    max-width: 333px;

    @include viewport--lg {
      max-width: 100%;
    }

    @include viewport--sd {
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 15px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: $color-darkmain;
    max-width: 384px;
    padding: 0;
    margin: 0;

    @include viewport--lg {
      max-width: 100%;
    }
  }
}
