body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: $font-family--Mont;
  color: $color-darkmain;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.line-through {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 54px;
    height: 2px;
    background-color: $color-darkred;
    transform: translate(-50%, -50%) rotate(-19deg);
  }
}
