.modal-menu {
  display: none;

  @include viewport--sm {
    display: block;

    background-color: transparent;
  }

  .modal__overlay {
    background-color: $color-beigelight;
    opacity: 0.4;
  }

  .modal__wrapper {
    padding: 0;
    justify-content: flex-start;
  }

  .modal__content {

    display: flex;
    flex-direction: column;
    width: 279px;
    padding: 29px 21px 18px 15px;
    overflow: auto;
    //height: 100vh;
    height: calc(var(--vh, 1vh) * 100);

    background-color: $color-darkmain;

    @media (max-height: 400px) {
      height: 100%;
    }

    .row1 {
      height: calc(100% - 148px);
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: -5px;
        left: -15px;

        width: calc(100% + 36px);
        height: 46px;

        background: linear-gradient(180deg, #2d3344 0%, rgba(45, 51, 68, 0) 100%);
        transform: matrix(1, 0, 0, -1, 0, 0);

        @media (max-height: 450px) {
          height: 35px;
        }
      }
    }

    .row2 {
      height: 148px;
      display: flex;
      flex-direction: column;
      margin-top: auto;
    }
  }
}

.modal-menu__header {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -15px;

    width: calc(100% + 36px);
    height: 1px;

    background-color: $color-darkgray;
    opacity: 0.2;
  }
}

.modal-menu__logo {
  width: 70%;

  svg {
    width: 158px;
    min-width: 158px;
    height: 26px;
  }
}

.modal-menu__whatsapp {
  position: relative;

  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 25%;

  svg {
    position: relative;
    top: -5px;
    left: -3px;
    width: 32px;
    min-width: 32px;
    height: 32px;
  }
}

.modal-menu__wrap {
  position: relative;
  height: calc(100% - 48px);
  overflow-y: scroll;
  overflow-x: hidden;
}

.modal-menu__list {
  @include reset-list;

  position: relative;
  overflow-y: scroll;

  width: 100%;

  padding-top: 11px;
  padding-bottom: 15px;

  .link {
    @include link;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 16px;
    line-height: 38px;
    color: $color-default-white;
  }

  .link--simple {
    display: block;
    line-height: 18px;
    color: $color-mediumgray;
    font-size: 12px;
    font-weight: $font-weight--regular;

    &:hover {
      color: $color-darkgray;
    }
  }
}

.modal-menu__item {
  @include reset-item;
  min-height: 32px;

  line-height: 23px;
}

.modal-menu__sublist {
  display: none;
  padding: 0 10px 3px 23px;

  .link {
    line-height: 18px;
    padding-top: 9px;
    padding-bottom: 9px;
    font-size: 14px;
  }
}

.hassublist {
  position: relative;
}

.hassublist.switсh-menu {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;

    display: block;
    width: 14px;
    height: 8px;

    background: url("../img/sprite/icon-arrow-select-gray.svg") no-repeat center center;
    background-size: contain;
    transform: translateY(-50%);
  }

  &--active {
    &::after {
      top: 30%;

      transform: rotate(180deg);
    }

    .modal-menu__sublist {
      display: block;
    }

    .modal-menu__sublist {
      display: block;
    }
  }
}

.modal-menu__button {
  position: relative;

  margin-top: 5px;
  margin-bottom: 39px;


  &::after {
    content: "";
    position: absolute;
    bottom: -23px;
    left: -15px;

    width: calc(100% + 36px);
    height: 2px;

    background-color: $color-darkgray;
    opacity: 0.2;
  }

  .btn-icon--m {
    max-width: 100%;
    min-height: 50px;
    padding-left: 16px;
  }
}

.modal-menu__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .modal-menu__social {
    svg {
      width: 42px;
      min-width: 42px;
      height: 42px;
      fill: $color-mediumgray !important; /* stylelint-disable-line */
      stroke: $color-mediumgray !important; /* stylelint-disable-line */
    }

    &:hover svg {
      fill: $color-green !important; /* stylelint-disable-line */
      stroke: $color-green !important; /* stylelint-disable-line */
    }
  }
}

.modal-menu__about {
  display: flex;
  flex-direction: column;

  .modal-menu__day {
    margin: 0;

    font-weight: $font-weight--medium;
    font-size: 12px;
    line-height: 16px;
    color: $color-mediumgray;
    letter-spacing: -0.02em;
  }

  .link {
    font-weight: $font-weight--bold;
    font-size: 24px;
    line-height: 38px;
    color: $color-default-white;
    letter-spacing: -0.02em;
  }
}

.modal-menu__chat {
  position: absolute;
  right: 5%;
  top: calc(100vh - 200px);

  svg {
    width: 40px;
    min-width: 40px;
    height: 40px;
  }

  @media (max-width: 360px) {
    right: 0;
  }
}
