.counter-list-wrapper {
  h2 {
    margin: 0;
    margin-bottom: 26px;

    font-weight: bold;
    font-size: 34px;
    line-height: 36px;
    letter-spacing: -0.02em;
  }

  p {
    margin: 0;

    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  li {
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  p,
  li {
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
  }
}

@include viewport--lg {
  .counter-list-wrapper {
    h2 {
      margin-bottom: 15px;
      font-size: 28px;
      line-height: 32px;
    }
  }
}

@include viewport--md {
  .counter-list-wrapper {
    p {
      margin-bottom: 17px;
    }
  }
}
