.order-dressing {
  padding: 88px 20px 68px 100px;
  background-color: $color-darkmain;
  position: relative;

  .container {
    @include viewport--lg {
      padding: 0;
    }
  }

  @include viewport--lg {
    padding: 88px 20px 68px 40px;
  }

  @include viewport--md {
    padding: 60px 20px 55px 30px;
  }

  @include viewport--sm {
    padding: 60px 20px 90px 17px;
  }

  @include viewport--sd {
    padding: 45px 20px 90px 17px;
  }


  &__wrapper {
    padding-left: 20px;
    display: grid;
    grid-template-columns: 62% 34%;
    gap: 50px;

    @media (min-width: 1460px) {
      padding-left: 73px;
    }

    @include viewport--lg {
      grid-template-columns: 62% 30%;
      gap: 25px;
      padding-left: 0;
    }

    @include viewport--md {
      grid-template-columns: 60% 30%;
    }

    @include viewport--sm {
      grid-template-columns: 1fr;
    }

    @include viewport--sd {
      gap: 15px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 42px;
    line-height: 46px;
    letter-spacing: -0.02em;
    color: $color-default-white;
    margin-bottom: 30px;
    max-width: 597px;

    @include viewport--md {
      font-size: 36px;
      line-height: 38px;
    }

    @include viewport--sd {
      font-size: 28px;
      line-height: 32px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.02em;
    color: $color-beigelight;
    max-width: 675px;
    margin: 0 0 37px;

    @include viewport--sd {
      font-size: 16px;
      line-height: 26px;
      max-width: 320px;
      margin: 0 0 25px;
    }
  }

  &__list {
    color: $color-lightgray;
    max-width: 715px;
    padding: 0 0 0 32px;
    margin: 0 0 90px;

    @include viewport--sd {
      margin: 0 0 38px;
    }

    li {
      position: relative;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 12px;
    }

    li::before {
      content: " ";
      position: absolute;
      top: 5px;
      left: -30px;
      width: 16px;
      height: 12px;
      background: url(../img/sprite/icon-check-white2.svg) 0 0 no-repeat;
    }
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;

    @media(max-width: 1100px) {
      position: absolute;
      left: 40px;
      bottom: 15px;
    }

    @include viewport--md {
      left: 30px;
    }

    @include viewport--sm {
      position: static;
    }
  }

  &__img {
    width: 260px;
    height: 260px;
    margin-top: 10px;
    margin-bottom: 30px;
    cursor: pointer;

    @include viewport--sd {
      margin-top: 0;
      margin-bottom: 15px;
    }


    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 100%;
    }
  }

  &__sub-text {
    margin: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: $color-mediumgray;
    max-width: 345px;
  }

  &__item {
    @media(max-width: 455px) {

      img {
        margin: 0 auto;
      }
    }
  }
}
