.preparation {
  padding-top: 99px;
  padding-bottom: 114px;

  .title {
    margin-bottom: 30px;
  }
}

.preparation__wrapper {
  display: flex;

  img,
  picture,
  a {
    flex-shrink: 0;
  }

  a {
    transition: $default-transition-settings;

    font-size: inherit;
    line-height: inherit;
    color: inherit;

    &:hover {
      opacity: 0.7;
    }
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;

    color: $color-darkgray;
  }
}

.preparation__text-wrapper {
  width: 54.5%;
  padding-right: 50px;
  padding-top: 21px;
  display: flex;
  border-right: 1px solid $color-beigelight;

  a {
    @include reset-link;
  }

  & > a {
    width: max-content;
  }

  img {
    width: 326px;
    height: 351px;

    margin-right: 20.5px;

    object-fit: cover;
  }

  .title {
    margin-bottom: 18px;
    display: block;

    font-weight: bold;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    .link {
      font-size: inherit;
      line-height: inherit;
    }
  }

  p {
    margin: 0;
  }
}

.preparation__text {
  padding-top: 12px;
}

.preparation__link-wrapper {
  padding-left: 69px;
  padding-top: 34px;
  width: 45.5%;

  .title {
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    letter-spacing: -0.02em;
  }
}

.preparation__list {
  @include reset-list;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.preparation__item {
  @include reset-item;
  margin-bottom: 11px;

  .link {
    @include reset-link;

    font-weight: bold;
    font-size: 18px;
    line-height: 22px;

    letter-spacing: -0.02em;

    color: $color-darkmain;
    transition: $default-transition-settings;

    &:hover {
      opacity: 0.7;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.preparation__rec-wrapper {
  display: flex;
  margin-bottom: 42px;

  & > a {
    width: max-content;
  }

  img {
    width: 183px;
    height: 132px;

    margin-right: 46px;
    object-fit: cover;
  }

  .preparation__text {
    padding-top: 2px;
    padding-right: 40px;

    .title {
      margin-bottom: 14px;
    }
  }
}

@include viewport--xl {
  .preparation {
    padding-top: 93px;
  }

  .preparation__link-wrapper {
    padding-left: 37px;
  }

  .preparation__rec-wrapper {
    .preparation__text {
      padding-right: 0;
    }
  }

  .preparation__text-wrapper {
    width: 59.5%;
    padding-right: 41px;

    img {
      margin-right: 24.5px;
    }

    .preparation__text {
      padding-top: 0;
    }
  }
}

@include viewport--lg {
  .preparation {
    padding-top: 77px;
    padding-bottom: 96px;
  }

  .preparation__text-wrapper {
    width: 66%;

    img {
      width: 280px;
      height: 302px;
    }

    .preparation__text {
      .title {
        margin-bottom: 25px;
      }
    }
  }

  .preparation__link-wrapper {
    width: 34%;
    padding-left: 51px;
    padding-top: 13px;
  }

  .preparation__rec-wrapper {
    flex-direction: column;
    margin-bottom: 30px;

    .preparation__text {
      .title {
        margin-bottom: 22px;
      }
    }

    img {
      margin-right: 0;
      margin-bottom: 32px;
      width: 158px;
      height: 114px;
    }
  }
}

@include viewport--md {
  .preparation {
    padding-top: 59px;
    padding-bottom: 70px;

    .title {
      margin-bottom: 6px;
    }
  }

  .preparation__wrapper {
    flex-direction: column;

    p {
      font-size: 12px;
      line-height: 22px;
    }
  }

  .preparation__text-wrapper {
    width: 100%;
    padding-bottom: 63px;
    padding-right: 0;
    flex-direction: column;

    border-right: 0;
    border-bottom: 1px solid $color-beigelight;

    img {
      margin-right: 0;
      margin-bottom: 28px;
    }

    .preparation__text {
      .title {
        padding-right: 25px;
        margin-bottom: 15px;
      }
    }
  }

  .preparation__link-wrapper {
    padding-top: 51px;
    width: 100%;
    padding-left: 0;


    .preparation__text {
      .title {
        padding-right: 50px;
      }

      p {
        font-size: 14px;
        line-height: 24px;
      }
    }
  }

  .preparation__item {
    padding-right: 50px;
  }
}
