.is-designer {
  background-color: $color-white-smoke;
  border-radius: 8px;
  width: 100%;
  padding: 22px 82px 22px 82px;
  margin-top: 25px;

  @include viewport--xl {
    padding-top: 61px;
    padding-bottom: 61px;
  }

  @include viewport--md {
    padding: 38px 22px 38px 38px;
  }

  @include viewport--mm {
    padding: 38px 22px 38px 22px;
  }

  &__wrapper {
    max-width: 1020px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;

    @include viewport--md {
      flex-wrap: wrap;
    }
  }

  &__right,
  &__left {
    display: flex;
    align-items: center;
  }

  &__left {
    //width: 100%;
    margin-right: 20px;
    min-width: 374px;

    @include viewport--md {
      width: 100%;
      margin-bottom: 34px;
    }

    @include viewport--mm {
      min-width: 100%;
     flex-direction: column;
    }
  }

  &__right {
    width: 100%;
    max-width: 745px;
    display: flex;
    flex-wrap: wrap;

    @include viewport--xl {
      max-width: 324px;
    }

    @include viewport--md {
      max-width: 100%;
      justify-content: space-between;
    }
  }

  &__img {
    width: 131px;
    height: 131px;
    min-width: 131px;
    min-height: 131px;
    background-color: $color-alto;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 44px;

    @include viewport--xl {
      margin-right: 31px;
    }

    @include viewport--md {
      width: 94px;
      height: 94px;
      min-width: 94px;
      min-height: 94px;
      margin-right: 28px;
    }

    img {
      object-fit: cover;
      object-position: center;
      max-width: unset;
      min-width: 100%;
      height: auto;
      min-height: 100%;
    }

    @include viewport--mm {
      margin-right: 0;
    }
  }

  &__title {
    @include reset-item;
    font-weight: 800;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -0.02em;
    color: $color-darkmain;

    @include viewport--md {
      font-size: 22px;
      line-height: 26px;
    }

    @include viewport--mm {
      text-align: center;
      margin-top: 15px;
    }
  }

  &__text {
    @include reset-item;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.02em;
    color: $color-darkmain;
    margin-right: 35px;
    max-width: 338px;

    @include viewport--xl {
      margin-bottom: 25px;
      margin-right: 0;
    }

    @include viewport--md {
      font-size: 14px;
      line-height: 22px;
      max-width: 100%;
    }
  }

  .btn--xl {
    min-width: 248px;

    @include viewport--mm {
      min-width: 100%;
    }
  }
}
