.read-more {
  .title {
    margin-bottom: 36px;
  }
}

.read-more__list {
  @include reset-list;

  margin-right: 100px;
  margin-top: 30px;
  max-width: calc(50% - 100px);

  &:nth-child(2n) {
    max-width: 50%;
    margin-right: 0;
    padding-left: 78px;
    border-left: 1px solid $color-beigelight;
  }
}

.read-more__item {
  @include reset-item;
  margin-bottom: 12px;

  a {
    @include reset-link;

    display: block;
    width: fit-content;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.02em;
    color: $color-darkgray;
    transition: $default-transition-settings;

    &:hover {
      color: $color-green;
    }
  }

  &:first-child {
    margin-bottom: 22px;

    a {
      font-weight: bold;
      font-size: 18px;
      line-height: 22px;
      color: $color-darkmain;
    }
  }
}

.read-more__list-wrapper {
  display: flex;
  flex-wrap: wrap;
}

@include viewport--lg {
  .read-more {
    .title {
      margin-bottom: 39px;
    }
  }

  .read-more__list {
    margin-right: 51px;
    max-width: calc(50% - 51px);

    &:nth-child(2n) {
      padding-left: 40px;
    }
  }

  .read-more__item {
    &:first-child {
      a {
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}

@include viewport--md {
  .read-more {
    .title {
      margin-bottom: 32px;
    }
  }

  .read-more__list-wrapper {
    flex-direction: column;
    margin-top: -31px;
  }

  .read-more__list {
    width: 100%;

    max-width: unset;

    margin: 0;
    margin-top: 31px;
    padding: 0;
    padding-bottom: 35px;
    border-bottom: 1px solid $color-beigelight;

    &:last-child {
      padding-bottom: 0;
      border: 0;
    }

    &:nth-child(2n) {
      max-width: unset;
      padding-left: 0;
      border: 0;
    }
  }

  .read-more__item {
    a {
      font-size: 13px;
      line-height: 20px;
    }

    &:first-child {
      margin-bottom: 17px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
