.cite {
  padding-right: 30px;
  display: flex;
  align-items: center;

  img {
    width: 42px;
    height: 42px;
    margin-right: 15px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
    object-fit: cover;
  }
}

.cite__about-author-wrapper {
  display: flex;
  flex-direction: column;
}

.cite__publication-date {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 12px;
  line-height: 22px;
  color: $color-mediumgray;
}

.cite__about-author {
  margin: 0;

  font-family: $font-family--Mont;
  font-weight: $font-weight--regular;
  font-size: 12px;
  line-height: 22px;
  color: $color-darkmain;
}
