.other-products {
  &--main-page {
    margin-bottom: 50px;

    @include viewport--md {
      position: relative;
      padding-bottom: 70px;
    }
  }

  &__title-wrapper {
    margin-bottom: 18px;

    @include viewport--md {
      a {
        position: absolute;
        bottom: 10px;
      }
    }
  }

  &__list {
    @include reset-list;

    width: 100%;

    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }

  &__item {
    @include reset-item;
    width: 23.65%;
    float: left;
    margin-bottom: 16px;
    height: 332px;

    @include viewport--lg {
      margin-bottom: 14px;
      height: 283px;
    }

    @include viewport--sm {
      width: 100%;
      margin-bottom: 16px;
      height: 180px;
    }

    &--height-xl {
      height: 680px;

      @include viewport--lg {
        height: 580px;
      }

      @include viewport--sm {
        height: 420px;
      }
    }

    &--width-xl {
      width: 49.1%;

      @include viewport--sm {
        width: 100%;
      }
    }

    &--height-s {
      height: 260px;

      @include viewport--lg {
        height: 222px;
      }

      @include viewport--sm {
        height: 180px;
      }
    }

    &--height-l {
      height: 404px;

      @include viewport--lg {
        height: 344px;
      }

      @include viewport--sm {
        height: 180px;
      }
    }

    &--height-xs {
      height: 234.66px;

      @include viewport--lg {
        height: 184px;
      }

      @include viewport--sm {
        height: 180px;
      }
    }
  }
}
