.warning {
  position: relative;
  padding-left: 98px;
  min-height: 68px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 1px;

    width: 72px;
    height: 67px;

    background-image: url("../img/svg/warning.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  p {
    margin: 0;

    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
  }
}

@include viewport--md {
  .warning {
    p {
      font-size: 14px;
      line-height: 22px;
    }
  }
}
