.cases-gallery {
  padding: 30px 0 73px;

  @include viewport--sm {
    padding: 15px 0 73px;
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  &__list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    margin: -30px 0 60px -39px;

    @include viewport--lg {
      margin-left: -32px;
    }

    @include viewport--md {
      margin-top: 0;
    }

    @include viewport--sm {
      margin-left: -20px;
      margin-top: -26px;
    }
  }

  &__list li {
    width: calc(100%/3 - 39px);
    margin: 44px 0 39px 38px;

    @include viewport--lg {
      width: calc(100%/3 - 39px);
      margin-left: 35px;
    }

    @include viewport--sm {
      width: 100%;
      margin-top: 35px;
      margin-left: 20px;
      margin-bottom: 19px;
    }
  }

  &__list li:nth-child(9) {
    margin-top: 76px;

    @include viewport--md {
      margin-top: 50px;
    }

    @include viewport--sm {
      margin-top: 25px;
    }
  }

  &__list li:nth-child(10) {
    margin-top: 76px;

    @include viewport--md {
      margin-top: 50px;
    }

    @include viewport--sm {
      margin-top: 25px;
    }
  }

  &__list li.max {
    min-width: 90%;
    width: 100%;
  }

  &__list li.big {
    width: calc((100%/3)*2 - 39px);

    @include viewport--sm {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__list li.half {
    min-width: calc(50% - 39px);
    width: calc(50% - 39px);

    @include viewport--sm {
      width: 100%;
      margin-bottom: 20px;
    }
  }


  &__more {
    margin: 0 auto;
    width: 427px;
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include viewport--md {
      width: 100%;
    }

    span {
      margin-top: 5px;
      margin-left: -12px;
      font-weight: 700;
      font-size: 13px;
      line-height: 22px;
      color: $color-darkmain;

    }
  }
}
