.collection {
  @include reset-link;
  width: 300px;
  display: block;
  max-width: 300px;
  cursor: pointer;
  overflow: hidden;
  -webkit-mask-image: -webkit-radial-gradient(#ffffff, #000000);
  /* stylelint-disable-line */

  img {
    transition: $default-transition-settings;
    border-radius: 4px;
    max-height: 154px;
    width: auto;
    height: 100%;
    object-fit: fill;
    object-position: center;
  }

  &--big {
    max-width: 300px;
  }

  &:hover {
    & .collection__text {
      color: $color-darkmain;
      transition: $default-transition-settings;
    }

    .collection__image-wrapper {
      img {
        transform: scale(1.05);
        opacity: 0.9;
      }
    }
  }
}

.collection__image-wrapper {
  overflow: hidden;
  -webkit-mask-image: -webkit-radial-gradient(#ffffff, #000000);
  /* stylelint-disable-line */
  position: relative;
  margin-bottom: 13px;
  border-radius: 4px;

  &--no-margin {
    margin-bottom: 0;
  }

  p {
    margin: 0;

    position: absolute;
    bottom: 12px;
    left: 15px;
    font-family: $font-family--Mont;
    font-weight: $font-weight--extrabold;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $color-default-white;

    @media (max-width: 350px) {
      font-size: 14px;
    }
  }

  &--text-center {
    p {
      top: 50%;
      left: 50%;
      right: auto;
      bottom: auto;
      transform: translate(-50%, -50%);
      text-align: center;

      @media (max-width: 350px) {
        left: 37%;
      }
    }
  }
}

.collection__text {
  margin: 0;

  font-family: $font-weight--regular;
  font-weight: $font-weight--regular;
  font-size: 11px;
  line-height: 16px;
  color: $color-darkgray;
}
