.product-card-slider {
  position: relative;
  overflow: hidden;

  .slider-button-fill-prev,
  .slider-button-fill-next {
    width: 48px;
    height: 48px;
  }

  .slider-button-fill-prev {
    left: 27px;
  }

  .slider-button-fill-next {
    right: 27px;
  }

  .card-tags {
    position: absolute;
    right: 23px;
    top: 34px;
  }

  .like {
    position: absolute;
    right: 23px;
    bottom: 25px;
  }

  ul.swiper-wrapper {
    @include reset-list;
  }
}
